import React from 'react';
import { Paper, Grid, LinearProgress } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles';
import { grey } from '@material-ui/core/colors';

export const configInput = {
    fullWidth: true,
    InputLabelProps: { shrink: true },
    required: true
}

export const configPicker = {
    autoOk: true,
    maxDateMessage: "Fecha es mayor al máximo permitido",
    minDateMessage: "Fecha es menor al mínimo permitido",
    format: "DD/MM/YYYY",
    invalidDateMessage: "Formato invalido",
}

const useStyles = makeStyles(theme => ({
    card: {
        backgroundColor: grey[100],
        height: '100%'
    },
    container: {
        padding: 24,
        height: '100%'
    }
}))



export function StructReport({ children, loading, ...props }) {
    const classes = useStyles();
    return (
        <Paper {...props} className={classes.card} elevation={0}>
            {
                loading &&
                <LinearProgress color="secondary" />
            }
            <Grid container className={classes.container} spacing={2}>
                {[...children]}
            </Grid>
        </Paper>
    )
}