import React from 'react'
import MaterialTable from 'material-table';
import {
    TextField, Typography, Grid, Checkbox, FormControlLabel
} from '@material-ui/core'
import RequestFetch from '../../../libraries/RequestFetch'
import { Snackbar } from '@material-ui/core';
import { AlertTitle } from '@material-ui/lab';
import MuiAlert from '@material-ui/lab/Alert';
import { useHistory } from 'react-router-dom';
import { localization, options } from '../../../libraries/ConfigCatalog';
import { withStyles } from '@material-ui/core/styles';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import { green, red } from '@material-ui/core/colors';
import { SessionContext } from '../../../routes/adminrh/CustomRoutes';
import RemoveCircleOutlinedIcon from '@material-ui/icons/RemoveCircleOutlined';
import LockRoundedIcon from '@material-ui/icons/LockRounded';
import DialogUser from './ResetPassword';

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const GreenCheckbox = withStyles({
    root: {
        color: red[600],
        '&$checked': {
            color: green[600],
        },
    },
    checked: {},
})((props) => <Checkbox style={{ padding: 5 }} color="default" {...props} />);


export default function Usuario({ type }) {

    const [password, setPassword] = React.useState({ open: false, data: {} });
    const { cabeceras, isTokenExpired } = React.useContext(SessionContext);
    const history = useHistory();
    const [data, setData] = React.useState([]);
    const [loading, setLoading] = React.useState(false);
    const [toast, setToast] = React.useState({
        open: false,
        message: "",
        severity: "",
        title: ""
    });


    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            setToast({ ...toast, open: false });
            return;
        }
        setToast({ ...toast, open: false });
    };

    const getData = (type) => {
        let mounted = true;
        setLoading(true);
        setData([]);

        const _exito = async (res) => {
            const respuesta = await res.json();
            console.log(respuesta);
            if (mounted) {
                setData(respuesta.usuarios.map(e => {
                    return { ...e, rfc: e.rfc.toUpperCase() }
                }));
                setLoading(false);
                if (respuesta.usuarios.length === 0) {
                    setToast({
                        open: true,
                        message: "No existen registros de empresas",
                        severity: "info",
                        title: "Ningun registro encontrado"
                    })
                }
            };
        }

        const _fallo = async (res) => {
            const response = await res.json();
            if (!isTokenExpired(response))
                if (mounted) {
                    setLoading(false);
                    setToast({
                        open: true,
                        message: "Ha ocurrido un error, intenta nuevamente",
                        severity: "error",
                        title: "Uuuups!"
                    })

                };
        }

        if (mounted) {
            RequestFetch.get({
                url: `${RequestFetch.BASE_URL}/api/rh/usuario?rol=${type}`,
                headers: cabeceras,
                exito: _exito,
                fallo: _fallo,
                error_request: () => {
                    history.push(`/server-error`);
                }
            });
        }

        return () => mounted = false;
        // eslint-disable-next-line
    }

    const columns = [
        { title: 'ID', field: "id_usuario", editable: 'never', hidden: true },
        {
            title: 'RFC', field: 'rfc', defaultSort: 'asc',
            editComponent: props => (
                <TextFielTable placeholder="Ej. Contrucción" {...props} />
            )
        },
        {
            title: 'Correo', field: 'correo',
            editComponent: props => (
                <TextFielTable placeholder="Ej. Contrucción" {...props} />
            )
        },
        {
            title: 'Activo', field: 'estatus', width: 60,
            render: ({ estatus }) => {
                return <Grid container direction="row" justify="center" alignItems="center">

                    <GreenCheckbox
                        icon={<RemoveCircleOutlinedIcon />} checkedIcon={<CheckCircleIcon />}
                        checked={estatus}
                    />
                </Grid>
            },
            editComponent: props => (
                <Grid container direction="row" justify="center" alignItems="center">
                    <FormControlLabel
                        style={{ marginLeft: 0, marginRight: 0 }}
                        control={
                            <GreenCheckbox
                                icon={<RemoveCircleOutlinedIcon />} checkedIcon={<CheckCircleIcon />}
                                checked={props.value === undefined ? true : props.value}
                                onChange={e => props.onChange(props.value === undefined ? true : !props.value)}
                                name="status"
                            />
                        }
                    />
                </Grid>
            )
        }
    ];


    React.useEffect(() => {
        getData(type);
        // eslint-disable-next-line
    }, [type])


    const getUserType = (type) => {
        switch (type) {
            case 'S':
                return 'Administrador'
            case 'G':
                return 'Internos'
            case 'E':
                return 'Externos'
            case 'C':
                return 'Representantes'
            default:
                break;
        }
    }

    const getTipo = (type) => {
        switch (type) {
            case 'S':
                return 'S'
            case 'G': case 'E':
                return 'E'
            case 'C':
                return 'R'
            default:
                break;
        }
    }


    const POST = (usuario) => {
        usuario['es_default'] = true;
        usuario['rol'] = type;
        usuario['tipo'] = getTipo(type);
        let mounted = true;
        const exito = async res => {
            const respuesta = await res.json();
            if (mounted) {
                usuario.id_usuario = respuesta.usuario[0].id_usuario;
                const newData = data;
                newData.push(usuario)
                setData(newData);
                setLoading(false);
                setToast({
                    open: true,
                    message: `Correo: ${respuesta.user.email} Contraseña: ${respuesta.user.password}`,
                    severity: "success",
                    title: "Registro exitoso!"
                });
            }
        }
        const fallo = async res => {
            const response = await res.json();
            if (!isTokenExpired(response))
                if (mounted) {
                    setLoading(false);
                    setToast({
                        open: true,
                        message: `El registro no se ha realizado, intenta de nuevo`,
                        severity: "error",
                        title: "Registro fallido!"
                    });
                }
        }

        const send = () => {
            RequestFetch.post({
                url: `${RequestFetch.BASE_URL}/api/rh/usuario`,
                headers: cabeceras,
                data: usuario,
                exito: exito,
                fallo: fallo,
                error_request: (error) => {
                    history.push(`/server-error`);
                }
            });
        }

        if (mounted) {
            setLoading(true);
            send();
        }

        return () => mounted = false;
    }

    const PUT = (usuario, oldusuario) => {
        let mounted = true;
        const exito = async res => {
            const response = await res.json();
            if (!isTokenExpired(response))
                if (mounted) {
                    const newData = data;
                    const index = newData.indexOf(oldusuario);
                    data[index] = usuario;
                    setData(newData);
                    setLoading(false);

                    setToast({
                        open: true,
                        message: `Usuario: ${usuario.rfc.toUpperCase()} se ha actualizado correctamente`,
                        severity: "success",
                        title: "Actualización exitoso!"
                    });
                }
        }
        const fallo = async res => {
            const respuesta = await res.json();
            console.log(respuesta);
            if (!isTokenExpired(respuesta))
                if (mounted) {
                    setLoading(false);
                    setToast({
                        open: true,
                        message: `usuario: ${usuario.rfc.toUpperCase()} no ha sido actualizado, intenta de nuevo`,
                        severity: "error",
                        title: "Actualización fallida!"
                    });
                }
        }

        const send = () => {
            const putData = {
                rfc: usuario.rfc,
                correo: usuario.correo,
                estatus: usuario.estatus
            }
            RequestFetch.put({
                url: `${RequestFetch.BASE_URL}/api/rh/usuario/${usuario.id_usuario}`,
                headers: cabeceras,
                data: putData,
                exito: exito,
                fallo: fallo,
                error_request: (error) => {
                    history.push(`/server-error`);
                }
            });
        }

        if (mounted) {
            setLoading(true);
            send();
        }
    }

    const DELETE = (usuario) => {
        let mounted = true;
        const exito = async res => {
            const response = await res.json();
            if (!isTokenExpired(response))
                if (mounted) {
                    let newData = data;
                    const index = newData.indexOf(usuario);
                    data.splice(index, 1);
                    setData(newData);
                    setLoading(false);
                    setToast({
                        open: true,
                        message: `Usuario: ${usuario.rfc.toUpperCase()} se ha eliminado correctamente`,
                        severity: "success",
                        title: "Eliminación exitosa!"
                    });
                }

        }
        const fallo = async res => {
            await res.json();
            if (mounted) {
                setLoading(false);
                setToast({
                    open: true,
                    message: `usuario: ${usuario.nombre_usuario.toUpperCase()} tiene registros que dependen de este, primero debes eliminarlos`,
                    severity: "error",
                    title: "Eliminación fallida!"
                });
            }
        }

        const send = () => {
            RequestFetch.deleted({
                url: `${RequestFetch.BASE_URL}/api/rh/usuario/${usuario.id_usuario}`,
                headers: cabeceras,
                exito: exito,
                fallo: fallo,
                error_request: () => {
                    history.push(`/server-error`);
                }
            });
        }

        if (mounted) {
            setLoading(true);
            send();
        }
    }

    const actions = [
        {
            icon: 'refresh',
            tooltip: 'Actualizar',
            isFreeAction: true,
            onClick: () => { getData(type) }
        },
        {
            icon: () => <LockRoundedIcon />,
            tooltip: 'Reestablecer Contraseña',
            onClick: (event, props) => setPassword({ open: true, data: props })
        }

    ];

    localization.body.editRow.deleteText = <Typography variant="button" gutterBottom>¿Está seguro que desea continuar?</Typography>


    return (
        <>
            <MaterialTable
                title={"Usuarios " + getUserType(type)}
                columns={columns}
                localization={localization}
                data={data}
                actions={actions}
                options={options}
                isLoading={loading}
                editable={{
                    onRowAdd: (newData) =>
                        new Promise((resolve, reject) => {
                            setTimeout(() => {
                                if (newData.rfc !== '' && newData.correo !== '') {
                                    POST({ ...newData, estatus: true });
                                    resolve();
                                }
                                else {
                                    setToast({
                                        open: true,
                                        message: "Rellena los campos \"RFC\" y \"Correo\"",
                                        severity: "warning",
                                        title: "Campos vacios!"
                                    })
                                    reject();
                                    return;
                                }
                            }, 600);
                        })
                    ,
                    onRowUpdate: (newData, oldData) =>
                        new Promise((resolve, reject) => {
                            setTimeout(() => {
                                if (newData.rfc !== '' && newData.correo !== '') {
                                    resolve();
                                    PUT(newData, oldData);
                                } else {
                                    setToast({
                                        open: true,
                                        message: "Rellena los campos \"RFC\" y \"Correo\"",
                                        severity: "warning",
                                        title: "Campos vacios!"
                                    })
                                    reject();
                                    return;
                                }
                            }, 600);
                        })
                    ,
                    onRowDelete: (oldData) =>
                        new Promise((resolve) => {
                            setTimeout(() => {
                                resolve();
                                DELETE(oldData);
                            }, 600);
                        })
                    ,
                }}
            />


            <Snackbar open={toast.open} onClose={handleClose}>
                <Alert onClose={handleClose} severity={toast.severity}>
                    <AlertTitle>{toast.title}</AlertTitle>
                    {toast.message}
                </Alert>
            </Snackbar>

            <DialogUser {...password} onClose={() => setPassword({ ...password, open: false })} notification={setToast} />

        </>
    );
}


const TextFielTable = ({ columnDef, rowData, onRowDataChange, ...props }) => {
    return <TextField
        {...props}
        fullWidth
        size="small"
        value={props.value || ''}
        onChange={e => props.onChange(e.target.value)}
    />
}

