import React from 'react'
import { Snackbar, Grid, Paper, Typography, Badge, CardHeader, Card } from '@material-ui/core';
import { AlertTitle, Skeleton } from '@material-ui/lab';
import MuiAlert from '@material-ui/lab/Alert';
import RequestFetch from '../../../libraries/RequestFetch'
import InputFile from './InputFile';
import { makeStyles } from '@material-ui/core/styles';
import SeeDocument from './SeeDocument'
import PropTypes from 'prop-types';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import { green } from '@material-ui/core/colors';
import Box from '@material-ui/core/Box';
import SentimentVeryDissatisfiedIcon from '@material-ui/icons/SentimentVeryDissatisfied';
import CheckCircleOutlineIcon from '@material-ui/icons/CheckCircleOutline';
import { withStyles } from '@material-ui/core/styles';
import { useHistory } from 'react-router-dom';
import { SessionContext } from '../../../routes/adminrh/CustomRoutes';

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={0}>
                    {children}
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

function a11yProps(index) {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`,
    };
}

const StyledBadge = withStyles((theme) => ({
    badge: {
        top: 5,
        right: -15,
        transform: 'scale(1) translate(50%, -50%)',
        transformOrigin: '100% 0%',
    },
}))(Badge);


const useStyles = makeStyles((theme) => ({
    root: {
        backgroundColor: theme.palette.background.paper,
        minHeight: '145px'
    },
    paper: {
        padding: theme.spacing(3)
    },
    frame: {
        width: '100%',
        height: '600px',
        border: 'none'
    },
    doc: {
        marginTop: '24px'
    },
    media: {
        height: 100,
        width: 100
    },
    iconSuccess: {
        color: green[500],
        fontSize: 100
    },
    iconError: {
        color: "#D0D0D0",
        fontSize: 100
    },
    pendientes: {
        padding: '16px'
    },
    title: {
        textAlign: 'center',
        paddingTop: '15px'
    }
}));

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

export default function Documentos(props) {
    const { cabeceras, isTokenExpired } = React.useContext(SessionContext);
    const classes = useStyles();
    const history = useHistory();
    const { id, tipo, onlySee } = props;
    const [value, setValue] = React.useState(0);
    const [tipoDoc, setTipoDoc] = React.useState(null);
    const [docEmple, setDocEmple] = React.useState(null);


    const [data, setData] = React.useState(null);
    const [dataEmp, setDataEmp] = React.useState(null);

    const [backdrop, setBackdrop] = React.useState(false);
    const [toast, setToast] = React.useState({
        open: false,
        message: "",
        severity: "",
        title: ""
    });
    //const [showPDF, setShowPDF] = React.useState(null);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    const getData = React.useCallback(() => {
        let mounted = true;
        const exitoAll = async res => {
            const respuesta = await res.json();
            if (mounted) setTipoDoc(respuesta.tipo_documento);
        }

        const exitoEmple = async res => {
            const respuesta = await res.json();
            if (Array.isArray(respuesta.documentos)) {
                const docs = respuesta.documentos;
                docs.forEach(item => { item['textShow'] = "Cargando..."; })
                if (mounted) setDocEmple(docs);
            }
        }

        const fallo = async res => {
            const response = await res.json();
            if (!isTokenExpired(response)) {
                setBackdrop(false);
                setToast({
                    open: true,
                    message: "Ha ocurrido un error, intenta nuevamente",
                    severity: "error",
                    title: "Uuups!"
                })
            }
        }

        const getAllDocs = () => {
            RequestFetch.get({
                url: `${RequestFetch.BASE_URL}/api/rh/tipo_documento`,
                headers: cabeceras,
                exito: exitoAll,
                fallo: fallo,
                error_request: (error) => { history.push('/server-error') }
            });
        }

        const getDocsEmple = () => {
            RequestFetch.get({
                url: `${RequestFetch.BASE_URL}/api/rh/${tipo}/documento/${id}`,
                headers: cabeceras,
                exito: exitoEmple,
                fallo: fallo,
                error_request: (error) => { history.push('/server-error') }
            });
        }

        if (mounted) {
            setBackdrop(true);
            getDocsEmple();
            getAllDocs();
        };

        return () => mounted = false;
        // eslint-disable-next-line
    }, [])

    React.useEffect(() => {
        getData();
    }, [getData]);

    React.useEffect(() => {
        let mounted = true;
        if (Array.isArray(tipoDoc) && Array.isArray(docEmple)) {
            const newDocEmple = docEmple;
            newDocEmple.forEach(item => {
                item['textShow'] = tipoDoc.find(element => element.id_tipo_documento === item.id_tipo_documento).tipo;
            });
            const difference = tipoDoc.filter(item => !docEmple.some(element => element.id_tipo_documento === item.id_tipo_documento)).filter(i => i[tipo] === true);
            if (mounted) {
                setBackdrop(false);
                if (difference.length > 0 && !onlySee) setValue(1);
                setDataEmp(newDocEmple);
                setData(difference);
            }
        }
        return () => mounted = false;
        // eslint-disable-next-line
    }, [tipoDoc, docEmple])

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            setToast({ ...toast, open: false });
            return;
        }
        setToast({ ...toast, open: false });
    };


    const deleteInput = (element) => {
        const newData = data.filter(item => item.id_tipo_documento !== element.id_tipo_documento);
        setData(newData);

        const newDataEmp = dataEmp;
        newDataEmp.push(element);
        setDataEmp(newDataEmp);
    };

    const deleteSee = (index, id_tipo_documento) => {
        const newData = dataEmp.filter(item => item.id_tipo_documento !== id_tipo_documento);
        setDataEmp(newData);

        const add = tipoDoc.find(element => element.id_tipo_documento === id_tipo_documento);
        const newTipoDoc = data;
        newTipoDoc.push(add);
        setData(newTipoDoc);
    }

    const openToast = (object) => {
        setToast(object);
    }

    /*const changeSeePdf = (url) => {
        setShowPDF(url);
    }*/

    const documentosEmpleado = () => {
        if (Array.isArray(dataEmp)) {
            if (dataEmp.length > 0) {
                return dataEmp.map((item, index) => {
                    return (
                        <Grid item xs={12} key={`see-document-${index}`}>
                            <SeeDocument index={index} data={item} id={id} tipo={tipo} toast={openToast} delete={deleteSee} backdrop={setBackdrop} />
                        </Grid>
                    )
                })
            }
            else {
                return <Grid
                    className={classes.pendientes}
                    container
                    direction="column"
                    justify="center"
                    alignItems="center"
                >
                    <Grid item>
                        <SentimentVeryDissatisfiedIcon className={classes.iconError} />
                    </Grid>
                    <Grid item>
                        <Typography variant="h6" gutterBottom>
                            Documentos vacios!
                    </Typography>
                    </Grid>
                    <Grid item>
                        <Typography variant="body1" gutterBottom>
                            Ningún documento ha sido guadado aún.
                    </Typography>
                    </Grid>
                </Grid>
            }
        }
    }

    const documentosGenerales = () => {
        if (Array.isArray(data)) {
            if (data.length > 0) {
                return data.map((item, index) => {
                    return (
                        <Grid item xs={12} key={`input-file-${index}`}>
                            <InputFile tipo={tipo} id={id} data={item} delete={deleteInput} index={index} toast={openToast} backdrop={setBackdrop} />
                        </Grid>
                    )
                })
            }
            else {
                return <Grid
                    className={classes.pendientes}
                    container
                    direction="column"
                    justify="center"
                    alignItems="center"
                >
                    <Grid item>
                        <CheckCircleOutlineIcon className={classes.iconSuccess} />
                    </Grid>
                    <Grid item>
                        <Typography variant="h6" gutterBottom>
                            Sin Pendientes!
                        </Typography>
                    </Grid>
                    <Grid item>
                        <Typography variant="body1" gutterBottom>
                            Todos los documentos ya fueron completados
                        </Typography>
                    </Grid>
                </Grid>
            }
        }
    }

    const skeleton = () => {
        return <Card elevation={0}>
            <CardHeader
                avatar={<Skeleton animation="wave" variant="rect" width={50} height={50} />}
                title={<Skeleton animation="wave" height={10} width="80%" style={{ marginBottom: 6 }} />}
                subheader={<Skeleton animation="wave" height={10} width="40%" />}
            />
        </Card>
    }

    return (
        <>
            <Paper className={classes.root} elevation={2}>
                <Typography className={classes.title} variant="button" display="block" gutterBottom>
                    Documentos {`${tipo}`}
                </Typography>
                <Tabs
                    value={value}
                    onChange={handleChange}
                    aria-label="simple tabs example"
                    indicatorColor="primary"
                    centered
                    textColor="primary">
                    <Tab label={
                        <StyledBadge color="primary"
                            badgeContent={dataEmp !== null ? dataEmp.length : '0'}>
                            Guardados
                                </StyledBadge>}
                        {...a11yProps(0)} />
                    {!onlySee &&
                        <Tab label={
                            <StyledBadge color="secondary"
                                badgeContent={data !== null ? data.length : '0'}>
                                Faltantes
                                </StyledBadge>} {...a11yProps(1)} />}
                </Tabs>
                <TabPanel value={value} index={0}>
                    {!backdrop ? documentosEmpleado() : skeleton()}
                </TabPanel>
                <TabPanel value={value} index={1}>
                    {!backdrop ? documentosGenerales() : skeleton()}
                </TabPanel>
            </Paper>
            <Snackbar open={toast.open} autoHideDuration={6000} onClose={handleClose}>
                <Alert onClose={handleClose} severity={toast.severity}>
                    <AlertTitle>{toast.title}</AlertTitle>
                    {toast.message}
                </Alert>
            </Snackbar>
        </>
    );
}


