import React, { useState, useContext } from 'react';
import { Grid, TextField, MenuItem, Button, Typography } from '@material-ui/core';
import { configInput, StructReport } from '../config';
import { BASE_URL } from '../../../../libraries/RequestFetch';
import { EstadoByCode } from '../../../../libraries/Estados';
import { useHistory } from 'react-router-dom';

import { ReportContext } from '../';

export function EmpleadoReporte1({ cabeceras, isTokenExpired, ...other }) {
    const [loading, setLoading] = useState(false);
    const { setReport } = useContext(ReportContext);
    const onGetData = async () => {
        setLoading(true);
        const response = await fetch(`${BASE_URL}/api/rh/empleado/reporte/entidades`, { method: 'GET', headers: cabeceras });
        const resp = await response.json();
        if (!isTokenExpired(resp)) {
            const { empleados } = resp;
            const labels = empleados.map(e => EstadoByCode(e.entidad));
            const data = empleados.map(e => e.cantidad);
            setReport({
                show: true,
                type: 'horizontalbar',
                title: 'Cantidad de empleados por estado de la republica.',
                message: 'A continuación se mostraran la cantidad de empleados en cada estado de la republica con al menos 1 empleado',
                data,
                labels,
                label: 'Empleados',
            })
        }
    }
    return (
        <StructReport loading={loading}>
            <Grid item xs={12} >
                <Typography variant="subtitle1" color="initial">
                    Cantidad de empleados por estado de la republica.
                </Typography>
                <Typography variant="subtitle2" color="initial">
                    (Solo se mostraran los estados con al menos 1 empleado).
                </Typography>
            </Grid>
            <Grid item xs={12} >
                <Grid container
                    style={{ height: '100%' }}
                    direction="column"
                    justify="flex-end"
                    alignItems="flex-end">
                    <Button color="secondary" disabled={loading} onClick={onGetData} >GENERAR REPORTE</Button>
                </Grid>
            </Grid>
        </StructReport>
    )
}

export function EmpleadoReporte2({ empresas, cabeceras, isTokenExpired }) {
    const { setReport } = useContext(ReportContext);
    const [loading, setLoading] = useState(false);
    const history = useHistory();

    const onGetData = async () => {
        setLoading(true);
        const response = await fetch(`${BASE_URL}/api/rh/empleado/reporte/inasistencias`, { method: 'GET', headers: cabeceras });
        const resp = await response.json();
        if (!isTokenExpired(resp)) {
            const { empleados } = resp;
            const labels = [
                { title: 'Nombre', id: 'nombre', hidden: false, align: 'left' },
                { title: 'Pertenece', id: 'pertenece', hidden: false, align: 'left' },
                { title: 'Labora', id: 'labora', hidden: false, align: 'left' },
                { title: 'Faltas', id: 'faltas', hidden: false, align: 'right' },
                { title: 'Acción', id: 'id_empleado', hidden: false, align: 'left' },
            ]
            const data = empleados.map(e => {
                return {
                    nombre: `${e.nombre} ${e.primer_apellido}`,
                    pertenece: empresas.find(i => i.id_empresa === e.id_empresa_pertenece).razon_social,
                    labora: empresas.find(i => i.id_empresa === e.id_empresa_labora).razon_social,
                    faltas: e.faltas,
                    id_empleado: <Button color="secondary" onClick={() => history.push(`/nomina/edit-empleado/${e.id_empleado}`)}>Ver</Button>
                }
            });
            setReport({
                show: true,
                type: 'table',
                title: 'Empleados que con un mayor numero de inasistencia.',
                message: 'A continuación se muestran los empleados que tiene un mayor numero de inasistencias.',
                data,
                labels,
                label: 'Empleados',
            })
        }
    }

    return (
        <StructReport loading={loading}>
            <Grid item xs={12}>
                <Typography variant="subtitle1" color="initial">
                    Empleados que con un mayor numero de inasistencia
                </Typography>
                <Typography variant="subtitle2" color="initial">
                    (Solo se muestran los empleados que hayan tenido registro de asistencias en alguna nómina).
                </Typography>
            </Grid>
            <Grid item xs={12} >
                <Grid container
                    style={{ height: '100%' }}
                    direction="column"
                    justify="flex-end"
                    alignItems="flex-end">
                    <Button color="secondary" disabled={loading} onClick={onGetData}>GENERAR REPORTE</Button>
                </Grid>
            </Grid>
        </StructReport>
    );
}

export function EmpleadoReporte3({ empresas, cabeceras, isTokenExpired }) {
    const { setReport } = useContext(ReportContext);
    const [loading, setLoading] = useState(false);
    const [empresa, setEmpresa] = useState({ pertenece: 'TODAS', labora: 'TODAS' });

    const onGetData = async () => {
        setLoading(true);
        let url = `${BASE_URL}/api/rh/empleado/reporte/categoria_imss`;
        if (empresa.pertenece !== 'TODAS' || empresa.labora !== 'TODAS') url += '?';
        if (empresa.pertenece !== 'TODAS') url += `&id_empresa_pertene=${empresa.pertenece}`;
        if (empresa.labora !== 'TODAS') url += `&id_empresa_labora=${empresa.labora}`;

        const response = await fetch(url, { method: 'GET', headers: cabeceras });
        const resp = await response.json();
        if (!isTokenExpired(resp)) {
            const { empleados } = resp;
            const labels = empleados.map(e => `CLASE ${e.categoria}`);
            const data = empleados.map(e => e.count);
            setReport({
                show: true,
                type: 'horizontalbar',
                title: 'Cantidad de empleados registrados determinada clase de IMSS.',
                message: 'Solo se mostrarán las clases con almenos un empleado registrado',
                data,
                labels,
                label: 'Empleados',
            })
        }
    }

    return (
        <StructReport loading={loading}>
            <Grid item xs={12}>
                <Typography variant="subtitle1" color="initial">
                    Cantidad de empleados registrados determinada clase de IMSS
                </Typography>
            </Grid>
            <Grid item xs={6}>
                <TextField {...configInput} select
                    value={empresa.pertenece}
                    defaultValue="TODAS" label="Empresa Pertenece"
                    onChange={({ target: { value } }) => setEmpresa({ ...empresa, pertenece: value })}>
                    <MenuItem value={"TODAS"} >TODAS</MenuItem>
                    {
                        empresas
                            .filter(e => e.es_interna)
                            .map(e => <MenuItem
                                key={`empleados-31-empresa-pertenece-${e.id_empresa}`}
                                value={e.id_empresa}>
                                {e.razon_social}
                            </MenuItem>)
                    }
                </TextField>
            </Grid>
            <Grid item xs={6}>
                <TextField {...configInput} select
                    value={empresa.labora}
                    defaultValue="TODAS" label="Empresa Labora"
                    onChange={({ target: { value } }) => setEmpresa({ ...empresa, labora: value })}>
                    <MenuItem value={"TODAS"} >TODAS</MenuItem>
                    {
                        empresas
                            .map(e => <MenuItem
                                key={`empleados-32-empresa-pertenece-${e.id_empresa}`}
                                value={e.id_empresa}>
                                {e.razon_social}
                            </MenuItem>)
                    }
                </TextField>
            </Grid>
            <Grid item xs={12}>
                <Grid container
                    style={{ height: '100%' }}
                    direction="column"
                    justify="flex-end"
                    alignItems="flex-end">
                    <Button color="secondary" onClick={onGetData}>GENERAR REPORTE</Button>
                </Grid>
            </Grid>
        </StructReport>
    )
}

export function EmpleadoReporte4({ cabeceras, isTokenExpired }) {
    const { setReport } = useContext(ReportContext);
    const [loading, setLoading] = useState(false);

    const onGetData = async () => {
        setLoading(true);
        const response = await fetch(`${BASE_URL}/api/rh/empleado/reporte/edades`, {
            method: 'GET',
            headers: cabeceras
        });
        const resp = await response.json();
        if (!isTokenExpired(resp)) {
            const { empleados } = resp;
            const keys = Object.keys(empleados[0])
            const labels = keys.map(key => key.replace(/_/g, '-'));
            const data = keys.map(key => empleados[0][key]);
            setReport({
                show: true,
                type: 'horizontalbar',
                title: 'Cantidad de empleados distribuidos por edad.',
                message: 'A continuación se mostraran la cantidad de empleados en distribuidos por edad',
                data,
                labels,
                label: 'Empleados',
            })
        }

    }
    return (
        <StructReport loading={loading}>
            <Grid item xs={12}>
                <Typography variant="subtitle1" color="initial">
                    Cantidad de empleados distribuidos por edad
                </Typography>
                <Typography variant="subtitle2" color="initial">
                    Los rangos de edades van de menor a 20, 20 a 29, 30 a 39, 40 a 49, 50 a 59, 60 en adelante
                </Typography>
            </Grid>
            <Grid item xs={12}>
                <Grid container
                    style={{ height: '100%' }}
                    direction="column"
                    justify="flex-end"
                    alignItems="flex-end">
                    <Button color="secondary" onClick={onGetData}>GENERAR REPORTE</Button>
                </Grid>
            </Grid>
        </StructReport>
    )
}

export default function ReportesEmpleados(props) {

    const xs = 12;
    const sm = 6
    const md = 4;
    return (
        <Grid spacing={2}
            container
            direction="row"
            justify="center"
            alignItems="stretch"
        >
            <Grid item xs={xs} sm={sm} md={md} >
                <EmpleadoReporte1 {...props} />
            </Grid>
            <Grid item xs={xs} sm={sm} md={md}>
                <EmpleadoReporte2 {...props} />
            </Grid>
            <Grid item xs={xs} sm={sm} md={md}>
                <EmpleadoReporte3 {...props} />
            </Grid>
            <Grid item xs={xs} sm={sm} md={md}>
                <EmpleadoReporte4 {...props} />
            </Grid>
        </Grid>


    )
}