import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent';
import InfoIcon from '@material-ui/icons/Info';
import RequestFetch from '../../../libraries/RequestFetch';
import MuiAlert from '@material-ui/lab/Alert';
import { green } from '@material-ui/core/colors';
import AlertTitle from '@material-ui/lab/AlertTitle';
import { Snackbar, Button, IconButton, ListSubheader, Typography, CardActions, Grid, CircularProgress } from '@material-ui/core';
import { useParams } from 'react-router-dom';
import Dialog from '@material-ui/core/Dialog';
import DeleteIcon from '@material-ui/icons/Delete';
import { Save as SaveIcon } from '@material-ui/icons'
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import Documentos from '../../../components/adminrh/ExtraComponents/Documentos'
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import { useHistory } from 'react-router-dom';
import Representante from '../FormEmpresa/Representante/Representante';
import Backdrop from '@material-ui/core/Backdrop';
import { withSnackbar } from 'notistack';
import ReplayIcon from '@material-ui/icons/Replay';
import { SessionContext } from '../../../routes/adminrh/CustomRoutes';

const useStyles = makeStyles(theme => ({
    root: {
        width: '100%',
        marginBottom: 24,
        backgroundColor: theme.palette.background.paper,
    },
    cardContent: {
        padding: '0px'
    },
    header: {
        background: '#fafafa'
    },
    container: {
        padding: theme.spacing(4),
    },
    uppercase: {
        textTransform: 'uppercase'
    },
    save: {
        color: theme.palette.common.white,
        backgroundColor: green[500],
        '&:hover': {
            backgroundColor: green[600],
        },
    },
    buttonProgress: {
        color: green[500],
        position: 'absolute',
        top: '50%',
        left: '50%',
        marginTop: -12,
        marginLeft: -12,
    },
    buttonDeleting: {
        position: 'absolute',
        top: '50%',
        left: '50%',
        marginTop: -12,
        marginLeft: -12,
    },
    wrapper: {
        margin: theme.spacing(1),
        position: 'relative',
    },
    title: {
        paddingRight: 42,
        textAlign: 'center',
        margin: '15px auto'
    },
    subheader: {
        textAlign: 'center'
    },
    delete: {
        marginRight: theme.spacing(1)
    },
    back: {
        left: -12,
        float: 'left'
    },
    backdrop: {
        zIndex: theme.zIndex.drawer + 1,
        color: "#fafafa",
    },
    butonHide: {
        opacity: 0,
    }
}));

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const initRepre = {
    existe: { value: false },
    id_representante: { value: '', verify: '' },
    rfc: { value: '', verify: '' },
    nombre: { value: '', verify: '' },
    primer_apellido: { value: '', verify: '' },
    segundo_apellido: { value: '', verify: '' },
    genero: { value: '', verify: '' },
    observaciones: { value: '', verify: '' },
    correo: { value: '', verify: '' },
}

const initTel = [
    { value: '', verify: '', tipo: 'F', helperText: '' },
    { value: '', verify: '', tipo: 'C', helperText: '' },
]

function EditRepresentante(props) {
    const { cabeceras, isTokenExpired } = React.useContext(SessionContext);
    const classes = useStyles();
    const history = useHistory();
    const params = useParams();
    const id = params.id === undefined ? props.id : params.id;
    const { onlySee } = props;

    const [dialog, setDialog] = React.useState({ open: false, type: '' });
    const [back, setBack] = React.useState(true);
    const [loading, setLoading] = React.useState(false);
    const [deleting, setDeleting] = React.useState(false);

    const [oldTel, setOldTel] = React.useState([]);
    const [oldRepre, setOldRepre] = React.useState({});

    const [repre, setRepre] = React.useState(initRepre);
    const [tele, setTele] = React.useState(initTel);


    const [toast, setToast] = React.useState({
        open: false,
        message: "",
        severity: "",
        title: ""
    });

    const timer = React.useRef();

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            setToast({ ...toast, open: false });
            return;
        }
        setToast({ ...toast, open: false });
    };

    const send = () => {

        const _telefonoRepre = () => {
            const newTel = [];
            tele.forEach(item => {
                const value = item.value.replace(/\s/g, '');
                if (value.length >= 5)
                    newTel.push({ numero: value, tipo: item.tipo })
            })
            if (newTel.length > 0)
                return {
                    id_representante: id,
                    telefonos: newTel
                }
            else return null;
        }

        const _separeRepresentante = () => {
            const newData = {
                nombre: repre.nombre.value,
                primer_apellido: repre.primer_apellido.value,
                segundo_apellido: repre.segundo_apellido.value,
                genero: repre.genero.value,
                correo: repre.correo.value,
                observaciones: repre.observaciones.value
            };

            const object = {};

            const keys = Object.keys(newData);
            keys.forEach(key => {
                if (oldRepre[key] !== newData[key])
                    object[key] = newData[key];
            })
            return object;
        }

        const _exito = async (res) => {
            const respuesta = await res.json();
            const { updated } = respuesta;
            if (updated) {
                props.enqueueSnackbar("Actualizado", { variant: 'success', preventDuplicate: true, });
            } else {
                setToast({
                    open: true,
                    message: `La RFC ${repre.rfc.value} ya se encuentra registrado`,
                    severity: "error",
                    title: "Registro fallido!"
                });
            }
        }

        const _fallo = async (res) => {
            const respuesta = await res.json();
            if (!isTokenExpired(respuesta))
                if (respuesta.error.detail.includes("rfc")) {
                    setToast({
                        open: true,
                        message: `La RFC ${repre.rfc.value} ya se encuentra registrado`,
                        severity: "error",
                        title: "Registro fallido!"
                    });
                }
        }

        const _ValidateRepre = () => {
            var keysRepre = ['rfc', 'nombre', 'primer_apellido', 'genero', 'correo']
            var newDataRepre = { ...repre };
            keysRepre.forEach(key => {
                if (key === 'rfc')
                    newDataRepre[`${key}`] = { value: repre[key].value, verify: `${repre[key].value}`.replace(/\s/g, '').length === 13 && repre[key].verify }
                else
                    newDataRepre[`${key}`] = { value: repre[key].value, verify: `${repre[key].value}`.length > 0 && repre[key].verify }
            });
            setRepre(newDataRepre);
            const resulRepre = keysRepre.filter(key => {
                return newDataRepre[key].verify !== true;
            });

            return resulRepre.length === 0;
        }

        const _ValidateTel = () => {
            const newData = [...tele];
            const oks = [];
            newData.forEach((element) => {
                const correo = element.value.replace(/\s/g, '');
                if (correo.length === 10) {
                    const verify = element.verify;
                    element.verify = verify;
                    oks.push(verify);
                }
            })
            const resul = oks.filter(item => {
                return item !== true;
            })
            if (oks.length === 0) {
                newData[0].verify = false;
                newData[0].helperText = "Registra al menos uno";
                newData[1].verify = false;
                newData[1].helperText = "Registra al menos uno";
                return false;
            }
            setTele(newData);
            return resul.length === 0;
        }

        const agregarTele = (object) => {
            RequestFetch.post({
                url: `${RequestFetch.BASE_URL}/api/rh/representante/telefono`,
                headers: cabeceras,
                data: object,
                exito: async (res) => {
                    console.log(await res.json())
                    props.enqueueSnackbar("Telefonos actualizados", { variant: 'success', preventDuplicate: true, });
                },
                fallo: _fallo,
                error_request: () => {
                    history.push(`/server-error`);
                }
            });
        }

        const actualizarTel = (actual, olds) => {
            console.log(actual, olds);

            for (let index = 0; index < actual.length; index++) {
                if (actual[index].numero !== olds[index].numero) {
                    //console.log(olds.find(e => e.numero === actual.telefonos[index].numero));
                    RequestFetch.put({
                        url: `${RequestFetch.BASE_URL}/api/rh/representante/telefono/${repre.id_representante.value}/${olds[index].numero}`,
                        headers: cabeceras,
                        data: actual[index],
                        exito: async (res) => {
                            console.log(await res.json())
                            props.enqueueSnackbar("Telefonos actualizados", { variant: 'success', preventDuplicate: true, });
                        },
                        fallo: async (res) => { console.log(await res.json()) },
                        error_request: (error) => {
                            history.push(`/server-error`);
                        }
                    })
                }
            }
        }

        const eliminarTel = (object) => {
            console.log("TODELETE", object);
            object.forEach(item => {
                RequestFetch.deleted({
                    url: `${RequestFetch.BASE_URL}/api/rh/representante/telefono/${repre.id_representante.value}/${item.numero}`,
                    headers: cabeceras,
                    exito: async (res) => {
                        console.log(await res.json())

                        props.enqueueSnackbar("Telefonos actualizados", { variant: 'success', preventDuplicate: true, });
                    },
                    fallo: async (res) => { console.log(await res.json()) },
                    error_request: (error) => { history.push(`/server-error`) }

                })
            })
        }

        const sendTelefonos = () => {
            const newTel = _telefonoRepre();
            if (newTel.telefonos.length > oldTel.length) {
                console.log(oldTel, newTel.telefonos);
                const toUpdate = [];
                oldTel.forEach(e => {
                    const index = newTel.telefonos.findIndex(i => i.tipo === e.tipo);
                    //console.log("ENCONTRADO", newTel.telefonos.find(i => i.tipo === e.tipo))
                    if (index >= 0) {
                        toUpdate.push(newTel.telefonos.splice(index, 1)[0]);
                    }
                });
                agregarTele(newTel);
                actualizarTel(toUpdate, oldTel);
                setOldTel([...newTel.telefonos, ...toUpdate])
            }
            else if (newTel.telefonos.length < oldTel.length) {
                console.log(newTel.telefonos, oldTel);
                const toUpdate = [];
                newTel.telefonos.forEach(e => {
                    const index = oldTel.findIndex(i => i.tipo === e.tipo);
                    //console.log("ENCONTRADO", oldTel.find(i => i.numero === e.numero))
                    if (index >= 0) {
                        toUpdate.push(oldTel[index]);
                        oldTel.splice(index, 1);
                    }
                });
                eliminarTel(oldTel);
                actualizarTel(newTel.telefonos, toUpdate);
                setOldTel(newTel.telefonos);
            }
            else {
                actualizarTel(newTel.telefonos, oldTel);
                setOldTel(newTel.telefonos);
            }
        }

        const sendRepre = () => {
            const newrepe = _separeRepresentante();
            if (Object.keys(newrepe).length > 0) {
                RequestFetch.put({
                    url: `${RequestFetch.BASE_URL}/api/rh/representante/${repre.id_representante.value}`,
                    headers: cabeceras,
                    data: newrepe,
                    exito: _exito,
                    fallo: _fallo,
                    error_request: (error) => {
                        history.push(`/server-error`);
                    }
                });
            }

        }


        const validaAll = () => {
            if (_ValidateRepre()) {
                if (_ValidateTel()) {
                    if (!loading) {
                        setLoading(true);
                        timer.current = setTimeout(() => {
                            setLoading(false);
                            props.enqueueSnackbar("Actualizado", { variant: 'success', preventDuplicate: true, });
                        }, 2000);
                    }
                    sendTelefonos();
                    sendRepre();
                } else {
                    setToast({
                        open: true,
                        message: 'Debes guardar al menos un teléfono',
                        severity: "warning",
                        title: "Revisa los telefonos!"
                    });
                    setLoading(false);
                }
            } else {

                setToast({
                    open: true,
                    message: 'Hay campos requeridos que estan vacios, revisalos porfavor',
                    severity: "warning",
                    title: "Campos vacios!"
                });
                setLoading(false);
            }
        }
        validaAll();
    }

    const deleted = () => {
        const deletePhones = () => {
            const fetches = oldTel.map(item => `${RequestFetch.BASE_URL}/api/rh/representante/telefono/${repre.id_representante.value}/${item.numero}`);
            Promise.all(
                fetches.map(url => fetch(url, { method: 'DELETE' }))
            ).then(responses =>
                Promise.all(responses.map(res => res.json()))
            ).then(text => {
                deleteRepre();
            }).catch(error => {
                console.log("PROMISES", error)
            });
        }

        const deleteRepre = () => {
            RequestFetch.deleted({
                url: `${RequestFetch.BASE_URL}/api/rh/representante/${repre.id_representante.value}`,
                headers: cabeceras,
                exito: async (res) => {
                    const { deleted } = await res.json();
                    if (deleted) {
                        props.enqueueSnackbar(`Representante con RFC: ${repre.rfc.value} eliminado`, { variant: 'success', preventDuplicate: true, });
                        timer.current = setTimeout(() => {
                            history.push("/nomina/listar-representante");
                        }, 1000);
                    }
                },
                fallo: async (res) => {
                    history.push("/server-error");
                },
                error_request: (error) => {
                    history.push("/server-error");
                }
            });
        }

        const findRepre = () => {
            RequestFetch.get({
                url: `${RequestFetch.BASE_URL}/api/rh/empresa?query=id_empresa-identificador&id_representante=${repre.id_representante.value}`,
                headers: cabeceras,
                exito: async (resp) => {
                    const { empresas } = await resp.json();
                    if (empresas.length > 0) {
                        empresas.map(item => props.enqueueSnackbar(
                            <Button size="small" style={{ color: '#FFFFFF' }}
                                onClick={() => { history.push(`/nomina/edit-empresa/${item.id_empresa}`) }}>{`Ver Empresa: ${item.identificador}`}</Button>,
                            { variant: 'info' }));
                        props.enqueueSnackbar('Dependencias en empresa(s)', { variant: 'error' });
                    } else {
                        deletePhones()
                    }
                },
                fallo: async (resp) => {
                    history.push("/server-error");
                },
                error_request: (error) => {
                    history.push("/server-error");
                }
            });
        }

        findRepre();
        if (!loading) {
            setDeleting(true);
            timer.current = setTimeout(() => {
                setDeleting(false);
            }, 1000);
        }
    }

    React.useEffect(() => {
        let mounted = true;
        if (id !== undefined) {
            const _exito = async (res) => {
                const respuesta = await res.json();
                if (mounted)
                    if (respuesta.length === 1) {
                        setOldRepre(respuesta[0]);
                        const { id_representante, rfc, nombre, primer_apellido, segundo_apellido, genero, correo, observaciones } = respuesta[0];
                        setRepre(prevState => {
                            return {
                                ...prevState,
                                existe: { value: false },
                                id_representante: { value: id_representante, verify: true },
                                rfc: { value: rfc, verify: true },
                                nombre: { value: nombre, verify: true },
                                primer_apellido: { value: primer_apellido, verify: true },
                                segundo_apellido: { value: segundo_apellido === null || segundo_apellido === undefined ? '' : segundo_apellido, verify: true },
                                genero: { value: genero, verify: true },
                                correo: { value: correo, verify: true },
                                observaciones: { value: observaciones === null || observaciones === undefined ? '' : observaciones, verify: true }
                            }
                        });
                        setBack(false);
                    }
                    else
                        history.push(`/nomina/page-not-found`);
            }

            const _exitoTel = async (res) => {
                const respuesta = await res.json();
                if (mounted) {
                    console.log("EDIT   ", respuesta.telefonos);
                    const newTel = [
                        { value: '', verify: '', tipo: 'C', helperText: '' },
                        { value: '', verify: '', tipo: 'F', helperText: '' },
                    ]

                    respuesta.telefonos.forEach(item => {
                        switch (item.tipo) {
                            case 'C': newTel[0] = { value: item.numero, verify: true, tipo: item.tipo, helperText: '' };
                                break;
                            case 'F': newTel[1] = { value: item.numero, verify: true, tipo: item.tipo, helperText: '' }
                                break;
                            default: newTel.push({ value: item.numero, verify: true, tipo: item.tipo, helperText: '' })
                                break;
                        }
                    });
                    setTele(newTel);
                    setOldTel(respuesta.telefonos);
                }
            }

            const _fallo = async (res) => {
                const respuesta = await res.json();
                if (!isTokenExpired(respuesta));
            }


            RequestFetch.get({
                url: `${RequestFetch.BASE_URL}/api/rh/representante/${id}?query=${RequestFetch.REPRESENTANTE_ALL}`,
                headers: cabeceras,
                exito: _exito,
                fallo: _fallo,
                error_request: (error) => {
                    history.push(`/server-error`);
                }
            });

            RequestFetch.get({
                url: `${RequestFetch.BASE_URL}/api/rh/representante/telefono/${id}`,
                headers: cabeceras,
                exito: _exitoTel,
                fallo: _fallo,
                error_request: (error) => {
                    history.push(`/server-error`);
                }
            });
        }
        return () => mounted = false;
        // eslint-disable-next-line
    }, [id])



    return (
        <React.Fragment>
            <Card className={classes.root}>
                <CardContent className={classes.cardContent}>
                    <List
                        component="nav"
                        aria-labelledby="nested-list-subheader"
                        subheader={
                            <ListSubheader component="div" display="block" className={classes.subheader}>
                                <IconButton
                                    className={clsx(classes.back, {
                                        [classes.butonHide]: onlySee
                                    })}
                                    disabled={onlySee}
                                    onClick={() => { history.goBack() }}>
                                    <KeyboardBackspaceIcon />
                                </IconButton>
                                <Typography className={classes.title} variant="button" gutterBottom>
                                    {`${onlySee ? '' : 'Editando '}`}representante
                                </Typography>
                            </ListSubheader>
                        }
                        className={classes.root}>
                        <ListItem className={classes.header}>
                            <ListItemIcon>
                                <InfoIcon />
                            </ListItemIcon>
                            <ListItemText primary="Informacion" />
                        </ListItem>
                        <Collapse in={true} timeout="auto" >
                            <Representante
                                data={repre}
                                setData={setRepre}
                                phones={tele}
                                setPhones={setTele}
                                expandable={true}
                                loading={loading}
                                onlySee={onlySee} />
                        </Collapse>
                    </List>
                </CardContent>
                {!onlySee &&
                    <CardActions>
                        <Grid
                            container
                            direction="row"
                            justify="flex-end"
                            alignItems="center">

                            <div className={classes.wrapper}>

                                {
                                    Object.keys(oldRepre).length > 0 &&
                                    (
                                        oldRepre.estatus === 'A' ?
                                            <Button
                                                color="secondary"
                                                disabled={deleting}
                                                className={classes.delete}
                                                startIcon={<DeleteIcon />}
                                                onClick={() => setDialog({ open: true, type: 'B' })}>
                                                {"Eliminar"}
                                                {deleting && <CircularProgress size={24} color="secondary" className={classes.buttonDeleting} />}
                                            </Button>
                                            :
                                            <Button
                                                color="primary"
                                                style={{ color: green[500], marginRight: 5 }}
                                                startIcon={<ReplayIcon />}
                                                onClick={() => setDialog({ open: true, type: 'A' })}>
                                                {"Activar representante"}
                                            </Button>
                                    )
                                }


                                {
                                    oldRepre.estatus !== 'B' &&
                                    <Button
                                        variant="contained"
                                        className={classes.save}
                                        disabled={loading}
                                        endIcon={<SaveIcon />}
                                        onClick={send}>
                                        {"Actualizar"}
                                        {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                                    </Button>
                                }

                            </div>
                        </Grid>
                    </CardActions>
                }

            </Card>
            {
                repre.id_representante.value !== '' &&
                <Grid container spacing={3}>
                    <Grid item xs={12} sm={6} xl={4}>
                        <Documentos id={id} tipo={"representante"} onlySee={onlySee} />
                    </Grid>
                </Grid>
            }

            <Snackbar open={toast.open} autoHideDuration={5000} onClose={handleClose}>
                <Alert onClose={handleClose} severity={toast.severity}>
                    <AlertTitle>{toast.title}</AlertTitle>
                    {toast.message}
                </Alert>
            </Snackbar>

            <Dialog
                open={dialog.open}
                TransitionComponent={Transition}
                onClose={() => setDialog({ open: false, type: '' })}>
                <DialogTitle>{`${dialog.type === 'B' ? 'Eliminar' : 'Activar'}`}</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Está apunto de {`${dialog.type === 'B' ? 'dar de baja' : 'activar'}`} al representante

                        ¿Está seguro que desea continuar?
                        </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setDialog({ open: false, type: '' })}>Cancelar</Button>
                    <Button onClick={() => {
                        deleted();
                        setDialog({ open: false, type: '' });
                    }}
                        color="secondary">{`${dialog.type === 'B' ? 'Eliminar' : 'Activar'}`}</Button>
                </DialogActions>
            </Dialog>

            <Backdrop className={classes.backdrop} open={back} >
                <CircularProgress color="inherit" />
            </Backdrop>
        </React.Fragment>
    );
}

export default withSnackbar(EditRepresentante);