import React from 'react'
import { Avatar, IconButton, Fab, CircularProgress, Divider } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles';
import { green } from '@material-ui/core/colors';
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import SaveIcon from '@material-ui/icons/Save'
import AttachmentOutlinedIcon from '@material-ui/icons/AttachmentOutlined';
import ClearOutlinedIcon from '@material-ui/icons/ClearOutlined'
import RequestFetch from '../../../libraries/RequestFetch';
import { SessionContext } from '../../../routes/adminrh/CustomRoutes';

const useStyles = makeStyles((theme) => ({
    wrapper: {
        margin: theme.spacing(2),
        position: 'relative',
    },
    buttonSuccess: {
        backgroundColor: green[500],
        '&:hover': {
            backgroundColor: green[700],
        },
    },
    fabProgress: {
        color: green[500],
        position: 'absolute',
        top: -4,
        left: -4,
        zIndex: 1,
    },
    square: {
        color: '#FFFFFF',
        backgroundColor: '#cb0606',
        fontSize: theme.spacing(2)
    },
    divider: {
        marginLeft: '30px',
        marginRight: '30px'
    }
}));

export default function InputFile(props) {
    const { cabeceras, isTokenExpired } = React.useContext(SessionContext);
    const classes = useStyles();
    const { id, tipo } = props;
    const [file, setFile] = React.useState(null);
    const [loading, setLoading] = React.useState(false);

    const sendFile = e => {
        if (file === null) {
            props.toast({
                open: true,
                message: "Sube un a archivo e intenta nuevamente",
                severity: "warning",
                title: "No archivo no encontrado!"
            })
        }
        else POST();
    }

    const POST = () => {
        let mounted = true;

        const form = () => {
            const myForm = new FormData();
            myForm.append("documento", file);
            myForm.append(`id_${tipo}`, id);
            myForm.append("id_tipo_documento", props.data.id_tipo_documento);
            return myForm;
        }

        const exito = async res => {
            const respuesta = await res.json();
            console.log(respuesta[0]);
            const add = {
                'id_tipo_documento': props.data.id_tipo_documento,
                'textShow': props.data.tipo,
                'fecha_modificacion': respuesta[0].fecha_modificacion,
                'url_documento': respuesta[0].url_documento
            }
            setFile(null);
            add[`id_${tipo}`] = id;
            setLoading(false);
            props.delete(add)
            props.toast({
                open: true,
                message: "Se ha guardado el documento, puedes revisarlo",
                severity: "success",
                title: "Guardado con exito!"
            })

        }

        const fallo = async res => {
            const respuesta = await res.json();
            if (!isTokenExpired(respuesta)) {
                console.log(respuesta);
                setLoading(false);
            }
        }

        const send = () => {
            const headers = new Headers();
            headers.append('authorization', cabeceras.authorization);
            RequestFetch.postForm({
                url: `${RequestFetch.BASE_URL}/api/rh/${tipo}/documento`,
                headers,
                data: form(),
                exito: exito,
                fallo: fallo,
                error_request: (error) => {
                    console.log("Errooooor");
                }
            });
        }

        if (mounted) {
            setLoading(true);
            send();
        }

        return () => mounted = false;
    }

    const uploadFile = e => {
        const { files } = e.target;
        setFile(files[0]);
    }

    const removeFile = e => {
        setFile(null);
    }


    return (
        <Card elevation={0}>
            <CardHeader
                avatar={
                    <IconButton disabled>
                        <Avatar variant="rounded" className={classes.square}>
                            PDF
                            </Avatar>
                    </IconButton>}
                action={
                    <div className={classes.wrapper}>
                        {
                            file !== null ?
                                <>
                                    <Fab
                                        disabled={loading}
                                        size="small"
                                        aria-label="save"
                                        color="primary"
                                        className={classes.buttonSuccess}
                                        onClick={sendFile}>
                                        <SaveIcon />
                                    </Fab>
                                    {loading && <CircularProgress size={48} className={classes.fabProgress} />}
                                </>
                                :
                                <>
                                    <input
                                        style={{ display: "none" }}
                                        id={`input-file-${props.data.id_tipo_documento}`}
                                        type="file"
                                        accept="application/pdf"
                                        onChange={uploadFile}
                                    />
                                    <Fab
                                        size="small"
                                        aria-label="save"
                                        color="primary"
                                        htmlFor={`input-file-${props.data.id_tipo_documento}`}
                                        component="label">
                                        <AttachmentOutlinedIcon />
                                    </Fab>
                                </>
                        }
                    </div>
                }
                title={props.data.tipo}
                subheader={
                    <>
                        {
                            file !== null ?
                                <>
                                    {`${file.name}`}
                                    <IconButton aria-label="delete" className={classes.margin} size="small" onClick={removeFile}>
                                        <ClearOutlinedIcon fontSize="inherit" />
                                    </IconButton>
                                </>
                                : 'Selecciona un archivo'
                        }
                    </>
                }
            />
            <Divider className={classes.divider} />
        </Card>
    );
}