
import { createMuiTheme, responsiveFontSizes } from '@material-ui/core/styles';
import { makeStyles } from '@material-ui/core/styles';

export const ThemeDashboard = responsiveFontSizes(createMuiTheme({
    palette: {
        primary: {
            light: '#3b434b',
            main: '#0A151F',
            dark: '#070e15',
            contrastText: '#fff',
        },
    },
}));

const drawerWidth = 240;
const colorText = '#D1D2D4';
const background = '#0A151F';

export const useStylesDashboard = makeStyles(theme => ({
    appBar: {
        backgroundImage: 'linear-gradient(45deg, #D84315, #F57C00)',
        transition: theme.transitions.create(["margin", "width"], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        [theme.breakpoints.down('sm')]: {
            marginLeft: '0px',
        },
        [theme.breakpoints.up('md')]: {
            marginLeft: '0px'
        }
    },
    appBarShift: {
        backgroundImage: 'linear-gradient(45deg, #D84315, #F57C00)',
        transition: theme.transitions.create(["margin", "width"], {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),
        [theme.breakpoints.down('sm')]: {
            width: '100%'
        },
        [theme.breakpoints.up('md')]: {
            width: `calc(100% - ${drawerWidth}px)`,
        }
    },
    hide: {
        display: "none"
    },
    drawer: {
        background: background,
        color: colorText,
        width: drawerWidth,
        flexShrink: 0,
    },
    drawerPaper: {
        background: background,
        color: colorText,
        width: drawerWidth,
        overflow: 'hidden',
    },
    drawerHeader: {
        display: "flex",
        alignItems: "center",
        padding: theme.spacing(1),
        // necessary for content to be below app bar
        ...theme.mixins.toolbar,
        justifyContent: "center"
    },
    content: {
        transition: theme.transitions.create("padding", {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        [theme.breakpoints.down('xs')]: {
            paddingLeft: '0px',
            margin: 0,
        },
        [theme.breakpoints.up('md')]: {
            paddingLeft: '0px',
            margin: theme.spacing(3),
        }
    },
    contentShift: {
        transition: theme.transitions.create("padding", {
            easing: theme.transitions.easing.easeOut,
            duration: theme.transitions.duration.enteringScreen,
        }),

        [theme.breakpoints.down('sm')]: {
            paddingLeft: '0px',
        },
        [theme.breakpoints.up('md')]: {
            paddingLeft: drawerWidth,
        }
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
        textAlign: 'center'
    },
}));