import React, { useContext } from 'react'
import RequestFetch from '../../../libraries/RequestFetch'
import moment from 'moment';
import MaterialTable from 'material-table';
import { useHistory, useParams } from 'react-router-dom';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import EditOutlinedIcon from '@material-ui/icons/EditOutlined';
import DeleteOutlineOutlinedIcon from '@material-ui/icons/DeleteOutlineOutlined';
import { Snackbar } from '@material-ui/core';
import { AlertTitle } from '@material-ui/lab';
import MuiAlert from '@material-ui/lab/Alert';
import SeeEmpresa from './SeeEmpresa';
import VisibilityIcon from '@material-ui/icons/Visibility';
import { EstadoByCode } from '../../../libraries/Estados';



import { SessionContext } from '../../../routes/adminrh/CustomRoutes';

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});



export default function TableEmpresa() {
    const { permisos, rol, cabeceras, isTokenExpired } = useContext(SessionContext);
    const history = useHistory();
    const { tipo } = useParams();

    const [data, setData] = React.useState([]);
    const [empresa, setEmpresa] = React.useState([]);
    const [giros, setGiros] = React.useState([]);
    const [regimenes, setRegimenes] = React.useState([]);

    const [dialog, setDialog] = React.useState(false);
    const [loading, setLoading] = React.useState(true);

    const [see, setSee] = React.useState(false);
    const [verEmpre, setVerEmpre] = React.useState({});

    const [deleted, setDeleted] = React.useState({});

    const [toast, setToast] = React.useState({
        open: false,
        message: "",
        severity: "",
        title: ""
    });

    const getData = React.useCallback(() => {
        let mounted = true;

        const _exitoEmpresa = async (res) => {
            const respuesta = await res.json();
            if (mounted) {
                if (permisos.length > 0 && rol === 'G') {
                    respuesta.empresas = respuesta.empresas.filter(e => {
                        return permisos.indexOf(e.id_empresa) >= 0;
                    })
                }
                setEmpresa(respuesta.empresas)
                if (respuesta.empresas.length === 0) {
                    setLoading(false);
                    setToast({
                        open: true,
                        message: "No existen registros de empresas",
                        severity: "info",
                        title: "Ningun registro encontrado"
                    })
                }
            };
        }
        const _exitoGiros = async (res) => {
            const respuesta = await res.json();
            if (mounted) setGiros(respuesta.giros);
        }

        const _exitoRegimenes = async (res) => {
            const respuesta = await res.json();
            if (mounted) setRegimenes(respuesta.regimenesCapitales);
        }
        const _fallo = async (res) => {
            const respuesta = await res.json();
            if (!isTokenExpired(respuesta))
                if (respuesta['error'] !== undefined)
                    if (respuesta['error']['httpCode'] === 500) {
                        history.push(`/page-not-found`);
                    }
        }

        const _falloReq = (error) => {
            //console.log(error);
            history.push(`/server-error`);
        }

        if (mounted) {
            setLoading(true);
            setEmpresa([]);
            setGiros([]);
            setRegimenes([]);
            setData([]);

            RequestFetch.get({
                url: `${RequestFetch.BASE_URL}/api/rh/regimen`,
                headers: cabeceras,
                exito: _exitoRegimenes,
                fallo: _fallo,
                error_request: _falloReq
            });

            RequestFetch.get({
                url: `${RequestFetch.BASE_URL}/api/rh/giro?query=id_giro-nombre_giro-descripcion`,
                headers: cabeceras,
                exito: _exitoGiros,
                fallo: _fallo,
                error_request: _falloReq
            });

            const peticion = (tipo !== undefined ? `&es_interna=${tipo}` : '');
            RequestFetch.get({
                url: `${RequestFetch.BASE_URL}/api/rh/empresa?query=${RequestFetch.EMPRESA_ALL}${peticion}`,
                headers: cabeceras,
                exito: _exitoEmpresa,
                fallo: _fallo,
                error_request: _falloReq
            });
        }
        return () => mounted = false;
        // eslint-disable-next-line
    }, [tipo])

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            setToast({ ...toast, open: false });
            return;
        }
        setToast({ ...toast, open: false });
    };

    const handleDelete = () => {

        const DELETEEmpresaExito = async (res) => {

            const respuesta = await res.json();
            const { deleted } = respuesta;
            setLoading(false);

            if (deleted) {
                const { razon_comercial, rfc } = deleted;
                setToast({
                    open: true,
                    message: `La Empresa ${razon_comercial} ${rfc} ha sido eliminada con exito`,
                    severity: "success",
                    title: "Eliminacion exitosa"
                });
                getData();
            }
            else {
                const { razon_comercial, rfc } = deleted;
                setToast({
                    open: true,
                    message: `${razon_comercial} ${rfc} ha ocurrido un error`,
                    severity: "error",
                    title: "Eliminacion fallida"
                });

            }
        }

        const DELETEEmpresaFallo = async (res) => {
            setLoading(false);
            setToast({
                open: true,
                message: `La Empresa ${deleted.razon_comercial} ${deleted.rfc} no se puede eliminar, tiene elementos que dependen de esta`,
                severity: "info",
                title: "Eliminacion fallida!"
            });
        }

        const DELETESendEmpresa = () => {
            setLoading(true);
            RequestFetch.deleted({
                url: `${RequestFetch.BASE_URL}/api/rh/empresa/${deleted.id_empresa}`,
                headers: cabeceras,
                exito: DELETEEmpresaExito,
                fallo: DELETEEmpresaFallo,
                error_request: () => {
                    history.push(`/server-error`);
                }
            });
        }


        setDialog(false);
        DELETESendEmpresa();
    }

    React.useEffect(() => {
        let mounted = true;
        if (mounted) getData();
        return () => mounted = false;
    }, [getData]);

    React.useEffect(() => {
        let mounted = true;
        const estatus = {
            P: 'Pendiente',
            A: 'Activo',
            B: 'Baja'
        }
        if (giros.length > 0 &&
            regimenes.length > 0 &&
            empresa.length > 0 &&
            data.length === 0 && mounted
        ) {
            const newData = [...empresa];
            newData.forEach(element => {
                element['fecha_constitucion'] = moment(element['fecha_constitucion']).format("DD/MM/YYYY");
                element['fecha_alta'] = moment(element['fecha_alta']).format("DD/MM/YYYY");
                element['es_interna'] = element['es_interna'] ? 'Interna' : 'Externa';
                element['id_regimen_capital'] = regimenes.find(item => item.id_regimen_capital === element['id_regimen_capital']).nombre_regimen;
                element['estatus'] = estatus[element['estatus']];
                element['id_giro'] = giros.find(item => item.id_giro === element['id_giro']).nombre_giro;
                element['entidad'] = EstadoByCode(element['entidad']);
            });
            if (mounted) {
                setData(newData);
                setLoading(false);
            };

        }

        return () => mounted = false;
    }, [giros, regimenes, empresa, data]);

    const localization = {
        header: {
            actions: 'Acciones'
        },
        body: {
            emptyDataSourceMessage: 'No hay registros que mostrar',
            editTooltip: 'Editar',
            deleteTooltip: 'Borrar',
            addTooltip: 'Añadir',
            editRow: {
                cancelTooltip: 'Cancelar',
                saveTooltip: 'Guardar',
                deleteText: '¿Estás seguro de eliminar esta empresa?'
            }
        },
        toolbar: {
            searchTooltip: 'Buscar',
            searchPlaceholder: 'Buscar',
            exportName: 'Exportar como CSV',
            exportTitle: 'Exportar',
            showColumnsTitle: 'Columas',
            showColumnsAriaLabel: 'Columnas',
            addRemoveColumns: 'Agregar o remover columnas'
        },
        pagination: {
            firstTooltip: 'Primera página',
            previousTooltip: 'Página anterior',
            nextTooltip: 'Siguiente página',
            lastTooltip: 'Última página',
            labelRowsSelect: 'filas',
            labelDisplayedRows: '{from}-{to} a {count}',
        }
    }

    const columns = [
        { title: 'Identificador', field: 'identificador', width: 200, removable: false, defaultSort: 'asc' },
        { title: 'Razón Comercial.', field: 'razon_comercial', width: 200, removable: false },
        { title: 'Régimen Capital', field: 'id_regimen_capital', width: 180 },
        { title: 'Razón Social.', field: 'razon_social', hidden: true, width: 300 },
        { title: 'RFC', field: 'rfc', width: 150 },
        { title: 'Constitución', field: 'fecha_constitucion', width: 150 },
        { title: 'Alta', field: 'fecha_alta', width: 150 },
        { title: 'Tipo', field: 'es_interna', width: 100 },
        { title: 'Estatus', field: 'estatus', hidden: true, width: 150 },
        { title: 'Giro', field: 'id_giro', hidden: true, width: 150 },
        { title: 'Calle', field: 'calle', width: 200 },
        { title: 'No.Ext', field: 'no_exterior', width: 100 },
        { title: 'No.Int', field: 'no_interior', hidden: true, width: 100, emptyValue: 'S/N' },
        { title: 'C.P.', field: 'cp', width: 100 },
        { title: 'Colonia', field: 'colonia', width: 200 },
        { title: 'Municipio', field: 'municipio', width: 200 },
        { title: 'Entidad', field: 'entidad', width: 150 },
        { title: 'Correo', field: 'correo', hidden: true, width: 250 },
        { title: '', field: "id_empresa", hidden: true, width: 0, editable: 'never', removable: false },
    ]

    const actions = [
        {
            icon: () => <VisibilityIcon />,
            tooltip: 'Ver',
            onClick: (event, rowData) => {
                setVerEmpre(rowData);
                setSee(true);
            }
        },
        {
            icon: () => <EditOutlinedIcon />,
            tooltip: 'Editar',
            onClick: (event, rowData) => history.push(`/nomina/edit-empresa/${rowData.id_empresa}`)
        },
        {
            icon: () => <DeleteOutlineOutlinedIcon />,
            tooltip: 'Eliminar',
            onClick: (event, rowData) => {
                setDialog(true);
                setDeleted(rowData);
            }
        },
        {
            icon: 'refresh',
            tooltip: 'Recargar',
            isFreeAction: true,
            onClick: getData
        },
    ];

    if (rol === 'R')
        actions.push(
            {
                icon: 'add',
                tooltip: 'Agregar',
                isFreeAction: true,
                onClick: (event, rowData) => history.push(`/nomina/nueva-empresa`)
            }
        );

    return (
        <React.Fragment>
            <MaterialTable
                localization={localization}
                title={(tipo !== undefined ?
                    (tipo === 'true' ? 'Empresas Internas' : 'Empresas Cliente')
                    : 'Todas las Empresas')
                }
                columns={columns}
                data={data}
                actions={actions}
                options={{
                    draggable: false, columnsButton: true,
                    exportAllData: true, exportButton: true, pageSize: 10,
                    pageSizeOptions: [10, 20, 50, 100],
                    fixedColumns: { left: 2 },
                    headerStyle: { fontWeight: 'bold' }
                }}
                isLoading={loading}
            />

            <Dialog
                open={dialog}
                TransitionComponent={Transition}
                onClose={() => setDialog(false)}>
                <DialogTitle>{"Elimnar un registro"}</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Está apunto de elimiar a la empresa
                        {` ${deleted.razon_social} ${deleted.id_regimen_capital} `}
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setDialog(false)}>Cancelar</Button>
                    <Button onClick={handleDelete} color="secondary">Elimnar</Button>
                </DialogActions>
            </Dialog>

            <SeeEmpresa open={see} setOpen={setSee} data={verEmpre} />


            <Snackbar open={toast.open} autoHideDuration={6000} onClose={handleClose}>
                <Alert onClose={handleClose} severity={toast.severity}>
                    <AlertTitle>{toast.title}</AlertTitle>
                    {toast.message}
                </Alert>
            </Snackbar>
        </React.Fragment>
    );
}