import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Dashboard from '../../views/adminrh/Dashboard';
import ErrorPage from '../../views/adminrh/ErrorPage';
import Login from '../../components/adminrh/SingIn';
import { PrivateRoute, PublicRoute } from './CustomRoutes'

function AdminRouters() {

    return (
        <React.Fragment>
            <Router>
                <Switch>
                    <PublicRoute restricted={true} exact path="/nomina/signin" redirect="/nomina">
                        <Login />
                    </PublicRoute>
                    <PrivateRoute path="/nomina" redirect='/nomina/signIn' >
                        <Dashboard />
                    </PrivateRoute>
                    <Route path="/server-error">
                        <ErrorPage type="ServerError" />
                    </Route>
                    <Route path="/*">
                        <ErrorPage type="NotFound" />
                    </Route>
                </Switch>
            </Router>
        </React.Fragment>
    )
}
export default AdminRouters;