import React, { useContext } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import { Grid, TextField, MenuItem, Button, Typography, InputAdornment } from '@material-ui/core'
import { KeyboardDatePicker } from '@material-ui/pickers';
import { Autocomplete } from '@material-ui/lab'
import RequestFetch from '../../../../libraries/RequestFetch';

import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import "moment/locale/es";
import { ArrowBack } from '@material-ui/icons';
import moment from "moment";
import AddCircleOutlineIcon from '@material-ui/icons/AddCircleOutline';
import CloseIcon from '@material-ui/icons/Close';
import DoneAllIcon from '@material-ui/icons/DoneAll';
import { withSnackbar } from 'notistack';
import { ValidacionCantidad } from '../../../../libraries/Mask';
import { SessionContext } from '../../../../routes/adminrh/CustomRoutes';


const useStyles = makeStyles(theme => ({
    container: {
        padding: theme.spacing(4),
    },
    select: {
        textAlign: 'left'
    },
    radioGroup: {
        marginTop: '15px'
    },
    uppercase: {
        textTransform: 'uppercase'
    }
}));

const textDisabled = makeStyles({
    root: {
        '&$disabled': {
            color: 'rgba(0, 0, 0, 0.87)'
        },
    },
    disabled: {},
}, { name: 'MuiInputBase' });


const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

function Empresa(props) {
    const classes = useStyles();
    textDisabled();
    const { cabeceras, isTokenExpired, rol, permisos } = useContext(SessionContext);
    const { editable } = props;
    const { data, setData, onlySee } = props;

    const [old, setOld] = React.useState({});
    const [dialog, setDialog] = React.useState({
        open: false
    });
    const [clases, setClases] = React.useState([]);

    const estados = [
        { title: "Alta", value: 'A' },
        { title: "Reingreso", value: 'R' },
        { title: "Taslado", value: 'T' }
    ];

    const changeTextRequired = e => {
        const { name, value } = e.target;
        setData(prevState => {
            return {
                ...prevState, [name]: { value: value, verify: `${value}`.length > 0 }
            }
        });
    }

    const changeAuto = (key, value) => {

        var newData = { ...data };
        newData[key] = { value: value, verify: (value instanceof Object) };

        if (key === 'id_empresa_labora' && value !== null)
            props.setIdentificador(value.identificador + "-");
        setData(newData);

    }

    const changeDate = (e, key) => {
        if (e !== null) {
            let verify;
            const cont_ext = data.fecha_contrato_externo.value
            const cont_ini = data.fecha_inicio_contrato.value;
            switch (key) {
                case "fecha_contrato_externo":
                    verify = cont_ini instanceof moment ?
                        e.isSameOrBefore(cont_ini) && e.isSameOrBefore(moment()) :
                        true;
                    break;
                case "fecha_inicio_contrato":
                    verify = cont_ext instanceof moment ?
                        e.isSameOrAfter(cont_ext) && e.isSameOrBefore(moment()) :
                        true;
                    break;
                case "fecha_fin_contrato":
                    verify = cont_ini instanceof moment ?
                        e.isAfter(cont_ini) && e.isAfter(moment()) :
                        true;
                    break;
                default: break;
            }
            setData(prevState => {
                return {
                    ...prevState, [key]: { value: e, verify: verify }
                }
            })
        }
    }

    const changeDateFin = (e, key) => {
        if (e !== null) {
            let newDate = {
                value: e.format("DD/MM/YYYY"),
                verify: (e instanceof moment && !isNaN(e) && e.isAfter(moment()))
            };
            setData(prevState => {
                return {
                    ...prevState, [key]: newDate
                }
            })
        }
    }

    const newContrato = () => {
        if (data.new) {
            setData({ ...old, new: false });
            setOld({});
        } else {
            setOld({ ...data });
            const newData = {};
            const keys = Object.keys(data);
            keys.forEach(key => newData[`${key}`] = { value: '', verify: '' });
            newData['new'] = true;
            setData({ ...newData, motivo_contrato: { value: 'A', verify: true } });
        }
    }

    const finishContrato = () => {
        RequestFetch.put({
            url: `${RequestFetch.BASE_URL}/api/rh/empleado/contrato/${data.id_contrato.value}`,
            headers: cabeceras,
            data: { fecha_fin_contrato: moment().format("YYYY/MM/DD") },
            exito: async (res) => {
                props.enqueueSnackbar("Contrato terminado", { variant: 'success', preventDuplicate: true, })
                const newData = {};
                const newOld = {};
                const keys = Object.keys(data);
                keys.forEach(key => {
                    newData[key] = { value: '', verify: '' }
                    newOld[key] = '';
                });
                newData['new'] = false;
                setData({ ...newData, motivo_contrato: { value: 'A', verify: true } });
                props.setOldData(newOld);
                props.setIdentificador(null);
            },
            fallo: async (res) => {
                const response = await res.json();
                if (!isTokenExpired(response))
                    props.enqueueSnackbar("Ha ocurrido un error", { variant: 'error', preventDuplicate: true, })
            },
            error_request: async (error) => console.log(await error.json())

        });
    }


    React.useEffect(() => {
        if (data.id_empresa_pertenece.value !== null) {
            const { id_empresa } = data.id_empresa_pertenece.value;
            const fetchData = async () => {
                if (id_empresa) {
                    const result = await fetch(`${RequestFetch.BASE_URL}/api/rh/empresa/registro_patronal?id_empresa=${id_empresa}`, { headers: cabeceras });
                    const response = await result.json();
                    if (!isTokenExpired(response)) {
                        const { registros } = response
                        //setData(prevState => { return { ...prevState, clave_patronal: { value: '', verify: '' } } });
                        setClases(registros);
                    }
                }
            }
            fetchData();
        }
        //eslint-disable-next-line
    }, [data])

    const getEmpresasPermisos = () => {
        if (rol === 'G') {
            return props.empresasAll.filter(e => permisos.some(p => p === e.id_empresa));
        }
        else return props.empresasAll;
    }

    //console.log(props.empresasAll);

    return (
        <Grid container spacing={4} className={classes.container}>
            <Grid item xs={12} sm={12} md={12} lg={12}>
                <Grid container spacing={2}>
                    <Grid item xs={12} sm={12} md={12} lg={12}>
                        <Typography variant="subtitle2" display="block" style={{ textAlign: 'center' }}>
                            {data.id_empresa_pertenece.value === '' ? "Sin Contrato" : "Contrato"}
                        </Typography>
                    </Grid>
                    <Grid item xs={12} sm={6} md={onlySee ? 12 : 5} lg={onlySee ? 12 : 5} xl={5}>
                        <Autocomplete
                            disabled={(editable && !data.new) || onlySee}
                            loading={props.empresasInt.length === 0}
                            options={props.empresasInt}
                            getOptionLabel={(option) => option.razon_social}
                            value={data.id_empresa_pertenece.value !== '' && props.empresasInt.length > 0 ? data.id_empresa_pertenece.value : null}
                            noOptionsText="Sin coincidencias"
                            onChange={(event, newValue) => changeAuto("id_empresa_pertenece", newValue)}
                            renderInput={(params) =>
                                <TextField  {...params}
                                    InputLabelProps={{ shrink: true }}
                                    placeholder="Ej. Grupo Empresarial"
                                    label="Empresa Pertenece"
                                    variant="outlined"
                                    required
                                    error={data.id_empresa_pertenece.verify !== '' ? !data.id_empresa_pertenece.verify : false}
                                />}
                        />
                    </Grid>
                    <Grid item xs={12} sm={6} md={onlySee ? 12 : 5} lg={onlySee ? 12 : 5} xl={5}>
                        <Autocomplete
                            disabled={(editable && !data.new) || onlySee}
                            loading={props.empresasAll.length === 0}
                            options={getEmpresasPermisos()}
                            getOptionLabel={(option) => option.razon_social}
                            value={data.id_empresa_labora.value !== '' && props.empresasAll.length > 0 ? data.id_empresa_labora.value : null}
                            noOptionsText="Sin coincidencias"
                            onChange={(event, newValue) => changeAuto("id_empresa_labora", newValue)}
                            renderInput={(params) =>
                                <TextField {...params}
                                    InputLabelProps={{ shrink: true }}
                                    placeholder="Ej. Grupo Empresarial"
                                    label="Empresa Labora"
                                    variant="outlined"
                                    required
                                    error={data.id_empresa_labora.verify !== '' ? !data.id_empresa_labora.verify : false}
                                />}
                        />
                    </Grid>
                    <Grid item xs={12} sm={6} md={2} lg={onlySee ? 4 : 2} xl={2}>
                        <TextField
                            disabled={(editable && !data.new) || onlySee}
                            variant="outlined"
                            required
                            fullWidth
                            label="Motivo"
                            select
                            name="motivo_contrato"
                            className={classes.select}
                            value={`${estados.length > 0 && data.motivo_contrato.value !== '' ? data.motivo_contrato.value : 'DEFAULT'}`}
                            InputLabelProps={{ shrink: true }}
                            onChange={changeTextRequired}
                            error={data.motivo_contrato.verify !== '' ? !data.motivo_contrato.verify : false}>
                            <MenuItem value="DEFAULT" disabled>Selecciona</MenuItem>
                            {
                                estados.map((item) => {
                                    return <MenuItem key={`motivo_contrato-${item.value}`} value={item.value}>{item.title}</MenuItem>
                                })

                            }
                        </TextField>
                    </Grid>
                    <Grid item xs={12} sm={6} md={4} lg={4}>
                        <KeyboardDatePicker
                            disabled={(editable && !data.new) || onlySee}
                            inputVariant="outlined"
                            maxDateMessage={"Fecha es mayor al máximo permitido"}
                            minDateMessage={"Fecha es menor al mínimo permitido"}
                            invalidDateMessage={"Formato invalido"}
                            InputLabelProps={{ shrink: true }}
                            fullWidth required
                            error={data.fecha_contrato_externo.verify !== '' ? !data.fecha_contrato_externo.verify : false}
                            placeholder="Día/Mes/Año"
                            id="fecha_contrato_externo"
                            label="Antiguedad"
                            format="DD/MM/YYYY"
                            value={data.fecha_contrato_externo.value !== '' ? moment(data.fecha_contrato_externo.value, "DD/MM/YYYY") : null}
                            maxDate={moment()}
                            onChange={e => changeDate(e, "fecha_contrato_externo")}
                            autoComplete='off'
                        />
                    </Grid>
                    <Grid item xs={12} sm={6} md={4} lg={4}>
                        <KeyboardDatePicker
                            disabled={(editable && !data.new) || onlySee}
                            inputVariant="outlined"
                            maxDateMessage={"Fecha es mayor al máximo permitido"}
                            minDateMessage={"Fecha es menor al mínimo permitido"}
                            invalidDateMessage={"Formato invalido"}
                            InputLabelProps={{ shrink: true }}
                            fullWidth required
                            error={data.fecha_inicio_contrato.verify !== '' ? !data.fecha_inicio_contrato.verify : false}
                            placeholder="Día/Mes/Año"
                            id="fecha_inicio_contrato"
                            label="Inicio Contrato"
                            format="DD/MM/YYYY"
                            value={data.fecha_inicio_contrato.value instanceof moment ? data.fecha_inicio_contrato.value : null}
                            maxDate={moment()}
                            onChange={e => changeDate(e, "fecha_inicio_contrato")}
                            autoComplete='off'
                        />
                    </Grid>
                    <Grid item xs={12} sm={6} md={4} lg={4}>
                        <KeyboardDatePicker
                            disabled={(editable && !data.new) || onlySee}
                            inputVariant="outlined"
                            maxDateMessage={"Fecha es mayor al máximo permitido"}
                            minDateMessage={"Fecha es menor al mínimo permitido"}
                            invalidDateMessage={"Formato invalido"}
                            InputLabelProps={{ shrink: true }}
                            fullWidth
                            error={data.fecha_fin_contrato.verify !== '' ? !data.fecha_fin_contrato.verify : false}
                            placeholder="Día/Mes/Año"
                            id="fecha_fin_contrato"
                            label="Fin Contrato"
                            format="DD/MM/YYYY"
                            value={data.fecha_fin_contrato.value !== '' ? moment(data.fecha_fin_contrato.value, "DD/MM/YYYY") : null}
                            minDate={moment().add(1, 'days')}
                            onChange={e => changeDateFin(e, "fecha_fin_contrato")}
                            autoComplete='off'
                        />
                    </Grid>
                </Grid>
            </Grid>

            <Grid item xs={12} sm={12} md={onlySee ? 12 : 4} lg={onlySee ? 12 : 4}>
                <Grid container spacing={2}>
                    <Grid item xs={12} sm={12} md={12} lg={12}>
                        <Typography variant="subtitle2" display="block" style={{ textAlign: 'center' }}>Fiscal</Typography>
                    </Grid>
                    <Grid item xs={12} sm={6} md={12} lg={12}>
                        <TextField
                            disabled={(editable && !data.new) || onlySee}
                            fullWidth
                            required
                            variant="outlined"
                            error={data.id_regimen_contratacion.verify !== '' ? !data.id_regimen_contratacion.verify : false}
                            label="Regimen Contratación"
                            select
                            name="id_regimen_contratacion"
                            className={classes.select}
                            value={`${props.regimenContrato.length > 0 && data.id_regimen_contratacion.value !== '' ? data.id_regimen_contratacion.value : 'DEFAULT'}`}
                            InputLabelProps={{ shrink: true }}
                            onChange={changeTextRequired}>
                            <MenuItem value="DEFAULT" disabled>Selecciona</MenuItem>
                            {props.regimenContrato.map((item) => {
                                return <MenuItem key={`regimen-contratacion-${item.id_regimen_contratacion}`} value={item.id_regimen_contratacion}>{item.regimen_contratacion}</MenuItem>
                            })}
                        </TextField>
                    </Grid>
                    <Grid item xs={12} sm={6} md={12} lg={12}>
                        <TextField
                            disabled={(editable && !data.new) || onlySee}
                            fullWidth
                            required
                            variant="outlined"
                            error={data.id_tipo_contrato.verify !== '' ? !data.id_tipo_contrato.verify : false}
                            label="Tipo Contrato"
                            select
                            name="id_tipo_contrato"
                            className={classes.select}
                            value={`${props.tipoContrato.length > 0 && data.id_tipo_contrato.value !== '' ? data.id_tipo_contrato.value : 'DEFAULT'}`}
                            InputLabelProps={{ shrink: true }}
                            onChange={changeTextRequired}>
                            <MenuItem value="DEFAULT" disabled>Selecciona</MenuItem>
                            {props.tipoContrato.map((item) => {
                                return <MenuItem key={`tipo-contrato${item.id_tipo_contrato}`} value={item.id_tipo_contrato}>{item.nombre_tipo_contrato}</MenuItem>
                            })}
                        </TextField>
                    </Grid>
                    <Grid item xs={12} sm={6} md={12} lg={12}>
                        <TextField
                            disabled={(editable && !data.new) || onlySee}
                            fullWidth
                            required
                            variant="outlined"
                            error={data.clave_patronal.verify !== '' ? !data.clave_patronal.verify : false}
                            label="Clave Patronal"
                            select
                            name="clave_patronal"
                            value={`${clases && clases.length > 0 && data.clave_patronal.value !== '' ? data.clave_patronal.value : 'DEFAULT'}`}
                            InputLabelProps={{ shrink: true }}
                            onChange={changeTextRequired}>
                            <MenuItem value="DEFAULT" disabled>Selecciona</MenuItem>
                            {clases.map((item) => {
                                return <MenuItem key={`registro_patronal-${item.clave_patronal}`} value={item.clave_patronal}>{`CL-${item.categoria} ${item.clave_patronal}`}</MenuItem>
                            })}
                        </TextField>
                    </Grid>
                </Grid>
            </Grid>
            <Grid item xs={12} sm={12} md={onlySee ? 12 : 8} lg={onlySee ? 12 : 8}>
                <Grid container spacing={2}>
                    <Grid item xs={12} md={12} lg={12}>
                        <Typography variant="subtitle2" display="block" style={{ textAlign: 'center' }} color="initial">Laboral</Typography>
                    </Grid>
                    <Grid item xs={12} sm={6} lg={5}>
                        <Autocomplete
                            disabled={(editable && !data.new) || onlySee}
                            loading={props.departamentos.length === 0}
                            options={props.departamentos}
                            getOptionLabel={(option) => option.nombre_departamento}
                            value={props.data.id_departamento.value !== '' && props.departamentos.length > 0 ? props.data.id_departamento.value : null}
                            noOptionsText="Sin coincidencias"
                            onChange={(event, newValue) => changeAuto("id_departamento", newValue)}
                            renderInput={(params) =>
                                <TextField {...params}
                                    InputLabelProps={{ shrink: true }}
                                    placeholder="Ej. Recursos Humanos"
                                    label="Departamento"
                                    variant="outlined"
                                    required
                                    error={props.data.id_departamento.verify !== '' ? !props.data.id_departamento.verify : false}
                                />}
                        />
                    </Grid>
                    <Grid item xs={12} sm={6} lg={5}>
                        <Autocomplete
                            disabled={(editable && !data.new) || onlySee}
                            loading={props.puestos.length === 0}
                            options={props.puestos}
                            getOptionLabel={(option) => option.nombre_puesto}
                            value={props.data.id_puesto.value !== '' && props.puestos.length > 0 ? props.data.id_puesto.value : null}
                            noOptionsText="Sin coincidencias"
                            onChange={(event, newValue) => changeAuto("id_puesto", newValue)}
                            renderInput={
                                (params) =>
                                    <TextField {...params}
                                        InputLabelProps={{ shrink: true }}
                                        placeholder="Ej. Gerente"
                                        variant="outlined"
                                        label="Puesto"
                                        required
                                        error={props.data.id_puesto.verify !== '' ? !props.data.id_puesto.verify : false}
                                    />
                            }
                        />
                    </Grid>
                    <Grid item xs={12} sm={6} lg={2}>
                        <TextField required
                            fullWidth
                            disabled={(editable && !data.new) || onlySee}
                            variant="outlined"
                            label="Jornada"
                            select
                            name="id_jornada"
                            value={`${props.jornadas.length > 0 && props.data.id_jornada.value !== '' ? props.data.id_jornada.value : 'DEFAULT'}`}
                            InputLabelProps={{ shrink: true }}
                            onChange={changeTextRequired}
                            error={props.data.id_jornada.verify !== '' ? !props.data.id_jornada.verify : false}>
                            <MenuItem value="DEFAULT" disabled>Selecciona</MenuItem>
                            {
                                props.jornadas.map((item) => {
                                    return <MenuItem key={`jornadas-${item.id_jornada}`} value={item.id_jornada}>{item.tipo}</MenuItem>
                                })
                            }
                        </TextField>
                    </Grid>

                    <Grid item xs={6} sm={6} lg={4}>
                        <TextField
                            disabled={onlySee}
                            variant="outlined"
                            required
                            label="Salario Diario"
                            fullWidth
                            name="salario_diario"
                            placeholder="Ej. $999.99"
                            InputLabelProps={{ shrink: true, }}
                            value={data.salario_diario.value}
                            onChange={changeTextRequired}
                            InputProps={{ inputComponent: ValidacionCantidad, startAdornment: <InputAdornment position="start">$</InputAdornment>, }}
                            error={data.salario_diario.verify !== '' ? !data.salario_diario.verify : false}
                            autoComplete='off'
                        />
                    </Grid>
                    <Grid item xs={6} sm={6} lg={4}>
                        <TextField
                            disabled={true}
                            variant="outlined"
                            label="Salario Integral"
                            fullWidth
                            placeholder="Ej. $999.99"
                            InputLabelProps={{ shrink: true, }}
                            InputProps={{
                                inputComponent: ValidacionCantidad,
                                startAdornment: <InputAdornment position="start">$</InputAdornment>,
                            }}
                        />
                    </Grid>
                    <Grid item xs={6} sm={6} lg={4}>
                        <TextField
                            disabled={onlySee}
                            variant="outlined"
                            required
                            label="Salario Real"
                            fullWidth
                            name="salario_capturado"
                            placeholder="Ej. $999.99"
                            InputLabelProps={{ shrink: true }}
                            value={data.salario_capturado.value}
                            InputProps={{
                                inputComponent: ValidacionCantidad,
                                startAdornment: <InputAdornment position="start">$</InputAdornment>,
                            }}
                            error={data.salario_capturado.verify !== '' ? !data.salario_capturado.verify : false}
                            onChange={changeTextRequired}
                            autoComplete='off'
                        />
                    </Grid>

                </Grid>
            </Grid>


            {
                !props.expandable &&
                <>
                    {
                        editable &&
                        <Grid container item
                            direction="row"
                            justify="flex-end"
                            alignItems="center" xs={12} sm={12} md={12} lg={12} >
                            {
                                (data['id_contrato']['value'] !== undefined && data['id_contrato']['value'] !== '') &&
                                <Button
                                    color="secondary"
                                    startIcon={<DoneAllIcon />}
                                    onClick={() => setDialog({ open: true, tipo: 'FIN' })}
                                >{`${'Terminar Contrato'}`}</Button>
                            }

                            <Button
                                color="primary"
                                endIcon={data.new ? <CloseIcon /> : <AddCircleOutlineIcon />}
                                onClick={data.new ? newContrato : () => setDialog({ open: true, tipo: 'NEW' })}
                            >{`${data.new ? 'Cancelar' : 'Nuevo Contrato'}`}</Button>
                        </Grid>
                    }
                    <Grid container item
                        direction="row"
                        justify="flex-end"
                        alignItems="center" xs={12} sm={12} md={12} lg={12} >

                        <Button startIcon={<ArrowBack />}
                            onClick={props.buttons.back}>Anterior
                    </Button>


                    </Grid>
                </>
            }

            <Dialog
                open={dialog.open}
                TransitionComponent={Transition}
                onClose={() => setDialog({ open: false })}>
                <DialogTitle>{`${dialog.tipo === 'NEW' ? 'Nuevo' : 'Terminar'} Contrato`}</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        {`${dialog.tipo === 'NEW' ?
                            'Está apunto de crear un nuevo contrato, si existe un anterior este finalizará con fecha de hoy' :
                            'El contrato finalizará con fecha de hoy'} ${moment().format('DD/MM/YYYY')}, `}

                        ¿Está seguro que desea continuar?
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setDialog({ open: false })}>Cancelar</Button>
                    <Button onClick={() => {
                        dialog.tipo === 'NEW' ? newContrato() : finishContrato();
                        setDialog({ open: false });
                    }}
                        color="secondary">Continuar</Button>
                </DialogActions>
            </Dialog>

        </Grid>
    )
}

export default withSnackbar(Empresa)