import React, { useState, useEffect } from "react";
import MaterialTable from "material-table";
import { MuiThemeProvider } from "@material-ui/core/styles";
import { Notification } from "../../AdminStateNomina/Notification";
import { InputKey } from "../../AdminStateNomina/InputKey";
import { handlerAdd, handlerUpdate, handlerDelete } from "./handlers";
import { localization, options, actions, theme } from '../../../../libraries/ConfigCatalog';
import { get } from "../crud";
import { BASE_URL } from "../../../../dev.json";
import { SessionContext } from '../../../../routes/adminrh/CustomRoutes'

const columns = [
  {
    title: "Clave",
    field: "id_percepcion_deduccion",
    editComponent: (props) => <InputKey {...props} />,
  },
  { title: "Nombre", field: "nombre" },
  { title: "Formula", field: "formula" },
  {
    title: "Aplicación",
    field: "aplicacion",
    lookup: { G: "General", I: "Individual" },
  },
  {
    title: "Tipo Nomina",
    field: "tipo_nomina",
    lookup: { N: "Normal", E: "Especial" },
  },

  {
    title: "Base cotización",
    field: "base_cotizacion",
    lookup: { V: "Variable", F: "Fijo" },
  },
  {
    title: "Estatus",
    field: "estatus",
    lookup: { A: "Alta", B: "Inactiva", C: "Calculo" },
  },
  {
    title: "Prestación",
    field: "prestacion",
    lookup: { E: "Efectivo", T: "Transferencia" },
  },
  {
    title: "Digito grabable",
    field: "digito_grabable",
    lookup: { G: "G", D: "E" },
  },
  {
    title: "Clave SAT",
    field: "clave_sat",
    editComponent: (props) => <InputKey isSAT {...props} />,
  },
  { title: "Tipo", field: "tipo", lookup: { P: "Percepción", D: "Deducción" } },
];

const url_api = `${BASE_URL}api/rh/nomina/percepcion_deduccion`;

export default () => {
  const { cabeceras, isTokenExpired } = React.useContext(SessionContext);
  const [data, setData] = useState([]);
  const [isLoading, setLoading] = useState(true);
  const [valuesAlert, setValuesAlert] = useState({
    open: false,
    message: "",
    severity: "",
    title: "",
  });

  const getData = () => {
    get({
      url_api, key: "percepciondeduccion", setLoading, setData,
      headers: cabeceras, isTokenExpired
    });
  }

  actions[0].onClick = getData;

  useEffect(() => {
    getData()
    //eslint-disable-next-line
  }, []);

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setValuesAlert({ ...valuesAlert, open: false });
  };

  const controllerAlerts = (key) => {
    const values = {
      open: true,
      severity: "success",
    };

    switch (key.toUpperCase()) {
      case "POST":
        values["message"] = "Creacion de nueva deduccion o percepcion exitosa";
        values["title"] = "Registro exitoso";
        break;
      case "PUT":
        values["message"] = "Realizada exitosamente";
        values["title"] = "Actualización";

        break;
      case "DELETE":
        values["message"] = "Realizada exitosamente";
        values["title"] = "Eliminación";
        break;
      case "ERROR_KEY":
        values["message"] = "Registro existente";
        values["title"] = "Invalido";
        values["severity"] = "info";
        break;
      case "REQUIERED":
        values["message"] = "Complete todos los campos";
        values["title"] = "Incompleto";
        values["severity"] = "info";
        break;
      case "INVALID_KEY":
        values["message"] = "Percepción o deducción SAT";
        values["title"] = "Invalida";
        values["severity"] = "warning";
        break;
      default:
        break;
    }
    setValuesAlert(values);
  };

  return (
    <>
      <MuiThemeProvider theme={theme}>
        <MaterialTable
          actions={actions}
          localization={localization}
          options={options}
          isLoading={isLoading}
          title="Percepciones y Deducciones"
          columns={columns}
          data={data}
          editable={{
            onRowAdd: handlerAdd({
              url_api,
              setLoading,
              data,
              setData,
              controllerAlerts,
              headers: cabeceras,
              isTokenExpired
            }),
            onRowUpdate: handlerUpdate({
              url_api,
              setLoading,
              data,
              setData,
              controllerAlerts,
              headers: cabeceras,
              isTokenExpired
            }),
            onRowDelete: handlerDelete({
              url_api,
              setLoading,
              data,
              setData,
              controllerAlerts,
              headers: cabeceras,
              isTokenExpired
            }),
          }}
        />
      </MuiThemeProvider>
      <Notification values={valuesAlert} handleClose={handleClose} />
    </>
  );
};
