import React from 'react'
import MaterialTable from 'material-table';
import { Typography } from '@material-ui/core'
import { Snackbar } from '@material-ui/core';
import MuiAlert from '@material-ui/lab/Alert';
import { localization, actions, options } from '../../../../libraries/ConfigCatalog';
import { useFactoryLaw } from '../useFactoryLaw';
import { OneIntegerFourDecimal, TwoInteger, YearMask } from '../MaskLeyes'
import FieldTable from '../FieldTable';

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

export default function TableIntegracion() {

    const {
        data,
        loading,
        refresh,
        post,
        remove,
        put
    } = useFactoryLaw({ type: 'integracion' });
    const [toast, setToast] = React.useState({
        open: false,
        severity: "",
        title: ""
    });


    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            setToast({ ...toast, open: false });
            return;
        }
        setToast({ ...toast, open: false });
    };

    actions[0].onClick = refresh;
    localization.body.editRow.deleteText = <Typography variant="button" gutterBottom>¿Está seguro que desea continuar?</Typography>

    /* 
    anio: "2018"
anio_antiguedad_inferior: 1
anio_antiguedad_superior: 1
dias_aguinaldo: 15
dias_vacaciones: 6
factor: "1.0452"
folio_integracion: 1
prima_vacacional: "0.25"
    */


    const keys = [
        'anio',
        'anio_antiguedad_inferior',
        'anio_antiguedad_superior',
        'dias_aguinaldo',
        'dias_vacaciones',
        'factor',
        'prima_vacacional'
    ]

    const columns = [
        { title: 'ID', field: "folio_integracion", editable: 'never', hidden: true },
        {
            title: 'Año', field: 'anio', defaultSort: 'desc',
            editComponent: props => <FieldTable {...props} placeholder="Ej. 2020"
                InputProps={{ inputComponent: YearMask }} />
        },
        {
            title: 'Año Inferior', field: 'anio_antiguedad_inferior',
            editComponent: props => <FieldTable {...props} placeholder="Ej. 1"
                InputProps={{ inputComponent: TwoInteger }} />
        },
        {
            title: 'Año Superior', field: 'anio_antiguedad_superior',
            editComponent: props => <FieldTable {...props} placeholder="Ej. 2"
                InputProps={{ inputComponent: TwoInteger }} />
        },
        {
            title: 'Días Aguinaldo', field: 'dias_aguinaldo',
            editComponent: props => <FieldTable {...props} placeholder="Ej. 15"
                InputProps={{ inputComponent: TwoInteger }} />
        },
        {
            title: 'Días Vacaciones', field: 'dias_vacaciones',
            editComponent: props => <FieldTable {...props} placeholder="Ej. 6"
                InputProps={{ inputComponent: TwoInteger }} />
        },

        {
            title: 'Factor', field: 'factor',
            editComponent: props => <FieldTable {...props} placeholder="Ej. 0.25"
                InputProps={{
                    inputComponent: OneIntegerFourDecimal
                }} />
        },
        {
            title: 'Prima Vacacional', field: 'prima_vacacional',
            editComponent: props => <FieldTable {...props} placeholder="Ej. 1.0452" InputProps={{
                inputComponent: OneIntegerFourDecimal
            }} />
        },
    ];

    const validLimit = (newData) => {
        const inferior = parseInt(newData.anio_antiguedad_inferior)
        const superior = parseInt(newData.anio_antiguedad_superior);
        return inferior <= superior;
    }

    return (
        <>
            <MaterialTable
                title="Factores de Integración"
                columns={columns}
                localization={localization}
                data={data}
                actions={actions}
                options={options}
                isLoading={loading}
                editable={{
                    onRowAdd: (newData) =>
                        new Promise((resolve, reject) => {
                            setTimeout(() => {
                                const addKeys = Object.keys(newData);
                                if (!keys.every(key => addKeys.includes(key) && !Number.isNaN(key))) {
                                    setToast({
                                        open: true,
                                        severity: "warning",
                                        title: "Campos vacios!"
                                    })
                                    reject();
                                    return;
                                }
                                else if (!validLimit(newData)) {
                                    setToast({
                                        open: true,
                                        severity: "warning",
                                        title: "Limite mayor no puede ser menor al mayor!"
                                    })
                                    reject();
                                    return;
                                }
                                else {
                                    resolve();
                                    post({ data: newData }, () => {
                                        setToast({
                                            open: true,
                                            severity: "success",
                                            title: "Creación exitosa",
                                        })
                                    })
                                }
                            }, 600);
                        })
                    ,
                    onRowUpdate: (newData, oldData) =>
                        new Promise((resolve, reject) => {
                            setTimeout(() => {
                                const newKeys = [...keys]
                                newKeys.push('folio_integracion');
                                const addKeys = Object.keys(newData);
                                if (!newKeys.every(key => addKeys.includes(key) && newData[key] !== '')) {
                                    setToast({
                                        open: true,
                                        severity: "warning",
                                        title: "Campos vacios!"
                                    })
                                    reject();
                                    return;
                                } else if (!validLimit(newData)) {
                                    setToast({
                                        open: true,
                                        severity: "warning",
                                        title: "Limite mayor no puede ser menor al mayor!"
                                    })
                                    reject();
                                    return;
                                } else if (keys.every(key => newKeys.includes(key) && !Number.isNaN(key)) && validLimit(newData)) {
                                    resolve();
                                    const { folio_integracion, ...data } = newData;
                                    put({ id: folio_integracion, data }, () => {
                                        setToast({
                                            open: true,
                                            severity: "success",
                                            title: "Actualización exitosa"
                                        })
                                    })
                                }
                            }, 600);
                        })
                    ,
                    onRowDelete: (oldData) =>
                        new Promise((resolve) => {
                            setTimeout(() => {
                                resolve();
                                remove({ id: oldData['folio_integracion'] }, () => {
                                    setToast({
                                        open: true,
                                        severity: "success",
                                        title: "Eliminación exitosa",
                                    })
                                });
                            }, 600);
                        })
                    ,
                }}
            />


            <Snackbar open={toast.open} autoHideDuration={6000} onClose={handleClose}>
                <Alert onClose={handleClose} severity={toast.severity}>
                    {toast.title}
                    {toast.message && toast.message}
                </Alert>
            </Snackbar>
        </>
    );
}