import React, { useEffect, useState, useContext } from 'react'
import { Paper, Typography, TableContainer, Table, TableHead, TableBody, TableRow, TableCell } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import { BASE_URL } from '../../../../libraries/RequestFetch'
import moment from 'moment';
import { Skeleton } from '@material-ui/lab';
import CancelIcon from '@material-ui/icons/Cancel';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import { green, red } from '@material-ui/core/colors';
import { SessionContext } from '../../../../routes/adminrh/CustomRoutes';

const useStyles = makeStyles((theme) => ({
    root: {
        backgroundColor: theme.palette.background.paper,
        minHeight: '60px'
    },
    paper: {
        padding: theme.spacing(3)
    },
    title: {
        textAlign: 'center',
        paddingTop: '15px'
    }
}));

export default function Contratos({ id }) {
    const classes = useStyles();
    const [contratos, setContratos] = useState();
    const { cabeceras, isTokenExpired } = useContext(SessionContext);
    useEffect(() => {
        fetch(`${BASE_URL}/api/rh/empleado/contrato?query=fecha_inicio_contrato-fecha_fin_contrato-salario_diario-salario_capturado&id_empleado=${id}`,
            { method: 'GET', headers: cabeceras })
            .then(resp => resp.json())
            .then(response => {
                if (!isTokenExpired(response)) {
                    if (response.credito) {
                        const newCreditos = response.credito.map(e => {
                            e['finished'] = moment.utc(e.fecha_fin_contrato).isBefore(moment.utc()) || null;
                            return e;
                        })
                        setContratos(newCreditos);
                    }
                }
            })
        // eslint-disable-next-line
    }, [id])

    const showFinalDate = (date) => {
        if (date === null)
            return "Indeterminado"
        else
            return moment.utc(date).format('DD/MM/YYYY')
    }

    return (
        <Paper className={classes.root} elevation={2}>
            <Typography className={classes.title} variant="button" display="block" gutterBottom>
                Historial de Contratos
            </Typography>
            <TableContainer>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell align="center">Inicio</TableCell>
                            <TableCell align="center">Fin</TableCell>
                            <TableCell align="center">$ Diario</TableCell>
                            <TableCell align="center">$ Capturado</TableCell>
                            <TableCell align="center">Estado</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {
                            Array.isArray(contratos) ?
                                contratos.map((e, i) =>
                                    <TableRow key={`contrato-element-${i}`}>
                                        <TableCell align="center">
                                            {moment.utc(e.fecha_inicio_contrato).format('DD/MM/YYYY')}
                                        </TableCell>
                                        <TableCell align="center">
                                            {showFinalDate(e.fecha_fin_contrato)}
                                        </TableCell>
                                        <TableCell align="center">
                                            ${e.salario_diario}
                                        </TableCell>
                                        <TableCell align="center">
                                            ${e.salario_capturado}
                                        </TableCell>
                                        <TableCell align="center">
                                            {
                                                !e.finished ?
                                                    <CheckCircleIcon style={{ color: green[400] }} />
                                                    :
                                                    <CancelIcon style={{ color: red[400] }} />
                                            }
                                        </TableCell>
                                    </TableRow>
                                )
                                :
                                <TableRow >
                                    <TableCell align="center">
                                        <Skeleton variant="text" />
                                    </TableCell>
                                    <TableCell align="center">
                                        <Skeleton variant="text" />
                                    </TableCell>
                                    <TableCell align="center">
                                        <Skeleton variant="text" />
                                    </TableCell>
                                    <TableCell align="center">
                                        <Skeleton variant="text" />
                                    </TableCell>
                                </TableRow>
                        }
                    </TableBody>
                </Table>
            </TableContainer>
        </Paper>
    )
}
