import React from 'react'
import { green } from '@material-ui/core/colors';
import MaterialTable from 'material-table';
import { TextField, Typography, FormControlLabel, Checkbox, Grid } from '@material-ui/core'
import RequestFetch from '../../../libraries/RequestFetch'
import { Snackbar } from '@material-ui/core';
import { AlertTitle } from '@material-ui/lab';
import MuiAlert from '@material-ui/lab/Alert';
import { useHistory } from 'react-router-dom';
import { withStyles } from '@material-ui/core/styles';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import RemoveCircleIcon from '@material-ui/icons/RemoveCircle';
import { localization, actions, options } from '../../../libraries/ConfigCatalog';

import { SessionContext } from '../../../routes/adminrh/CustomRoutes'

const GreenCheckbox = withStyles({
    root: {
        '&$checked': {
            color: green[600],
        },
    },
    checked: {},
})((props) => <Checkbox style={{ padding: 5 }} color="default" {...props} />);

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

export default function TableDocumento() {
    const { cabeceras, isTokenExpired } = React.useContext(SessionContext);
    const history = useHistory();
    const [data, setData] = React.useState([]);
    const [loading, setLoading] = React.useState(false);
    const [toast, setToast] = React.useState({
        open: false,
        message: "",
        severity: "",
        title: ""
    });

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            setToast({ ...toast, open: false });
            return;
        }
        setToast({ ...toast, open: false });
    };


    const getData = React.useCallback(() => {
        let mounted = true;
        setLoading(true);
        setData([]);

        const _exito = async (res) => {
            const respuesta = await res.json();
            console.log(respuesta);
            if (mounted) {
                setLoading(false);
                if (respuesta.tipo_documento.length > 0) {
                    const newData = respuesta.tipo_documento;
                    newData.forEach(e => {
                        e['empresa'] = e['empresa'] !== undefined && e['empresa'] !== null && e['empresa'] !== '' ? e['empresa'] : false;
                        e['empleado'] = e['empleado'] !== undefined && e['empleado'] !== null && e['empleado'] !== '' ? e['empleado'] : false;
                        e['representante'] = e['representante'] !== undefined && e['representante'] !== null && e['representante'] !== '' ? e['representante'] : false;
                    });
                    setData(newData);
                } else {
                    setToast({
                        open: true,
                        message: "",
                        severity: "info",
                        title: "Ningun registro encontrado"
                    })
                }
            };
        }

        const _fallo = async (res) => {
            const response = await res.json();
            if (!isTokenExpired(response))
                if (mounted) {
                    setLoading(false);
                    setToast({
                        open: true,
                        message: "Ha ocurrido un error, intenta nuevamente",
                        severity: "error",
                        title: "Uuuups!"
                    })

                };
        }

        if (mounted) {
            RequestFetch.get({
                url: `${RequestFetch.BASE_URL}/api/rh/tipo_documento`,
                headers: cabeceras,
                exito: _exito,
                fallo: _fallo,
                error_request: () => {
                    history.push(`/server-error`);
                }
            });
        }

        return () => mounted = false;
        // eslint-disable-next-line
    }, [history]);

    const columns = [
        {
            title: 'Documento', field: 'tipo', width: 300,
            editComponent: props => (
                <TextField
                    multiline
                    fullWidth
                    size="small"
                    value={props.value || ''}
                    placeholder="Ej. RFC ..."
                    onChange={e => props.onChange(e.target.value)}
                />
            )
        },
        {
            title: 'Descripción', field: 'descripcion', width: 300,
            editComponent: props => (
                <TextField
                    multiline
                    fullWidth
                    size="small"
                    value={props.value || ''}
                    placeholder="Ej. Registro Federal de Contribuyentes..."
                    onChange={e => props.onChange(e.target.value)}
                />
            )
        },
        {
            title: 'Empresa', field: 'empresa',
            render: ({ empresa }) => {
                return <Grid container direction="row" justify="center" alignItems="center">
                    <GreenCheckbox
                        icon={<RemoveCircleIcon />} checkedIcon={<CheckCircleIcon />}
                        checked={empresa}
                    />
                </Grid>
            },
            editComponent: props => (
                <Grid container direction="row" justify="center" alignItems="center">
                    <FormControlLabel
                        style={{ marginLeft: 0, marginRight: 0 }}
                        control={
                            <GreenCheckbox
                                icon={<RemoveCircleIcon />} checkedIcon={<CheckCircleIcon />}
                                checked={props.value === undefined ? false : props.value}
                                onChange={e => props.onChange(props.value === undefined ? true : !props.value)}
                                name="empresa"
                            />
                        }
                    />
                </Grid>
            )
        },
        {
            title: 'Empleado', field: 'empleado',
            render: ({ empleado }) => {
                return <Grid container direction="row" justify="center" alignItems="center">
                    <GreenCheckbox
                        icon={<RemoveCircleIcon />} checkedIcon={<CheckCircleIcon />}
                        checked={empleado}
                    />
                </Grid>
            },
            editComponent: props => (
                <Grid container direction="row" justify="center" alignItems="center">
                    <FormControlLabel
                        style={{ marginLeft: 0, marginRight: 0 }}
                        control={
                            <GreenCheckbox
                                icon={<RemoveCircleIcon />} checkedIcon={<CheckCircleIcon />}
                                checked={props.value === undefined ? false : props.value}
                                onChange={e => props.onChange(props.value === undefined ? true : !props.value)}
                                name="empleado"
                            />
                        }
                    />
                </Grid>
            )
        },
        {
            title: 'Representante', field: 'representante',
            render: ({ representante }) => {
                return <Grid container direction="row" justify="center" alignItems="center">
                    <GreenCheckbox
                        icon={<RemoveCircleIcon />} checkedIcon={<CheckCircleIcon />}
                        checked={representante}
                    />
                </Grid>
            },
            editComponent: props => (
                <Grid container direction="row" justify="center" alignItems="center">
                    <FormControlLabel
                        style={{ marginLeft: 0, marginRight: 0 }}
                        control={
                            <GreenCheckbox
                                icon={<RemoveCircleIcon />} checkedIcon={<CheckCircleIcon />}
                                checked={props.value === undefined ? false : props.value}
                                onChange={e => props.onChange(props.value === undefined ? true : !props.value)}
                                name="representante"
                            />
                        }
                    />
                </Grid>
            )
        },
        { title: '', field: "id_tipo_documento", width: 20, editable: 'never', hidden: true, removable: false },
    ];

    actions[0].onClick = getData;
    localization.body.editRow.deleteText = <Typography variant="button" gutterBottom>¿Está seguro que desea continuar?</Typography>


    const POST = (documento) => {
        let mounted = true;
        const exito = async res => {
            const respuesta = await res.json();
            if (mounted) {
                documento.id_tipo_documento = respuesta[0].id_tipo_documento;
                const newData = data;
                newData.push(documento)
                setData(newData);
                setLoading(false);
                setToast({
                    open: true,
                    message: `Documento: ${documento.tipo.toUpperCase()} creado correctamente`,
                    severity: "success",
                    title: "Registro exitoso!"
                });
            }
        }
        const fallo = async res => {
            const response = await res.json();
            if (!isTokenExpired(response))
                if (mounted) {
                    setLoading(false);
                    setToast({
                        open: true,
                        message: `El registro no se ha realizado, intenta de nuevo`,
                        severity: "error",
                        title: "Registro fallido!"
                    });
                }
        }

        const send = () => {

            RequestFetch.post({
                url: `${RequestFetch.BASE_URL}/api/rh/tipo_documento`,
                headers: cabeceras,
                data: documento,
                exito: exito,
                fallo: fallo,
                error_request: (error) => {
                    history.push(`/server-error`);
                }
            });
        }

        if (mounted) {
            setLoading(true);
            send();
        }

        return () => mounted = false;
    }
    const PUT = (documento, olddocumento) => {
        let mounted = true;

        const exito = async res => {
            const respuesta = await res.json();
            console.log(respuesta);
            const newData = data;
            const index = newData.indexOf(olddocumento);
            data[index] = documento;
            if (mounted) {
                setData(newData);
                setLoading(false);
                setToast({
                    open: true,
                    message: `Documento: ${documento.tipo.toUpperCase()} se ha actualizado correctamente`,
                    severity: "success",
                    title: "Actualización exitoso!"
                });
            }
        }
        const fallo = async res => {
            const response = await res.json();
            if (!isTokenExpired(response))
                if (mounted) {
                    setLoading(false);
                    setToast({
                        open: true,
                        message: `Documento: ${documento.tipo.toUpperCase()} no ha sido actualizado, intenta de nuevo`,
                        severity: "error",
                        title: "Actualización fallida!"
                    });
                }
        }

        const send = () => {
            const putData = {
                tipo: documento.tipo,
                descripcion: documento.descripcion,
                empresa: documento.empresa,
                empleado: documento.empleado,
                representante: documento.representante
            }

            RequestFetch.put({
                url: `${RequestFetch.BASE_URL}/api/rh/tipo_documento/${documento.id_tipo_documento}`,
                headers: cabeceras,
                data: putData,
                exito: exito,
                fallo: fallo,
                error_request: (error) => {
                    history.push(`/server-error`);
                }
            });
        }

        if (mounted) {
            setLoading(true);
            send();
        }
    }
    const DELETE = (documento) => {
        let mounted = true;
        const exito = async res => {
            await res.json();

            let newData = data;
            const index = newData.indexOf(documento);
            data.splice(index, 1);

            if (mounted) {
                setData(newData);
                setLoading(false);
                setToast({
                    open: true,
                    message: `Documento: ${documento.tipo.toUpperCase()} se ha eliminado correctamente`,
                    severity: "success",
                    title: "Eliminación exitosa!"
                });
            }

        }
        const fallo = async res => {
            const response = await res.json();
            if (!isTokenExpired(response))
                if (mounted) {
                    setLoading(false);
                    setToast({
                        open: true,
                        message: `Documento: ${documento.tipo.toUpperCase()} tiene registros que dependen de este, primero debes eliminarlos`,
                        severity: "error",
                        title: "Eliminación fallida!"
                    });
                }
        }

        const send = () => {
            RequestFetch.deleted({
                url: `${RequestFetch.BASE_URL}/api/rh/tipo_documento/${documento.id_tipo_documento}`,
                headers: cabeceras,
                exito: exito,
                fallo: fallo,
                error_request: () => {
                    history.push(`/server-error`);
                }
            });
        }

        if (mounted) {
            setLoading(true);
            send();
        }
    }

    React.useEffect(() => {
        getData();
        // eslint-disable-next-line
    }, [])


    return (
        <>
            <MaterialTable
                title="Documentos"
                columns={columns}
                localization={localization}
                data={data}
                actions={actions}
                options={options}
                isLoading={loading}
                editable={{
                    onRowAdd: (newData) =>
                        new Promise((resolve, reject) => {
                            setTimeout(() => {
                                console.log(newData);
                                if (newData.tipo !== undefined && newData.descripcion !== undefined && (newData.empresa || newData.empleado || newData.representante)) {
                                    POST(newData);
                                    resolve();
                                }
                                else {
                                    setToast({
                                        open: true,
                                        message: "Rellena los campos \"Documento\", \"Descripción\", debe ser asignado a al menos una entidad",
                                        severity: "warning",
                                        title: "Campos vacios!"
                                    })
                                    reject();
                                    return;
                                }
                            }, 600);
                        })
                    ,
                    onRowUpdate: (newData, oldData) =>
                        new Promise((resolve, reject) => {
                            setTimeout(() => {
                                if (newData.tipo !== '' && newData.descripcion !== '' && (newData.empresa || newData.empleado || newData.representante)) {
                                    resolve();
                                    PUT(newData, oldData);
                                } else {
                                    setToast({
                                        open: true,
                                        message: "Rellena los campos \"Documento\", \"Descripción\", debe ser asignado a al menos una entidad",
                                        severity: "warning",
                                        title: "Campos vacios!"
                                    })
                                    reject();
                                    return;
                                }
                            }, 600);
                        })
                    ,
                    onRowDelete: (oldData) =>
                        new Promise((resolve, reject) => {
                            setTimeout(() => {
                                resolve();
                                DELETE(oldData);
                            }, 600);
                        }),
                }}
            />


            <Snackbar open={toast.open} autoHideDuration={6000} onClose={handleClose}>
                <Alert onClose={handleClose} severity={toast.severity}>
                    <AlertTitle>{toast.title}</AlertTitle>
                    {toast.message}
                </Alert>
            </Snackbar>
        </>
    );
}