import React, { useState, createContext, createRef, useContext } from 'react';
import ReactDOM from "react-dom";
import { Grid, Paper, Box, Typography, Button, ButtonGroup, Hidden } from '@material-ui/core'
import { TableCell, TableRow, Table, TableHead, TableBody, TableContainer } from '@material-ui/core'
import EmpleadosReportes from './ReportesEmpleados/'
import EmpresasReportes from './ReportesEmpresas/'
import { Pie, Bar, HorizontalBar } from 'react-chartjs-2';
import BackIcon from '@material-ui/icons/KeyboardBackspace';
import DownloadIcon from '@material-ui/icons/GetApp';
import { savePDF } from "@progress/kendo-react-pdf";
import { useEmpresas } from './helper'
import BarIcon from '@material-ui/icons/Equalizer';
import PieIcon from '@material-ui/icons/PieChart';
import ImageLogo from '../../../assets/img/get.ico';

import { green, red, blue, orange, yellow, purple, cyan, pink, lime } from '@material-ui/core/colors/'

import { SessionContext } from '../../../routes/adminrh/CustomRoutes';

export const ReportContext = createContext(null);
const ReportRef = createRef(null);
const initialReport = { show: false, title: '', message: '', data: '', labels: '', type: '', label: '' }
export function Reportes() {
    const { ...contextProps } = useContext(SessionContext);
    const { empresas } = useEmpresas();
    const [report, setReport] = useState({ ...initialReport });

    const onChangeGhrapic = (type) => {
        setReport({ ...report, type })
    }

    const onClose = () => {
        setReport({ ...initialReport });
    }
    const onDownload = () => {
        savePDF(ReactDOM.findDOMNode(ReportRef.current), {
            paperSize: 'Legal',
            margin: 20,
            scale: 0.7,
            fileName: `Reporte`
        });
    }

    return (
        <ReportContext.Provider value={{ report, setReport }}>
            <Paper>
                <Box style={{ padding: 16 }}>
                    {
                        !report.show ?
                            <Grid
                                container
                                direction="row"
                                justify="center"
                                alignItems="flex-start"
                                spacing={2}>
                                <Grid item xs={12}>
                                    <Typography variant="button" display="block" style={{ textAlign: 'center' }}>
                                        Reportes de Empleados
                                </Typography>
                                </Grid>
                                <Grid item xs={12}>
                                    <EmpleadosReportes empresas={empresas} {...contextProps} />
                                </Grid>
                                <Grid item xs={12}>
                                    <Typography variant="button" display="block" style={{ textAlign: 'center' }}>
                                        Reportes de Empresas
                                </Typography>
                                </Grid>
                                <Grid item xs={12}>
                                    <EmpresasReportes empresas={empresas}  {...contextProps} />
                                </Grid>
                            </Grid>
                            :
                            <Grid
                                container
                                direction="row"
                                justify="center"
                                alignItems="flex-start"
                                spacing={2}>
                                <Grid item xs={12}>
                                    <Grid
                                        container
                                        direction="row"
                                        justify="space-between"
                                        alignItems="flex-start" >
                                        <Button startIcon={<BackIcon />} onClick={onClose}>
                                            <Hidden xsDown>
                                                Volver
                                            </Hidden>
                                        </Button>
                                        <Typography variant="button" color="initial" >
                                            Reporte Generado
                                        </Typography>
                                        <Button endIcon={<DownloadIcon />} color="secondary" onClick={onDownload}>
                                            <Hidden xsDown>
                                                Descargar
                                            </Hidden>
                                        </Button>
                                    </Grid>
                                </Grid>
                                {
                                    report.type !== 'table' &&
                                    <Grid item xs={12}>
                                        <Grid
                                            container
                                            direction="row"
                                            justify="center"
                                            alignItems="center" >
                                            <ButtonGroup variant="text" color="secondary" aria-label="text primary button group">
                                                <Button endIcon={<BarIcon style={{ transform: 'rotate(90deg)' }} />} onClick={() => onChangeGhrapic('horizontalbar')}>
                                                    <Hidden xsDown>
                                                        Horizontal
                                                    </Hidden>
                                                </Button>
                                                <Button endIcon={<BarIcon />} onClick={() => onChangeGhrapic('bar')}>
                                                    <Hidden xsDown>
                                                        Vertical
                                                    </Hidden>
                                                </Button>
                                                <Button endIcon={<PieIcon />} onClick={() => onChangeGhrapic('pie')}>
                                                    <Hidden xsDown>
                                                        Circular
                                                    </Hidden>
                                                </Button>
                                            </ButtonGroup>
                                        </Grid>
                                    </Grid>}
                                <Grid item ref={ReportRef} >
                                    <Grid
                                        container
                                        direction="column"
                                        spacing={2}>
                                        <Grid item xs={12}>
                                            <Grid container
                                                direction="row"
                                                justify="center"
                                                alignItems="flex-end" >
                                                <Grid item xs={2}>
                                                    <img src={ImageLogo} alt="pdfimage" height={100} crossOrigin="anonymous" />
                                                </Grid>
                                                <Grid item xs={10}>
                                                    <Grid container
                                                        direction="column"
                                                        justify="flex-start"
                                                        alignItems="flex-start" >
                                                        <Grid item xs={12}>
                                                            <Typography variant="body1" color="initial" display="block" >
                                                                Reporte: {report.title}
                                                            </Typography>
                                                        </Grid>
                                                        <Grid item xs={12}>
                                                            <Typography variant="body2" color="initial" display="block">
                                                                {report.message}
                                                            </Typography>
                                                        </Grid>
                                                    </Grid>
                                                </Grid>

                                            </Grid>
                                        </Grid>
                                        <Grid item xs={12}>
                                            <Grid container
                                                direction="row"
                                                justify="center"
                                                alignItems="center">
                                                <Ghrapic {...report} />
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                </Grid>
                            </Grid>
                    }
                </Box>
            </Paper>
        </ReportContext.Provider>
    )
}


const Ghrapic = (props) => {
    const data = getDataStruct({ ...props });
    switch (props.type) {
        case 'pie':
            return <Pie data={data} />;
        case 'bar':
            return <Bar data={data} />;
        case 'horizontalbar':
            return <HorizontalBar data={data} />;
        case 'table':
            return <ReportTable data={props.data} labels={props.labels} />
        default: return null;
    }
}


const getDataStruct = ({ type, data, labels, label }) => {
    const options = {
        maintainAspectRatio: false
    }
    switch (type) {
        case 'pie':
            return {
                labels: labels.map((l, i) => `${l}: ${data[i]}`),
                datasets: [{
                    data,
                    backgroundColor: [
                        red[300],
                        green[300],
                        orange[300],
                        blue[300],
                        yellow[300],
                        purple[300],
                        cyan[300],
                        pink[300],
                        lime[300]
                    ],
                    hoverBackgroundColor: [
                        red[600],
                        green[600],
                        orange[600],
                        blue[500],
                        yellow[600],
                        purple[600],
                        cyan[600],
                        pink[600],
                        lime[600]
                    ],
                    stack: 0
                }],
                options
            };
        case 'bar': case 'horizontalbar': default:
            return {
                labels,
                datasets: [
                    {
                        label,
                        backgroundColor: 'rgba(245, 124, 0,0.2)',
                        borderColor: 'rgba(245, 124, 0,1)',
                        borderWidth: 1,
                        hoverBackgroundColor: 'rgba(216, 67, 21,0.4)',
                        hoverBorderColor: 'rgba(216, 67, 21,1)',
                        data,
                        stack: 0
                    }
                ],
                options
            };
    }
}

const ReportTable = ({ data, labels }) => {
    return (
        <TableContainer>
            <Table size="small" aria-label="a dense table">
                <TableHead>
                    <TableRow>
                        {
                            labels.map(({ title, hidden, align, id }) => {
                                if (!hidden)
                                    return <TableCell
                                        key={`header-table-ghrapic-${id}`}
                                        align={align} >
                                        {title}
                                    </TableCell>
                                return null
                            })
                        }
                    </TableRow>
                </TableHead>
                <TableBody>
                    {data.map((row, index) => (
                        <TableRow key={`table-row-ghrapic-${index}`}>
                            {
                                labels.map(({ hidden, align, id }, i) => {
                                    console.log(row[id]);
                                    if (!hidden)
                                        return <TableCell
                                            align={align}
                                            key={`table-cell-ghrapic-${i}`}>
                                            {row[id]}
                                        </TableCell>
                                    return null;
                                })
                            }
                        </TableRow>
                    ))}
                </TableBody>
            </Table>
        </TableContainer>
    )
}