import React from 'react'
import {
    TextField,
    Dialog, DialogTitle, DialogContent, Button, DialogActions, Grid, DialogContentText, LinearProgress, Typography
} from '@material-ui/core'
import { Formik, Form, Field } from "formik";
import * as Yup from "yup";
import { BASE_URL } from '../../../libraries/RequestFetch';
import { SessionContext } from '../../../routes/adminrh/CustomRoutes';


export default function DialogUser({ open, data, onClose, notification }) {
    const [loading, setLoading] = React.useState(false)
    const [error, setError] = React.useState(false);
    const { cabeceras, isTokenExpired } = React.useContext(SessionContext);

    const Schema = Yup.object().shape({
        password: Yup.string().required("Contraseña Requerida"),
        changepassword: Yup.string().when("password", {
            is: val => (val && val.length > 0 ? true : false),
            then: Yup.string().oneOf(
                [Yup.ref("password")],
                "Las contraseñas no coinciden"
            )
        })
    });

    const onPostPassword = async ({ password }) => {
        setLoading(true);
        try {
            const toSend = JSON.stringify({ contrasenia: password })
            const resp = await fetch(`${BASE_URL}/api/rh/usuario/${data.id_usuario}`,
                { method: 'PUT', headers: cabeceras, body: toSend });
            const response = await resp.json();
            if (!isTokenExpired(response)) {
                const { error } = response;
                setLoading(false);
                if (error) {
                    setError(true)
                    setTimeout(() => setError(false), 1000);
                } else {
                    notification({
                        open: true,
                        message: "",
                        severity: "success",
                        title: "Contraseña Reestablecida"
                    })
                    onClose()
                }
            }
        } catch (error) {
            console.log(error);
        }
        setLoading(false);
    }

    return (
        <Dialog open={open} onClose={onClose} aria-labelledby="dialog" maxWidth={"xs"} fullWidth>
            {loading && <LinearProgress color="secondary" />}
            <Formik
                initialValues={{
                    password: "",
                    changepassword: ""
                }}
                validationSchema={Schema}
                onSubmit={onPostPassword}>
                {(props) => {
                    return <Form>
                        <DialogTitle>Reestablecer Contraseña</DialogTitle>
                        <DialogContent>
                            <DialogContentText>
                                La contraseña se reestablecerá para el usuario:
                                </DialogContentText>
                            <DialogContentText>
                                <b>{data.correo}</b>
                            </DialogContentText>
                            <Grid
                                spacing={2}
                                container
                                direction="column"
                                justify="center"
                                alignItems="stretch">
                                <Grid item >
                                    <Field name="password" label="Contraseña" placeholder="************" component={InputFormikUI} type="password" />
                                </Grid>
                                <Grid item >
                                    <Field name="changepassword" label="Confirma Contraseña" placeholder="************" component={InputFormikUI} type="password" />
                                </Grid>
                            </Grid>
                            {error &&
                                <Typography variant="button" color="secondary">Ha ocurrido un error, intenta nuevamente</Typography>
                            }
                        </DialogContent>
                        <DialogActions>
                            <Button onClick={onClose} color="default">
                                Cancel
                            </Button>
                            <Button onClick={props.handleSubmit} color="secondary" type="submit">
                                Guardar
                            </Button>
                        </DialogActions>
                    </Form>

                }}
            </Formik>
        </Dialog>
    )
}

export const InputFormikUI = ({ field, form, children, ...other }) => {
    return <TextField
        size="small"
        autoComplete='off'
        fullWidth
        name={field.name}
        value={field.value}
        error={form.errors[field.name] && form.touched[field.name]}
        helperText={form.errors[field.name] && form.touched[field.name] && String(form.errors[field.name])}
        onChange={field.onChange}
        onBlur={field.onBlur}
        {...other}
    >
        {children}
    </TextField>
}