import React from 'react'
import RequestFetch from '../../../libraries/RequestFetch'
import moment from 'moment';
import MaterialTable, { MTableToolbar } from 'material-table';
import { useHistory, useParams } from 'react-router-dom';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import EditOutlinedIcon from '@material-ui/icons/EditOutlined';
import DeleteOutlineOutlinedIcon from '@material-ui/icons/DeleteOutlineOutlined';
import { Snackbar, TextField, MenuItem } from '@material-ui/core';
import { AlertTitle } from '@material-ui/lab';
import MuiAlert from '@material-ui/lab/Alert';
import VisibilityIcon from '@material-ui/icons/Visibility';
import { EstadoByCode, isCode } from '../../../libraries/Estados';

import SeeEmpleado from './SeeEmpleado';
import { SessionContext } from '../../../routes/adminrh/CustomRoutes'

const estatus = {
    P: 'Pendiente',
    A: 'Activo',
    B: 'Baja'
}
const estado_civil = {
    'C': 'Casado',
    'S': 'Soltero',
    'D': 'Divorciado',
    'V': 'Viudo',
    'I': 'Indistinto',
}

const genero = {
    'F': 'Femenino',
    'M': 'Masculino',
    'O': 'Otro'
}

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

export default function TableEmpleado() {
    const { cabeceras, isTokenExpired, permisos, rol } = React.useContext(SessionContext);
    const history = useHistory();
    const params = useParams();
    const { interna, externa } = params;

    const [tipos, setTipos] = React.useState({
        interna, externa
    });

    //const classes = useStyles();
    const [extraData, setExtraData] = React.useState({ bancos: [], nacionalidades: [] })
    const [data, setData] = React.useState([]);
    const [empresas, setEmpresas] = React.useState([]);
    const [dialog, setDialog] = React.useState(false);
    const [loading, setLoading] = React.useState(true);
    const [deleted, setDeleted] = React.useState({});
    const [toast, setToast] = React.useState({
        open: false,
        message: "",
        severity: "",
        title: ""
    });

    const [see, setSee] = React.useState(false);
    const [verEmple, setVerEmple] = React.useState({});


    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            setToast({ ...toast, open: false });
            return;
        }
        setToast({ ...toast, open: false });
    };

    const handleDelete = () => {

        let mounted = true;

        const DELETEEmpleadoExito = async (res) => {
            const respuesta = await res.json();
            console.log(respuesta);
            if (respuesta.updated) {
                const { nombre, primer_apellido, rfc } = deleted;
                if (mounted) {
                    setToast({
                        open: true,
                        message: `${nombre} ${primer_apellido} ${rfc} ha sido dato de baja con exito`,
                        severity: "warning",
                        title: "Eliminacion exitosa"
                    });
                    getData();
                }
            }
            else {
                if (mounted) {
                    setLoading(false);
                    const { nombre, primer_apellido, rfc } = deleted;
                    setToast({
                        open: true,
                        message: `${nombre} ${primer_apellido} ${rfc} ha ocurrido un error`,
                        severity: "error",
                        title: "Eliminacion fallida"
                    });
                }

            }
        }

        const DELETEEmpleadoFallo = async (res) => {
            if (!isTokenExpired(res))
                if (mounted) {
                    const { nombre, primer_apellido, rfc } = deleted;
                    setLoading(false);
                    setToast({
                        open: true,
                        message: `${nombre} ${primer_apellido} ${rfc} ha ocurrido un error`,
                        severity: "error",
                        title: "Eliminacion fallida"
                    });
                }
        }

        const _falloReq = () => {
            history.push(`/server-error`);
        }

        const DELETESendEmpleado = () => {
            setLoading(true);
            const newform = new FormData();
            newform.append('estatus', 'B');
            const headers = new Headers();
            headers.append('authorization', cabeceras.authorization);
            RequestFetch.putForm({
                url: `${RequestFetch.BASE_URL}/api/rh/empleado/${deleted.id_empleado}`,
                headers: headers,
                data: newform,
                exito: DELETEEmpleadoExito,
                fallo: DELETEEmpleadoFallo,
                error_request: _falloReq
            });
        }

        if (mounted) {
            setDialog(false);
            DELETESendEmpleado();
        }
    }

    const fetchEmpleados = ({ interna, externa }) => {
        let url = `${RequestFetch.BASE_URL}/api/rh/empleado/?query=${RequestFetch.EMPLEADO_ALL}`;
        if (interna !== 'all' || externa !== 'all') {
            if (interna !== 'all') url += `&id_empresa_pertene=${interna}`;
            if (externa !== 'all') url += `&id_empresa_labora=${externa}`;
        }
        return fetch(url, { method: 'GET', headers: cabeceras })
    }

    const clearEmpleados = (nacionalidades, bancos, empleados) => {
        if (empleados.length === 0) {
            setLoading(false);
            if (!toast.open) {
                setToast({
                    open: true,
                    message: "No existen registros de empleados",
                    severity: "info",
                    title: "Ningun registro encontrado"
                })
            }
        }
        empleados.forEach(element => {
            if (element['identificador'] === '') element['identificador'] = null;
            element['fecha_nacimiento'] = moment.utc(element['fecha_nacimiento']).format("DD/MM/YYYY");
            element['estatus'] = estatus[element['estatus']];
            element['estado_civil'] = estado_civil[element['estado_civil']];
            element['genero'] = genero[element['genero']];
            element['entidad_reside'] = !isCode(element['entidad_reside']) ? element['entidad_reside'] : EstadoByCode(element['entidad_reside']);
            element['id_nacionalidad'] = nacionalidades.find(item => item.id_nacionalidad === element['id_nacionalidad']).pais_nac;
            element['id_banco'] = element['id_banco'] !== null ? bancos.find(item => item.id_banco === element['id_banco']).nombre_banco : '';
        });
        return empleados
    }

    const getData = () => {
        let mounted = true;
        if (mounted) {
            setLoading(true);
            const options = { method: 'GET', headers: cabeceras }
            Promise.all([
                fetch(`${RequestFetch.BASE_URL}/api/rh/nacionalidad`, options),
                fetch(`${RequestFetch.BASE_URL}/api/rh/banco`, options),
                fetch(`${RequestFetch.BASE_URL}/api/rh/empresa?query=id_empresa-es_interna-razon_social`, options),
                fetchEmpleados({ interna, externa })
            ]).then(async ([nacionalidad, banco, empresa, empleado]) => {
                const nacionalidades = await nacionalidad.json();
                const bancos = await banco.json();
                const empresas = await empresa.json();
                const empleados = await empleado.json();
                if (!isTokenExpired(nacionalidades) && !isTokenExpired(bancos) && !isTokenExpired(empresas) && !isTokenExpired(empleados)) {
                    return {
                        nacionalidades,
                        bancos: bancos.bancos,
                        empresas: empresas.empresas,
                        empleados: empleados.empleados
                    }
                }
                else return {}

            }).then(({ nacionalidades, bancos, empresas, empleados }) => {
                if (nacionalidades && bancos && empresas && empleados)
                    if (mounted) {
                        if (rol === 'G') empleados = empleados.filter(e => permisos.some(p => e.id_empresa_labora === p));
                        setEmpresas(empresas);
                        setExtraData({ bancos, nacionalidades });
                        setData(clearEmpleados(nacionalidades, bancos, empleados));
                    }
                setLoading(false);
            }).catch((err) => {
                //console.log(err);
                history.push(`/server-error`);
            });
        }
        return () => mounted = false;
        // eslint-disable-next-line
    };

    const onChangeSelect = (es_interna, { target: { value } }) => {
        let url = '';
        setData([]);
        setLoading(true);
        let newTipos = { interna: '', externa: '' }
        if (es_interna) {
            url = `/nomina/listar-empleado-by/${value}/${tipos.externa}`;
            newTipos = { ...tipos, interna: value };
            setTipos(newTipos);
        }
        else {
            url = `/nomina/listar-empleado-by/${tipos.interna}/${value}`;
            newTipos = { ...tipos, externa: value };
            setTipos(newTipos);
        }
        history.push(url);
        fetchEmpleados(newTipos)
            .then(resp => resp.json())
            .then((res) => {
                if (!isTokenExpired(res)) {
                    setData(clearEmpleados(extraData.nacionalidades, extraData.bancos, res.empleados));
                    setLoading(false);
                }
            })
            .catch((err) => {
                //console.log(err);
                history.push(`/server-error`);
            });
    }

    React.useEffect(() => {
        let mounted = true;
        if (mounted) getData();
        return () => mounted = false;

        // eslint-disable-next-line
    }, []);

    const localization = {
        header: {
            actions: 'Acciones'
        },
        body: {
            emptyDataSourceMessage: 'No hay registros que mostrar',
            editTooltip: 'Editar',
            deleteTooltip: 'Borrar',
            addTooltip: 'Añadir',
            editRow: {
                cancelTooltip: 'Cancelar',
                saveTooltip: 'Guardar',
                deleteText: '¿Estás seguro de eliminar este empleado?'
            }
        },
        toolbar: {
            searchTooltip: 'Buscar',
            searchPlaceholder: 'Buscar',
            exportName: 'Exportar como CSV',
            exportTitle: 'Exportar',
            showColumnsTitle: 'Columas',
            showColumnsAriaLabel: 'Columnas',
            addRemoveColumns: 'Agregar o remover columnas'
        },
        pagination: {
            firstTooltip: 'Primera página',
            previousTooltip: 'Página anterior',
            nextTooltip: 'Página siguiente',
            lastTooltip: 'Última página',
            labelRowsSelect: 'filas',
            labelDisplayedRows: '{from}-{to} a {count}',
        },
        grouping: { placeholder: 'Arrastre los encabezados aquí para agrupar por' }
    }

    const columns = [
        { title: 'Identificador', field: 'identificador', width: 200, removable: false, emptyValue: 'SIN CONTRATO ACTIVO', allSort: 'asc' },
        { title: 'RFC', field: 'rfc', width: 200, removable: false },
        { title: 'Nombre', field: 'nombre', width: 180, removable: false },
        { title: 'Primer Apellido', field: 'primer_apellido', width: 160, removable: false },
        { title: 'Segundo Apellido', field: 'segundo_apellido', hidden: true, width: 160, emptyValue: 'N/A' },
        { title: 'Fecha Nac.', field: 'fecha_nacimiento', width: 140 },
        { title: 'Estado Civil', field: 'estado_civil', width: 140 },
        { title: 'Sexo', field: 'genero', width: 80 },
        { title: 'CURP', field: 'curp', width: 200 },
        { title: 'Nacionalidad', field: 'id_nacionalidad', width: 150 },
        { title: 'No. IMSS', field: 'no_imss', width: 150 },

        { title: 'Estatus', field: 'estatus', width: 100 },

        { title: 'Calle', field: 'calle', width: 200 },
        { title: 'No.Ext', field: 'no_exterior', width: 100 },
        { title: 'No.Int', field: 'no_interior', hidden: true, width: 100, emptyValue: 'S/N' },
        { title: 'CP.', field: 'cp', width: 100 },
        { title: 'Colonia', field: 'colonia', width: 200 },
        { title: 'Municipio', field: 'municipio', width: 200 },
        { title: 'Estado', field: 'entidad_reside', width: 150 },

        { title: 'Salario D.', field: 'salario_diario', hidden: true, width: 130, type: 'numeric' },
        { title: 'Salario C.', field: 'salario_capturado', hidden: true, width: 130, type: 'numeric' },
        { title: 'No. Cuenta', field: 'no_cuenta', hidden: true, width: 170, emptyValue: 'N/A' },
        { title: 'No. Tarjeta', field: 'no_tarjeta', hidden: true, width: 170, emptyValue: 'N/A' },
        { title: 'CLABE', field: 'clabe', hidden: true, width: 200, emptyValue: 'N/A' },
        { title: 'Banco', field: 'id_banco', hidden: true, width: 150 },
        { title: '', field: "id_empleado", hidden: true, width: 0, editable: 'never', removable: false },
    ]

    const actions = [
        {
            icon: () => <VisibilityIcon />,
            tooltip: 'Ver',
            onClick: (event, rowData) => {
                //console.log(rowData);
                setVerEmple(rowData);
                setSee(true);
            }
        },
        {
            icon: () => <EditOutlinedIcon />,
            tooltip: 'Editar',
            onClick: (event, rowData) => history.push(`/nomina/edit-empleado/${rowData.id_empleado}`)
        },
        {
            icon: () => <DeleteOutlineOutlinedIcon />,
            tooltip: 'Eliminar',
            onClick: (event, rowData) => {
                setDialog(true);
                setDeleted(rowData);
            }
        },
        {
            icon: 'refresh',
            tooltip: 'Recargar',
            isFreeAction: true,
            onClick: getData
        },
        {
            icon: 'add',
            tooltip: 'Agregar',
            isFreeAction: true,
            onClick: (event, rowData) => history.push(`/nomina/nuevo-empleado`)
        }
    ];

    const getEmpresasPermisos = () => {
        if (rol === 'G')
            return empresas.filter(e => permisos.some(p => e.id_empresa === p));
        else return empresas;
    }

    const components = {
        Toolbar: props => (
            <div>
                <MTableToolbar {...props} />
                <div style={{ padding: '0px 8px 8px 24px' }}>
                    <TextField style={{ minWidth: 250, marginRight: 20 }}
                        select
                        label="Empresa Pertenece"
                        value={empresas.length > 0 ? tipos.interna : 'all'}
                        onChange={e => onChangeSelect(true, e)}>
                        <MenuItem value="all">TODOS</MenuItem>
                        {
                            empresas.filter(e => e.es_interna)
                                .map(e => <MenuItem key={`empresas_interna=${e.id_empresa}`} value={`${e.id_empresa}`}>
                                    {e.razon_social}
                                </MenuItem>)
                        }
                    </TextField>
                    <TextField style={{ minWidth: 250 }}
                        select
                        label="Empresa Labora"
                        defaultValue="all"
                        value={empresas.length > 0 ? tipos.externa : 'all'}
                        onChange={e => onChangeSelect(false, e)}>
                        <MenuItem value="all">TODOS</MenuItem>
                        {
                            getEmpresasPermisos().map(e => <MenuItem key={`empresas_all=${e.id_empresa}`} value={`${e.id_empresa}`}>
                                {e.razon_social}
                            </MenuItem>)
                        }
                    </TextField>
                </div>
            </div>
        ),
    }

    return (
        <React.Fragment>
            <MaterialTable
                localization={localization}
                title="Empleados"
                columns={columns}
                data={data}
                actions={actions}
                components={components}
                options={{
                    draggable: false,
                    columnsButton: true,
                    exportAllData: true,
                    exportButton: true,
                    pageSize: 10,
                    pageSizeOptions: [10, 20, 50, 100],
                    fixedColumns: {
                        left: 2,
                    },
                    headerStyle: {
                        fontWeight: 'bold'
                    }
                }}
                isLoading={loading}
            />

            <Dialog
                open={dialog}
                TransitionComponent={Transition}
                onClose={() => setDialog(false)}>
                <DialogTitle>{"Eliminar empleado"}</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Está apunto de eliminar al empleado
                        {` ${deleted.nombre} ${deleted.primer_apellido} `}
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setDialog(false)}>Cancelar</Button>
                    <Button onClick={handleDelete} color="secondary">Eliminar</Button>
                </DialogActions>
            </Dialog>


            <SeeEmpleado open={see} setOpen={setSee} data={verEmple} />

            <Snackbar open={toast.open} autoHideDuration={6000} onClose={handleClose}>
                <Alert onClose={handleClose} severity={toast.severity}>
                    <AlertTitle>{toast.title}</AlertTitle>
                    {toast.message}
                </Alert>
            </Snackbar>
        </React.Fragment>
    );
}