import React, { createContext } from 'react';
import AppBar from '@material-ui/core/AppBar';
import CssBaseline from '@material-ui/core/CssBaseline';
import clsx from "clsx";
import Drawer from '@material-ui/core/Drawer';
import Avatar from '@material-ui/core/Avatar';
import Hidden from '@material-ui/core/Hidden';
import IconButton from '@material-ui/core/IconButton';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import MenuIcon from '@material-ui/icons/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import CloseIcon from '@material-ui/icons/Close';

import ReactResizeDetector from 'react-resize-detector';
import { MuiPickersUtilsProvider } from '@material-ui/pickers';
import MomentUtils from "@date-io/moment";
import moment from 'moment';
import "moment/locale/es";
import { SnackbarProvider } from 'notistack';
import { ThemeProvider } from '@material-ui/core/styles';
import { ThemeDashboard, useStylesDashboard } from './DashboardStyle'
import { useHistory } from 'react-router-dom';
//STYLED SCROLL BAR
import './Dashboard.css';
import { RoutesMenu, routesUser } from '../../components/adminrh/Dash/Permissions';

import { SessionContext } from '../../routes/adminrh/CustomRoutes';

export const DashBoardContext = createContext();
function Dashboard(props) {
    const history = useHistory();
    const classes = useStylesDashboard();
    const { logout, rol } = React.useContext(SessionContext);

    const { container } = props;
    const notistackRef = React.createRef();

    const [size, setSize] = React.useState(0);
    const [open, setOpen] = React.useState(true);
    const [mobileOpen, setMobileOpen] = React.useState(false);
    const [anchorEl, setAnchorEl] = React.useState(null);
    const [user, setUser] = React.useState();
    const openMenu = Boolean(anchorEl);


    const onResize = (event) => {
        if (size !== event) {
            setSize(event)
            if (event < 1024)
                setOpen(false);
            else
                setOpen(!open ? false : true);
        }

    };

    const handleDrawerToggle = () => {
        setMobileOpen(!mobileOpen);
    };

    const handleDrawerOpen = () => {
        setOpen(!open);
    };

    const handleMenu = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };


    const drawer = (
        <div>
            <RoutesMenu type={rol} />
        </div>
    );

    const onClickDismiss = key => () => {
        notistackRef.current.closeSnackbar(key);
    }

    return (
        <DashBoardContext.Provider value={{ setUser }}>
            <SnackbarProvider maxSnack={5}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'right',
                }}
                ref={notistackRef}
                action={(key) => (
                    <IconButton onClick={onClickDismiss(key)}>
                        <CloseIcon style={{ color: 'white' }} fontSize="small" />
                    </IconButton>
                )}
            >
                <ReactResizeDetector handleWidth handleHeight onResize={onResize} />
                <div>
                    <CssBaseline />
                    <AppBar position="fixed"
                        className={clsx(classes.appBar, {
                            [classes.appBarShift]: open
                        })}>
                        <Toolbar>
                            <Hidden smDown implementation="css">
                                <IconButton
                                    color="inherit"
                                    aria-label="open drawer"
                                    onClick={handleDrawerOpen}
                                    edge="start"
                                    className={classes.menuButton}
                                >
                                    <MenuIcon />
                                </IconButton>
                            </Hidden>
                            <Hidden mdUp implementation="css">
                                <IconButton
                                    color="inherit"
                                    aria-label="open drawer"
                                    onClick={handleDrawerToggle}
                                    edge="start"
                                >
                                    <MenuIcon />
                                </IconButton>
                            </Hidden>
                            <Typography variant="button" noWrap className={classes.title}>
                                Grupo Estrategia Tributaria
                            </Typography>
                            <div>
                                {user &&
                                    user.url && user.url !== null && user.url !== '' ?
                                    <Avatar onClick={handleMenu} src={`http://res.cloudinary.com/get-consultoria/image/upload/v1/${user.url}`} className={classes.avatar} /> :
                                    <Avatar onClick={handleMenu}>{
                                        user ? user.nombre && user.nombre[0]
                                            :
                                            'U'
                                    }</Avatar>
                                }
                                <Menu
                                    id="menu-appbar"
                                    anchorEl={anchorEl}
                                    anchorOrigin={{
                                        vertical: 'top',
                                        horizontal: 'right',
                                    }}
                                    keepMounted
                                    transformOrigin={{
                                        vertical: 'top',
                                        horizontal: 'right',
                                    }}
                                    open={openMenu}
                                    onClose={handleClose}
                                >
                                    <MenuItem onClick={() => {
                                        logout();
                                        history.push('/nomina');
                                    }}>Cerrar Session</MenuItem>
                                </Menu>
                            </div>
                        </Toolbar>
                    </AppBar>
                    <nav className={classes.drawer} aria-label="mailbox folders">
                        <Hidden mdUp implementation="css">
                            <Drawer
                                container={container}
                                variant="temporary"
                                anchor={"left"}
                                open={mobileOpen}
                                onClose={handleDrawerToggle}
                                classes={{
                                    paper: classes.drawerPaper
                                }}
                                ModalProps={{
                                    keepMounted: true // Better open performance on mobile.
                                }}
                            >
                                {drawer}
                            </Drawer>
                        </Hidden>
                        <Hidden smDown implementation="css">
                            <Drawer
                                className={classes.drawer}
                                variant="persistent"
                                anchor="left"
                                open={open}
                                classes={{
                                    paper: classes.drawerPaper
                                }}
                            >
                                {drawer}
                            </Drawer>
                        </Hidden>
                    </nav>
                    <main className={clsx(classes.content, { [classes.contentShift]: open })}>
                        <div className={classes.drawerHeader} />
                        <ThemeProvider theme={ThemeDashboard}>
                            <MuiPickersUtilsProvider libInstance={moment} locale="es" utils={MomentUtils}>
                                {routesUser(rol)}
                            </MuiPickersUtilsProvider>
                        </ThemeProvider>
                    </main>
                </div>
            </SnackbarProvider>
        </DashBoardContext.Provider>
    );
}

export default Dashboard;
