import React from 'react'
import MaskedInput from "react-text-mask";
import createNumberMask from 'text-mask-addons/dist/createNumberMask'


const defaultMaskOptions = {
    prefix: '',
    suffix: '',
    includeThousandsSeparator: true,//no incluir ','
    thousandsSeparatorSymbol: '',
    decimalSymbol: '.',
    allowNegative: false,
    allowLeadingZeroes: false,
}

export const OneIntegerFourDecimal = ({ inputRef, ...other }) => {
    const currencyMask = createNumberMask({
        ...defaultMaskOptions,
        decimalLimit: 4,
        integerLimit: 1,
        allowDecimal: true
    })
    return (
        <MaskedInput
            {...other}
            ref={ref => {
                inputRef(ref ? ref.inputElement : null);
            }}//{inputRef}
            mask={currencyMask}
            placeholderChar={'\u2000'}
        />
    )
}

export const YearMask = ({ inputRef, ...other }) => {
    return (
        <MaskedInput
            {...other}
            ref={ref => {
                inputRef(ref ? ref.inputElement : null);
            }}//{inputRef}
            mask={['2', '0', /\d/, /\d/]}
            placeholderChar={'\u2000'}
        />
    )
}

export const TwoInteger = ({ inputRef, ...other }) => {
    const currencyMask = createNumberMask({
        ...defaultMaskOptions,
        decimalLimit: 0,
        integerLimit: 2,
        allowDecimal: false
    })
    return (
        <MaskedInput
            {...other}
            ref={ref => {
                inputRef(ref ? ref.inputElement : null);
            }}//{inputRef}
            mask={currencyMask}
            placeholderChar={'\u2000'}
        />
    )
}
export const LimitMask = ({ inputRef, ...other }) => {
    const currencyMask = createNumberMask({
        ...defaultMaskOptions,
        decimalLimit: 2,
        integerLimit: 9,
        allowDecimal: true
    })
    return (
        <MaskedInput
            {...other}
            ref={ref => {
                inputRef(ref ? ref.inputElement : null);
            }}//{inputRef}
            mask={currencyMask}
            placeholderChar={'\u2000'}
        />
    )
}

