import React from 'react'
import RequestFetch from '../../../libraries/RequestFetch'
import MaterialTable from 'material-table';
import { useHistory } from 'react-router-dom';
import EditOutlinedIcon from '@material-ui/icons/EditOutlined';
import { Snackbar } from '@material-ui/core';
import { AlertTitle } from '@material-ui/lab';
import MuiAlert from '@material-ui/lab/Alert';
import SeeRepresentante from './SeeRepresentante';
import VisibilityIcon from '@material-ui/icons/Visibility';
import { SessionContext } from '../../../routes/adminrh/CustomRoutes';


function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}
const genero = {
    M: 'Mujer',
    H: 'Hombre'
}

const estatus = {
    A: 'Activo',
    B: 'Baja',
    P: 'Pendiente'
}

export default function TableRepresentante() {
    const history = useHistory();
    const { cabeceras, isTokenExpired } = React.useContext(SessionContext);
    const [data, setData] = React.useState([]);
    const [repres, setRepres] = React.useState([]);
    const [loading, setLoading] = React.useState(true);
    const [seeRepre, setSeeRepre] = React.useState(false);
    const [verRepre, setVerRepre] = React.useState({});
    const [toast, setToast] = React.useState({
        open: false,
        message: "",
        severity: "",
        title: ""
    });

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            setToast({ ...toast, open: false });
            return;
        }
        setToast({ ...toast, open: false });
    };





    const getData = React.useCallback(() => {
        let mounted = true;
        setLoading(true);
        setData([]);
        setRepres([]);

        RequestFetch.get({
            url: `${RequestFetch.BASE_URL}/api/rh/representante?query=${RequestFetch.REPRESENTANTE_ALL}`,
            headers: cabeceras,
            exito: async (res) => {
                const respuesta = await res.json();
                if (mounted) {
                    setData(respuesta.representantes);
                    setLoading(false);
                }
            },
            fallo: async (res) => {
                const response = await res.json();
                if (!isTokenExpired(response));
            },
            error_request: () => history.push(`/server-error`)
        });
        return () => mounted = false;
        // eslint-disable-next-line
    }, [history]);

    React.useEffect(() => {
        getData();
        // eslint-disable-next-line
    }, []);

    React.useEffect(() => {
        let mounted = true;

        if (data.length > 0 && repres.length === 0) {
            const newData = [...data];
            newData.forEach(item => {
                item['genero'] = genero[item['genero']]
                item['estatus'] = estatus[item['estatus']]
            })

            if (mounted) {
                setRepres(newData);
                setLoading(false);
            };

        }

        return () => mounted = false;
    }, [data, repres]);

    const localization = {
        header: {
            actions: 'Acciones'
        },
        body: {
            emptyDataSourceMessage: 'No hay registros que mostrar',
            editTooltip: 'Editar',
            deleteTooltip: 'Borrar',
            addTooltip: 'Añadir',
            editRow: {
                cancelTooltip: 'Cancelar',
                saveTooltip: 'Guardar',
                deleteText: '¿Estás seguro de eliminar este puesto?'
            }
        },
        toolbar: {
            searchTooltip: 'Buscar',
            searchPlaceholder: 'Buscar',
            exportName: 'Exportar como CSV',
            exportTitle: 'Exportar',
            showColumnsTitle: 'Columas',
            showColumnsAriaLabel: 'Columnas',
            addRemoveColumns: 'Agregar o remover columnas'
        },
        pagination: {
            firstTooltip: 'Primera página',
            previousTooltip: 'Página anterior',
            nextTooltip: 'Siguiente página',
            lastTooltip: 'Última página',
            labelRowsSelect: 'filas',
            labelDisplayedRows: '{from}-{to} a {count}',
        }
    }

    const columns = [
        { title: 'RFC', field: 'rfc', width: 220, defaultSort: 'asc' },
        { title: 'Nombre', field: 'nombre', width: 200 },
        { title: 'Primer Apellido', field: 'primer_apellido', width: 200 },
        { title: 'Segundo Apellido', field: 'segundo_apellido', width: 200 },
        { title: 'Genero', field: 'genero', width: 100 },
        { title: 'Correo', field: 'correo', width: 300 },
        { title: 'Estaus', field: 'estatus', width: 100 },
        { title: '', field: "id_representante", hidden: true, width: 0, removable: false },
    ]

    const actions = [
        {
            icon: () => <VisibilityIcon />,
            tooltip: 'Ver',
            onClick: (event, rowData) => {
                setVerRepre(rowData);
                setSeeRepre(true);
            }
        },
        {
            icon: () => <EditOutlinedIcon />,
            tooltip: 'Editar',
            onClick: (event, rowData) => {
                history.push(`edit-representante/${rowData.id_representante}`)
            }
        },
        /*
        {
            icon: () => <DeleteOutlineOutlinedIcon />,
            tooltip: 'Eliminar',
            onClick: (event, rowData) => {
                setDeleted(rowData);
                setDialog(true);
            },
        },*/
        {
            icon: 'refresh',
            tooltip: 'Recargar',
            isFreeAction: true,
            onClick: getData
        },
        {
            icon: 'add',
            tooltip: 'Agregar',
            isFreeAction: true,
            onClick: (event, rowData) => history.push(`nuevo-representante`)
        }
    ];

    return (
        <React.Fragment>
            <MaterialTable
                localization={localization}
                title="Representantes"
                columns={columns}
                data={repres}
                actions={actions}
                options={{
                    paginationType: 'normal',
                    exportAllData: true,
                    exportButton: true,
                    pageSize: 10,
                    pageSizeOptions: [10, 20, 50, 100],
                    fixedColumns: {
                        left: 2,
                    },
                    headerStyle: {
                        fontWeight: 'bold'
                    }
                }}
                isLoading={loading}
            />

            {
                /*<Dialog
                open={dialog}
                TransitionComponent={Transition}
                onClose={() => setDialog(false)}>
                <DialogTitle>{"Eliminar un representante"}</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Está apunto de elimiar a un representante
                        {` ${deleted.nombre} ${deleted.primer_apellido} ${deleted.segundo_apellido}`}
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setDialog(false)}>Cancelar</Button>
                    <Button onClick={handleDelete} color="secondary">Elimnar</Button>
                </DialogActions>
            </Dialog>*/
            }

            <SeeRepresentante open={seeRepre} setOpen={setSeeRepre} data={verRepre} />

            <Snackbar open={toast.open} autoHideDuration={6000} onClose={handleClose}>
                <Alert onClose={handleClose} severity={toast.severity}>
                    <AlertTitle>{toast.title}</AlertTitle>
                    {toast.message}
                </Alert>
            </Snackbar>
        </React.Fragment>
    );
}