import React from 'react'
import { IconButton, Avatar, Button, Divider } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles';
import { green } from '@material-ui/core/colors';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import RequestFetch from '../../../libraries/RequestFetch';
import moment from 'moment';
import { SessionContext } from '../../../routes/adminrh/CustomRoutes';

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const useStyles = makeStyles((theme) => ({
    wrapper: {
        position: 'relative',
    },
    buttonSuccess: {
        backgroundColor: green[500],
        '&:hover': {
            backgroundColor: green[700],
        },
    },
    fabProgress: {
        color: green[500],
        position: 'absolute',
        top: -4,
        left: -4,
        zIndex: 1,
    },
    square: {
        color: '#FFFFFF',
        backgroundColor: '#cb0606',
        fontSize: theme.spacing(2)
    },
    divider: {
        marginLeft: '30px',
        marginRight: '30px'
    }, action: {
        top: '50%'
    }
}));

export default function SeeDocument(props) {
    const { cabeceras, isTokenExpired } = React.useContext(SessionContext);
    const classes = useStyles();
    const [dialog, setDialog] = React.useState(false);

    const { id_tipo_documento, url_documento, textShow, fecha_modificacion } = props.data;
    const { id, tipo, index } = props;

    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleClick = event => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const DELETE = () => {
        let mounted = true;
        const exito = async res => {
            const respuesta = await res.json();
            console.log(respuesta);
            props.delete(index, id_tipo_documento);
            props.backdrop(false);
            props.toast({
                open: true,
                message: "Se ha elimado el documento, agrega uno nuevo",
                severity: "success",
                title: "Elimación con exito!"
            })
        }

        const fallo = async res => {
            const respuesta = await res.json();
            if (!isTokenExpired(respuesta)) {
                props.backdrop(false);
                props.toast({
                    open: true,
                    message: "No se ha podido eliminar el archivo, intenta de nuevo",
                    severity: "error",
                    title: "Uuups! Ha ocurrido un error"
                })
            }
        }

        const send = () => {
            RequestFetch.deleted({
                url: `${RequestFetch.BASE_URL}/api/rh/${tipo}/documento/${id}/${id_tipo_documento}`,
                headers: cabeceras,
                exito: exito,
                fallo: fallo,
                error_request: (error) => {
                    console.log("Errooooor");
                }
            });
        }

        if (mounted) {
            setDialog(false);
            props.backdrop(true);
            send();
        }

        return () => mounted = false;
    }



    return (
        <>
            <Card elevation={0}>
                <CardHeader
                    avatar={
                        <IconButton aria-label="pdf" onClick={() => window.open(`${url_documento.url}&raw=1`, '_blank')}>
                            <Avatar variant="rounded" className={classes.square}>
                                PDF
                            </Avatar>
                        </IconButton>
                    }
                    action={
                        <div className={classes.action}>
                            <IconButton aria-label="settings" onClick={handleClick}>
                                <MoreVertIcon />
                            </IconButton>
                            <Menu
                                anchorEl={anchorEl}
                                keepMounted
                                open={Boolean(anchorEl)}
                                onClose={handleClose}
                            >
                                <MenuItem onClick={() => {
                                    //props.showPDF(`${url_documento.url}&raw=1`);
                                    handleClose()
                                }}
                                >Abrir</MenuItem>
                                <MenuItem onClick={() => {
                                    setAnchorEl(null); setDialog(true)
                                }}>Eliminar</MenuItem>
                            </Menu>
                        </div>
                    }
                    title={textShow}
                    subheader={`Modificación: ${moment(fecha_modificacion).format('DD/MM/YYYY')}`}
                />
                <Divider className={classes.divider} />
            </Card>

            <Dialog
                open={dialog}
                TransitionComponent={Transition}
                onClose={() => setDialog(false)}>
                <DialogTitle>{"Eliminar documento"}</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Está apunto de eliminar el documento {`"${textShow}"`}, ¿Está seguro de continuar?
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setDialog(false)}>Cancelar</Button>
                    <Button onClick={DELETE} color="secondary">Eliminar</Button>
                </DialogActions>
            </Dialog>

        </>
    )
}
