import React, { useEffect, useState, useContext, createContext } from 'react'
import { BASE_URL } from '../../../../libraries/RequestFetch';
import { DatePicker } from '@material-ui/pickers';
import CalendarTodayIcon from '@material-ui/icons/CalendarToday';
import moment from 'moment'
import {
    Grid,
    Paper,
    Typography,
    TableContainer,
    Table,
    TableBody,
    TableRow,
    TableCell,
    TableHead,
    Button,
    ListItemText,
    ListItem,
    Collapse,
    LinearProgress,
    Dialog,
    DialogActions,
    DialogTitle,
    DialogContent,
    DialogContentText,
    ListItemIcon
} from '@material-ui/core';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import { makeStyles } from '@material-ui/styles';
import { Skeleton } from '@material-ui/lab';
import { SessionContext } from '../../../../routes/adminrh/CustomRoutes';
import { useHistory } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
    root: {
        backgroundColor: theme.palette.background.paper,
        minHeight: '60px'
    },
    paper: {
        padding: theme.spacing(3)
    },
    title: {
        textAlign: 'center',
        paddingTop: '15px'
    }
}));

const CalendarioContext = createContext(null);

export default function Calendario({ idContrato, periodo }) {
    const history = useHistory();
    const { cabeceras, isTokenExpired } = React.useContext(SessionContext);
    const classes = useStyles();
    const [nominas, setNominas] = React.useState();


    const getAllDatas = async () => {
        try {
            const respuesta = await fetch(
                `${BASE_URL}/api/rh/nomina?id_contrato=${idContrato}`,
                {
                    method: 'GET',
                    headers: cabeceras
                });
            const resp = await respuesta.json();
            if (!isTokenExpired(resp)) {
                let { nomina } = resp;
                nomina = nomina.sort((a, b) =>
                    moment(a.fecha_inicio_periodo)
                        .format('YYYYMMDD') - moment(b.fecha_inicio_periodo)
                            .format('YYYYMMDD'))
                const nominasByYear = groupByYear(nomina);
                const anio = moment().format('YYYY');
                nominasByYear[anio] = nominasByYear[anio] ? nominasByYear[anio] : [];
                setNominas(nominasByYear);
            }
        } catch (error) {
            history.push('/server-error');
        }
    }

    const groupByYear = (nomina) => {
        let result = nomina.reduce((prev, current) => {
            prev[moment(current.fecha_inicio_periodo).format('YYYY')] = prev[moment(current.fecha_inicio_periodo).format('YYYY')] || [];
            prev[moment(current.fecha_inicio_periodo).format('YYYY')].push(current);
            return prev;
        }, {})
        return result;
    }

    useEffect(() => {
        getAllDatas();
        // eslint-disable-next-line
    }, []);

    return (
        <CalendarioContext.Provider value={{ getAllDatas, idContrato, periodo }}>
            <Paper className={classes.root} elevation={2}>
                <Typography className={classes.title} variant="button" display="block" gutterBottom>
                    Calendario de Nóminas
                </Typography>
                {
                    nominas ?
                        Object.keys(nominas).length > 0 &&
                        Object.keys(nominas).map((key, index) =>
                            <CollapseTable data={nominas[key]} name={key} periodo={periodo} idContrato={idContrato} key={`collapsible-table-${index}`} defaulOpen={index === Object.keys(nominas).length - 1} />
                        )
                        :
                        <Grid container spacing={2} style={{ padding: 15 }}>
                            <Grid item xs={6}>
                                <Skeleton animation="wave" height={70} />
                            </Grid>
                            <Grid item xs={6}>
                                <Skeleton animation="wave" height={70} />
                            </Grid>
                        </Grid>
                }
            </Paper>
        </CalendarioContext.Provider>
    )
}


function CollapseTable({ data, name, periodo, idContrato, defaulOpen }) {
    const history = useHistory();
    const [open, setOpen] = useState(defaulOpen ? true : false);
    return (
        <>
            <ListItem button onClick={() => setOpen(!open)}>
                <ListItemIcon>
                    <CalendarTodayIcon />
                </ListItemIcon>
                <ListItemText primary={name} />
                {open ? <ExpandLess /> : <ExpandMore />}
            </ListItem>
            <Collapse in={open} timeout="auto" unmountOnExit>
                {
                    data.length > 0 ?
                        <TableContainer>
                            <Table size="small">
                                <TableHead >
                                    <TableRow >
                                        <TableCell align="center">Fecha Incio</TableCell>
                                        <TableCell align="center">Fecha Fin</TableCell>
                                        <TableCell align="center">Estatus</TableCell>
                                        <TableCell align="center">Acción</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {
                                        data.map((i, index) => {
                                            return <TableRow key={`nominas-inicio-fin-${index}`} hover>
                                                <TableCell align="center">
                                                    {moment.utc(i.fecha_inicio_periodo).format('DD/MMM/YYYY')}
                                                </TableCell>
                                                <TableCell align="center">
                                                    {moment.utc(i.fecha_fin_periodo).format('DD/MMM/YYYY')}
                                                </TableCell>
                                                <TableCell align="center">
                                                    {getStatus(i.estatus)}
                                                </TableCell>
                                                <TableCell align="center">
                                                    <Button variant="text" color="secondary" style={{ padding: 0 }} fullWidth
                                                        onClick={() => {
                                                            const anio = moment.utc(i.fecha_fin_periodo).format('YYYY');
                                                            history.push(`/nomina/activas/${i.id_nomina}&${i.id_contrato}&${i.id_empresa}&${anio}`)
                                                        }}>
                                                        Ver
                                                    </Button>
                                                </TableCell>
                                            </TableRow>
                                        })
                                    }
                                </TableBody>
                            </Table>
                        </TableContainer>
                        :
                        <CreateNominas periodo={periodo} idContrato={idContrato} />
                }
            </Collapse>
        </>
    );
}


function CreateNominas({ periodo, idContrato }) {

    const { cabeceras, isTokenExpired } = React.useContext(SessionContext);
    const [fecha, setFecha] = React.useState(moment().startOf('month'));
    const [loading, setLoading] = React.useState(false);
    const [dialog, setDialog] = React.useState(false);
    const { getAllDatas } = useContext(CalendarioContext)

    const createNominas = async () => {
        setLoading(true)
        const data = {
            fecha_inicio: fecha,
            id_contrato: idContrato,
            periodo: periodo
        }
        /* 
        console.log(data);
        //api/rh/nomina/funtion/:nombreDeFuncion */
        const respuesta = await fetch(`${BASE_URL}/api/rh/nomina/funtion/calendar`,
            {
                method: 'POST',
                headers: cabeceras,
                body: JSON.stringify(data)
            });

        const response = await respuesta.json();
        if (!isTokenExpired(response)) {
            getAllDatas();
            setLoading(false);
            setDialog(false);
        }
    }

    return (
        <>
            <Grid spacing={2} style={{ padding: 24 }}
                container
            >
                <Grid item sm={6}>
                    <DatePicker
                        inputVariant="outlined"
                        maxDateMessage={"La fecha no puede ser anterior a un año"}
                        minDateMessage={"Fecha es menor al mínimo permitido"}
                        invalidDateMessage={"Formato invalido"}
                        InputLabelProps={{ shrink: true }}
                        fullWidth
                        required
                        value={fecha}
                        onChange={e => setFecha(e)}
                        placeholder="Mes/Día/Año"
                        id="fecha_nacimiento"
                        label="Fecha de Inicio"
                        format="DD/MM/YYYY"
                        autoComplete='off'
                        minDate={moment().startOf('month')}
                        maxDate={finalDate(periodo)}
                    />
                </Grid>
                <Grid item sm={6} container
                    direction="row"
                    justify="center"
                    alignItems="center">
                    <Button variant="text" color="secondary" onClick={() => setDialog(true)}>
                        Crear Calendario
                    </Button>
                </Grid>
            </Grid>
            <Dialog open={dialog} onClose={() => setDialog(false)} aria-labelledby="">
                {loading &&
                    <LinearProgress color="secondary" />
                }
                <DialogTitle id="">Crear Nominas</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        ¿Está apunto de crear el calendario de nominas?
                </DialogContentText>
                    <DialogContentText>
                        Inicio: <b>{fecha.format('DD/MM/YYYY')}</b>
                    </DialogContentText>
                    <DialogContentText>
                        Periodo: <b>{getPeriodo(periodo)}</b>
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setDialog(false)} color="default">
                        Cancel
                </Button>
                    <Button disabled={loading} color="secondary" onClick={createNominas}>
                        Confirmar
                </Button>
                </DialogActions>
            </Dialog>
        </>
    );
}


const finalDate = (periodo) => {
    switch (periodo) {
        case 'S': return moment.utc().endOf('year').subtract(6, 'days');
        case 'C': return moment.utc().endOf('year').subtract(13, 'days');
        case 'Q': return moment.utc().endOf('year').startOf('month').add(15, 'days');
        case 'M': return moment.utc().endOf('year').startOf('month').add(1, 'days');
        default: return moment();
    }

}

const getPeriodo = (periodo) => {
    switch (periodo) {
        case 'S': return 'Semanal';
        case 'C': return 'Catorcenal';
        case 'Q': return 'Quincenal';
        case 'M': return 'Mensual';
        default: return '';
    }
}

const getStatus = (status) => {
    switch (status) {
        case 'C': return 'Pendiente'
        case 'P': return 'Prenómina'
        case 'N': return 'Nómina'
        case 'A': return 'Pagada'
        default: return 'Indeterminado'
    }
}