import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent';
import InfoIcon from '@material-ui/icons/Info';
import RequestFetch, { BASE_URL } from '../../../libraries/RequestFetch';
import MuiAlert from '@material-ui/lab/Alert';
import { green } from '@material-ui/core/colors';
import { useHistory } from 'react-router-dom';
import AlertTitle from '@material-ui/lab/AlertTitle';
import { Grid, CardActions, Snackbar, Button, ListSubheader, Typography, IconButton } from '@material-ui/core';
import { Save as SaveIcon } from '@material-ui/icons';
import CircularProgress from '@material-ui/core/CircularProgress';
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import Representante from '../FormEmpresa/Representante/Representante';
import Validacion from '../../../libraries/Validacion';
import { SessionContext } from '../../../routes/adminrh/CustomRoutes';
import { withSnackbar } from 'notistack';

const useStyles = makeStyles(theme => ({
    root: {
        width: '100%',
        backgroundColor: theme.palette.background.paper,
    },
    cardContent: {
        padding: '0px'
    },
    header: {
        background: '#fafafa'
    },
    container: {
        padding: theme.spacing(4),
    },
    uppercase: {
        textTransform: 'uppercase'
    },
    save: {
        color: theme.palette.common.white,
        backgroundColor: green[500],
        '&:hover': {
            backgroundColor: green[600],
        },
    },
    buttonProgress: {
        color: green[500],
        position: 'absolute',
        top: '50%',
        left: '50%',
        marginTop: -12,
        marginLeft: -12,
    },
    wrapper: {
        margin: theme.spacing(1),
        position: 'relative',
    },
    title: {
        paddingRight: 42,
        textAlign: 'center',
        margin: '15px auto'
    },
    back: {
        left: -12,
        float: 'left'
    },
    subheader: {
        textAlign: 'center'
    },
}));

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const initRepre = {
    existe: { value: false },
    id_representante: { value: '', verify: '' },
    rfc: { value: '', verify: '' },
    nombre: { value: '', verify: '' },
    primer_apellido: { value: '', verify: '' },
    segundo_apellido: { value: '', verify: '' },
    genero: { value: '', verify: '' },
    observaciones: { value: '', verify: '' },
    correo: { value: '', verify: '' },
}

const initTel = [
    { value: '', verify: '', tipo: 'C', helperText: '' },
    { value: '', verify: '', tipo: 'F', helperText: '' },
]

function FormRepresentante(props) {
    const { cabeceras, isTokenExpired } = React.useContext(SessionContext);
    const classes = useStyles();
    const history = useHistory();

    const [loading, setLoading] = React.useState(false);
    const [toast, setToast] = React.useState({
        open: false,
        message: "",
        severity: "",
        title: ""
    });

    const [repre, setRepre] = React.useState(initRepre);
    const [phones, setPhones] = React.useState(initTel);


    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            setToast({ ...toast, open: false });
        }
        setToast({ ...toast, open: false });
    };

    const send = () => {

        const _telefonoRepre = (id) => {
            const newTel = [];
            phones.forEach(item => {
                const value = item.value.replace(/\s/g, '');
                if (value.length >= 5)
                    newTel.push({ numero: value, tipo: item.tipo })
            })
            if (newTel.length > 0)
                return {
                    id_representante: id,
                    telefonos: newTel
                }
            else return null;
        }

        const _separeRepresentante = () => {
            const newData = { representante: '' };
            newData.representante = {
                rfc: repre.rfc.value.toUpperCase(),
                nombre: repre.nombre.value,
                primer_apellido: repre.primer_apellido.value,
                segundo_apellido: repre.segundo_apellido.value,
                genero: repre.genero.value,
                correo: repre.correo.value,
                observaciones: repre.observaciones.value
            };
            return newData;
        }

        const _exito = async (res) => {
            const respuesta = await res.json();
            const { representate, created } = respuesta;
            if (created) {
                setRepre((prevState) => {
                    return { ...prevState, id_representante: { value: representate[0].id_representante } }
                })
                sendTelefonoRepre(representate[0].id_representante);
                createUser();
            } else {
                setLoading(false);
                setToast({
                    open: true,
                    message: `La RFC ${repre.rfc.value} ya se encuentra registrado`,
                    severity: "error",
                    title: "Registro fallido!"
                });
            }
        }

        const _exitoTel = async (res) => {
            const respuesta = await res.json();
            const { created } = respuesta;
            console.log(respuesta);
            setLoading(false);
            if (created) {
                props.enqueueSnackbar("Teléfonos creados", { variant: 'success', preventDuplicate: true, });
                setRepre(initRepre);
                setPhones(initTel);
            }
        }

        const _fallo = async (res) => {
            const respuesta = await res.json();
            if (!isTokenExpired(respuesta));
            /*
            const { error } = respuesta;
            let tel = "";

            if (error.message.includes(repre.telefono_cel.value))
                tel = repre.telefono_cel.value;
            else
                tel = repre.telefono_fijo.value;

            setLoading(false);
            setToast({
                open: true,
                message: `El telefono: ${tel} ya está asociado a otro representante`,
                severity: "error",
                title: "Registro con fallido!"
            });
*/

        }

        const _ValidateRepre = () => {
            var keysRepre = ['rfc', 'nombre', 'primer_apellido', 'genero', 'correo']
            var newDataRepre = { ...repre };

            keysRepre.forEach(key => {
                if (key === 'rfc')
                    newDataRepre[key] = { ...repre[key], verify: `${repre[key].value}`.replace(/\s/g, '').length === 13 }
                else if (key === 'correo')
                    newDataRepre[key] = { ...repre[key], verify: Validacion.esCorreo(repre[key].value) }
                else
                    newDataRepre[key] = { ...repre[key], verify: `${repre[key].value}`.length > 0 && repre[key].verify }

            });
            setRepre(newDataRepre);
            const resulRepre = keysRepre.filter(key => newDataRepre[key].verify !== true);

            return resulRepre.length === 0;
        }

        const _ValidateTel = () => {
            const newData = [...phones];
            const oks = [];
            newData.forEach((element) => {
                const correo = element.value.replace(/\s/g, '');
                if (correo.length === 10) {
                    const verify = element.verify;
                    element.verify = verify;
                    oks.push(verify);
                }
            })
            const resul = oks.filter(item => {
                return item !== true;
            })
            if (oks.length === 0) {
                newData[0].verify = false;
                newData[0].helperText = "Registra al menos uno";
                newData[1].verify = false;
                newData[1].helperText = "Registra al menos uno";
                return false;
            }
            setPhones(newData);
            return resul.length === 0;
        }

        const sendTelefonoRepre = (id) => {
            const tel = _telefonoRepre(id);
            if (tel !== null)
                RequestFetch.post({
                    url: `${RequestFetch.BASE_URL}/api/rh/representante/telefono`,
                    headers: cabeceras,
                    data: _telefonoRepre(id),
                    exito: _exitoTel,
                    fallo: _fallo,
                    error_request: () => {
                        history.push(`/server-error`);
                    }
                });
        }

        const sendRepre = () => {
            RequestFetch.post({
                url: `${RequestFetch.BASE_URL}/api/rh/representante`,
                headers: cabeceras,
                data: _separeRepresentante(),
                exito: _exito,
                fallo: _fallo,
                error_request: () => {
                    history.push(`/server-error`);
                }
            });
        }



        const createUser = async () => {
            const body = JSON.stringify({
                rfc: repre.rfc.value.toUpperCase(),
                correo: repre.correo.value,
                rol: 'C',
                tipo: 'R'
            });
            const resp = await fetch(`${BASE_URL}/api/rh/usuario`, { method: 'POST', headers: cabeceras, body });
            const response = resp.json();
            if (!isTokenExpired(response)) {
                const { error } = response
                if (error)
                    props.enqueueSnackbar("Usuario no creado", { variant: 'error', preventDuplicate: true, });
                else
                    props.enqueueSnackbar("Usuario creado", { variant: 'success', preventDuplicate: true, });
            }
        }

        const validaAll = () => {
            if (_ValidateRepre()) {
                if (_ValidateTel()) {
                    setLoading(true);
                    if (repre.id_representante.value === '')
                        sendRepre();
                    else
                        sendTelefonoRepre(repre.id_representante.value);
                } else {
                    setToast({
                        open: true,
                        message: 'Debes guardar al menos un teléfono',
                        severity: "warning",
                        title: "Revisa los telefonos!"
                    });
                    setLoading(false);
                }
            } else {
                setToast({
                    open: true,
                    message: 'Hay campos requeridos que estan vacios, revisalos porfavor',
                    severity: "warning",
                    title: "Campos vacios!"
                });
                setLoading(false);
            }
        }
        validaAll();
    }



    return (
        <React.Fragment>
            <Card className={classes.root}>
                <CardContent className={classes.cardContent}>
                    <List
                        component="nav"
                        aria-labelledby="nested-list-subheader"
                        subheader={
                            <ListSubheader component="div" display="block" className={classes.subheader}>
                                <IconButton className={classes.back} onClick={() => { history.goBack() }}>
                                    <KeyboardBackspaceIcon />
                                </IconButton>
                                <Typography className={classes.title} variant="button" gutterBottom>
                                    formulario representante
                                </Typography>
                            </ListSubheader>
                        }
                        className={classes.root}>
                        <ListItem className={classes.header}>
                            <ListItemIcon>
                                <InfoIcon />
                            </ListItemIcon>
                            <ListItemText primary="Informacion" />
                        </ListItem>
                        <Collapse in={true} timeout="auto" unmountOnExit >
                            <Representante
                                data={repre}
                                setData={setRepre}
                                phones={phones}
                                setPhones={setPhones}
                                expandable={true}
                                loading={loading}
                                edit={false} />
                        </Collapse>
                    </List>
                </CardContent>
                <CardActions>
                    <Grid
                        container
                        direction="row"
                        justify="flex-end"
                        alignItems="center">

                        <div className={classes.wrapper}>

                            <Button
                                variant="contained"
                                className={classes.save}
                                disabled={loading || repre.existe.value}
                                endIcon={<SaveIcon />}
                                onClick={send}>
                                {"Guardar"}
                                {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                            </Button>

                        </div>
                    </Grid>
                </CardActions>

            </Card>
            <Snackbar open={toast.open} autoHideDuration={5000} onClose={handleClose}>
                <Alert onClose={handleClose} severity={toast.severity}>
                    <AlertTitle>{toast.title}</AlertTitle>
                    {toast.message}
                </Alert>
            </Snackbar>
        </React.Fragment>
    );
}

export default withSnackbar(FormRepresentante);