import React from 'react'
import { Grid, TextField, MenuItem, Button } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { ArrowBack, ArrowForward } from '@material-ui/icons';
import {
    ValidacionNumeroCuenta,
    ValidacionNumeroTarjeta,
    ValidacionCLABE,
} from '../../../../libraries/Mask'

const useStyles = makeStyles(theme => ({
    container: {
        padding: theme.spacing(4),
    },
    uppercase: {
        textTransform: 'uppercase'
    },
    capitalize: {
        textTransform: 'capitalize'
    },
}));



export default function Financiero(props) {
    const { data, setData, onlySee } = props;
    const classes = useStyles();

    const changeTextNoRequired = e => {
        const { name, value } = e.target;
        setData(prevState => {
            return {
                ...prevState, [name]: { value: value, verify: true }
            }
        })
    }

    return (
        <Grid container spacing={3} className={classes.container}>
            <Grid item xs={12} sm={6} lg={onlySee ? 6 : 3}>
                <TextField
                    disabled={onlySee}
                    variant="outlined"
                    label="No. Cuenta"
                    fullWidth
                    placeholder="Ej. 010 010 1010"
                    InputLabelProps={{ shrink: true, }}
                    value={data.no_cuenta.value}
                    name="no_cuenta"
                    InputProps={{ inputComponent: ValidacionNumeroCuenta }}
                    onChange={changeTextNoRequired}
                    error={data.no_cuenta.verify !== '' ? !data.no_cuenta.verify : false}
                    autoComplete='off'
                />
            </Grid>
            <Grid item xs={12} sm={6} lg={onlySee ? 6 : 3}>
                <TextField
                    disabled={onlySee}
                    variant="outlined"
                    label="No. Tarjeta"
                    fullWidth
                    name="no_tarjeta"
                    placeholder="Ej. 0101 0101 0101 0101"
                    InputLabelProps={{ shrink: true }}
                    value={data.no_tarjeta.value}
                    onChange={changeTextNoRequired}
                    InputProps={{ inputComponent: ValidacionNumeroTarjeta }}
                    error={data.no_tarjeta.verify !== '' ? !data.no_tarjeta.verify : false}
                    autoComplete='off'
                />
            </Grid>
            <Grid item xs={12} sm={6} lg={onlySee ? 6 : 4}>
                <TextField
                    disabled={onlySee}
                    variant="outlined"
                    label="CLABE"
                    fullWidth
                    placeholder="Ej. 565 656 56565656565 6"
                    InputLabelProps={{ shrink: true, }}
                    name="clabe"
                    value={data.clabe.value}
                    onChange={changeTextNoRequired}
                    InputProps={{ inputComponent: ValidacionCLABE }}
                    error={data.clabe.verify !== '' ? !data.clabe.verify : false}
                    autoComplete='off'
                />
            </Grid>
            <Grid item xs={12} sm={6} lg={onlySee ? 6 : 2}>
                <TextField
                    disabled={onlySee}
                    variant="outlined"
                    fullWidth
                    label="Banco"
                    select
                    name="id_banco"
                    className={classes.select}
                    value={`${data.id_banco.value !== '' ? data.id_banco.value : 'DEFAULT'}`}
                    error={data.id_banco.verify !== '' ? !data.id_banco.verify : false}
                    onChange={changeTextNoRequired}>
                    <MenuItem value="DEFAULT" disabled>Selecciona</MenuItem>
                    {props.bancos.map((item) => {
                        return <MenuItem key={`banco-${item.id_banco}`} value={item.id_banco}>{item.nombre_banco}</MenuItem>
                    })}
                </TextField>
            </Grid>


            {
                !props.expandable &&
                <Grid container item
                    direction="row"
                    justify="flex-end"
                    alignItems="center" xs={12} sm={12} md={12} lg={12} >
                    <Button startIcon={<ArrowBack />}
                        onClick={props.buttons.back}>Anterior</Button>
                    <Button
                        variant="outlined"
                        color="primary"
                        endIcon={<ArrowForward />}
                        onClick={props.buttons.next}
                    >Siguiente</Button>
                </Grid>
            }
        </Grid>
    )
}
