import React from 'react'
import { Grid, Typography, Button, Link } from '@material-ui/core'
import { makeStyles, withStyles, createMuiTheme, ThemeProvider, responsiveFontSizes } from '@material-ui/core/styles';
import HomeIcon from '@material-ui/icons/Home';
import { ReactComponent as NotFoundSVG } from '../../../src/assets/svg/not-found.svg';
import { ReactComponent as ServerErrorSVG } from '../../../src/assets/svg/server-error.svg';


const useStyles = makeStyles(() => ({
    container: {
        //position: 'relative',
        //width: '100%',
        //height: '100%'
        //backgroundImage: 'linear-gradient(180deg, rgba(245,124,0,0.5) 0%, rgba(255,255,255,0) 100%)'
    },
    svg: {
        padding: '50px 0px',
        height: 300,
        maxWidth: '100%',
        minWidth: '200px'
    },
    letrero: {
        fontSize: '50px',
        marginBottom: '20px'
    },
    button: {
        marginTop: 40,
        animation: `$pulse 1s infinite`
    },
    "@keyframes pulse": {
        "0%": {
            boxShadow: '0 0 0 0 rgba(245,124,0,0.7)'
        },
        "70%": {
            boxShadow: '0 0 0 12px rgba(245,124,0,0)'
        },
        "100%": {
            boxShadow: '0 0 0 0 rgba(245,124,0,0)'
        }
    },
    footer: {
        marginTop: 70
    }
}));

let theme = createMuiTheme();
theme = responsiveFontSizes(theme);



const ColorButton = withStyles(() => ({
    root: {
        color: '#FFFFFF',
        backgroundColor: '#F57C00',
        '&:hover': {
            backgroundColor: '#D84315',
        },
    },
}))(Button);

export default function ErrorPage(props) {
    const classes = useStyles();

    const Copyright = () => {
        return (
            <Typography variant="body2" color="textSecondary" align="center" display="block" className={classes.footer}>
                <Link color="inherit" href="https://getconsultoria.com/">{"Copyright © GET Consultoria "}{new Date().getFullYear()}
                    {'.'}</Link>
            </Typography>
        );
    }

    const serverError = {
        SVG: <ServerErrorSVG className={classes.svg} />,
        H5: 'No se ha podido establecer conexión con el servidor.',
        Title1: 'Es muy probable que esté en mantenimiento.',
        Title2: 'Antes de todo ¿Ya revisaste tu conexión de internet?'
    }

    const pageNotFound = {
        SVG: <NotFoundSVG className={classes.svg} />,
        H5: 'La página que solicitó no se encontró.',
        Title1: 'Hemos buscado esta página por todas partes.',
        Title2: '¿Estás seguro de que la URL del sitio web es correcta?'
    }
    const data = props.type === 'NotFound' ? pageNotFound : serverError;
    return (
        <ThemeProvider theme={theme} >
            <Grid container className={classes.container}
                direction="column"
                justify="center"
                alignItems="center">
                <Grid item xs={12} sm={12} md={12} lg={12}>
                    {data.SVG}
                </Grid>
                <Grid item xs={12} sm={12} md={12} lg={12}>
                    <Typography variant="h4" display="block" gutterBottom style={{ textAlign: 'center' }}>
                        {data.H5}
                    </Typography>
                    <Typography variant="h5" display="block" gutterBottom style={{ textAlign: 'center', padding: 10 }}>
                        {data.Title1}
                    </Typography>
                    <Typography variant="h6" display="block" gutterBottom style={{ textAlign: 'center', padding: 10 }}>
                        {data.Title2}
                    </Typography>
                </Grid>
                <Grid item xs={12} md={12} lg={12}>
                    <ColorButton
                        className={classes.button}
                        size="large"
                        href="/nomina"
                        startIcon={<HomeIcon />}>
                        Ir a la página principal
                    </ColorButton>
                </Grid>
                <Grid item xs={12} md={12} lg={12}>
                    <Copyright />
                </Grid>
            </Grid>

        </ThemeProvider>
    );
}
