import React from 'react';
import { Grid, FormControlLabel, Checkbox, TextField, MenuItem, FormControl, Typography } from '@material-ui/core';


const horas = [
    '00:00', '01:00', '02:00', '03:00', '04:00', '05:00', '06:00', '07:00', '08:00', '09:00', '10:00', '11:00',
    '12:00', '13:00', '14:00', '15:00', '16:00', '17:00', '18:00', '19:00', '20:00', '21:00', '22:00', '23:00'
]

export default function DiaLaboral(props) {
    const [horaIni, setHoraIni] = React.useState([...horas]);
    const [horaFin, setHorafin] = React.useState([...horas]);
    const { onlySee } = props;

    const onChangeEntrada = (event) => {
        const value = event.target.value.split(':')[0];
        const newHoras = horas.filter((item) => {
            return value < parseInt(item.split(':')[0]);
        });

        props.onChange(props.position, 'hora_entrada', event, newHoras[0]);
        setHorafin(newHoras);
    }

    const onChangeSalida = (event) => {
        props.onChange(props.position, 'hora_salida', event)
        const value = event.target.value.split(':')[0];
        const newHoras = horas.filter((item) => {
            return value > parseInt(item.split(':')[0]);
        });
        setHoraIni(newHoras);
    }


    return (
        <Grid container spacing={1}>
            <Grid item xs={4} sm={4}>
                <FormControl fullWidth>
                    <FormControlLabel
                        disabled={onlySee}
                        control={<Checkbox
                            checked={props.data.checked}
                            color="secondary"
                            style={{ marginLeft: 10 }}
                            onClick={() => props.onChangeDisabled(props.position)} />}
                        label={<Typography variant="body2" color="initial">{props.data.dia_semana}</Typography>}
                    />
                </FormControl>
            </Grid>
            <Grid item xs={4} sm={4}>
                <TextField
                    fullWidth
                    variant="outlined"
                    disabled={!props.data.checked || onlySee}
                    select
                    size="small"
                    InputLabelProps={{ shrink: true }}
                    value={`${props.data.hora_entrada !== props.data.hora_salida && horaIni.find(item => item === props.data.hora_entrada) !== undefined ? props.data.hora_entrada : 'DEFAULT'}`}
                    onChange={(event) => { onChangeEntrada(event) }}>
                    <MenuItem value="DEFAULT" disabled>Hora</MenuItem>
                    {horaIni.map(item => <MenuItem key={`hora-entrada-${item.split(':')[0]}`} value={item}>{item}</MenuItem>)}
                </TextField>
            </Grid>
            <Grid item xs={4} sm={4}>
                <TextField
                    fullWidth
                    variant="outlined"
                    disabled={!props.data.checked || onlySee}
                    select
                    size="small"
                    InputLabelProps={{ shrink: true }}
                    value={`${props.data.hora_entrada !== props.data.hora_salida && horaFin.find(item => item === props.data.hora_salida) !== undefined ? props.data.hora_salida : 'DEFAULT'}`}
                    onChange={(event) => { onChangeSalida(event) }}>
                    <MenuItem value="DEFAULT" disabled>Hora</MenuItem>
                    {horaFin.map(item => <MenuItem key={`hora-salida-${item.split(':')[0]}`} value={item}>{item}</MenuItem>)}
                </TextField>
            </Grid>
        </Grid>
    )
}
