import moment from "moment";

let diasMensuales = 0.0;
export function calculateNomina(rows, { periodo, aplico_costo_social, dias, comision, perido_fin, imss_aplicado, isr_aplicado, retencion_aplicada }, totales, fiscales) {
    diasMensuales = getDiasMensuales(perido_fin);
    let newTotales = {};
    if (evalueFiscales(fiscales)) {
        for (let row of rows) {
            if (row.tipo) {
                if (parseInt(dias) === parseInt(row.dias.faltas.cantidad_inasistencias))
                    row.dias.total = 0;
                else
                    row.dias.total = dias - row.dias.faltas.cantidad_inasistencias;
                _percepciones(row, fiscales.integracion, fiscales.uma, perido_fin, dias);
                const excentos = totalExcentos(row.percepciones.percepciones);
                const grabados = totalGrabados(row.percepciones.percepciones);

                _deducciones(row, paraISR(grabados, fiscales.isr, fiscales.subsidio, periodo), fiscales.prima, parseFloat(fiscales.uma), perido_fin, dias, aplico_costo_social, periodo, imss_aplicado, isr_aplicado);
                const subsidioAlEmpleao = row.deducciones.deducciones.find(e => e.id_percepcion_deduccion === 'D100')
                row.percepciones.total = excentos + grabados;
                if (subsidioAlEmpleao) row.percepciones.total = row.percepciones.total + subsidioAlEmpleao.value;
                row.deducciones.total = totalDeducciones(row.deducciones.deducciones);
            }
            else {
                row.percepciones.total = totalPercepciones(row.percepciones.percepciones);
                row.deducciones.total = totalDeducciones(row.deducciones.deducciones);
            }

            row['display'] = {
                identificador: row.empleado.identificador,
                nombre: row.tipo ? `${row.empleado.nombre} ${row.empleado.primer_apellido} ${row.empleado.segundo_apellido}`.toUpperCase() : row.empleado.nombre.toUpperCase(),
                tipo: row.tipo,
                dias: row.dias.total ? row.dias.total : 0.0,
                percepciones: row.percepciones.total,
                deducciones: row.deducciones.total,
                total: row.percepciones.total - row.deducciones.total,
                costo_social: row.costo_social
            }

        }
        newTotales = calculateTotales(rows, comision, imss_aplicado, isr_aplicado, dias, retencion_aplicada);
        return { empleados: rows, totales: newTotales };
    }
    else return { empleados: [], totales: newTotales }
}

function evalueFiscales({ isr, integracion, uma, subsidio, prima }) {
    return isr.length > 0 && integracion.length > 0 && parseFloat(uma) > 0 && subsidio.length > 0 && prima.length > 0
}

function calculateTotales(rows, _comision, _imss, _isr, dias, _retencion) {

    const newTotales = {};
    const nomina = parseFloat(rows.reduce((acum, { empleado, tipo, percepciones }) => {
        if (tipo)
            return acum + (empleado.salario_diario * dias);
        else
            return acum + percepciones.total;
    }, 0));
    const comision = nomina * parseFloat(_comision);
    const costo_social = parseFloat(rows.reduce((acum, { costo_social }) => acum + parseFloat(costo_social ? costo_social : 0), 0)).toFixedDown(5);
    const isn = nomina * 0.03;
    let isr = 0;
    let imss = 0;
    if (_isr) {

        isr = parseFloat(sumDeduccion(rows, 'D001'));
        newTotales['isr'] = isr;
    }
    else newTotales['isr'] = 0;
    if (_imss) {
        imss = parseFloat(sumDeduccion(rows, 'D002'));
        newTotales['imss'] = imss;
    } else newTotales['imss'] = 0;

    const subtotal = nomina + comision + costo_social + isn + isr + imss;
    const iva = subtotal * 0.16;

    let retencion = 0;
    let total = 0;
    if (_retencion) {
        retencion = subtotal * 0.06;
        newTotales['retencion'] = retencion;
        total = subtotal + iva - retencion;
    } else {
        newTotales['retencion'] = 0;
        total = subtotal + iva;
    }


    newTotales['nomina'] = nomina;
    newTotales['comision'] = comision;
    newTotales['costo_social'] = costo_social;
    newTotales['isn'] = isn;
    newTotales['subtotal'] = subtotal;
    newTotales['iva'] = iva;
    newTotales['total'] = total;

    return newTotales;
}

function sumDeduccion(rows, id) {
    let total = 0;
    rows.forEach(row => {
        const { deducciones: { deducciones } } = row;
        if (deducciones.length > 0) {
            const result = deducciones.find(e => e.id_percepcion_deduccion === id);
            if (result) total += result.value;
        }
    })
    return total;
}

function getDiasMensuales(fechafin) {
    return moment(fechafin).isLeapYear() ? 30.5 : 30.41667;
}


function _percepciones(row, FI, uma, fechaFin, dias) {
    sueldo(row, FI, moment(fechaFin).format('YYYY'));
    vacaciones(row, FI, moment(fechaFin).format('YYYY'));
    diaFestivo(row);
    primaDominical(row, uma);
    primaVacacional(row, FI, uma);
    aguinaldo(row, dias, uma);
}

function paraISR(grabado, ISR, subS, periodo) {

    let baseGrabable = grabado;
    if (baseGrabable > 0) {
        let { limite_inferior, cuota_fija, excendente_limite_inferior } = ISR.find(i => parseFloat(i.limite_inferior) <= baseGrabable && baseGrabable <= parseFloat(i.limite_superior));

        limite_inferior = parseFloat(limite_inferior);
        cuota_fija = parseFloat(cuota_fija);
        excendente_limite_inferior = parseFloat(excendente_limite_inferior);

        const excedente_sobre_limite_inf = baseGrabable - limite_inferior;
        const impuesto_marginal = excedente_sobre_limite_inf * excendente_limite_inferior;
        let impuesto_segun = 0;
        switch (periodo) {
            case 'S': impuesto_segun = (impuesto_marginal + cuota_fija) / 4; break;
            case 'Q': case 'C': impuesto_segun = (impuesto_marginal + cuota_fija) / 2; break;
            case 'M': impuesto_segun = (impuesto_marginal + cuota_fija); break;
            default: impuesto_segun = 0;
        }

        let { cantidad } = subS.find(i => i.limite_inferior <= baseGrabable && baseGrabable <= i.limite_superior);
        cantidad = parseFloat(cantidad) / 2;
        //console.log(cantidad)
        return { impuesto_segun, subsidio: cantidad }
    }
    else
        return { impuesto_segun: 0, subsidio: 0 };
}

//DEDUCCIONES
function _deducciones(row, forISR, primaRiesgo, uma, fechaFin, dias, costo_scial, periodo, aplicarImss, aplicarIsr) {
    const { impuesto_segun, subsidio } = forISR;
    ISR(row, impuesto_segun, subsidio, aplicarIsr);
    const { imss } = costoSocial(row, primaRiesgo, uma, moment(fechaFin).format('YYYY'), dias, costo_scial, periodo);

    IMSS(row, imss, aplicarImss);
    SUBSIDIO(row, impuesto_segun, subsidio);

}

function SUBSIDIO(row, impuesto_segun, subsidio) {
    const indexISR = row.deducciones.deducciones.findIndex(i => i.id_percepcion_deduccion === 'D100');
    if (indexISR >= 0 && impuesto_segun >= 0 && subsidio >= 0) {
        if (impuesto_segun < subsidio) {
            row.deducciones.deducciones[indexISR].value = ((subsidio - impuesto_segun) / 2).toFixedDown(4);
        }
        else row.deducciones.deducciones[indexISR].value = 0;
    }

}

function ISR(row, impuesto_segun, subsidio, aplicarIsr) {
    const indexISR = row.deducciones.deducciones.findIndex(i => i.id_percepcion_deduccion === 'D001');
    if (indexISR >= 0 && impuesto_segun >= 0 && subsidio >= 0) {
        if (impuesto_segun > subsidio) {
            row.deducciones.deducciones[indexISR].value = (impuesto_segun - subsidio).toFixedDown(4);
            if (aplicarIsr) row.deducciones.deducciones[indexISR]['sumable'] = false;
            else {
                const { sumable, ...rest } = row.deducciones.deducciones[indexISR];
                row.deducciones.deducciones[indexISR] = rest;
            }
        }
        else row.deducciones.deducciones[indexISR].value = 0;
    }

}

function IMSS(row, imss, aplicarIMSS) {
    const indexIMSS = row.deducciones.deducciones.findIndex(i => i.id_percepcion_deduccion === 'D002');
    if (indexIMSS >= 0) {
        row.deducciones.deducciones[indexIMSS].value = imss;
        if (aplicarIMSS) row.deducciones.deducciones[indexIMSS]['sumable'] = false;
        else {
            const { sumable, ...rest } = row.deducciones.deducciones[indexIMSS];
            row.deducciones.deducciones[indexIMSS] = rest;
        }
    }
}

function costoSocial(row, primaRiesgo, uma, year, dias, costoSocial, periodo) {
    const diasDelAnio = moment([year]).isLeapYear() ? 366 : 365;
    const { valor_por_riesgo } = primaRiesgo.find(e => e.clave_patronal === row.empleado.clave_patronal);
    const cf_enfermedad_maternidad = uma * diasMensuales * 0.204; //DE DONDE SALE ESTOS VALORES? :c
    let excedente_vsm = row.empleado.sbc - (3 * uma);
    if (excedente_vsm < 0) excedente_vsm = 0;
    const excedente_pat = excedente_vsm * diasMensuales * 0.011;
    const excedente_obr = excedente_vsm * diasMensuales * 0.004;
    const prestaciones_diner_pat = row.empleado.sbc * diasMensuales * 0.007;
    const prestaciones_diner_obr = row.empleado.sbc * diasMensuales * 0.0025;
    const gastos_medicos_pensionados_pat = row.empleado.sbc * diasMensuales * 0.0105;
    const gastos_medicos_pensionados_obt = row.empleado.sbc * diasMensuales * 0.00375;
    const riesgos_trabajo = row.empleado.sbc * diasMensuales * (valor_por_riesgo / 100);
    const invalidez_seguro_vida_patron = row.empleado.sbc * diasMensuales * 0.0175;
    const invalidez_seguro_vida_obrero = row.empleado.sbc * diasMensuales * 0.00625;
    const guarderias_prestaciones = row.empleado.sbc * diasMensuales * 0.01;
    const imss_patronal = cf_enfermedad_maternidad + excedente_pat + prestaciones_diner_pat + gastos_medicos_pensionados_pat + riesgos_trabajo + invalidez_seguro_vida_patron + guarderias_prestaciones;
    const imss_obrero = excedente_obr + prestaciones_diner_obr + gastos_medicos_pensionados_obt + invalidez_seguro_vida_obrero;
    const imss_mensual = imss_patronal + imss_obrero;

    const retiro = row.empleado.sbc * 0.02 * (diasMensuales * 2);
    const cv_patronal = row.empleado.sbc * 0.0315 * (diasMensuales * 2);
    //CV OBRERO o RCV OBRERO
    const cv_obrero = row.empleado.sbc * 0.0112501 * (diasMensuales * 2);
    const rvc_patronal = retiro + cv_patronal;
    const rcv_bimestral = rvc_patronal + cv_obrero;
    const infonavit = ((row.empleado.sbc * 31) * 0.05) * 2 / 62 * 61;

    //const promedio_mensual_patronal = ((imss_patronal * 2) + rvc_patronal + infonavit) / 2;
    const promedio_mensual = ((imss_mensual * 2) + rcv_bimestral + infonavit) / 2;
    const promedio_anual = promedio_mensual * 12;
    const promedio_diario = promedio_anual / diasDelAnio;
    const promedio_quincenal = promedio_diario * dias;
    const promedio_semanal = promedio_diario * 7;

    //const total_cuotas_patronales = promedio_mensual;

    let promedio_mensual_obrero = ((imss_obrero * 2) + cv_obrero) / 4;
    switch (periodo) {
        case 'S': promedio_mensual_obrero = ((imss_obrero * 2) + cv_obrero) / 8; break;
        case 'Q': case 'C': promedio_mensual_obrero = ((imss_obrero * 2) + cv_obrero) / 4; break;
        case 'M': promedio_mensual_obrero = ((imss_obrero * 2) + cv_obrero) / 2; break;
        default: promedio_mensual_obrero = 0; break;
    }
    let imss = (promedio_mensual_obrero).toFixedDown(4);
    imss = isNaN(imss) ? 0 : imss;

    let costo_social = 0;
    switch (costoSocial) {
        case 'S': costo_social = promedio_semanal; break;
        case 'Q': costo_social = promedio_quincenal; break;
        case 'M': costo_social = promedio_mensual; break;
        case 'C': costo_social = promedio_diario * 14; break;
        default: costo_social = 0; break;
    }
    row.costo_social = costo_social;

    return { imss, costo_social }
}

//PERCEPCIONES
function sueldo(row, FI, year) {
    //SUELDO
    row.empleado.salario_capturado = parseFloat(row.empleado.salario_capturado);
    const indexSueldo = row.percepciones.percepciones.findIndex(i => i.id_percepcion_deduccion === 'P001');
    if (indexSueldo >= 0) {
        let diff = moment().diff(moment(row.empleado.fecha_contrato_externo), 'years');
        if (diff === 0) diff = 1;
        let { factor } = FI.find(f => (parseInt(f.anio_antiguedad_inferior) >= diff && parseInt(f.anio_antiguedad_superior) <= diff));
        factor = parseFloat(factor)
        row.percepciones.percepciones[indexSueldo].value = (row.empleado.salario_capturado * row.dias.total).toFixedDown(4);
        row.empleado['sbc'] = (row.empleado.salario_capturado * factor).toFixedDown(4);
    }
}

function vacaciones(row, FI, year) {
    const sd = row.empleado.salario_capturado;
    const indexVacaciones = row.percepciones.percepciones.findIndex(i => i.id_percepcion_deduccion === 'P009')
    if (indexVacaciones >= 0) {
        const dias = moment([year]).isLeapYear() ? 366 : 365;
        let diff = moment().diff(moment(row.empleado.fecha_contrato_externo), 'years');
        if (diff === 0) diff = 1;
        const { dias_vacaciones } = FI.find(f => (parseInt(f.anio_antiguedad_inferior) >= diff && parseInt(f.anio_antiguedad_superior) <= diff));
        row.percepciones.percepciones[indexVacaciones].value = sd * dias_vacaciones / dias * dias;
    }
}

function diaFestivo(row) {
    const indexDiaFest = row.percepciones.percepciones.findIndex(i => i.id_percepcion_deduccion === 'P109');
    if (indexDiaFest >= 0) {
        row.percepciones.percepciones[indexDiaFest].value = row.empleado.salario_capturado;
    }
}

function primaDominical(row, uma) {
    const indexPrima = row.percepciones.percepciones.findIndex(i => i.id_percepcion_deduccion === 'P019');
    if (indexPrima >= 0) {
        row.percepciones.percepciones[indexPrima].value = row.empleado.salario_capturado * 0.25;
        const comparativa = row.percepciones.percepciones[indexPrima].value <= uma ? row.percepciones.percepciones[indexPrima].value : uma;
        row.percepciones.percepciones[indexPrima]['excento'] = comparativa;
        row.percepciones.percepciones[indexPrima]['grabado'] = row.percepciones.percepciones[indexPrima].value - comparativa
        //console.log(row.percepciones.percepciones[indexPrima]);
    }
}

function primaVacacional(row, FI, uma) {
    const indexPrima = row.percepciones.percepciones.findIndex(i => i.id_percepcion_deduccion === 'P010');
    if (indexPrima >= 0) {
        let diff = moment().diff(moment(row.empleado.fecha_contrato_externo), 'years');
        if (diff === 0) diff = 1;
        const { dias_vacaciones } = FI.find(f => (parseInt(f.anio_antiguedad_inferior) >= diff && parseInt(f.anio_antiguedad_superior) <= diff));
        row.percepciones.percepciones[indexPrima].value = row.empleado.salario_capturado * dias_vacaciones * 0.25;

        const comparativa = row.percepciones.percepciones[indexPrima].value <= (15 * uma) ? row.percepciones.percepciones[indexPrima].value : (15 * uma);
        row.percepciones.percepciones[indexPrima]['excento'] = comparativa;
        row.percepciones.percepciones[indexPrima]['grabado'] = row.percepciones.percepciones[indexPrima].value - comparativa;
        //console.log(row.percepciones.percepciones[indexPrima]);
    }
}

function aguinaldo(row, dias, uma) {
    const indexAguinaldo = row.percepciones.percepciones.findIndex(i => i.id_percepcion_deduccion === 'P004');
    if (indexAguinaldo >= 0) {
        row.percepciones.percepciones[indexAguinaldo].value = row.empleado.salario_capturado * dias;
        const comparativa = row.percepciones.percepciones[indexAguinaldo].value <= (30 * uma) ? row.percepciones.percepciones[indexAguinaldo].value : (30 * uma);
        row.percepciones.percepciones[indexAguinaldo]['excento'] = comparativa;
        row.percepciones.percepciones[indexAguinaldo]['grabado'] = row.percepciones.percepciones[indexAguinaldo].value - comparativa;
        //console.log(row.percepciones.percepciones[indexAguinaldo]);
    }
}

//TOTALES
function totalGrabados(array) {
    if (array.length > 0) {
        return parseFloat(array.reduce((acum, { value, grabado }) => {
            if (grabado) acum += parseFloat(grabado)
            else acum += parseFloat(value)
            return acum;
        }, 0)).toFixedDown(5);
    }
    return 0;
}

function totalExcentos(array) {
    if (array.length > 0) {
        return parseFloat(array.reduce((acum, { value, excento, id_percepcion_deduccion }) => {
            if (excento) acum += parseFloat(excento)
            else if (id_percepcion_deduccion === 'P020') acum += parseFloat(value)
            return acum;
        }, 0)).toFixedDown(5);
    }
    return 0;
}

function totalPercepciones(array) {
    if (array.length > 0)
        return parseFloat(array.reduce((acum, { value, sumable, id_percepcion_deduccion }) => {
            if (sumable !== false) {
                acum += parseFloat(value)
            }
            return acum;
        }, 0)).toFixedDown(5);
    return 0;
}

function totalDeducciones(array) {
    if (array.length > 0)
        return parseFloat(array.reduce((acum, { value, sumable, id_percepcion_deduccion }) => {
            if (sumable !== false && id_percepcion_deduccion !== 'D100') {
                acum += parseFloat(value)
            }
            return acum;
        }, 0)).toFixedDown(5);
    return 0;
}


// eslint-disable-next-line 
Number.prototype.toFixedDown = function (digits) {
    var re = new RegExp("(\\d+\\.\\d{" + digits + "})(\\d)"),
        m = this.toString().match(re);
    return m ? parseFloat(m[1]) : this.valueOf();
};








