import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import { Grid, TextField, MenuItem, Button, Typography } from '@material-ui/core';
import { KeyboardDatePicker } from '@material-ui/pickers';
import { ArrowForward } from '@material-ui/icons';
import moment from "moment";
import { ValidacionNumeroIMSS, ValidacionRFCFisica, ValidacionCURP } from '../../../../libraries/Mask';
import GeneraCURP from '../../../../libraries/GeneraCURP';
import ImageAvatar from '../../ExtraComponents/ImgAvatar'
import { EstadosFull } from '../../../../libraries/Estados';


const useStyles = makeStyles(theme => ({
    container: {
        padding: theme.spacing(4),
    },
    select: {
        textAlign: 'left'
    },
    radioGroup: {
        marginTop: '15px'
    },
    uppercase: {
        textTransform: 'uppercase'
    },
}));

export default function Informacion(props) {
    const { data, setData, nacionalidadades, expandable, buttons, onlySee } = props;
    const classes = useStyles();
    const [estados] = React.useState([
        { title: "Casado", value: 'C' },
        { title: "Soltero", value: 'S' },
        { title: "Divorciado", value: 'D' },
        { title: "Viudo", value: 'V' },
        { title: "Indistinto", value: 'I' },
    ]);


    React.useEffect(() => {
        const clearVowels = (palabra) => {
            var newPal = palabra.toUpperCase();
            newPal = newPal.replace('á', 'a');
            newPal = newPal.replace('é', 'e');
            newPal = newPal.replace('í', 'i');
            newPal = newPal.replace('ó', 'o');
            newPal = newPal.replace('ú', 'u');
            return newPal;
        }

        const nombre = clearVowels(data.nombre.value);
        const apellido1 = clearVowels(data.primer_apellido.value);
        const apellido2 = clearVowels(data.segundo_apellido.value);
        const nacimiento = moment(data.fecha_nacimiento.value, "DD/MM/YYYY").format('DD/MM/YYYY');
        const genero = data.genero.value === 'M' ? 'H' : 'M';
        //console.log(nombre.length, apellido1.length, apellido2.length, nacimiento.length, genero.length)
        if (nombre.length > 0 && apellido1.length > 0 && apellido2.length > 0 && nacimiento.length > 0 && genero.length > 0) {
            const genera = GeneraCURP(nombre, apellido1, apellido2, nacimiento, genero).replace(/\s/g, '');
            if (data.curp.value.length !== 18 && data.rfc.value.length !== 13) {
                setData((prevState) => {
                    return {
                        ...prevState,
                        curp: { value: genera, verify: (`${genera}`.length === 18) },
                        rfc: { value: genera.substring(0, genera.length - 1), verify: (`${genera}`.length === 13) }
                    }
                })
            }


        }
        // eslint-disable-next-line
    }, [
        data.nombre.value,
        data.primer_apellido.value,
        data.segundo_apellido.value,
        data.fecha_nacimiento.value,
        data.genero.value,
    ]);

    const changeTextRequired = e => {
        const { name, value } = e.target;
        let verify;
        switch (name) {
            case 'curp': verify = value.replace(/\s/g, '').length === 18; break;
            case 'rfc': verify = value.replace(/\s/g, '').length === 13; break;
            default: verify = `${e.target.value}`.length > 0; break;
        }
        setData({ ...data, [name]: { value: value, verify: verify } })
    }

    const changeTextNoRequired = e => {
        setData(prevState => {
            return {
                ...prevState, [e.target.name]: { value: e.target.value, verify: true }
            }
        })
    }

    const changeDate = (e, key) => {
        if (e !== null) {
            let newDate = { value: e, verify: moment().subtract(16, 'year').isAfter(e) };
            setData(prevState => {
                return {
                    ...prevState, [key]: newDate
                }
            })
        }
    }

    const changeImg = file => {
        setData((prevState) => {
            return {
                ...prevState,
                fileImg: file
            }
        });
    }


    return (
        <Grid container spacing={3} className={classes.container}>
            <Grid item xs={12} sm={12} md={onlySee ? 12 : 3} lg={onlySee ? 12 : 2}>
                <Grid container spacing={0}>
                    <Grid item xs={12} sm={12} md={12} lg={12} >
                        <Typography variant="subtitle2" color="initial" gutterBottom style={{ textAlign: 'center' }}>Foto Empleado</Typography>
                    </Grid>
                    <Grid item xs={12} sm={12} md={12} lg={12} >
                        <ImageAvatar file={data.fileImg} url={data.url_foto.value} changeImg={changeImg} onlySee={onlySee} />
                    </Grid>
                </Grid>
            </Grid>
            <Grid item xs={12} md={onlySee ? 12 : 9} lg={onlySee ? 12 : 10}>
                <Grid container spacing={2}>
                    <Grid item sm={12}>
                        <Typography variant="subtitle2" style={{ textAlign: 'center' }}>General</Typography>
                    </Grid>
                    <Grid item xs={12} sm={4} md={onlySee ? 6 : 4} lg={6}>
                        <TextField
                            disabled={onlySee}
                            required name="nombre"
                            variant="outlined"
                            error={data.nombre.verify !== '' ? !data.nombre.verify : false} label="Nombre" type="text" fullWidth placeholder="Ej. Juana" InputLabelProps={{ shrink: true }} value={data.nombre.value}
                            onChange={changeTextRequired} inputProps={{ className: classes.capitalize }}
                            autoComplete='off'
                        />
                    </Grid>
                    <Grid item xs={12} sm={4} md={onlySee ? 6 : 4} lg={onlySee ? 6 : 3}>
                        <TextField
                            disabled={onlySee}
                            required
                            variant="outlined"
                            name="primer_apellido"
                            label="Primer Apellido"
                            fullWidth type="text"
                            error={data.primer_apellido.verify !== '' ? !data.primer_apellido.verify : false}
                            placeholder="Ej. Pérez"
                            InputLabelProps={{ shrink: true }}
                            value={data.primer_apellido.value}
                            onChange={changeTextRequired}
                            inputProps={{ className: classes.capitalize }}
                            autoComplete='off'
                        />
                    </Grid>
                    <Grid item xs={12} sm={4} md={onlySee ? 6 : 4} lg={onlySee ? 6 : 3}>
                        <TextField
                            disabled={onlySee}
                            variant="outlined"
                            label="Segundo Apellido"
                            fullWidth
                            type="text"
                            name="segundo_apellido"
                            error={data.segundo_apellido.verify !== '' ? !data.segundo_apellido.verify : false}
                            placeholder="Ej. López"
                            InputLabelProps={{ shrink: true }}
                            value={data.segundo_apellido.value} onChange={changeTextNoRequired}
                            inputProps={{ className: classes.capitalize }}
                            autoComplete='off'
                        />
                    </Grid>
                    <Grid item xs={12} sm={3} md={onlySee ? 3 : 2} lg={onlySee ? 3 : 2}>
                        <TextField
                            disabled={onlySee}
                            required
                            fullWidth
                            variant="outlined"
                            name="genero"
                            error={data.genero.verify !== '' ? !data.genero.verify : false} label="Sexo" select className={classes.select} value={`${data.genero.value !== '' ? data.genero.value : 'DEFAULT'}`} onChange={changeTextRequired} InputLabelProps={{ shrink: true }}>
                            <MenuItem value="DEFAULT" disabled>Selecciona</MenuItem>
                            <MenuItem value="F">Femenino</MenuItem>
                            <MenuItem value="M">Masculino</MenuItem>
                            <MenuItem value="O">Otro</MenuItem>
                        </TextField>
                    </Grid>

                    <Grid item xs={12} sm={3} md={onlySee ? 3 : 2} lg={onlySee ? 3 : 2}>
                        <TextField
                            disabled={onlySee}
                            required
                            fullWidth
                            variant="outlined"
                            name="estado_civil"
                            error={data.estado_civil.verify !== '' ? !data.estado_civil.verify : false} label="Estado Civil" select className={classes.select} value={`${estados.length > 0 && data.estado_civil.value !== '' ? data.estado_civil.value : 'DEFAULT'}`} InputLabelProps={{ shrink: true }} onChange={changeTextRequired}>
                            <MenuItem value="DEFAULT" disabled>Selecciona</MenuItem>
                            {estados.map((item) => {
                                return <MenuItem key={`estado-civil-${item.value}`} value={item.value}>{item.title}</MenuItem>
                            })}
                        </TextField>
                    </Grid>

                    <Grid item xs={12} sm={3} md={onlySee ? 6 : 4} lg={onlySee ? 6 : 4}>
                        <KeyboardDatePicker
                            disabled={onlySee}
                            inputVariant="outlined"
                            maxDateMessage={"La edad mínina de registro es 16 años"}
                            minDateMessage={"Fecha es menor al mínimo permitido"}
                            invalidDateMessage={"Formato invalido"}
                            InputLabelProps={{ shrink: true }}
                            fullWidth
                            required
                            error={data.fecha_nacimiento.verify !== '' ? !data.fecha_nacimiento.verify : false}
                            placeholder="Mes/Día/Año"
                            id="fecha_nacimiento"
                            label="Nacimiento"
                            format="DD/MM/YYYY"
                            value={data.fecha_nacimiento.value !== '' ? moment(data.fecha_nacimiento.value, "DD/MM/YYYY") : null}
                            maxDate={moment().subtract(16, 'year')}
                            onChange={(e) => changeDate(e, "fecha_nacimiento")}
                            autoComplete='off'
                        />
                    </Grid>

                    <Grid item xs={12} sm={3} md={onlySee ? 6 : 4} lg={onlySee ? 6 : 4}>
                        <TextField
                            required
                            fullWidth
                            variant="outlined"
                            name="id_nacionalidad"
                            disabled={onlySee}
                            label="País Origen" error={data.id_nacionalidad.verify !== '' ? !data.id_nacionalidad.verify : false} placeholder="Selecciona" select className={classes.select} value={`${nacionalidadades.length > 0 && data.id_nacionalidad.value !== '' ? data.id_nacionalidad.value : 'DEFAULT'}`} InputLabelProps={{ shrink: true }} onChange={changeTextRequired}>
                            <MenuItem value="DEFAULT" disabled>Selecciona</MenuItem>
                            {nacionalidadades.map((item) => {
                                return <MenuItem key={`nacionalidad-${item.id_nacionalidad}`} value={item.id_nacionalidad}>{item.pais_nac}</MenuItem>
                            })}
                        </TextField>

                    </Grid>

                    <Grid item xs={12} sm={6} md={6} lg={onlySee ? 6 : 3}>
                        <TextField
                            disabled={onlySee}
                            required
                            variant="outlined"
                            name="rfc"
                            label="RFC"
                            fullWidth
                            error={data.rfc.verify !== '' ? !data.rfc.verify : false}
                            placeholder="Ej. ABCD010101M01"
                            InputLabelProps={{ shrink: true }}
                            value={data.rfc.value}
                            onChange={changeTextRequired}
                            inputProps={{ className: classes.uppercase }}
                            InputProps={{ inputComponent: ValidacionRFCFisica }}
                            helperText="Completa el RFC"
                            autoComplete='off'
                        />
                    </Grid>
                    <Grid item xs={12} sm={6} lg={onlySee ? 6 : 4}>
                        <TextField
                            disabled={onlySee}
                            required
                            variant="outlined"
                            name="curp"
                            label="CURP"
                            fullWidth
                            error={data.curp.verify !== '' ? !data.curp.verify : false}
                            placeholder="Ej. ABCD010101MEFGHI01"
                            InputLabelProps={{ shrink: true }}
                            value={data.curp.value}
                            onChange={changeTextRequired}
                            inputProps={{ className: classes.uppercase }}
                            InputProps={{ inputComponent: ValidacionCURP }}
                            helperText="Completa tu CURP"
                            autoComplete='off'
                        />
                    </Grid>

                    <Grid item xs={12} sm={4} md={onlySee ? 6 : 4} lg={onlySee ? 6 : 2}>
                        <TextField
                            disabled={onlySee} autoComplete='off'
                            required variant="outlined" name="no_imss" error={data.no_imss.verify !== '' ? !data.no_imss.verify : false} label="No. IMSS" fullWidth placeholder="Ej. 01010101010" InputLabelProps={{ shrink: true }} value={data.no_imss.value} onChange={changeTextRequired} inputProps={{ className: classes.uppercase }} InputProps={{ inputComponent: ValidacionNumeroIMSS }} />
                    </Grid>
                    <Grid item xs={12} sm={4} md={onlySee ? 6 : 4} lg={onlySee ? 6 : 3}>
                        <TextField
                            disabled={onlySee}
                            fullWidth
                            required
                            variant="outlined"
                            error={data.entidad_labora.verify !== '' ? !data.entidad_labora.verify : false}
                            label="Entidad donde Labora"
                            select
                            name="entidad_labora"
                            className={classes.select}
                            value={`${EstadosFull.length > 0 && data.entidad_labora.value !== '' ? data.entidad_labora.value : 'DEFAULT'}`}
                            InputLabelProps={{ shrink: true }}
                            onChange={changeTextRequired}>
                            <MenuItem value="DEFAULT" disabled>Selecciona</MenuItem>
                            {EstadosFull.map((item) => {
                                return <MenuItem key={`estados-fiscales-${item.code}`} value={item.code}>{item.name}</MenuItem>
                            })}
                        </TextField>
                    </Grid>

                    {
                        !expandable &&
                        <Grid
                            container
                            item
                            direction="row"
                            justify="flex-end"
                            alignItems="flex-end" xs={12} sm={4} md={4} lg={12}>
                            <Button
                                variant="outlined"
                                color="primary"
                                endIcon={<ArrowForward />}
                                onClick={buttons.next}
                            >Siguiente</Button>
                        </Grid>
                    }
                </Grid>
            </Grid>

        </Grid>
    )
}