import React, { useState, useEffect } from 'react'
import { Grid, Button, Dialog, DialogContent, DialogActions, DialogTitle, DialogContentText, Typography, LinearProgress } from '@material-ui/core';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import LocalAtmIcon from '@material-ui/icons/LocalAtm';
import { Avatar, IconButton } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles';
import { green } from '@material-ui/core/colors';
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import AttachmentOutlinedIcon from '@material-ui/icons/AttachmentOutlined';
import ClearOutlinedIcon from '@material-ui/icons/ClearOutlined';
import PictureAsPdfIcon from '@material-ui/icons/PictureAsPdf';
import { put } from '../SettingsNomina/helperSettings';
import { post as postComprobante, get as getComprobante } from './helper';
import { SessionContext } from '../../../../routes/adminrh/CustomRoutes';
import { NominaContext } from '../Nomina';

const useStyles = makeStyles((theme) => ({
    wrapper: {
        margin: theme.spacing(2),
        position: 'relative',
    },
    buttonSuccess: {
        backgroundColor: green[500],
        '&:hover': {
            backgroundColor: green[700],
        },
    },
    fabProgress: {
        color: green[500],
        position: 'absolute',
        top: -4,
        left: -4,
        zIndex: 1,
    },
    square: {
        color: '#FFFFFF',
        backgroundColor: '#cb0606',
        fontSize: theme.spacing(2)
    },
    divider: {
        marginLeft: '30px',
        marginRight: '30px'
    }
}));

const simpleDialogState = {
    title: '',
    message: '',
    open: false,
    onConfirm: () => { }
}

export default function ButtonsStatus({ nomina, setNomina, idNomina, idEmpresa }) {
    const [simpleDialog, setSimpleDialog] = useState({ ...simpleDialogState })
    const [dialogPay, setDialogPay] = useState(false);
    const { cabeceras, isTokenExpired } = React.useContext(SessionContext);

    const onClick = (avance, to) => {
        let message, title, open = true

        const onConfirm = () => {
            put(idNomina, { estatus: to }, cabeceras)
                .then((resp) => {
                    if (!isTokenExpired(resp))
                        setNomina({ ...nomina, estatus: to });
                })
        }

        title = avance ? 'Avazando...' : 'Retrocediendo...';
        message = avance ? "Estas por ir al siguiente paso " : "Estas por retroceder al paso anterior ";
        switch (to) {
            case 'P':
                message += "\"Prenómina\" podras hacer modificaciones de la nómina";
                setSimpleDialog({ message, title, open, onConfirm });
                break;
            case 'N':
                message += "\"Nómina\" ya no podras hacer modificaciones de la nómina";
                setSimpleDialog({ message, title, open, onConfirm });
                break;
            case 'A':
                message += "\"Pago Aprobado\"";
                setDialogPay(true);
                break;
            case 'D':
                message += "\"Dispersar\" dinero al los empleados"; break;
            default: break;
        }
    }

    return (
        <Grid item>
            <TwoButtons setNomina={setNomina} nomina={nomina} onClick={onClick} />
            <DialogConfirm dialog={simpleDialog} setDialog={setSimpleDialog} />
            <DialogPay open={dialogPay} setOpen={setDialogPay} idNomina={idNomina} idEmpresa={idEmpresa} />
        </Grid>
    )
}

const TwoButtons = ({ nomina, onClick }) => {
    const { estatus } = nomina;

    switch (estatus) {
        case 'C':
            return <Button size="small"
                endIcon={<ArrowForwardIcon />}
                color="secondary"
                onClick={() => onClick(true, 'P')}>Prenómina</Button>
        case 'P':
            return <Button size="small"
                endIcon={<ArrowForwardIcon />}
                color="secondary"
                onClick={() => onClick(true, 'N')}>Nómina</Button>
        case 'N':
            return <>
                <Button size="small"
                    startIcon={<ArrowBackIcon />}
                    color="primary"
                    onClick={() => onClick(false, 'P')}>Prenómina</Button>
                <Button size="small"
                    color="secondary"
                    endIcon={<LocalAtmIcon />}
                    onClick={() => onClick(true, 'A')}>Pagar</Button>
            </>
        case 'A':
            return <VerNomina idNomina={nomina.id_nomina} />
        default: return null;
    }

}

const VerNomina = ({ idNomina }) => {

    const { cabeceras, isTokenExpired } = React.useContext(SessionContext);
    const [url, setUrl] = useState();
    useEffect(() => {
        getComprobante(idNomina, cabeceras).then(response => {
            if (!isTokenExpired(response))
                setUrl(response.documento[0].url_archivo);
        })
        //eslint-disable-next-line
    }, [idNomina]);

    return (
        <Button
            size="small"
            endIcon={<PictureAsPdfIcon />}
            disabled={url === undefined}
            onClick={() => window.open(`${url}&raw=1`, '_blank')}
            color="secondary">
            Comprobante
        </Button>
    );
}

const DialogConfirm = ({ dialog, setDialog }) => {
    const { title, message, open, onConfirm } = dialog;

    const onClose = () => setDialog({ ...dialog, open: false });

    return (
        <Dialog open={open} onClose={onClose} aria-labelledby="">
            <DialogTitle>{title}</DialogTitle>
            <DialogContent>
                <DialogContentText>
                    {message}
                </DialogContentText>
            </DialogContent>
            <DialogActions>
                <Button onClick={onClose} color="default">
                    Cancel
        </Button>
                <Button onClick={() => { onConfirm(); onClose(); }} color="secondary">
                    Confirmar
        </Button>
            </DialogActions>
        </Dialog>)
}

const DialogPay = ({ open, setOpen, idNomina, idEmpresa }) => {
    const [error, setError] = useState("");
    const [file, setFile] = useState(null);
    const [loading, setLoading] = useState(false);
    const { cabeceras, isTokenExpired } = React.useContext(SessionContext);
    const { generalSettings, setGeneralSettings } = React.useContext(NominaContext);

    const sendComprobante = () => {
        if (file !== null) {
            setLoading(true);
            const form = new FormData();
            form.append('comprobante', file);
            form.append('id_nomina', idNomina);
            form.append('id_empresa', idEmpresa);

            const headers = new Headers();
            headers.append('authorization', cabeceras.authorization);

            postComprobante(form, headers).then(resp => {
                if (!isTokenExpired(resp)) {
                    put(idNomina, { estatus: 'A' }, cabeceras).then(response => {
                        if (!isTokenExpired(response)) {
                            setGeneralSettings({ ...generalSettings, estatus: 'A' })
                            setOpen(false);
                        }
                    })
                }
            });
            /*  Promise.all([
                 postComprobante({ id_nomina: idNomina, comprobante: file }),
                
             ]).then(respuesta => {
                 console.log(respuesta);
             }) */
        }
        else {
            setError(true);
            setTimeout(() => setError(false), 1500);
        };
    }
    return (
        <Dialog open={open} onClose={() => setOpen(false)} aria-labelledby="">
            {loading &&
                <LinearProgress color="secondary" />
            }
            <DialogTitle>{"Estás por notificar a los empleados"}</DialogTitle>
            <DialogContent>
                <DialogContentText >
                    Una vez que subas el comprobante de pago de la nómina,
                    los empleados recibirán la notificación de su pago.
                </DialogContentText>
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <InputFile value={file} onChange={setFile} label={"Comprobante de Pago"} />
                    </Grid>
                </Grid>
                {error &&
                    <Typography variant="button" style={{ color: 'red' }}>{error}</Typography>
                }
            </DialogContent>
            <DialogActions>
                <Button onClick={() => setOpen(false)} color="default">
                    Cancel
                </Button>
                <Button onClick={() => sendComprobante()} color="secondary">
                    Confirmar
                </Button>
            </DialogActions>
        </Dialog>
    )
}


const InputFile = ({ value, onChange, label }) => {
    const classes = useStyles();

    const upLoadFile = ({ target: { files } }) => onChange(files[0]);
    const removeFile = () => onChange(null);

    return (
        <Card elevation={0}>
            <CardHeader style={{ padding: 0 }}
                avatar={
                    <IconButton disabled>
                        <Avatar variant="rounded" className={classes.square}>
                            PDF
                            </Avatar>
                    </IconButton>}
                action={
                    <div className={classes.wrapper}>
                        {
                            value === null &&
                            <>
                                <input
                                    style={{ display: "none" }}
                                    id={`input-file-ticket`}
                                    type="file"
                                    accept="application/pdf"
                                    onChange={upLoadFile}
                                />
                                <Button
                                    size="small"
                                    aria-label="save"
                                    color="primary"
                                    htmlFor={`input-file-ticket`}
                                    component="label"
                                    endIcon={<AttachmentOutlinedIcon />}>
                                    Subir
                                </Button>
                            </>
                        }
                    </div>
                }
                title={label}
                subheader={
                    <>
                        {
                            value !== null ?
                                <>
                                    {`${value.name}`}
                                    <IconButton aria-label="delete" className={classes.margin} size="small" onClick={removeFile}>
                                        <ClearOutlinedIcon fontSize="inherit" />
                                    </IconButton>
                                </>
                                : 'Selecciona un archivo'
                        }
                    </>
                }
            />
        </Card>
    );
}