import React from 'react';

//MATERIAL UI
import TextField from '@material-ui/core/TextField';
import { KeyboardDatePicker } from '@material-ui/pickers'


export const DatePickerField = ({ field, form, ...other }) => {
    const error = form.errors[field.name] && form.touched[field.name] && String(form.errors[field.name]);
    return (
        <KeyboardDatePicker
            clearable
            fullWidth
            name={field.name}
            value={field.value}
            format="DD/MM/YYYY"
            maxDateMessage={"No vayas al futuro!"}
            minDateMessage={"No vayas al pasado!"}
            invalidDateMessage={"Formato invalido"}
            error={Boolean(form.errors[field.name] && form.touched[field.name])}
            onError={e => {
                if (e !== error && e !== '') {
                    form.setFieldError(field.name, e);
                }
            }}
            onBlur={field.onBlur}
            onChange={date => form.setFieldValue(field.name, date)}
            {...other}
        />
    );
};

export const InputFormikUI = ({ field, form, children, ...other }) => {
    return <TextField
        size="small"
        autoComplete='off'
        fullWidth
        name={field.name}
        value={field.value}
        error={form.errors[field.name] && form.touched[field.name]}
        helperText={form.errors[field.name] && form.touched[field.name] && String(form.errors[field.name])}
        onChange={field.onChange}
        onBlur={field.onBlur}
        {...other}
    >
        {children}
    </TextField>
}