import React from 'react'
import IconButton from '@material-ui/core/Fab'
import FullscreenIcon from '@material-ui/icons/Fullscreen';
import FullscreenExitIcon from '@material-ui/icons/FullscreenExit';
import { makeStyles } from '@material-ui/core/styles';


const useStyles = makeStyles((theme) => ({
    button: {
        marginTop: '5px !important',
        float: 'right',
        color: '#4caf50 !important',
        backgroundColor: 'transparent !important',
        animation: `$pulse 3s infinite`
    },
    "@keyframes pulse": {
        "0%": {
            boxShadow: '0 0 0 0 rgba(76, 175, 80,0.7)'
        },
        "70%": {
            boxShadow: '0 0 0 5px rgba(76, 175, 80,0)'
        },
        "100%": {
            boxShadow: '0 0 0 0 rgba(76, 175, 80,0)'
        }
    }
}));

export default function FloatButton(props) {
    const classes = useStyles();
    return (
        <IconButton size="small" aria-label="full-screen" className={classes.button} onClick={props.onClick}>
            {props.open ? <FullscreenExitIcon fontSize="small" /> : <FullscreenIcon />}
        </IconButton>
    )
}
