
import { BASE_URL } from '../../../../libraries/RequestFetch'

export const post = async (data, headers) => {
    return fetch(
        `${BASE_URL}/api/rh/nomina/detalle_nomina`,
        {
            method: 'POST',
            headers,
            body: JSON.stringify(data)
        }
    ).then(response => response.json())
}

export const remove = async (idNomina, idPD, headers) => {
    return fetch(
        `${BASE_URL}/api/rh/nomina/detalle_nomina/${idNomina}/${idPD}`,
        { method: 'DELETE', headers }
    ).then(response => response.json());
}

export const put = async (idNomina, idPD, data, headers) => {
    return fetch(
        `${BASE_URL}/api/rh/nomina/detalle_nomina/${idNomina}/${idPD}`,
        {
            method: 'PUT',
            headers,
            body: JSON.stringify(data)
        }
    ).then(response => response.json());
}