
import { BASE_URL } from '../../../../libraries/RequestFetch'

export const post = async (data, headers) => {
    console.log(data);
    return fetch(
        `${BASE_URL}/api/rh/nomina/nomina_variable`,
        {
            method: 'POST',
            headers,
            body: JSON.stringify(data)
        }
    ).then(response => {
        if (response.ok)
            return response.json();
        else throw new Error(response);
    })
}

export const remove = async (idNomina, folio, headers) => {
    return fetch(
        `${BASE_URL}/api/rh/nomina/nomina_variable/${idNomina}/${folio}`,
        { method: 'DELETE', headers }
    ).then(response => response.json());
}

export const put = async (idNomina, folio, data, headers) => {
    return fetch(
        `${BASE_URL}/api/rh/nomina/nomina_variable/${idNomina}/${folio}`,
        {
            method: 'PUT',
            headers,
            body: JSON.stringify(data)
        }
    ).then(response => response.json());
}

