import React from 'react';
import { Grid, TextField, Button, IconButton, Typography, InputAdornment } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { ArrowBack, ArrowForward } from '@material-ui/icons';
import PhoneIcon from '@material-ui/icons/Phone';
import DeleteIcon from '@material-ui/icons/Delete';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import { ValidacionTelefono, ValidacionEmail } from '../../../../libraries/Mask';

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const useStyles = makeStyles(theme => ({
    container: {
        padding: theme.spacing(4),
    }
}));

export default function Contacto(props) {
    const [dialog, setDialog] = React.useState({ open: false, value: '', pos: '' })
    const classes = useStyles();
    const { phones, setPhones } = props;
    const { data, setData } = props;
    const { onlySee } = props;

    const addTel = () => {
        const newTel = [...phones];
        newTel.push({ value: '', verify: '', tipo: `${newTel.length - 2}`, helperText: '' });
        setPhones(newTel);
    }

    const removeTel = (pos) => {
        let newTel = [...phones];
        const obj = newTel.splice(pos, 1)[0];
        if (obj.value.replace(/\s/g, '').length === 0)
            setPhones(newTel);
        else
            setDialog({ open: true, value: obj.value, pos: pos })
    }

    const removeConfirm = ({ pos }) => {
        let newTel = [...phones];
        newTel.splice(pos, 1);
        setPhones(newTel);
    }

    const changeTel = (pos, event) => {
        const { value, name } = event.target;
        let newTel = [...phones];

        newTel[pos].value = value;
        newTel[pos].tipo = name;

        const repetido = repeted(pos, [...newTel]);

        newTel[pos].verify = value.length > 0 ? repetido : true;
        newTel[pos].helperText = value.length > 0 && !repetido ? 'Repetido' : '';

        newTel.forEach((item, index) => {
            if (repeted(index, [...newTel])) {
                item.helperText = ''
                item.verify = true;
            }
        })
        setPhones(newTel);
    }

    const repeted = (pos, array) => {
        const repeted = array.filter(item => item.value === array[pos].value);
        return repeted.length === 1;
    }

    const changeTextRequired = e => {
        const { name, value } = e.target;
        setData(prevState => {
            return {
                ...prevState, [name]: { value: value, verify: `${value}`.length > 0 }
            }
        })
    }

    const changeTextNoRequired = e => {
        const { name, value } = e.target;
        setData(prevState => {
            return {
                ...prevState, [name]: { value: value, verify: true }
            }
        })
    }

    const extraPhones = () => {
        const array = [...phones];
        array.splice(0, 2);

        return array.map((item, index) => {
            return <Grid key={`telefono-extra-empleado-${index + 1}`} item xs={12} sm={6} md={6} lg={6}>
                <TextField
                    variant="outlined"
                    name={`${index + 1}`}
                    required
                    label={`Tel. Extra ${index + 1}`}
                    fullWidth
                    placeholder="Ej. 111 222 3344"
                    InputLabelProps={{ shrink: true }}
                    value={item.value}
                    onChange={(e) => changeTel(index + 2, e)}
                    error={item.verify !== '' ? !item.verify : false}
                    helperText={item.helperText}
                    InputProps={{
                        inputComponent: ValidacionTelefono,
                        endAdornment: <InputAdornment position="end">
                            <IconButton aria-label="delete" onClick={() => removeTel(index + 2)}>
                                <DeleteIcon />
                            </IconButton>
                        </InputAdornment>
                    }}
                    autoComplete='off'
                />
            </Grid>

        });
    }

    return (
        <>
            <Grid container spacing={3} className={classes.container}>
                <Grid item xs={12} sm={12} md={onlySee ? 12 : 6} lg={onlySee ? 12 : 6}>
                    <Grid container spacing={2}>
                        <Grid item xs={12} md={12} lg={12}>
                            <TextField
                                required
                                variant="outlined"
                                label="Correo"
                                fullWidth
                                placeholder="Ej. ejemplo@ejemplo.com"
                                InputLabelProps={{ shrink: true }}
                                value={data.correo.value}
                                name="correo"
                                disabled={onlySee}
                                onChange={changeTextRequired}
                                error={data.correo.verify !== '' ? !data.correo.verify : false}
                                InputProps={{ inputComponent: ValidacionEmail }}
                                autoComplete='off'
                            />
                        </Grid>
                        <Grid item xs={12} md={12} lg={12}>
                            <Typography variant="subtitle2" style={{ textAlign: 'center' }} color="initial"> Telefonos</Typography>
                        </Grid>
                        <Grid item xs={6} md={6} lg={6}>
                            <TextField
                                variant="outlined"
                                name="C"
                                disabled={onlySee}
                                label="Tel. Celular"
                                fullWidth
                                placeholder="Ej. 111 222 3344"
                                InputLabelProps={{ shrink: true }}
                                value={phones[0].value} onChange={(e) => changeTel(0, e)}
                                InputProps={{ inputComponent: ValidacionTelefono }}
                                error={phones[0].verify !== '' ? !phones[0].verify : false}
                                helperText={phones[0].helperText}
                                autoComplete='off'
                            />
                        </Grid>
                        <Grid item xs={6} md={6} lg={6}>
                            <TextField
                                variant="outlined"
                                name="F"
                                label="Tel. Fijo"
                                disabled={onlySee}
                                fullWidth
                                placeholder="Ej. 111 222 3344"
                                InputLabelProps={{ shrink: true }}
                                value={phones[1].value}
                                onChange={(e) => changeTel(1, e)}
                                InputProps={{ inputComponent: ValidacionTelefono }}
                                error={phones[1].verify !== '' ? !phones[1].verify : false}
                                helperText={phones[1].helperText}
                                autoComplete='off'
                            />
                        </Grid>

                        {extraPhones()}

                        {
                            !onlySee &&
                            <Grid item xs={12} md={12} lg={12}>
                                <Grid container
                                    direction="column"
                                    justify="center"
                                    alignItems="center">
                                    <Button
                                        color="primary"
                                        size="small"
                                        onClick={addTel}
                                        startIcon={<PhoneIcon />}>
                                        Agregar</Button>
                                </Grid>
                            </Grid>
                        }

                    </Grid>
                </Grid>

                <Grid item container xs={12} md={onlySee ? 12 : 6} lg={onlySee ? 12 : 6}>
                    <TextField
                        variant="outlined"
                        label="Observaciones"
                        fullWidth
                        multiline
                        disabled={onlySee}
                        placeholder={`Ej. Representante: \nPago en cheque\nCada 14 del mes`}
                        InputLabelProps={{ shrink: true }}
                        value={data.observaciones.value}
                        name="observaciones"
                        onChange={changeTextNoRequired}
                    />
                </Grid>

                {
                    !props.expandable &&
                    <Grid container item
                        direction="row"
                        justify="flex-end"
                        alignItems="center" xs={12} sm={12} md={12} lg={12} >
                        <Button startIcon={<ArrowBack />}
                            onClick={props.buttons.back}>Anterior</Button>
                        <Button
                            variant="outlined"
                            color="primary"
                            endIcon={<ArrowForward />}
                            onClick={props.buttons.next}
                        >Siguiente</Button>
                    </Grid>
                }
            </Grid>

            <Dialog
                open={dialog.open}
                TransitionComponent={Transition}
                onClose={() => setDialog({ open: false })}>
                <DialogTitle>{`Eliminar teléfono`}</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        {`Está apunto de eliminar el teléfono ${dialog.value}`}
                    </DialogContentText>
                    <DialogContentText>
                        ¿Está seguro que desea continuar?
                        </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setDialog({ open: false })}>Cancelar</Button>
                    <Button onClick={() => {
                        //console.log({...dialog});
                        removeConfirm({ ...dialog });
                        setDialog({ open: false });
                    }}
                        color="secondary">Eliminar</Button>
                </DialogActions>
            </Dialog>


        </>
    )
}
