import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import { Grid, TextField, MenuItem, Button } from '@material-ui/core';
import { ArrowBack, ArrowForward } from '@material-ui/icons';
import RequestFetch from '../../../../libraries/RequestFetch';
import { EstadosFull, CodeByEstado } from '../../../../libraries/Estados';
const useStyles = makeStyles(theme => ({
    container: {
        padding: theme.spacing(4)
    }
}));

const textDisabled = makeStyles({
    root: {
        '&$disabled': {
            color: 'rgba(0, 0, 0, 0.87)'
        },
    },
    disabled: {},
}, { name: 'MuiInputBase' });

export default function Direccion(props) {
    const { data, setData } = props;
    const { onlySee } = props;

    const classes = useStyles();
    textDisabled();
    const [asentamientos, setAsentamientos] = React.useState([]);
    const [existeCP, setExisteCP] = React.useState(false);
    const [helper, setHelper] = React.useState("");

    const [codigo, setCodigo] = React.useState('');

    const changeCodigoPostal = (e) => {
        const { value } = e.target;
        setData(prevState => {
            return {
                ...prevState,
                'cp': { value: value, verify: `${value}`.length === 5 },
                'municipio': { value: '', verify: '' },
                'entidad': { value: '', verify: '' },
                'colonia': { value: '', verify: '' },
            }
        })
        setAsentamientos([]);
        if (value.length === 5) {
            setCodigo(value)
        }
    }

    React.useEffect(() => {
        let mounted = true;
        if (data.cp.value.length === 5 && asentamientos.length === 0) {
            const _exito = async (res) => {
                const respuesta = await res.json();
                const { asentamiento, municipio, estado } = respuesta.response;
                if (mounted) {
                    setHelper("Encontrado");
                    setData(prevState => {
                        return {
                            ...prevState,
                            'municipio': { value: municipio, verify: true },
                            'entidad': { value: CodeByEstado(estado), verify: true },
                        }
                    })
                    setExisteCP(false);
                    setAsentamientos(asentamiento);
                }
            }


            const _fallo = async (res) => {
                if (mounted) {
                    setHelper("No encontrado");
                    setExisteCP(true);
                }
            }

            const getData = () => {
                RequestFetch.get({
                    url: `https://api-sepomex.hckdrk.mx/query/info_cp/${data.cp.value}?type=simplified`,
                    headers: new Headers(),
                    exito: _exito,
                    fallo: _fallo,
                    error_request: (error) => {
                        if (mounted) {
                            setHelper("No encontrado");
                            setExisteCP(true);
                        }
                    }
                });
            }
            if (mounted)
                getData();
        }
        return () => mounted = false;
        // eslint-disable-next-line
    }, [codigo, data.cp.value, asentamientos])


    const changeTextRequired = e => {
        const { name, value } = e.target;
        setData(prevState => {
            return {
                ...prevState, [name]: { value: value, verify: `${value}`.length > 0 }
            }
        })
    }

    const changeTextNoRequired = e => {
        const { name, value } = e.target;
        setData(prevState => {
            return {
                ...prevState, [name]: { value: value, verify: true }
            }
        })
    }

    const handleKeyPress = event => {
        if (!/[0-9]/.test(event.key)) {
            event.preventDefault();
        }
    };

    return (
        <Grid container spacing={3} className={classes.container}>
            <Grid item xs={12} sm={6} md={6} lg={6}>
                <TextField
                    disabled={onlySee}
                    required
                    variant="outlined"
                    label="Calle"
                    fullWidth name="calle"
                    placeholder="Ej. Calle, Av., Priv."
                    type="text"
                    InputLabelProps={{ shrink: true }}
                    value={data.calle.value}
                    onChange={changeTextRequired}
                    error={data.calle.verify !== '' ? !data.calle.verify : false}
                    inputProps={{ maxLength: 60 }}
                    autoComplete='off'
                />
            </Grid>
            <Grid item xs={12} sm={3} md={3} lg={3}>
                <TextField
                    variant="outlined"
                    label="No. Exterior"
                    fullWidth
                    disabled={onlySee}
                    name="no_exterior"
                    placeholder="Ej. 100-A, S/N"
                    type="text"
                    InputLabelProps={{ shrink: true }}
                    value={data.no_exterior.value}
                    onChange={changeTextNoRequired}
                    inputProps={{ maxLength: 5 }}
                    autoComplete='off'
                />
            </Grid>
            <Grid item xs={12} sm={3} md={3} lg={3}>
                <TextField
                    variant="outlined"
                    label="No. Interior"
                    fullWidth
                    name="no_interior"
                    placeholder="Ej. dpto 10, S/N"
                    type="text"
                    InputLabelProps={{ shrink: true }}
                    value={data.no_interior.value}
                    onChange={changeTextNoRequired}
                    disabled={onlySee}
                    inputProps={{ maxLength: 5 }}
                    autoComplete='off'
                />
            </Grid>
            <Grid item xs={12} sm={3} md={onlySee ? 6 : 3} lg={onlySee ? 6 : 3}>
                <TextField
                    required
                    variant="outlined"
                    label="Código Postal"
                    name="cp"
                    fullWidth
                    placeholder="Ej. 91193"
                    InputLabelProps={{ shrink: true }}
                    value={data.cp.value}
                    onChange={changeCodigoPostal}
                    error={data.cp.verify !== '' ? !data.cp.verify : false}
                    inputProps={{ maxLength: 5, onKeyPress: handleKeyPress }}
                    disabled={onlySee}
                    helperText={helper}
                    autoComplete='off'
                />
            </Grid>
            <Grid item xs={12} sm={3} md={onlySee ? 6 : 3} lg={onlySee ? 6 : 3}>
                {
                    !existeCP ?
                        <TextField
                            disabled={onlySee}
                            required
                            variant="outlined"
                            fullWidth name="colonia"
                            label="Colonia"
                            select
                            value={`${asentamientos.length > 0 && data.colonia.value !== '' ? data.colonia.value : 'DEFAULT'}`}
                            placeholder="Selecciona"
                            InputLabelProps={{ shrink: true }}
                            onChange={changeTextRequired}
                            error={data.colonia.verify !== '' ? !data.colonia.verify : false}>

                            <MenuItem value="DEFAULT" disabled>Selecciona</MenuItem>
                            {
                                asentamientos.map((item, index) => {
                                    return <MenuItem key={`colonia-${index}`} value={item}>{item}</MenuItem>
                                })
                            }
                        </TextField>
                        :

                        <TextField
                            disabled={onlySee}
                            required
                            variant="outlined"
                            fullWidth
                            name="colonia"
                            label="Colonia"
                            value={data.colonia.value}
                            placeholder="Ej. Centro"
                            InputLabelProps={{ shrink: true }}
                            onChange={changeTextRequired}
                            error={data.colonia.verify !== '' ? !data.colonia.verify : false}
                            autoComplete='off'
                        />
                }

            </Grid>
            <Grid item xs={12} sm={3} md={onlySee ? 6 : 3} lg={onlySee ? 6 : 3}>
                <TextField
                    required
                    variant="outlined"
                    name="municipio"
                    label="Municipio"
                    fullWidth
                    disabled={!existeCP || onlySee}
                    placeholder="Ej. Del. Col. Alc. Loc."
                    type="text"
                    InputLabelProps={{ shrink: true }}
                    value={data.municipio.value}
                    onChange={changeTextRequired}
                    error={data.municipio.verify !== '' ? !data.municipio.verify : false}
                    autoComplete='off'
                />
            </Grid>

            <Grid item xs={12} sm={3} md={onlySee ? 6 : 3} lg={onlySee ? 6 : 3}>
                <TextField
                    required
                    variant="outlined"
                    fullWidth
                    disabled={!existeCP || onlySee}
                    name="entidad"
                    label="Estado"
                    select
                    value={`${EstadosFull.length > 0 && data.entidad.value !== '' ? data.entidad.value : 'DEFAULT'}`}
                    placeholder="Selecciona"
                    InputLabelProps={{ shrink: true }}
                    onChange={changeTextRequired}
                    error={data.entidad.verify !== '' ? !data.entidad.verify : false}>
                    <MenuItem value="DEFAULT" disabled>Selecciona</MenuItem>
                    {
                        EstadosFull.map((item, index) => {
                            return <MenuItem key={`estados-${item.code}`} value={item.code}>{item.name}</MenuItem>
                        })
                    }
                </TextField>
            </Grid>
            {
                !props.expandable &&
                <Grid container item
                    direction="row"
                    justify="flex-end"
                    alignItems="center" xs={12} sm={12} md={12} lg={12}>
                    <Button startIcon={<ArrowBack />}
                        onClick={props.buttons.back}>Anterior</Button>

                    <Button
                        variant="outlined"
                        color="primary"
                        endIcon={<ArrowForward />}
                        onClick={props.buttons.next}
                    >Siguiente</Button>
                </Grid>
            }
        </Grid>
    )
}
