import { post, put, remove } from "../crud";



const isEmpty = (newData) => {
  const {
    id_percepcion_deduccion,
    nombre,
    aplicacion,
    tipo_nomina,
    base_cotizacion,
    estatus,
    prestacion,
    digito_grabable,
  } = newData;
  return !(
    id_percepcion_deduccion &&
    nombre &&
    aplicacion &&
    tipo_nomina &&
    base_cotizacion &&
    estatus &&
    prestacion &&
    digito_grabable
  );
};

export const handlerAdd = (props) => {
  const { url_api, controllerAlerts, ...otherProps } = props;
  return (newData) =>
    new Promise((resolve, reject) => {
      if (!isEmpty(newData)) {
        post({
          url_api,
          values: newData,
          controllerAlerts,
          resolve,
          reject,
          ...otherProps,
        });
      } else {
        controllerAlerts("REQUIERED");
        reject();
      }
    });
};

export const handlerUpdate = (props) => {
  const { url_api, controllerAlerts, ...otherProps } = props;
  return (newData, oldData) => {
    return new Promise((resolve, reject) => {
      const { id_percepcion_deduccion } = oldData;
      if (!isEmpty(newData)) {
        put({
          url: `${url_api}/${id_percepcion_deduccion}`,
          values: newData,
          controllerAlerts,
          resolve,
          oldValues: oldData,
          reject,
          ...otherProps
        });
      } else {
        controllerAlerts("REQUIERED");
        reject();
      }
    });
  };
};

export const handlerDelete = (props) => {
  const { url_api, ...otherProps } = props;
  return (oldData) =>
    new Promise((resolve, reject) => {
      const { id_percepcion_deduccion } = oldData;
      remove({
        url: `${url_api}/${id_percepcion_deduccion}`,
        oldData,
        resolve,
        ...otherProps
      });
    });
};
