import React from 'react'
//MATERIAL UI COMPONENTS
import {
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    Button,
    Box,
    Grid,
    MenuItem,
    LinearProgress,
    Typography
} from '@material-ui/core';

//FORMIK FIELDS
import { InputFormikUI } from '../FormikUI';
import { ValidacionNumeroCuenta, ValidacionNumeroTarjeta, ValidacionCLABE, ValidacionCantidadNomina } from '../../../../libraries/Mask'

//VALIDATIONS
import { Formik, Field, Form } from 'formik';
import * as Yup from 'yup';

import { put, post, remove } from './helperVariable';
import { NominaContext } from '../Nomina';
import { makeStyles } from '@material-ui/core/styles';
import { green } from '@material-ui/core/colors';
import { SessionContext } from '../../../../routes/adminrh/CustomRoutes';

const useStyles = makeStyles(theme => ({
    save: {
        color: green[600],
        borderColor: green[600],
        '&:hover': {
            color: green[700],
            borderColor: green[700],
            backgroundColor: green[50]
        },
    },
}))

export default function AddVariable(props) {
    const classes = useStyles();
    const { cabeceras, isTokenExpired } = React.useContext(SessionContext);
    const [loading, setLoading] = React.useState(false);
    const [deleted, setDeleted] = React.useState(false);
    const [error, setError] = React.useState(false);
    const { open, setOpen, identificador } = props;
    const { empleados, setEmpleados, bancos, idNomina } = React.useContext(NominaContext);
    const [title, setTitle] = React.useState(identificador ? 'Editar' : 'Agregar');
    const [message, setMessage] = React.useState('')

    //deducciones: '',
    let newEmpleado = {
        nombre: '',
        percepciones: '',
        no_tarjeta: '',
        no_cuenta: '',
        clabe: '',
        id_banco: ''
    };
    if (identificador) {
        let temp = empleados.find(e => e.empleado.identificador === identificador);
        newEmpleado = {
            nombre: temp.empleado['nombre'],
            percepciones: temp.percepciones.percepciones[0].value,
            no_tarjeta: temp.empleado['no_tarjeta'],
            no_cuenta: temp.empleado['no_cuenta'],
            clabe: temp.empleado['clabe'],
            id_banco: temp.empleado['id_banco'],
        }
    }


    // percepciones: Yup.number()
    //     .test('Es decimal', 'Requerido', value => (value + "").match(/\d+(\.\d{1,2})?/))
    //     .typeError('Dias debe ser un numero'),

    const errorFetch = (error) => {
        console.log(error);
        if (error !== 'TypeError: Failed to fetch')
            setMessage('Revisa tu conexión de internet');
        else
            setMessage('Ha ocurrido un error, revisa los campos');

        setLoading(false);
        setError(true);
    }

    const removeVar = () => {
        setLoading(true);
        let temp = empleados.findIndex(e => e.empleado.identificador === identificador);
        const { folio } = empleados[temp].empleado;
        remove(idNomina, folio, cabeceras)
            .then(response => {
                if (!isTokenExpired(response))
                    if (response['deleted']) {
                        const newEmpleado = empleados.filter(e => e.empleado.identificador !== identificador);
                        setLoading(false);
                        setOpen(false);
                        setEmpleados(newEmpleado);
                    }
            }).catch(errorFetch)

    }

    return (
        <Formik
            initialValues={newEmpleado}
            validationSchema={
                Yup.object().shape({
                    nombre: Yup.string()
                        .max(50, 'Muy largo!')
                        .required('Requerido'),
                    percepciones: Yup.number().test('Es decimal',
                        'No es un número',
                        value => (value + "").match(/\d+(\.\d{1,2})?/)
                    ).typeError('Dias debe ser un numero').required('Requerido'),
                    no_tarjeta: Yup.string(),
                    no_cuenta: Yup.string(),
                    clabe: Yup.string(),
                    id_banco: Yup.number(),
                })
            }
            onSubmit={values => {
                //console.log(values);
                setLoading(true);
                const newEmpleado = [...empleados]
                if (identificador) {
                    let index = empleados.findIndex(e => e.empleado.identificador === identificador);
                    const { toSend } = constructData(values);
                    //console.log(toSend)
                    put(idNomina, newEmpleado[index].empleado.folio, toSend, cabeceras)
                        .then(resp => {

                            if (!isTokenExpired(resp)) {
                                const { error } = resp
                                if (!error) {
                                    newEmpleado[index].empleado.nombre = values.nombre;
                                    newEmpleado[index].percepciones.percepciones[0].value = values.percepciones;
                                    newEmpleado[index].empleado.no_cuenta = values.no_cuenta;
                                    newEmpleado[index].empleado.no_tarjeta = values.no_tarjeta;
                                    newEmpleado[index].empleado.clabe = values.clabe;
                                    newEmpleado[index].empleado.id_banco = values.id_banco;
                                    setLoading(false);
                                    setOpen(false);
                                    setEmpleados(newEmpleado);
                                } else {
                                    setLoading(false);
                                    setError(true);
                                }
                            }

                        }).catch(errorFetch)

                } else {
                    const newValue = constructData(values, idNomina);
                    post(newValue.toSend, cabeceras)
                        .then(response => {
                            if (!isTokenExpired(response)) {
                                const newId = ('00000' + response[0].folio)
                                newValue.toPush['empleado']['folio'] = response[0].folio;
                                newValue.toPush['empleado']['identificador'] = `NOMVARIABLE-${newId.substring(newId.length - 5)}`
                                newEmpleado.push(newValue.toPush);
                                setLoading(false);
                                setEmpleados(newEmpleado);
                                setOpen(false);
                            }

                        }).catch(errorFetch)
                }

            }}
        >
            {
                (props) => {
                    return (
                        <Dialog open={open} onClose={() => { props.handleReset(); setOpen(false) }} aria-labelledby="">
                            {loading && <LinearProgress color="secondary" />}
                            <Form>
                                <DialogTitle>{title} persona variable</DialogTitle>
                                <DialogContent>
                                    <Box>
                                        <Grid container spacing={2}>
                                            <Grid item xs={8}>
                                                <Field name="nombre" placeholder="Ej. Juán Martínez..." label="Nombre" component={InputFormikUI} />
                                            </Grid>
                                            <Grid item xs={4}>
                                                <Field name="percepciones" label="Monto" placeholder="Ej.15.25" component={InputFormikUI} InputProps={{ inputComponent: ValidacionCantidadNomina }} />
                                            </Grid>
                                            <Grid item xs={6}>
                                                <Field name="no_cuenta" label="No. Cuenta" placeholder="Ej.15.25" component={InputFormikUI} InputProps={{ inputComponent: ValidacionNumeroCuenta }} />
                                            </Grid>
                                            <Grid item xs={6}>
                                                <Field name="no_tarjeta" label="No. Tarjeta" placeholder="Ej.15.25" component={InputFormikUI} InputProps={{ inputComponent: ValidacionNumeroTarjeta }} />
                                            </Grid>
                                            <Grid item xs={6}>
                                                <Field name="clabe" label="CLABE" placeholder="Ej.15.25" component={InputFormikUI} InputProps={{ inputComponent: ValidacionCLABE }} />
                                            </Grid>
                                            <Grid item xs={6}>
                                                <Field name="id_banco" label="Banco" placeholder="Ej.15.25" defaultValue={''} select component={InputFormikUI} >
                                                    <MenuItem value={``} disabled>Selecciona</MenuItem>
                                                    {bancos.map((item, index) => {
                                                        return <MenuItem key={`selected-banco-${index}`} value={item.id_banco}>{item.nombre_banco}</MenuItem>
                                                    })}
                                                </Field>
                                            </Grid>

                                            {(deleted || error) &&
                                                <Grid item xs={12}>
                                                    <Typography display="block" style={{ marginTop: 10 }} variant="button" color="secondary">
                                                        {message}
                                                    </Typography>
                                                </Grid>
                                            }
                                        </Grid>
                                    </Box>
                                </DialogContent>

                                {
                                    !deleted ?
                                        <DialogActions style={{ justifyContent: 'space-between' }}>
                                            <Button variant="text" color="default"
                                                onClick={() => {
                                                    props.handleReset();
                                                    setOpen(false)
                                                }}>
                                                Cerrar
                                                </Button>
                                            <div>
                                                {identificador &&
                                                    <Button variant="text" color="secondary" onClick={() => {
                                                        setTitle("Eliminar")
                                                        setMessage('Estás seguro que deseas eliminarlo?')
                                                        setDeleted(true);
                                                    }}>
                                                        Eliminar
                                                    </Button>
                                                }
                                                <Button onClick={() => {
                                                    props.handleSubmit();
                                                }} className={classes.save} variant="outlined" color="default">
                                                    Guardar
                                                </Button>
                                            </div>
                                        </DialogActions>
                                        :
                                        <DialogActions>
                                            <Button variant="text" color="default"
                                                onClick={() => setDeleted(false)}>
                                                Cancelar
                                             </Button>
                                            <Button variant="contained" disableElevation={true} color="secondary"
                                                onClick={removeVar}>
                                                Eliminar
                                            </Button>
                                        </DialogActions>
                                }

                            </Form>
                        </Dialog>

                    )
                }
            }
        </Formik>

    )
};

function constructData(values, id_nomina) {
    const toSend = {};

    toSend['nombre'] = values.nombre;
    toSend['monto'] = values.percepciones;

    if (id_nomina) toSend['id_nomina'] = id_nomina;

    const no_cuenta = values.no_cuenta.replace(/\s/g, '')
    if (no_cuenta !== '')
        toSend['no_cuenta'] = no_cuenta;

    const no_tarjeta = values.no_tarjeta.replace(/\s/g, '')
    if (no_tarjeta !== '')
        toSend['no_tarjeta'] = no_tarjeta;

    const clabe = values.clabe.replace(/\s/g, '')
    if (clabe !== '')
        toSend['clabe'] = clabe;

    if (values.id_banco !== '')
        toSend['id_banco'] = parseInt(values.id_banco);

    return {
        toPush: {
            empleado: {
                nombre: values.nombre,
                no_cuenta: values.no_cuenta,
                no_tarjeta: values.no_tarjeta,
                clabe: values.clabe,
                id_banco: values.id_banco,
                id_nomina
            },
            tipo: false,
            dias: { total: 0 },
            percepciones: {
                total: 0, percepciones:
                    [{ id_percepcion_deduccion: 'OTRAS', nombre: `OTRAS PERCEPCIONES`, value: values.percepciones }]
            },
            deducciones: {
                total: 0,
                deducciones: []
            }
        },
        toSend
    }
}

