export const initialContrato = (contrato) => {
    const newContrato = {
        fecha_inicio: { value: '', verify: '' },
        fecha_fin: { value: '', verify: '' },
        comision: { value: '', verify: '' },
        periodo_calculo: { value: '', verify: '' },
        motivo_contrato: { value: '', verify: 'A' }
    }
    if (contrato) {
        newContrato['id_contrato'] = { value: '', verify: '' }
        newContrato[contrato] = false
    };
    return newContrato;
}

