import { useState, useEffect, useContext } from 'react';
import { BASE_URL } from '../../../libraries/RequestFetch';
import { SessionContext } from '../../../routes/adminrh/CustomRoutes';

export function useEmpresas() {
    const { cabeceras, isTokenExpired } = useContext(SessionContext);
    const [empresas, setEmpresas] = useState([]);

    const getEmpresas = async () => {
        const response = await fetch(`${BASE_URL}/api/rh/empresa?query=id_empresa-razon_social-es_interna`,
            {
                method: 'GET',
                headers: cabeceras
            });
        const resp = await response.json();
        if (!isTokenExpired(resp)) {
            const { empresas } = resp;
            setEmpresas(empresas);
        }
    }

    useEffect(() => {
        getEmpresas()
        //eslint-disable-next-line
    }, [])

    return { empresas }
}