import React, { useContext, useState } from 'react';
import { TableCell, TableRow, Box, Table, TableBody, Tooltip, Button, TextField, Typography, InputAdornment } from '@material-ui/core'
import AddIcon from '@material-ui/icons/Add'
import { withStyles } from '@material-ui/core/styles';
import CheckCircle from '@material-ui/icons/CheckCircle';
import RemoveCircle from '@material-ui/icons/RemoveCircleRounded';
import { Dialog, DialogContent, DialogTitle, DialogActions, LinearProgress } from '@material-ui/core';
import { useTheme } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import EditIcon from '@material-ui/icons/Edit';
import { ValidacionCantidadNomina } from '../../../../libraries/Mask';
import ModalPD from './ModalPD';
import { NominaContext } from '../Nomina'
import AddVariable from '../AddNewVariable';
import { put, remove } from './helperPD';
import { useStyles } from './Styles'
import DisplayFaltas from './DisplayFaltas'
import { SessionContext } from '../../../../routes/adminrh/CustomRoutes'



const PrincipalRow = withStyles((theme) => ({
    root: {
        '&:hover': {
            cursor: 'pointer',
        },
        '&$selected, &$selected:hover': { backgroundColor: 'rgba(216, 67, 21, 0.1)', }
    },
    selected: {}
}))(props => <TableRow {...props} />);



export default function CollapseRow(props) {
    const { row, openAll } = props;
    const classes = useStyles();
    const [addVar, setAddVar] = useState(false);
    const [open, setOpen] = useState(false);
    const { format, seePDF } = useContext(NominaContext);


    return (
        <React.Fragment>
            <PrincipalRow
                onClick={() => setOpen(!open)}
                hover selected={open || openAll}>
                <TableCell>{row.empleado.identificador}</TableCell>
                <TableCell component="th" scope="row"> {row.display.nombre}</TableCell>
                <TableCell align="center">
                    {
                        row.display.tipo ?
                            !seePDF ? <CheckCircle className={classes.interna} /> : "SI"
                            :
                            !seePDF ? <RemoveCircle className={classes.externa} /> : "NO"

                    }
                </TableCell>
                <TableCell align="right">{row.display.dias}</TableCell>
                <TableCell align="right">{format(row.display.costo_social)}</TableCell>
                <TableCell align="right">{format(row.display.percepciones)}</TableCell>
                <TableCell align="right">{format(row.display.deducciones)}</TableCell>
                <TableCell align="right">{format(row.display.total)}</TableCell>
            </PrincipalRow>
            {
                (open || openAll) &&
                <TableRow>
                    <TableCell></TableCell>
                    <TableCell>
                        <ShowData empleado={row.empleado} tipo={row.tipo} onClick={() => setAddVar(true)} />
                    </TableCell>
                    <TableCell></TableCell>
                    <TableCell padding="none" align="center">
                        <DisplayFaltas row={row} identificador={row.empleado.identificador} />

                    </TableCell>
                    <TableCell ></TableCell>
                    <TableCell padding="none">
                        <DisplayPD elements={row} elementKey="percepciones" identificador={row.empleado.identificador} />
                    </TableCell>
                    <TableCell padding="none">
                        <DisplayPD elements={row} elementKey="deducciones" identificador={row.empleado.identificador} />
                    </TableCell>
                    <TableCell ></TableCell>
                </TableRow>
            }

            {addVar && <AddVariable open={addVar} setOpen={setAddVar} identificador={row.empleado.identificador} />}
        </React.Fragment>
    );
}

function DisplayPD(props) {
    const classes = useStyles();
    const { elements, elementKey, identificador } = props;
    const [open, setOpen] = React.useState(false);
    const { format, generalSettings, seePDF } = React.useContext(NominaContext);
    const { rol } = React.useContext(SessionContext);

    const [modifyPD, setModifyPD] = React.useState({
        open: false,
        element: {},
    });
    return (
        <>
            <Table>
                <TableBody>
                    {
                        elements[elementKey][elementKey].map((i, index) => {
                            return <TableRow
                                key={`item-${elementKey}-${index}`}
                                onClick={() => {
                                    if (rol !== 'C')
                                        if (elements.tipo)
                                            if (generalSettings.estatus === 'C' || generalSettings.estatus === 'P')
                                                setModifyPD({ open: true, element: i })
                                }}
                                className={classes.internalRow}
                                hover>
                                <TableCell className={classes.internalCell} size="small" align="left">
                                    <Tooltip title={i.nombre}>
                                        <Box component="div" className={classes.internalElipsis}>{i.nombre}</Box>
                                    </Tooltip>
                                </TableCell>
                                <TableCell className={classes.internalCell} size="small" align="right">
                                    <Box className={classes.internalMoney}><b>{`${format(i.value)}`}</b></Box>
                                </TableCell>
                            </TableRow>
                        })
                    }
                    {
                        rol !== 'C' &&
                        elements.tipo &&
                        !seePDF &&
                        (generalSettings.estatus === 'C' || generalSettings.estatus === 'P') &&
                        <TableRow>
                            <TableCell className={classes.internalCell} size="small" align="center" colSpan="2">
                                <Button
                                    className={classes.internalMoney}
                                    variant="text"
                                    color="default"
                                    startIcon={<AddIcon />}
                                    size="small"
                                    onClick={() => setOpen(true)}>
                                    Agregar
                            </Button>
                            </TableCell>
                        </TableRow>
                    }
                </TableBody>
            </Table>
            {
                open &&
                <ModalPD
                    open={open}
                    elements={elements[elementKey][elementKey]}
                    keyelement={elementKey}
                    onClose={() => setOpen(false)}
                    identificador={identificador}
                />
            }

            {
                modifyPD.open &&
                <ModifyPD
                    open={modifyPD.open}
                    onClose={() => setModifyPD({ ...modifyPD, open: false })}
                    element={modifyPD.element}
                    defaultValue={modifyPD.element.value}
                    identificador={identificador} />
            }
        </>
    )
}

function ModifyPD(props) {
    const classes = useStyles();
    const { open, onClose, element, identificador, defaultValue } = props;
    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('xs'));

    const [deleted, setDeleted] = React.useState(false);
    const [loading, setLoading] = React.useState(false);
    const [value, setValue] = React.useState(defaultValue ? defaultValue : 0);
    const { empleados, setEmpleados } = React.useContext(NominaContext);
    const { cabeceras, isTokenExpired } = React.useContext(SessionContext);

    const onClick = (type) => {
        setLoading(true);
        const keyElement = String(element.id_percepcion_deduccion).charAt(0) === 'D' ? "deducciones" : "percepciones"
        const indexEmpleado = empleados.findIndex(i => i.empleado.identificador === identificador);
        const newEmpleado = [...empleados];
        const indexPD = newEmpleado[indexEmpleado][keyElement][keyElement].findIndex(e => e === element);

        if (type === 'DELETE') {

            const { id_nomina, id_percepcion_deduccion } = newEmpleado[indexEmpleado][keyElement][keyElement].splice(indexPD, 1)[0];
            remove(id_nomina, id_percepcion_deduccion, cabeceras)
                .then(response => {
                    if (!isTokenExpired(response)) {
                        setLoading(false);
                        setEmpleados(newEmpleado)
                        setValue('')
                        onClose();
                    }
                }).catch(error => console.log(error));
        }
        else {
            const { id_nomina, id_percepcion_deduccion } = newEmpleado[indexEmpleado][keyElement][keyElement][indexPD];
            put(id_nomina, id_percepcion_deduccion, { monto: parseFloat(value).toFixed(2) }, cabeceras)
                .then(response => {
                    if (!isTokenExpired(response)) {
                        setLoading(false);
                        newEmpleado[indexEmpleado][keyElement][keyElement][indexPD].value = parseFloat(value);
                        setEmpleados(newEmpleado)
                        setValue('')
                        onClose();
                    }
                }).catch(error => console.log(error));
        }
    }

    return (
        <Dialog
            open={open}
            onClose={onClose}
            maxWidth="xs"
            fullScreen={fullScreen}
            fullWidth={true}>
            {loading && <LinearProgress color="secondary" />}
            <DialogTitle >
                {`${String(element.id_percepcion_deduccion).charAt(0) === 'D' ? "DEDUCCIÓN" : "PERCEPCIÓN"} ${element.nombre}`}</DialogTitle>
            <DialogContent>
                <TextField
                    autoFocus
                    fullWidth
                    label="Cantidad"
                    placeholder="Ej. $999.99"
                    InputLabelProps={{ shrink: true, }}
                    value={value}
                    onChange={e => setValue(e.target.value)}
                    InputProps={{ inputComponent: ValidacionCantidadNomina, startAdornment: <InputAdornment position="start">$</InputAdornment>, }}
                    autoComplete='off'
                />
                {
                    deleted &&
                    <Typography display="block" style={{ marginTop: 10 }} variant="button" color="secondary">Estás seguro que deseas eliminarlo?</Typography>

                }

            </DialogContent>
            {
                !deleted ?
                    <DialogActions style={{ justifyContent: 'space-between' }}>
                        <Button variant="text" color="default" onClick={onClose}>
                            Cerrar
                        </Button>
                        <div>
                            <Button variant="text" color="secondary" onClick={() => {
                                parseInt(value) === 0 ? onClick('DELETE') : setDeleted(true)
                            }}>
                                Eliminar
                            </Button>
                            <Button className={classes.save} variant="outlined" color="default" onClick={() => onClick('SAVE')}>
                                Guardar
                            </Button>
                        </div>
                    </DialogActions>
                    :
                    <DialogActions>
                        <Button variant="text" color="default" onClick={() => setDeleted(false)}>
                            Cancelar
                        </Button>
                        <Button variant="contained" disableElevation={true} color="secondary" onClick={() => onClick('DELETE')}>
                            Eliminar
                        </Button>
                    </DialogActions>
            }
        </Dialog>
    );
}



function ShowData(props) {
    const classes = useStyles();
    const { bancos, generalSettings } = React.useContext(NominaContext);
    const { rol } = React.useContext(SessionContext);
    const { tipo, onClick } = props;
    const { identificador, nombre, ...newEmpleado } = props.empleado;

    const showKeys = () => {
        const keys = Object.keys(newEmpleado);
        const elements = []
        keys.forEach((key) => {
            if (newEmpleado[key]) {
                switch (key) {
                    case 'salario_capturado': elements.push({ name: "SALARIO CAPTURADO", value: `$${newEmpleado[key]}` }); break;
                    case 'salario_diario': elements.push({ name: "SALARIO REAL", value: `$${newEmpleado[key]}` }); break;
                    case 'no_tarjeta': elements.push({ name: "NUMERO DE TARJETA", value: newEmpleado[key] }); break;
                    case 'no_cuenta': elements.push({ name: "NUMERO DE CUENTA", value: newEmpleado[key] }); break;
                    case 'clabe': elements.push({ name: "CLABE INTERBANCARIA", value: newEmpleado[key] }); break;
                    case 'id_banco': elements.push({ name: "BANCO", value: bancos.find(i => i.id_banco = newEmpleado[key])['nombre_banco'] }); break;
                    default: break;
                }
            }
        });

        return elements.map((item, index) => {
            return <TableRow key={`$table-show-keys-${index}`} className={classes.internalRow} hover>
                <TableCell className={classes.internalCell} size="small" align="left">
                    <Tooltip title={item.name}>
                        <Box component="div" className={classes.internalElipsis}>{item.name}</Box>
                    </Tooltip>
                </TableCell>
                <TableCell className={classes.internalCell} size="small" align="right">
                    <Box className={classes.internalMoney}><b>{`${item.value}`}</b></Box>
                </TableCell>
            </TableRow>
        })
    }
    return (
        <React.Fragment>
            <Table>
                <TableBody>
                    {showKeys()}
                    {
                        rol !== 'C' &&
                        !tipo &&
                        (generalSettings.estatus === 'P' || generalSettings.estatus === 'C') &&
                        <TableRow>
                            <TableCell className={classes.internalCell} size="small" align="center" colSpan="2">
                                <Button
                                    className={classes.internalMoney}
                                    variant="text"
                                    color="default"
                                    startIcon={<EditIcon />}
                                    size="small"
                                    onClick={onClick}>
                                    Editar
                            </Button>
                            </TableCell>
                        </TableRow>
                    }
                </TableBody>
            </Table>
        </React.Fragment>
    );
}

