import React, { useState } from 'react';
import MuiListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import Collapse from '@material-ui/core/Collapse';
import List from '@material-ui/core/List';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { Link, withRouter } from "react-router-dom";
import ListSubheader from '@material-ui/core/ListSubheader';


const colorText = '#D1D2D4';

const useStyles = makeStyles(theme => ({
    nested: {
        paddingLeft: theme.spacing(4),
    },
    icons: {
        color: colorText,
        position: 'relative'
    }
}));

const ListItem = withStyles({
    root: {
        "&$selected": {
            backgroundColor: "#66686B",
            color: "white"
        },
        "&$selected:hover": {
            backgroundColor: "#66686B",
            color: "white"
        },
        "&:hover": {
            backgroundColor: "#66686B",
            color: "white"
        }
    },
    selected: {}
})(MuiListItem);

function ListMenu({ items, name, ...otherProps }) {
    const classes = useStyles();
    return (
        <List subheader={
            <ListSubheader className={classes.icons} component="div">
                {name}
            </ListSubheader>
        }
        >
            {items.map((item, index) => <ListItemSuper key={`list-item-superior-${index}`} {...item} {...otherProps} />)}
        </List>
    )
}


function ListItemSuper({ name, childs, icon, location }) {

    const classes = useStyles();
    const [open, setOpen] = useState(childs.find(c => c.route === location.pathname) ? true : false);
    return (
        <React.Fragment>
            <MuiListItem button onClick={() => setOpen(!open)} >
                <ListItemIcon className={classes.icons}>
                    {icon}
                </ListItemIcon>
                <ListItemText primary={name} />
                {open ? <ExpandLess /> : <ExpandMore />}
            </MuiListItem>
            <Collapse in={open} timeout="auto" unmountOnExit>
                {
                    childs.map((child, _index) => <ListItemChild
                        {...child}
                        key={`list-item-child-${_index}`}
                        selected={child.selected}
                        index={_index}
                        location={location}
                    />
                    )
                }
            </Collapse>
        </React.Fragment>
    )
}

function ListItemChild({ name, route, icon, index, location: { pathname }, ...otherProps }) {
    const classes = useStyles();
    return (
        <List component="div" disablePadding>
            <ListItem button
                {...otherProps}
                className={classes.nested}
                component={Link}
                selected={route === pathname}
                to={route}>
                <ListItemIcon className={classes.icons}>{icon}</ListItemIcon>
                <ListItemText primary={name} />
            </ListItem>
        </List>
    )
}

export default withRouter(ListMenu);