import React from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Button from '@material-ui/core/Button';
import DialogTitle from '@material-ui/core/DialogTitle';
import { makeStyles } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemIcon from '@material-ui/core/ListItemIcon'
import Checkbox from '@material-ui/core/Checkbox';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import TextField from '@material-ui/core/TextField';
import InputAdornment from '@material-ui/core/InputAdornment';
import { useTheme } from '@material-ui/core/styles';
import SearchIcon from '@material-ui/icons/Search';
import { NominaContext } from '../Nomina';
import { post, remove } from './helperPD';
import { LinearProgress } from '@material-ui/core';
import { SessionContext } from '../../../../routes/adminrh/CustomRoutes'

const useStyles = makeStyles((theme) => ({
    rootTab: {
        flexGrow: 1,
        backgroundColor: theme.palette.background.paper,
    },
    rootList: {
        backgroundColor: theme.palette.background.paper,
        position: 'relative',
        overflow: 'auto',
        [theme.breakpoints.down('xs')]: {
            height: '100%',
            width: '100%',
        },
        [theme.breakpoints.up('sm')]: {
            height: 400,
            width: 450,
        }
    },
    listSection: {
        backgroundColor: 'inherit',
    },
    ul: {
        backgroundColor: 'inherit',
        padding: 0,
    },
    rootContent: {
        paddingLeft: 0,
        paddingRight: 0,
        [theme.breakpoints.down('xs')]: {
            overflow: 'hidden',
        },
    },
}));


function PinnedSubheaderList(props) {
    const { cabeceras, isTokenExpired } = React.useContext(SessionContext)
    const classes = useStyles();
    const [search, setSearch] = React.useState('');
    const { elements, keyelement, identificador, setLoading } = props;
    const { percepciones, deducciones } = React.useContext(NominaContext);
    const { empleados, setEmpleados, idNomina, idContrato } = React.useContext(NominaContext);

    const separe = () => {
        let newArray = keyelement === 'percepciones' ? percepciones : deducciones;
        newArray.forEach(i => i.checked = elements.some(e => i.id_percepcion_deduccion === e.id_percepcion_deduccion))
        return newArray;
    }


    const filterSearch = (array, search) => {
        if (search !== '') {
            return array.filter(item => item.nombre.toLowerCase().includes(search.toLowerCase()));
        } else {
            return array;
        }
    }

    const changeCheck = (e, item) => {
        setLoading(true);
        const check = e.target.checked;
        const indexEmpleado = empleados.findIndex(i => i.empleado.identificador === identificador);
        const newEmpleado = [...empleados];
        try {
            if (check) {
                post({
                    id_nomina: idNomina,
                    id_percepcion_deduccion: item.id_percepcion_deduccion,
                    id_contrato: newEmpleado[indexEmpleado]['empleado']['id_contrato'],
                    monto: 0
                }, cabeceras).then(data => {
                    if (!isTokenExpired(data)) {
                        newEmpleado[indexEmpleado][keyelement][keyelement].push({
                            id_nomina: idNomina,
                            id_contrato: idContrato,
                            nombre: item.nombre,
                            id_percepcion_deduccion: item.id_percepcion_deduccion,
                            value: 0.0,
                            save: true
                        })
                        setEmpleados(newEmpleado);
                        setLoading(false);
                    }
                })


            } else {
                remove(idNomina, item.id_percepcion_deduccion, cabeceras)
                    .then(data => {
                        if (!isTokenExpired(data)) {
                            const indexRemove = newEmpleado[indexEmpleado][keyelement][keyelement].findIndex(i => i.id_percepcion_deduccion === item.id_percepcion_deduccion);
                            newEmpleado[indexEmpleado][keyelement][keyelement].splice(indexRemove, 1)
                            setEmpleados(newEmpleado);
                            setLoading(false);
                        }
                    });

            }
        } catch (err) {
            setLoading(false);
        }
    }
    return (
        <>
            <Box p={3}>
                <TextField
                    fullWidth
                    label="Buscar"
                    value={search}
                    onChange={e => setSearch(e.target.value)}
                    InputProps={{
                        startAdornment: (
                            <InputAdornment position="start">
                                <SearchIcon />
                            </InputAdornment>
                        ),
                    }}
                />
            </Box>
            <List className={classes.rootList} subheader={<li />}>
                {
                    filterSearch(separe(), search).map((item, index) => (
                        <ListItem key={`item-percepcion-${index}`}>
                            <ListItemIcon>
                                <Checkbox
                                    disabled={item.es_obligatoria}
                                    checked={item.checked}
                                    onChange={e => changeCheck(e, item)}
                                    edge="start"
                                />
                            </ListItemIcon>
                            <ListItemText primary={item.nombre} />
                        </ListItem>
                    ))
                }
            </List>
        </>
    );
}

export default function ModalPD({ onClose, ...others }) {
    const classes = useStyles()
    const [loading, setLoading] = React.useState(false);
    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('xs'));

    return (
        <Dialog fullScreen={fullScreen} {...others} aria-labelledby="form-dialog-title">
            {loading && <LinearProgress color="secondary" />}
            <DialogTitle id="form-dialog-title">Agregar </DialogTitle>
            <DialogContent className={classes.rootContent}>
                <PinnedSubheaderList {...others} setLoading={setLoading} />
            </DialogContent>
            <DialogActions>
                <Button variant="text" color="secondary" onClick={onClose}>
                    Ok
                </Button>
            </DialogActions>
        </Dialog>
    );
}