import React, { useEffect } from 'react';
import Cryptr from 'cryptr';

export const keyStorage = 'fjFKsjfo_';
export const keyCrypt = 'iToxD'
export const useSession = () => {
    const [getSession, setSession] = React.useState({
        rol: undefined,
        permisos: undefined,
        token: undefined
    });

    useEffect(() => {
        const descrypt = async () => {
            const cryptedElement = localStorage.getItem(keyStorage);
            if (cryptedElement) {
                const cryptr = new Cryptr(keyCrypt);
                const contentEncrypt = cryptr.decrypt(cryptedElement);
                setSession(JSON.parse(contentEncrypt));
            }
        }
        descrypt();
    }, []);

    return { getSession }
}

export const isLogin = () => {
    const element = localStorage.getItem(keyStorage);
    if (element)
        return true;
    return false;
};

export const logout = () => {
    localStorage.removeItem(keyStorage);
};
