import React, { useState, useEffect, useContext } from "react";
import SwipeableViews from "react-swipeable-views";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import TabPanel from "./TabPanel";
import { ThemeProvider } from "@material-ui/styles";
import { Theme } from "../TableCatalogos/ThemeProvider";
import { OptionsPanel } from "./OptionsPanel";
import moment from "moment";
import { SessionContext } from '../../../routes/adminrh/CustomRoutes';
import { getNominas } from "./peticiones";
import { BASE_URL } from "../../../dev.json";

const url_api = `${BASE_URL}api/rh/nomina/funtion/paginationNomina`;

const month = moment().month() + 1;
const year = moment().year();

const a11yProps = (index) => {
  return {
    id: `full-width-tab-${index}`,
    "aria-controls": `full-width-tabpanel-${index}`,
  };
};

const addFlags = (nominas) => {
  nominas.forEach((nomina) => {
    nomina.flag = true;
  });
};

const useStyles = makeStyles((theme) => ({
  root: {
    width: "auto",
  },
  tap: {
    fontSize: "0.8em",
  },
}));

export default function FullWidthTabs() {
  const classes = useStyles();
  const theme = useTheme();
  const [mes, setMes] = useState(month);
  const [anio, setAnio] = useState(year);
  const [value, setValue] = useState(0);
  const [nominasSemanal, setNominasSemanal] = useState([]);
  const [nominasCatorcenal, setNominasCatorcenal] = useState([]);
  const [nominasQuincenal, setNominasQuincenal] = useState([]);
  const [nominasMensual, setNominasMensual] = useState([]);
  const { cabeceras, isTokenExpired, permisos, rol } = useContext(SessionContext);


  useEffect(() => {
    getNominas({
      url_api,
      setN: setNominasSemanal,
      body: {
        mes,
        anio,
        periodo: "S",
      },
      headers: cabeceras,
      isTokenExpired,
      addFlags,
      permisos,
      rol
    });
    getNominas({
      url_api,
      setN: setNominasCatorcenal,
      body: {
        mes,
        anio,
        periodo: "C",
      },
      headers: cabeceras,
      isTokenExpired,
      addFlags,
      permisos,
      rol
    });
    getNominas({
      url_api,
      setN: setNominasQuincenal,
      body: {
        mes,
        anio,
        periodo: "Q",
      },
      headers: cabeceras,
      isTokenExpired,
      addFlags,
      permisos,
      rol
    });
    getNominas({
      url_api,
      setN: setNominasMensual,
      body: {
        mes,
        anio,
        periodo: "M",
      },
      headers: cabeceras,
      isTokenExpired,
      addFlags,
      permisos,
      rol
    });
    // eslint-disable-next-line
  }, [mes, anio]);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleChangeIndex = (index) => {
    setValue(index);
  };

  return (
    <ThemeProvider theme={Theme}>
      <OptionsPanel
        year={anio}
        changeYear={setAnio}
        month={mes}
        changeMonth={setMes}
        valuesSearch={
          {
            nominasSemanal,
            setNominasSemanal,
            nominasCatorcenal,
            setNominasCatorcenal,
            nominasQuincenal,
            setNominasQuincenal,
            nominasMensual,
            setNominasMensual,
            value
          }
        }
      />
      <div className={classes.root}>
        <Paper>
          <Tabs
            value={value}
            onChange={handleChange}
            indicatorColor="primary"
            textColor="primary"
            variant="fullWidth"
            aria-label=""
          >
            <Tab label="Semanal" {...a11yProps(0)} className={classes.tap} />
            <Tab label="Catorcenal" {...a11yProps(1)} className={classes.tap} />
            <Tab label="Quincenal" {...a11yProps(2)} className={classes.tap} />
            <Tab label="Mensual" {...a11yProps(3)} className={classes.tap} />
          </Tabs>
        </Paper>
        <SwipeableViews
          axis={theme.direction === "rtl" ? "x-reverse" : "x"}
          index={value}
          onChangeIndex={handleChangeIndex}
        >
          <TabPanel
            value={value}
            index={0}
            dir={theme.direction}
            nominas={nominasSemanal}
          />
          <TabPanel
            value={value}
            index={1}
            dir={theme.direction}
            nominas={nominasCatorcenal}
          />
          <TabPanel
            value={value}
            index={2}
            dir={theme.direction}
            nominas={nominasQuincenal}
          />
          <TabPanel
            value={value}
            index={3}
            dir={theme.direction}
            nominas={nominasMensual}
          />
        </SwipeableViews>
      </div>
    </ThemeProvider>
  );
}
