import React, { useState, useContext } from 'react';
import { savePDF } from "@progress/kendo-react-pdf";
import ReactDOM from "react-dom";
import {
    Table,
    TableBody,
    TableContainer,
    Paper,
    Box,
    Grid,
    Typography,
    IconButton,
    Hidden,
    FormControlLabel,
    Switch,
    MenuItem,
    TextField,
    LinearProgress,
    Button
} from '@material-ui/core';
import SettingsIcon from '@material-ui/icons/Settings';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import { useDataNomina } from './fechingNomina';
import moment from 'moment';
import numeral from 'numeral';
import GetAppIcon from '@material-ui/icons/GetApp';
import AddVariable from './AddNewVariable';
import SortHead from './SortHead';
import CollapseRow from './CollapseRow';
import SettingsNomina from './SettingsNomina';
import TotalRows from './TotalRows';
import ButtonStatus from './ButtonsStatus';
import { useParams } from 'react-router-dom';
import ImageLogo from '../../../assets/img/get.ico';
import { SessionContext } from '../../../routes/adminrh/CustomRoutes';

const headCells = [
    { id: 'identificador', align: 'left', disablePadding: false, label: 'Identificador' },
    { id: 'nombre', align: 'left', disablePadding: false, label: 'Nombre' },
    { id: 'tipo', align: 'center', disablePadding: false, label: 'Contratado' },
    { id: 'dias', align: 'right', disablePadding: false, label: 'Dias' },
    { id: 'costo_social', align: 'right', disablePadding: false, label: 'Costo Social' },
    { id: 'percepciones', align: 'right', disablePadding: false, label: 'Percepciones' },
    { id: 'deducciones', align: 'right', disablePadding: false, label: 'Deducciones' },
    { id: 'total', align: 'right', disablePadding: false, label: 'Neto a Pagar' },
];

export const NominaContext = React.createContext(null);

export default function Nomina() {
    const { idNomina, idContrato, idEmpresa, anio } = useParams();
    const { rol } = useContext(SessionContext);

    const {
        loading,
        bancos,
        percepciones,
        deducciones,
        totales,
        generalSettings, setGeneralSettings,
        empleados, setEmpleados,
    } = useDataNomina({ idNomina, idContrato, idEmpresa, anio });

    const [openAll, setOpenAll] = useState(false);
    const [order, setOrder] = useState('desc');
    const [orderBy, setOrderBy] = useState('tipo');
    const [selected, setSelected] = useState([]);
    const [settings, setSettings] = useState(false);
    const [addVar, setAddVar] = useState(false);
    const [formato, setFormato] = useState(2);
    const [seePDF, setSeePDF] = useState(false);

    const NominaRef = React.createRef();

    function format(num) {
        if (formato === 0)
            return numeral(num).format('$0,0', Math.round);
        else if (formato === 2)
            return numeral(num).format('$0,0.00', Math.round);
        else if (formato === 3)
            return numeral(num).format('$0,0.000', Math.round);
        else
            return numeral(num).format('$0,0.0000', Math.round);
    }


    function handleSelectAllClick(event) {
        if (event.target.checked) {
            const newSelecteds = empleados.map((n) => n.name);
            setSelected(newSelecteds);
            return;
        }
        setSelected([]);
    };

    function handleRequestSort(event, property) {
        const isAsc = orderBy === property && order === 'asc';
        setOrder(isAsc ? 'desc' : 'asc');
        setOrderBy(property);
    };

    function getComparator(order, orderBy) {
        function descendingComparator(a, b, orderBy) {
            if (b['display'][orderBy] < a['display'][orderBy]) {
                return -1;
            }
            if (b['display'][orderBy] > a['display'][orderBy]) {
                return 1;
            }
            return 0;
        }

        return order === 'desc'
            ? (a, b) => descendingComparator(a, b, orderBy)
            : (a, b) => -descendingComparator(a, b, orderBy);
    }

    function stableSort(array, comparator) {
        const stabilizedThis = array.map((el, index) => [el, index]);
        stabilizedThis.sort((a, b) => {
            const order = comparator(a[0], b[0]);
            if (order !== 0) return order;
            return a[1] - b[1];
        });
        return stabilizedThis.map((el) => el[0]);
    }


    const onSavePDF = () => {
        setSeePDF(true);
    }

    React.useEffect(() => {
        if (seePDF) {
            const { current } = NominaRef;
            savePDF(ReactDOM.findDOMNode(current), {
                paperSize: "Legal",
                landscape: true,
                margin: 20,
                scale: 0.6,
                fileName: `Nomina ${generalSettings.identificador}`
            }, setSeePDF(false));
        }
        //eslint-disable-next-line
    }, [seePDF])


    return (
        <NominaContext.Provider value={{
            generalSettings, setGeneralSettings,
            percepciones,
            deducciones,
            empleados, setEmpleados,
            bancos, format,
            idNomina, idContrato,
            seePDF
        }}>
            <Paper ref={NominaRef}>
                {loading &&
                    <LinearProgress variant="query" color="secondary" />
                }
                <Box>
                    <Grid container style={{ padding: 16 }}>
                        <Grid item xs={10} sm={8}>
                            <Typography variant="h6" color="initial">
                                Nomina: {generalSettings.identificador !== '' ? generalSettings.identificador : "Cargando..."}
                            </Typography>
                            <Typography variant="body1" color="initial">
                                Perido: {
                                    generalSettings.perido_inicio !== null && generalSettings.perido_fin !== null ?
                                        ` ${moment(generalSettings.perido_inicio).format("DD/MM/YYYY")} al ${moment(generalSettings.perido_fin).format("DD/MM/YYYY")}`
                                        : 'Cargando...'}
                            </Typography>
                            <Typography variant="body2" color="initial">
                                Fecha Pago: {`${generalSettings.fecha_pago !== null ? moment(generalSettings.fecha_pago).format("DD/MM/YYYY") : 'Sin especificar'}`}
                            </Typography>
                            <Typography variant="body2" color="initial">Razón social para facturar:  {`${generalSettings.razon_social !== '' ? generalSettings.razon_social : 'Cargando..'}`}</Typography>
                        </Grid>
                        <Grid item xs={2} sm={4}>
                            {
                                (!seePDF && !loading) ?
                                    <Grid container
                                        direction="column"
                                        justify="flex-start"
                                        alignItems="flex-end" >

                                        <FormControlLabel
                                            style={{ marginRight: 0, height: 30 }}
                                            control={<Switch checked={openAll} onChange={() => setOpenAll(!openAll)} size="small" color="secondary" />}
                                            label={<Typography variant="subtitle2" color="initial">
                                                <Hidden xsDown>{"Ver todo"}</Hidden>
                                            </Typography>}
                                            labelPlacement="start"
                                        />
                                        <FormControlLabel style={{ marginRight: 0, height: 30 }}
                                            control={
                                                <TextField value={formato} select onChange={e => setFormato(e.target.value)}>
                                                    <MenuItem value={0}>0</MenuItem>
                                                    <MenuItem value={2}>2</MenuItem>
                                                    <MenuItem value={3}>3</MenuItem>
                                                    <MenuItem value={4}>4</MenuItem>
                                                </TextField>}
                                            label={<Typography variant="subtitle2" color="initial" style={{ marginRight: 5 }}>
                                                <Hidden xsDown>{"Decimales"}</Hidden>
                                            </Typography>}
                                            labelPlacement="start"
                                        />

                                        <Button onClick={onSavePDF} endIcon={<GetAppIcon />} size="small">
                                            DESCARGAR COMO PDF
                                        </Button>

                                        {rol !== 'C' &&
                                            <ButtonStatus nomina={generalSettings} idNomina={idNomina} idEmpresa={idEmpresa} setNomina={setGeneralSettings} />
                                        }
                                    </Grid>
                                    :
                                    <Grid container
                                        direction="column"
                                        justify="flex-start"
                                        alignItems="flex-end" >
                                        <img src={ImageLogo} alt="pdfimage" height={100} />
                                    </Grid>
                            }
                        </Grid>

                        {
                            rol !== 'C' &&
                            !seePDF &&
                            !loading &&
                            (generalSettings.estatus === 'E' || generalSettings.estatus === 'P') &&
                            <Grid item xs={12} sm={12}>
                                <Grid container
                                    direction="row"
                                    justify="space-between"
                                    alignItems="flex-end">
                                    <Grid item>
                                        <Grid container direction="row">
                                            <Typography
                                                variant="subtitle2"
                                                color="initial"
                                                display="block"
                                                style={{ cursor: 'pointer' }}
                                                onClick={() => setSettings(true)}>
                                                <Hidden xsDown>{"Ajustes de nómina"}</Hidden>
                                                <IconButton size="small" >
                                                    <SettingsIcon size="small" />
                                                </IconButton>
                                            </Typography>
                                            <Typography
                                                variant="subtitle2"
                                                color="initial"
                                                display="block"
                                                style={{ cursor: 'pointer' }}
                                                onClick={() => setAddVar(true)}>
                                                <Hidden xsDown>{"Agregar variable"}</Hidden>
                                                <IconButton size="small">
                                                    <AddCircleIcon size="small" />
                                                </IconButton>
                                            </Typography>

                                        </Grid>
                                    </Grid>
                                </Grid>
                            </Grid>
                        }

                    </Grid>
                </Box>
                <TableContainer>
                    <Table size="small">
                        <SortHead
                            numSelected={selected.length}
                            order={order}
                            orderBy={orderBy}
                            onSelectAllClick={handleSelectAllClick}
                            onRequestSort={handleRequestSort}
                            rowCount={empleados.length}
                            headCells={headCells}
                        />
                        <TableBody>
                            {
                                stableSort(empleados, getComparator(order, orderBy))
                                    .map((row, index) => <CollapseRow
                                        key={`collapse-row-${index}`}
                                        row={row} openAll={openAll}
                                    />)
                            }
                            <TotalRows totales={totales} />
                        </TableBody>
                    </Table>
                </TableContainer>
            </Paper>

            {settings && <SettingsNomina open={settings} setOpen={setSettings} />}
            {addVar && <AddVariable open={addVar} setOpen={setAddVar} />}
        </NominaContext.Provider>

    );
}
