import React from 'react';
import { Grid } from '@material-ui/core';
import DiaLaboral from '../ExtraComponents/DiaLaboral';
import { Typography, FormControlLabel, Switch } from '@material-ui/core';

export default function Dias(props) {
    const [equals, setEquals] = React.useState(false);
    const { onlySee } = props;


    const onChange = (pos, key, event) => {
        let newDias = [...props.horario];
        if (key === 'hora_entrada')
            newDias[pos].hora_entrada = event.target.value;
        else
            newDias[pos].hora_salida = event.target.value;
        if (equals) {
            allEquals(newDias);
        }
    }

    const disabled = (pos) => {
        const newDias = [...props.horario];
        newDias[pos].checked = !newDias[pos].checked;
        if (!newDias[pos].checked) {
            newDias[pos].hora_entrada = '00:00';
            newDias[pos].hora_salida = '00:00';
        } else if (equals && newDias[pos].checked) {
            allEquals(newDias);
        }
        props.setHorario(newDias);

    }

    const allEquals = (newDias) => {
        const item = newDias.find(item => item.checked);
        if (item !== undefined)
            newDias.forEach(element => {
                if (element.checked) {
                    element.hora_salida = item.hora_salida;
                    element.hora_entrada = item.hora_entrada;
                }
            });
        props.setHorario(newDias);
    }

    const onChangeSwitch = (event) => {
        if (event.target.checked) {
            setEquals(event.target.checked);
            allEquals([...props.horario]);
        }
        else setEquals(event.target.checked);
    }

    return (
        <>
            <Grid container item xs={12} md={12} lg={12}>
                <Grid container spacing={1}>
                    <Grid item xs={4}>
                        <Typography variant="body2" color="initial" display='block' style={{ textAlign: 'center' }}>Día</Typography>
                    </Grid>
                    <Grid item xs={4}>
                        <Typography variant="body2" color="initial" display='block' style={{ textAlign: 'center' }}>Entrada</Typography>

                    </Grid>
                    <Grid item xs={4}>
                        <Typography variant="body2" color="initial" display='block' style={{ textAlign: 'center' }}>Salida</Typography>
                    </Grid>
                </Grid>
            </Grid>

            {props.horario.map((item, index) => {
                return <Grid key={`dia-semana-${item.dia_semana}`} item xs={12} md={12} lg={12}>
                    <DiaLaboral
                        data={item}
                        position={index}
                        onChange={onChange}
                        onChangeDisabled={disabled}
                        onlySee={onlySee}
                    />
                </Grid>
            })}

            <Grid container item xs={12} md={12} lg={12}>
                <Grid container spacing={2}>
                    <Grid item xs={12} md={12} lg={12}
                        container
                        direction="row"
                        justify="flex-end"
                        alignItems="center">
                        <FormControlLabel
                            disabled={onlySee}
                            value="start"
                            control={<Switch color="secondary" onChange={onChangeSwitch} />}
                            label={<Typography variant="subtitle2" color="initial">Replicar para todos</Typography>}
                            labelPlacement="start"
                        />
                    </Grid>
                </Grid>
            </Grid>
        </>
    )
}
