import { requestHttp, generateOptions } from "../../../libraries/requestHttp";

export const get = async (params) => {
  const { url_api, key, setLoading, setData, headers, isTokenExpired } = params;
  const response = await requestHttp(url_api, generateOptions(undefined, "GET", headers));
  const { error, data } = response;

  if (!error) {
    setData(data[key]);
  } else {
    if (!isTokenExpired(error)) {
      console.log(error);
    }
  }
  setLoading(false);
};

export const post = async (params) => {
  const {
    url_api,
    values,
    setLoading,
    data,
    setData,
    controllerAlerts,
    resolve,
    reject,
    headers,
    isTokenExpired
  } = params;
  setLoading(true);
  const response = await requestHttp(url_api, generateOptions(values, "POST", headers));

  const { error } = response;
  if (!error) {
    const newData = [...data];
    newData.push(values);
    setData(newData);
    controllerAlerts("POST");
  } else {
    if (!isTokenExpired(error)) {
      managerErrors(error, controllerAlerts);
      reject();
    }
  }
  setLoading(false);
  resolve();
};

export const put = async (params) => {
  const {
    url,
    values,
    oldValues,
    resolve,
    reject,
    setLoading,
    data,
    setData,
    controllerAlerts,
    headers,
    isTokenExpired
  } = params;
  setLoading(true);
  const response = await requestHttp(url, generateOptions(values, "PUT", headers));
  const { error } = response;
  if (!error) {
    const newData = [...data];
    newData[data.indexOf(oldValues)] = values;
    setData(newData);
    controllerAlerts("PUT");
  } else {
    if (!isTokenExpired(error)) {
      managerErrors(error, controllerAlerts);
      reject();
    }
  }
  setLoading(false);
  resolve();
};

export const remove = async (params) => {
  const {
    url,
    oldData,
    resolve,
    setLoading,
    data,
    setData,
    controllerAlerts,
    reject,
    headers,
    isTokenExpired
  } = params;
  setLoading(true);
  const response = await requestHttp(url, generateOptions({}, "DELETE", headers));
  const { error } = response;
  if (!error) {
    const newData = [...data];
    newData.splice(newData.indexOf(oldData), 1);
    setData(newData);
    controllerAlerts("DELETE");
  } else {
    if (!isTokenExpired(error)) {
      managerErrors(error, controllerAlerts);
      reject();
    }
  }
  setLoading(false);
  resolve();
};

const managerErrors = (error, controllerAlerts) => {
  switch (error["error"]["db_code"]) {
    case "23505":
      controllerAlerts("INVALID_KEY");
      break;
    case "23503":
      controllerAlerts("ERROR_REFERENCED");
      break;

    default:
      console.log(error["error"]);
      break;
  }
};
