import React from 'react'
import MaterialTable from 'material-table';
import { TextField, Typography } from '@material-ui/core'
import RequestFetch from '../../../libraries/RequestFetch'
import { Snackbar } from '@material-ui/core';
import { AlertTitle } from '@material-ui/lab';
import MuiAlert from '@material-ui/lab/Alert';
import { useHistory } from 'react-router-dom';
import { localization, actions, options } from '../../../libraries/ConfigCatalog';
import { SessionContext } from '../../../routes/adminrh/CustomRoutes'

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

export default function TableGiro() {
    const { cabeceras, isTokenExpired } = React.useContext(SessionContext);
    const history = useHistory();
    const [data, setData] = React.useState([]);
    const [loading, setLoading] = React.useState(false);
    const [toast, setToast] = React.useState({
        open: false,
        message: "",
        severity: "",
        title: ""
    });

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            setToast({ ...toast, open: false });
            return;
        }
        setToast({ ...toast, open: false });
    };

    const getData = React.useCallback(() => {
        let mounted = true;
        setLoading(true);
        setData([]);

        const _exito = async (res) => {
            const respuesta = await res.json();
            if (mounted) {
                setData(respuesta.jornadas);
                setLoading(false);
                if (respuesta.jornadas.length === 0) {
                    setToast({
                        open: true,
                        message: "No existen registros de empresas",
                        severity: "info",
                        title: "Ningun registro encontrado"
                    })
                }
            };
        }

        const _fallo = async (res) => {
            const response = await res.json();
            if (!isTokenExpired(response))
                if (mounted) {
                    setLoading(false);
                    setToast({
                        open: true,
                        message: "Ha ocurrido un error, intenta nuevamente",
                        severity: "error",
                        title: "Uuuups!"
                    })

                };
        }

        if (mounted) {
            RequestFetch.get({
                url: `${RequestFetch.BASE_URL}/api/rh/jornada?query=*`,
                headers: cabeceras,
                exito: _exito,
                fallo: _fallo,
                error_request: () => {
                    history.push(`/server-error`);
                }
            });
        }

        return () => mounted = false;
        //eslint-disable-next-line
    }, [history]);



    const columns = [
        { title: 'ID', field: "id_jornada", width: 20, editable: 'never', hidden: true },
        {
            title: 'Nombre Jornada', field: 'tipo', width: 150, defaultSort: 'asc',
            editComponent: props => (
                <TextField
                    fullWidth
                    size="small"
                    value={props.value || ''}
                    placeholder="Ej. Nocturna"
                    onChange={e => props.onChange(e.target.value)}
                />
            )
        },
        {
            title: 'Descripción', field: 'descripcion', width: 300,
            editComponent: props => (
                <TextField
                    fullWidth
                    size="small"
                    value={props.value || ''}
                    placeholder="Ej. Horario laboral entre 8pm. y 8am."
                    onChange={e => props.onChange(e.target.value)}
                />
            )
        }
    ];

    actions[0].onClick = getData;
    localization.body.editRow.deleteText = <Typography variant="button" gutterBottom>¿Está seguro que desea continuar?</Typography>


    const POST = (jornada) => {
        let mounted = true;
        const exito = async res => {
            const respuesta = await res.json();
            if (mounted) {
                jornada.id_jornada = respuesta.jornada[0].id_jornada;
                const newData = data;
                newData.push(jornada)
                setData(newData);
                setLoading(false);
                setToast({
                    open: true,
                    message: `Jornada: ${jornada.tipo.toUpperCase()} creado correctamente`,
                    severity: "success",
                    title: "Registro exitoso!"
                });
            }
        }
        const fallo = async res => {
            const response = await res.json();
            if (!isTokenExpired(response))
                if (mounted) {
                    setLoading(false);
                    setToast({
                        open: true,
                        message: `El registro no se ha realizado, intenta de nuevo`,
                        severity: "error",
                        title: "Registro fallido!"
                    });
                }
        }

        const send = () => {
            RequestFetch.post({
                url: `${RequestFetch.BASE_URL}/api/rh/jornada`,
                headers: cabeceras,
                data: jornada,
                exito: exito,
                fallo: fallo,
                error_request: (error) => {
                    history.push(`/server-error`);
                }
            });
        }

        if (mounted) {
            setLoading(true);
            send();
        }

        return () => mounted = false;
    }

    const PUT = (jornada, oldjornada) => {

        let mounted = true;

        const exito = async res => {
            await res.json();
            const newData = data;
            const index = newData.indexOf(oldjornada);
            data[index] = jornada;
            if (mounted) {
                setData(newData);
                setLoading(false);

                setToast({
                    open: true,
                    message: `Jornada: ${jornada.tipo.toUpperCase()} se ha actualizado correctamente`,
                    severity: "success",
                    title: "Actualización exitoso!"
                });
            }
        }
        const fallo = async res => {
            const response = await res.json();
            if (!isTokenExpired(response))
                if (mounted) {
                    setLoading(false);
                    setToast({
                        open: true,
                        message: `Jornada: ${jornada.tipo.toUpperCase()} no ha sido actualizado, intenta de nuevo`,
                        severity: "error",
                        title: "Actualización fallida!"
                    });
                }
        }

        const send = () => {
            const putData = {
                tipo: jornada.tipo,
                descripcion: jornada.descripcion
            }
            RequestFetch.put({
                url: `${RequestFetch.BASE_URL}/api/rh/jornada/${jornada.id_jornada}`,
                headers: cabeceras,
                data: putData,
                exito: exito,
                fallo: fallo,
                error_request: (error) => {
                    history.push(`/server-error`);
                }
            });
        }

        if (mounted) {
            setLoading(true);
            send();
        }
    }

    const DELETE = (jornada) => {
        let mounted = true;
        const exito = async res => {
            await res.json();

            let newData = data;
            const index = newData.indexOf(jornada);
            data.splice(index, 1);

            if (mounted) {
                setData(newData);
                setLoading(false);
                setToast({
                    open: true,
                    message: `Jornada: ${jornada.tipo.toUpperCase()} se ha eliminado correctamente`,
                    severity: "success",
                    title: "Eliminación exitosa!"
                });
            }

        }
        const fallo = async res => {
            const response = await res.json();
            if (!isTokenExpired(response))
                if (mounted) {
                    setLoading(false);
                    setToast({
                        open: true,
                        message: `Jornada: ${jornada.tipo.toUpperCase()} tiene registros que dependen de este, primero debes eliminarlas`,
                        severity: "error",
                        title: "Eliminación fallida!"
                    });
                }
        }

        const send = () => {
            RequestFetch.deleted({
                url: `${RequestFetch.BASE_URL}/api/rh/jornada/${jornada.id_jornada}`,
                headers: cabeceras,
                exito: exito,
                fallo: fallo,
                error_request: () => {
                    history.push(`/server-error`);
                }
            });
        }

        if (mounted) {
            setLoading(true);
            send();
        }
    }

    React.useEffect(() => {
        getData();
        // eslint-disable-next-line
    }, [history])


    return (
        <>
            <MaterialTable
                title="Jornadas"
                columns={columns}
                localization={localization}
                data={data}
                actions={actions}
                options={options}
                isLoading={loading}
                editable={{
                    onRowAdd: (newData) =>
                        new Promise((resolve, reject) => {
                            setTimeout(() => {
                                if (newData.tipo !== '' && newData.descripcion !== '' && newData.tipo !== undefined && newData.descripcion !== undefined) {
                                    POST(newData);
                                    resolve();
                                }
                                else {
                                    setToast({
                                        open: true,
                                        message: "Rellena los campos \"Nombre Jornada\" y \"Descripción\"",
                                        severity: "warning",
                                        title: "Campos vacios!"
                                    })
                                    reject();
                                    return;
                                }
                            }, 600);
                        })
                    ,
                    onRowUpdate: (newData, oldData) =>
                        new Promise((resolve, reject) => {
                            setTimeout(() => {
                                if (newData.tipo !== '' && newData.descripcion !== '' && newData.tipo !== undefined && newData.descripcion !== undefined) {
                                    resolve();
                                    PUT(newData, oldData);
                                } else {
                                    setToast({
                                        open: true,
                                        message: "Rellena los campos \"Nombre Jornada\" y \"Descripción\"",
                                        severity: "warning",
                                        title: "Campos vacios!"
                                    })
                                    reject();
                                    return;
                                }
                            }, 600);
                        }),
                    onRowDelete: (oldData) =>
                        new Promise((resolve, reject) => {
                            setTimeout(() => {
                                resolve();
                                DELETE(oldData);
                            }, 600);
                        }),
                }}
            />


            <Snackbar open={toast.open} autoHideDuration={6000} onClose={handleClose}>
                <Alert onClose={handleClose} severity={toast.severity}>
                    <AlertTitle>{toast.title}</AlertTitle>
                    {toast.message}
                </Alert>
            </Snackbar>
        </>
    );
}