import { requestHttp, generateOptions } from "../../../libraries/requestHttp";

export const getNominas = async (params) => {
  const { url_api, setN, body, addFlags, headers, isTokenExpired, rol, permisos } = params;
  const response = await requestHttp(url_api, generateOptions(body, 'POST', headers));
  const { error, data } = response;
  if (!error) {
    if (rol === 'G' || rol === 'C') data['result'] = data['result'].filter(e => permisos.some(p => p === e.id_empresa))
    addFlags(data['result']);
    setN(data['result']);
    console.log(data['result']);
  } else if (!isTokenExpired(error)) {
    console.log(error);
  }
};