import React from 'react';
import { Grid, Button, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { ArrowBack, ArrowForward } from '@material-ui/icons';
import Dias from '../Dias';

const useStyles = makeStyles(theme => ({
    container: {
        padding: theme.spacing(4),
    }
}));



export default function Horario(props) {
    const classes = useStyles();
    const { onlySee } = props;

    return (
        <Grid container
            direction="row"
            justify="center"
            alignItems="center"
            spacing={3} className={classes.container}>
            <Grid item xs={12} md={onlySee ? 10 : 6} lg={onlySee ? 10 : 6} xl={onlySee ? 8 : 6}>
                <Typography variant="subtitle2" display="block" style={{ textAlign: 'center' }} color="initial" gutterBottom>Dias Laborales</Typography>
                <Dias horario={props.horario} setHorario={props.setHorario} onlySee={onlySee} />
            </Grid>


            {
                !props.expandable &&
                <Grid container item
                    direction="row"
                    justify="flex-end"
                    alignItems="center" xs={12} sm={12} md={12} lg={12} >
                    <Button startIcon={<ArrowBack />}
                        onClick={props.buttons.back}>Anterior</Button>
                    <Button
                        variant="outlined"
                        color="primary"
                        endIcon={<ArrowForward />}
                        onClick={props.buttons.next}
                    >Siguiente</Button>
                </Grid>
            }
        </Grid>
    )
}
