import { useEffect, useState, useContext } from 'react'
import { BASE_URL } from '../../../libraries/RequestFetch';
import { SessionContext } from '../../../routes/adminrh/CustomRoutes';
//import { useHistory } from 'react-router-dom';

const ListURL = {
    isr: '/api/rh/nomina/catalogo/isr',
    integracion: '/api/rh/nomina/catalogo/integracion',
    uma: '/api/rh/nomina/catalogo/uma',
    subsidio: '/api/rh/nomina/catalogo/subsidio'
}

export function useFactoryLaw({ type }) {
    const { cabeceras, isTokenExpired } = useContext(SessionContext);
    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);

    const post = async ({ data }, callback) => {
        try {
            setLoading(true);
            const URI = ListURL[type];
            const resp = await fetch(`${BASE_URL}${URI}`, { method: 'POST', headers: cabeceras, body: JSON.stringify(data) });
            const response = await resp.json();
            if (!isTokenExpired(response)) {
                await refresh();
                setLoading(false);
                if (callback) callback();
            }
        } catch (error) {
            console.log(error);
        }
    }

    const remove = async ({ id }, callback) => {
        try {
            setLoading(true);
            const URI = ListURL[type];
            const resp = await fetch(`${BASE_URL}${URI}/${id}`, { method: 'DELETE', headers: cabeceras });
            const response = await resp.json();
            if (!isTokenExpired(response)) {
                await refresh();
                setLoading(false);
                if (callback) callback();
            }
        } catch (error) {
            console.log(error);
        }
    }

    const put = async ({ id, data }, callback) => {

        try {
            setLoading(true);
            const URI = ListURL[type];
            const resp = await fetch(`${BASE_URL}${URI}/${id}`, { method: 'PUT', headers: cabeceras, body: JSON.stringify(data) });
            const response = await resp.json();
            if (!isTokenExpired(response)) {
                await refresh();
                setLoading(false);
                if (callback) callback();
            }
        } catch (error) {
            console.log(error);
        }
    }

    const getData = async () => {
        try {
            setLoading(true);
            const URI = ListURL[type];
            const resp = await fetch(`${BASE_URL}${URI}`, { method: 'GET', headers: cabeceras });
            const response = await resp.json();
            if (!isTokenExpired(response)) {
                setData(response[type]);
                setLoading(false);
            }
        } catch (error) {
            console.log(error);
        }
    }

    const refresh = () => {
        getData();
    }

    useEffect(() => {
        refresh()
        // eslint-disable-next-line
    }, [type])

    return { data, refresh, loading, post, remove, put }
}
