import CircularProgress from '@material-ui/core/CircularProgress';
import { Save as SaveIcon } from '@material-ui/icons'
import AlertTitle from '@material-ui/lab/AlertTitle';
import { useParams, useHistory } from "react-router-dom";
import ListSubheader from '@material-ui/core/ListSubheader';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import Backdrop from '@material-ui/core/Backdrop';
import Validacion from '../../../libraries/Validacion';
import { withSnackbar } from 'notistack';
import { CodeByEstado, isCode } from '../../../libraries/Estados';
import Documentos from '../ExtraComponents/Documentos';
import ExpandButton from '../Dash/ExpandButton';
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import ReplayIcon from '@material-ui/icons/Replay';
import Contrato from './Contrato/Contrato';
import AssignmentTurnedInIcon from '@material-ui/icons/AssignmentTurnedIn';
import CalendarioNominas from './CalendarioNominas';
import PrimaRiesgo from './PrimaRiesgo';
import { SessionContext } from '../../../routes/adminrh/CustomRoutes'
import { initRepre, initInfo, initDomi, initCont, initTelEmpre, initTelRepre, initialPart, initialContrato, initialRegistros } from './initialStates';

const useStyles = makeStyles(theme => ({
    root: {
        width: '100%',
        backgroundColor: theme.palette.background.paper,
        marginBottom: 24,
    },
    cardContent: {
        padding: '0px'
    },
    header: {
        background: '#fafafa'
    },
    title: {
        flexGrow: 1,
        textAlign: 'center',
        marginTop: '15px ',
        marginBottom: '15px'
    },
    actions: {
        padding: theme.spacing(4),
    },
    save: {
        color: theme.palette.common.white,
        backgroundColor: green[500],
        '&:hover': {
            backgroundColor: green[600],
        },
    },
    buttonProgress: {
        color: green[500],
        position: 'absolute',
        top: '50%',
        left: '50%',
        marginTop: -12,
        marginLeft: -12,
    },
    wrapper: {
        margin: '0px 24px 8px 16px',
        position: 'relative',
    },
    delete: {
        marginRight: theme.spacing(1)
    },
    subheader: {
        textAlign: 'center'
    },
    backdrop: {
        zIndex: theme.zIndex.drawer + 1,
        color: "#fafafa",
    },
    back: {
        left: -12,
        float: 'left'
    },
    butonHide: {
        opacity: 0,
    }
}));


const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}




function EditEmpresa(props) {
    const classes = useStyles();
    const history = useHistory();
    const params = useParams();
    const timer = React.useRef();
    const { cabeceras, isTokenExpired, permisos, rol } = React.useContext(SessionContext);

    const { onlySee } = props;

    const id = params.id === undefined ? props.id : params.id;


    const [dialog, setDialog] = React.useState({ open: false, type: '' });
    const [part, setPart] = React.useState(initialPart());
    const [loading, setLoading] = React.useState(false);
    const [backdrop, setBackdrop] = React.useState(true);

    const [informacion, setInformacion] = React.useState(initInfo());
    const [direccion, setDireccion] = React.useState(initDomi());
    const [contacto, setContacto] = React.useState(initCont())
    const [empresaTel, setEmpresaTel] = React.useState(initTelEmpre());
    const [contrato, setContrato] = React.useState(initialContrato('new'));
    const [identificador, setIdentificador] = React.useState("");
    const [registros, setRegistros] = React.useState(initialRegistros());


    const [oldContrato, setOldContrato] = React.useState({});
    const [oldRegistros, setOldRegistros] = React.useState(initialContrato());

    const [oldTel, setOldTel] = React.useState([]);
    const [oldData, setOldData] = React.useState({});


    const [repre, setRepre] = React.useState(initRepre());
    const [repreTel, setRepreTel] = React.useState(initTelRepre());

    const [toast, setToast] = React.useState({
        open: false,
        message: "",
        severity: "",
        title: ""
    });

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            setToast({ ...toast, open: false });
            return;
        }
        setToast({ ...toast, open: false });
    };

    const handleClick = (pos) => {
        setPart({ ...part, actual: pos })
    };

    const auxiliar = (() => {

        const separeEmpresa = (id_repre) => {
            const data = { ...informacion, ...direccion, ...contacto }
            const keys = Object.keys(data);
            const form = new FormData();

            keys.forEach((key) => {
                let value = '';
                switch (key) {
                    case 'fecha_constitucion': case 'fecha_alta':
                        value = moment(data[key].value);
                        const oldValue = moment(oldData[key]);
                        if (!value.isSame(oldValue) && value.isValid()) {
                            //console.log(value, oldData[key]);
                            form.append(key, value);
                        }
                        break;
                    case 'id_representante':
                        value = oldData.id_representante;
                        //console.log(value, repre.id_representante.value);
                        if (value !== repre.id_representante.value)
                            form.append('id_representante', repre.id_representante.value);
                        break;
                    case 'id_empresa': case 'fileImg': case 'identificador': case 'url_logo': break;
                    case 'rfc':
                        value = data.rfc.value.toUpperCase();
                        if (value !== oldData[key])
                            form.append('rfc', value);
                        break;
                    case 'es_interna':
                        if (`${data[key].value}` !== `${oldData[key]}`) {
                            form.append(key, data[key].value);
                        }
                        break;
                    default:
                        value = data[key].value;
                        if (value !== oldData[key] && value !== '') {
                            form.append(key, data[key].value + '');
                        }
                        break;
                }
            });

            if (data.fileImg !== null)
                form.append('logo', data.fileImg);
            let cont = 0;
            for (var pair of form.entries()) {
                console.log(pair[0] + ', ' + pair[1]);
                cont++;
            }
            if (cont === 0) return null;
            else return form;
        }

        const separeEmpresaTel = () => {
            const newTel = [];
            empresaTel.forEach(item => {
                const value = item.value.replace(/\s/g, '');
                if (value.length >= 5)
                    newTel.push({ numero: value, tipo: item.tipo })
            })
            if (newTel.length > 0)
                return {
                    id_empresa: informacion.id_empresa.value,
                    telefonos: newTel
                }
            else return null;
        }

        const separeContrato = (id) => {
            let keys = [];
            const newContrato = {};
            keys = Object.keys(contrato)
            keys.forEach(key => {
                if (key.includes('fecha')) {
                    if (moment(contrato[key].value).format("YYYY-MM-DD") !== moment(oldContrato[key]).format("YYYY-MM-DD")) {
                        newContrato[key] = moment(contrato[key].value).format("YYYY-MM-DD");
                    }
                } else {
                    if (contrato[key].value !== oldContrato[key])
                        newContrato[key] = contrato[key].value;
                }
            });
            if (Object.keys(newContrato).length > 0) {
                return { id_empresa: id, ...newContrato };
            }
            return null;

        }

        const separeContratoPost = () => {
            //console.log(contrato, oldContrato);
            let keys = ['fecha_inicio', 'fecha_fin', 'comision', 'motivo_contrato', 'periodo_calculo'];
            const newContrato = {};
            keys.forEach(key => {
                if (key.includes('fecha')) {
                    const date = moment(contrato[key].value).format("YYYY/MM/DD");
                    if (date !== 'Invalid date')
                        newContrato[key] = date;
                }
                else if (key === 'comision') {
                    const comision = parseInt(contrato[key].value) / 100
                    newContrato[key] = !isNaN(comision) ? comision : 0.0
                }
                else {
                    newContrato[key] = contrato[key].value;
                }
            });
            return { id_empresa: id, ...newContrato };
        }

        const separeRegistrosPatronales = (id) => {
            const registrosPatronales = [];
            registros.forEach(i => {
                let { helperText, verify, ...y } = i;
                y.clave_patronal = y.clave_patronal.replace(/\s/g, '');
                registrosPatronales.push({ ...y });
            });
            return {
                id_empresa: informacion.id_empresa.value,
                registros: registrosPatronales
            };
        }

        const separeRepreTel = () => {
            const newTel = [];
            repreTel.forEach(item => {
                const value = item.value.replace(/\s/g, '');
                if (value.length >= 5)
                    newTel.push({ numero: value, tipo: item.tipo })
            })
            if (newTel.length > 0)
                return {
                    id_representante: repre.id_representante.value,
                    telefonos: newTel
                }
            else return null;
        }

        const separeRepresentante = () => {
            var newData = {
                representante: {
                    rfc: repre.rfc.value.toUpperCase(),
                    nombre: repre.nombre.value,
                    primer_apellido: repre.primer_apellido.value,
                    segundo_apellido: repre.segundo_apellido.value,
                    genero: repre.genero.value,
                    correo: repre.correo.value,
                    observaciones: repre.observaciones.value,
                }
            }

            return newData;
        }


        const validaContrato = () => {
            if (!contrato.new) return true;
            let newContrato = {};
            let keys = [];
            const oks = [];
            if (informacion.es_interna.value === 'false') {
                newContrato = { ...contrato };
                keys = Object.keys(contrato);
                keys.forEach(key => {
                    if (key !== 'new' && key !== 'id_empresa' && key !== 'id_contrato') {

                        newContrato[key]['verify'] = `${contrato[key].value}`.length > 0 && contrato[key].verify;
                        //console.log(newContrato[key])
                        oks.push(newContrato[key]['verify']);
                    }
                });
                //console.log(oks);
                const resul = oks.filter(i => {
                    return i !== true;
                });
                setContrato(newContrato);
                return resul.length === 0;

            } else {
                keys = ['fecha_inicio', 'periodo_calculo'];
                keys.forEach(key => {
                    newContrato[key].verify = `${contrato[key].value}`.length > 0 && contrato[key].verify;
                });
                newContrato['motivo_contrato'] = 'A';
                newContrato['comision'] = ' 0'
            }


            const result = keys.filter(key => {
                return newContrato[key].verify !== true;
            });

            setContrato({ ...contrato, ...newContrato });
            //console.log(result);
            return result.length === 0;
        }

        const validaRegistrosPatronales = () => {
            if (informacion.es_interna.value === 'true') {
                const newRegistros = [...registros];
                const oks = []
                newRegistros.forEach(i => {
                    i.verify = (i.clave_patronal.replace(/\s/g, '').length === 11 && Number.isInteger(parseInt(i.categoria)) && i.verify);
                    if (!i.verify) i.helperText = "Verifica la clase y clave";
                    oks.push(i.verify)
                });
                setRegistros(newRegistros);
                const resul = oks.filter(item => {
                    return item !== true;
                })

                return resul.length === 0;
            } else return true;

        }

        const validaInformacion = () => {
            const keys = ['razon_comercial', 'razon_social', 'rfc', 'fecha_constitucion', 'fecha_alta', 'id_regimen_capital', 'es_interna', 'id_giro', 'estatus'];
            const newData = { ...informacion }
            keys.forEach(key => {
                newData[key].verify = `${newData[key].value}`.length > 0 && newData[key].verify;
            });
            setInformacion(newData);
            const result = keys.filter(key => {
                return newData[key].verify !== true;
            });
            return result.length === 0;
        }

        const validaDireccion = () => {
            const keys = ['calle', 'cp', 'colonia', 'municipio', 'entidad'];
            const newData = { ...direccion }
            keys.forEach(key => {
                newData[key].verify = `${newData[key].value}`.length > 0 && newData[key].verify;
            });
            setDireccion(newData);
            const result = keys.filter(key => {
                return newData[key].verify !== true;
            });
            return result.length === 0;
        }

        const validaContacto = () => {
            const keys = ['correo'];
            const newData = { ...contacto }
            newData['correo'].verify = Validacion.esCorreo(newData['correo'].value) && newData['correo'].value.length > 0;
            setContacto(newData);
            const result = keys.filter(key => {
                return newData[key].verify !== true;
            });
            return result.length === 0;
        }

        const ValidaRepre = () => {
            var keysRepre = ['rfc', 'nombre', 'primer_apellido', 'genero', 'correo']
            var newDataRepre = { ...repre };
            keysRepre.forEach(key => {
                if (key === 'rfc')
                    newDataRepre[`${key}`] = { value: repre[key].value, verify: `${repre[key].value}`.replace(/\s/g, '').length === 13 && repre[key].verify }
                else
                    newDataRepre[`${key}`] = { value: repre[key].value, verify: `${repre[key].value}`.length > 0 && repre[key].verify }
            });
            setRepre(newDataRepre);
            const resulRepre = keysRepre.filter(key => {
                return newDataRepre[key].verify !== true;
            });

            return resulRepre.length === 0;
        }

        const ValidaNumeroRepre = () => {
            const newData = [...repreTel];
            const oks = [];
            newData.forEach((element) => {
                const correo = element.value.replace(/\s/g, '');

                if (correo.length === 10) {
                    const verify = element.verify;
                    element.verify = verify;
                    oks.push(verify);
                }
            })
            const resul = oks.filter(item => {
                return item !== true;
            })
            if (oks.length === 0) {
                newData[0].verify = false;
                newData[0].helperText = "Registra al menos uno";
                newData[1].verify = false;
                newData[1].helperText = "Registra al menos uno"
                return false;
            }
            setRepreTel(newData);
            return resul.length === 0;
        }

        const ValidaNumeroEmpresa = () => {
            const newData = [...empresaTel];
            const oks = [];
            newData.forEach((element) => {
                const correo = element.value.replace(/\s/g, '');
                if (correo.length === 10) {
                    const verify = element.verify;
                    element.verify = verify;
                    oks.push(verify);
                }
            })
            const resul = oks.filter(item => {
                return item !== true;
            })
            if (oks.length === 0) {
                newData[0].verify = false;
                newData[0].helperText = "Registra al menos uno";
                newData[1].verify = false;
                newData[1].helperText = "Registra al menos uno"
                return false;
            }
            setEmpresaTel(newData);
            return resul.length === 0;
        }

        const openTab = part => {
            setToast({
                open: true,
                message: "Es necesario rellenar los campos requeridos",
                severity: "warning",
                title: "Campos vacios!"
            });
            setPart({ ...part, actual: part });
            setLoading(false);
        }


        const ValidaAll = () => {
            if (validaInformacion())
                if (validaDireccion())
                    if (validaContacto())
                        if (ValidaNumeroEmpresa())
                            if (validaContrato())
                                if (validaRegistrosPatronales())
                                    if (ValidaRepre())
                                        if (ValidaNumeroRepre())
                                            return true;
                                        else { openTab(5); return false; }
                                    else { openTab(4); return false; }
                                else { openTab(3); return false; }
                            else { openTab(3); return false; }
                        else { openTab(2); return false; }
                    else { openTab(2); return false; }
                else { openTab(1); return false; }
            else { openTab(0); return false; }
        }

        /*
        const telefonos = (inicio, fin) => {
            const newTel = [];
            for (let index = inicio; index < fin; index++) {
                newTel.push({ numero: empresaTel[index].value.replace(/\s/g, ''), tipo: empresaTel[index].tipo })
            }

            return {
                id_empresa: informacion.id_empresa.value,
                telefonos: newTel
            }
        }*/

        return {
            separeContratoPost,
            separeContrato,
            separeRegistrosPatronales,
            separeEmpresa,
            separeEmpresaTel,
            separeRepreTel,
            separeRepresentante,
            ValidaAll
        }
    })();

    const buttons = (() => {

        const next = () => {
            setPart({ ...part, actual: part.actual + 1, global: part.global + 1 });
        }

        const back = () => {
            setPart({ ...part, actual: part.actual - 1 });
        }

        const send = () => {
            //setLoading(true);
            //Actualiza Empresa
            const PUTSendEmpresa = (id_repre) => {
                const send = auxiliar.separeEmpresa(id_repre);
                const headers = new Headers();
                headers.append('authorization', cabeceras.authorization);
                if (send !== null) {
                    RequestFetch.putForm({
                        url: `${RequestFetch.BASE_URL}/api/rh/empresa/${informacion.id_empresa.value}`,
                        headers,
                        data: send,
                        exito: async (res) => {
                            //console.log(await res.json());
                            setLoading(false);
                            props.enqueueSnackbar("Empresa actualizada", { variant: 'success', preventDuplicate: true, });
                        },
                        fallo: async (res) => {

                            const respuesta = await res.json();

                            if (!isTokenExpired(respuesta)) {
                                setLoading(false);
                                //console.log(respuesta);
                                const _toast = {
                                    open: true,
                                    severity: "error",
                                }
                                if (respuesta.error.detail.includes("correo")) {
                                    toast['message'] = "Correo duplicado";
                                    toast['title'] = "Ya está asociado a otra empresa, proporciona uno distinto";
                                }
                                else if (respuesta.error.detail.includes("razon_social")) {
                                    toast['message'] = "Razón Social duplicada";
                                    toast['title'] = "Es posible que la empresa ya esté registrada";
                                }
                                else if (respuesta.error.detail.includes("rfc")) {
                                    toast['message'] = "RFC duplicado";
                                    toast['title'] = "Es posible que la empresa ya esté registrada";
                                }
                                setToast(_toast);
                            }
                        },
                        error_request: (error) => {
                            history.push("/server-error");
                        }
                    });
                    const obj = {};
                    for (var pair of send.entries()) {
                        obj[pair[0]] = pair[1];
                    }
                    setOldData(old => { return { ...old, ...obj } });
                }
            }

            const PUTSendEmpresaTel = () => {

                const agregarTele = (object) => {
                    RequestFetch.post({
                        url: `${RequestFetch.BASE_URL}/api/rh/empresa/telefono`,
                        headers: cabeceras,
                        data: object,
                        exito: async (res) => {
                            //console.log(await res.json());
                            props.enqueueSnackbar("Teléfonos agregados", { variant: 'success', preventDuplicate: true, })
                        },
                        fallo: async (res) => {
                            const respuesta = res.json();
                            if (!isTokenExpired(respuesta));
                        },
                        error_request: () => {
                            history.push(`/server-error`);
                        }
                    });
                }

                const actualizarTel = (actual, olds) => {
                    ////console.log(actual, olds);
                    for (let index = 0; index < actual.length; index++) {
                        const tipoOld = olds.find(i => i.tipo === actual[index].tipo);
                        if (tipoOld['numero'] !== actual[index].numero) {
                            RequestFetch.put({
                                url: `${RequestFetch.BASE_URL}/api/rh/empresa/telefono/${informacion.id_empresa.value}/${olds[index].numero}`,
                                headers: cabeceras,
                                data: actual[index],
                                exito: async (res) => {
                                    //console.log(await res.json())
                                    props.enqueueSnackbar("Teléfonos actualizados", { variant: 'success', preventDuplicate: true, })
                                },
                                fallo: async (res) => {
                                    const respuesta = res.json();
                                    if (!isTokenExpired(respuesta));
                                },
                                error_request: (error) => {
                                    history.push(`/server-error`);
                                }
                            })
                        }
                    }
                }

                const eliminarTel = (object) => {
                    ////console.log("TODELETE", object);
                    object.forEach(item => {
                        if (Object.keys(item) > 0)
                            RequestFetch.deleted({
                                url: `${RequestFetch.BASE_URL}/api/rh/empresa/telefono/${informacion.id_empresa.value}/${item.numero}`,
                                headers: cabeceras,
                                exito: async (res) => {
                                    //console.log(await res.json())
                                    props.enqueueSnackbar("Teléfonos eliminados", { variant: 'success', preventDuplicate: true, })
                                },
                                fallo: async (res) => {
                                    const respuesta = res.json();
                                    if (!isTokenExpired(respuesta));
                                },
                                error_request: (error) => {
                                    history.push(`/server-error`);
                                }

                            })
                    })
                }

                const newTel = auxiliar.separeEmpresaTel();

                if (newTel.telefonos.length > oldTel.length) {
                    //console.log(oldTel, newTel.telefonos);
                    const toUpdate = [];
                    oldTel.forEach(e => {
                        const index = newTel.telefonos.findIndex(i => i.tipo === e.tipo);
                        //console.log("ENCONTRADO", newTel.telefonos.find(i => i.tipo === e.tipo))
                        if (index >= 0) {
                            toUpdate.push(newTel.telefonos.splice(index, 1)[0]);
                        }
                    });
                    agregarTele(newTel);
                    actualizarTel(toUpdate, oldTel);
                    setOldTel([...newTel.telefonos, ...toUpdate])
                }
                else if (newTel.telefonos.length < oldTel.length) {
                    //console.log(newTel.telefonos, oldTel);
                    const toUpdate = [];
                    newTel.telefonos.forEach(e => {
                        const index = oldTel.findIndex(i => i.tipo === e.tipo);
                        ////console.log("ENCONTRADO", oldTel.find(i => i.numero === e.numero))
                        if (index >= 0) {
                            toUpdate.push(oldTel[index]);
                            oldTel.splice(index, 1);
                        }
                    });
                    eliminarTel(oldTel);
                    actualizarTel(newTel.telefonos, toUpdate);
                    setOldTel(newTel.telefonos);
                }
                else {
                    actualizarTel(newTel.telefonos, oldTel);
                    setOldTel(newTel.telefonos);
                }
            }

            const POSTSendTelRepre = (id) => {
                //console.log(auxiliar.separeRepreTel(id));
                RequestFetch.post({
                    url: `${RequestFetch.BASE_URL}/api/rh/representante/telefono`,
                    headers: cabeceras,
                    data: auxiliar.separeRepreTel(id),
                    exito: async (res) => {
                        const respuesta = await res.json();
                        console.log(respuesta);

                        props.enqueueSnackbar("Representante creado", { variant: 'success', preventDuplicate: true, })
                    },
                    fallo: async (res) => {
                        const respuesta = res.json();
                        if (!isTokenExpired(respuesta));
                    },
                    error_request: () => {
                        history.push(`server-error`);
                    }
                });
            }

            const POSTSendReprese = () => {
                RequestFetch.post({
                    url: `${RequestFetch.BASE_URL}/api/rh/representante`,
                    data: auxiliar._separeRepresentante(),
                    headers: cabeceras,
                    exito: async (res) => {
                        const respuesta = await res.json();
                        const { representate, created } = respuesta;
                        if (created) {
                            setRepre(prevState => {
                                return {
                                    ...prevState,
                                    id_representante: { value: representate[0].id_representante },
                                    existe: { value: true }
                                }
                            });
                            POSTSendTelRepre(representate[0].id_representante)
                            PUTSendEmpresa(representate[0].id_representante);
                            if (informacion.es_interna.value) {
                                POSTSendRegistros(informacion.id_empresa.value)
                            }
                            props.enqueueSnackbar("Representante creado", { variant: 'success', preventDuplicate: true, })
                        }
                    },
                    fallo: async (res) => {
                        const respuesta = res.json();
                        if (!isTokenExpired(respuesta));
                    },
                    error_request: (error) => {
                        setLoading(false);
                        history.push("/server-error");
                    }
                });
            }

            const POSTSendRegistros = () => {
                const agregarRegistro = (object) => {
                    //console.log("TOADD", object);
                    RequestFetch.post({
                        url: `${RequestFetch.BASE_URL}/api/rh/empresa/registro_patronal`,
                        headers: cabeceras,
                        data: object,
                        exito: async (res) => {
                            //console.log(await res.json());
                            props.enqueueSnackbar("Registros agregados", { variant: 'success', preventDuplicate: true, })
                        },
                        fallo: async (res) => {
                            const respuesta = res.json();
                            if (!isTokenExpired(respuesta));
                        },
                        error_request: () => {
                            history.push(`/server-error`);
                        }
                    });
                }

                const actualizarRegistro = (actual, olds) => {
                    //console.log("TOUPDATE", actual, olds);
                    for (let index = 0; index < actual.length; index++) {
                        //console.log(actual[index].categoria, olds[index].categoria, actual[index].clave_patronal, olds[index].clave_patronal)
                        if (actual[index].categoria !== olds[index].categoria || actual[index].clave_patronal !== olds[index].clave_patronal) {
                            RequestFetch.put({
                                url: `${RequestFetch.BASE_URL}/api/rh/empresa/registro_patronal/${olds[index].clave_patronal}`,
                                headers: cabeceras,
                                data: actual[index],
                                exito: async (res) => {
                                    //console.log(await res.json())
                                    props.enqueueSnackbar("Registros Patronales actualizados", { variant: 'success', preventDuplicate: true, })
                                },
                                fallo: async (res) => {
                                    const respuesta = res.json();
                                    if (!isTokenExpired(respuesta));
                                },
                                error_request: (error) => {
                                    history.push(`/server-error`);
                                }
                            })
                        }
                    }
                }

                const eliminarRegistro = (object) => {
                    //console.log("TODELETE", object);
                    object.forEach(item => {
                        if (Object.keys(item).length > 0) {
                            //console.log(`${RequestFetch.BASE_URL}/api/rh/empresa/registro_patronal/${item.clave_patronal}`);
                            RequestFetch.deleted({
                                url: `${RequestFetch.BASE_URL}/api/rh/empresa/registro_patronal/${item.clave_patronal}`,
                                headers: cabeceras,
                                exito: async (res) => {
                                    //console.log(await res.json())
                                    props.enqueueSnackbar("Registros eliminados", { variant: 'success', preventDuplicate: true, })
                                },
                                fallo: async (res) => {
                                    const respuesta = res.json();
                                    if (!isTokenExpired(respuesta));
                                },
                                error_request: (error) => {
                                    history.push(`/server-error`);
                                }

                            })
                        }
                    })
                }

                const newTel = auxiliar.separeRegistrosPatronales();
                //console.log(newTel, oldContrato);
                if (newTel.registros.length > oldRegistros.length) {
                    //console.log(oldRegistros, newTel.registros);
                    const toUpdate = [];
                    oldRegistros.forEach(e => {
                        const index = newTel.registros.findIndex(i => i.categoria === e.categoria);
                        //console.log("ENCONTRADO", newTel.registros.find(i => i.categoria === e.categoria))
                        if (index >= 0) {
                            toUpdate.push(newTel.registros.splice(index, 1)[0]);
                        }
                    });
                    agregarRegistro(newTel);
                    actualizarRegistro(toUpdate, oldRegistros);
                    setOldRegistros([...newTel.registros, ...toUpdate])
                }
                else if (newTel.registros.length < oldRegistros.length) {
                    //console.log(newTel.registros, oldRegistros);
                    const toUpdate = [];
                    newTel.registros.forEach(e => {
                        const index = oldRegistros.findIndex(i => i.tipo === e.tipo);
                        ////console.log("ENCONTRADO", oldTel.find(i => i.categoria === e.categoria))
                        if (index >= 0) {
                            toUpdate.push(oldRegistros[index]);
                            oldRegistros.splice(index, 1);
                        }
                    });
                    eliminarRegistro(oldRegistros);
                    actualizarRegistro(newTel.registros, toUpdate);
                    setOldRegistros(newTel.registros);
                }
                else {
                    actualizarRegistro(newTel.registros, oldRegistros);
                    setOldRegistros(newTel.registros);
                }
            }

            const PUTContrato = () => {
                console.log(contrato);
                if (contrato.new) {
                    console.log("POST");
                    const forpost = auxiliar.separeContratoPost();

                    if (oldContrato.id_contrato !== undefined && oldContrato.id_contrato !== '') {
                        //console.log("SE HACE EL PUT", oldContrato.id_contrato)
                        RequestFetch.put({
                            url: `${RequestFetch.BASE_URL}/api/rh/empresa/contrato/${oldContrato.id_contrato}`,
                            headers: cabeceras,
                            data: { fecha_fin: moment().format("YYYY/MM/DD") },
                            exito: async (res) => console.log(await res.json()),
                            fallo: async (res) => {
                                const respuesta = res.json();
                                if (!isTokenExpired(respuesta));
                            },
                            error_request: (error) => history.push('/server-error')

                        });
                    }
                    RequestFetch.post({
                        url: `${RequestFetch.BASE_URL}/api/rh/empresa/contrato`,
                        headers: cabeceras,
                        data: forpost,
                        exito: async (res) => {
                            const resp = await res.json();
                            const contrato = resp.contratoEmpresa;
                            setOldContrato({ ...forpost, id_contrato: contrato[0].id_contrato });
                            setContrato(state => {
                                return { ...state, id_contrato: { value: contrato[0].id_contrato }, new: false }
                            });
                            props.enqueueSnackbar("Contrato creado", { variant: 'success', preventDuplicate: true, })
                        },
                        fallo: async (res) => {
                            const respuesta = res.json();
                            if (!isTokenExpired(respuesta));
                        },
                        error_request: (error) => history.push('/server-error')
                    });

                    const newform = new FormData();
                    newform.append('estatus', 'A');
                    const newHeaders = { ...cabeceras };
                    newHeaders['Content-Type'] = "multipart/form-data";

                    RequestFetch.putForm({
                        url: `${RequestFetch.BASE_URL}/api/rh/empresa/${informacion.id_empresa.value}`,
                        headers: newHeaders,
                        data: newform,
                        exito: async (res) => {
                            const { updated } = await res.json();
                            if (updated)
                                props.enqueueSnackbar("Identificador creado", { variant: 'success', preventDuplicate: true, })
                        },
                        fallo: async (res) => {
                            const respuesta = res.json();
                            if (!isTokenExpired(respuesta));
                        },
                        error_request: (error) => {
                            history.push('/server-error')
                        }
                    });
                }
                else {
                    //console.log("PUT");
                    const cont = auxiliar.separeContrato();
                    const forpost = auxiliar.separeContratoPost();

                    if (!informacion.es_interna.value) {
                        if (cont !== null)
                            RequestFetch.put({
                                url: `${RequestFetch.BASE_URL}/api/rh/empresa/contrato/${contrato.id_contrato.value}`,
                                headers: cabeceras,
                                data: cont,
                                exito: async (res) => {
                                    props.enqueueSnackbar("Contrato actualizado", { variant: 'success', preventDuplicate: true, })
                                    setOldContrato({ ...oldContrato, ...cont })
                                },
                                fallo: async (res) => {
                                    const respuesta = res.json();
                                    if (!isTokenExpired(respuesta));
                                },
                                error_request: (error) => {
                                    history.push('/server-error')
                                }
                            });
                        else
                            RequestFetch.post({
                                url: `${RequestFetch.BASE_URL}/api/rh/empresa/contrato`,
                                headers: cabeceras,
                                data: forpost,
                                exito: async (res) => {
                                    const resp = await res.json();
                                    const contrato = resp.contratoEmpresa;
                                    setOldContrato({ ...forpost, id_contrato: contrato[0].id_contrato });
                                    setContrato(state => {
                                        return { ...state, id_contrato: { value: contrato[0].id_contrato }, new: false }
                                    });
                                    props.enqueueSnackbar("Contrato creado", { variant: 'success', preventDuplicate: true, })
                                },
                                fallo: async (res) => {
                                    const respuesta = res.json();
                                    if (!isTokenExpired(respuesta));
                                },
                                error_request: (error) => history.push('/server-error')
                            });
                    }

                }

            }



            if (auxiliar.ValidaAll()) {
                if (!loading) {
                    setLoading(true);
                    timer.current = setTimeout(() => {
                        setLoading(false);
                        props.enqueueSnackbar("Actualizado", { variant: 'success', preventDuplicate: true, });
                    }, 2000);
                }
                if (repre.existe.value) {
                    PUTSendEmpresa();
                    PUTSendEmpresaTel();
                    PUTContrato();
                    if (informacion.es_interna.value === 'true') {
                        POSTSendRegistros(informacion.id_empresa.value)
                    }
                }
                else {
                    POSTSendReprese();
                }
            }

        }

        const deleted = (type) => {

            //console.log("Eliminando")
            const newform = new FormData();
            newform.append('estatus', type);

            const newHeaders = { ...cabeceras };
            newHeaders['Content-Type'] = "multipart/form-data";

            RequestFetch.putForm({
                url: `${RequestFetch.BASE_URL}/api/rh/empresa/${informacion.id_empresa.value}`,
                headers: cabeceras,
                data: newform,
                exito: async (res) => {
                    const respuesta = await res.json();
                    const { estatus } = respuesta[0];

                    if (estatus === type) {
                        if (type === 'B') {
                            props.enqueueSnackbar("Empresa dada de baja", { variant: 'warning', preventDuplicate: true, })
                        }
                        else {
                            props.enqueueSnackbar("Empresa activada", { variant: 'info', preventDuplicate: true, })
                        }
                        setOldData({ ...oldData, estatus: type })
                    }
                },
                fallo: async (res) => {
                    const respuesta = res.json();
                    if (!isTokenExpired(respuesta));
                },
                error_request: async (error) => {
                    history.push("/server-error");
                }
            });
        }

        return {
            send, next, back, deleted
        }
    })();

    React.useEffect(() => {
        let mounted = true;

        const clearEmpre = (empresa, step, fnStep) => {
            const object = { ...step };
            const keys = Object.keys(object);
            keys.forEach((key) => {

                if (key.includes('fecha')) {
                    object[key] = {
                        value: empresa[key] !== undefined && empresa[key] !== null ? moment(empresa[key]) : '',
                        verify: true
                    }
                }
                else if (key === 'identificador') {
                    object[key] = empresa[key];
                }
                else if (key === 'entidad') {
                    object[key] = {
                        value: isCode(empresa[key]) ? empresa[key] : CodeByEstado(empresa[key]),
                        verify: true
                    }
                }
                else if (key === 'es_interna')
                    object[key] = {
                        value: empresa[key] !== undefined && empresa[key] !== null ? empresa[key] + "" : '',
                        verify: true
                    }
                else if (key !== 'fileImg')
                    object[key] = {
                        value: empresa[key] !== undefined && empresa[key] !== null ? empresa[key] : '',
                        verify: true
                    }
            })
            fnStep(object);
        }
        const fallo = async (res) => {
            const respuesta = res.json();
            if (!isTokenExpired(respuesta));
        }

        const sendEmpresa = () => {
            RequestFetch.get({
                url: `${RequestFetch.BASE_URL}/api/rh/empresa?query=${RequestFetch.EMPRESA_ALL}&id_empresa=${id}`,
                headers: cabeceras,
                exito: async (res) => {
                    try {
                        const respuesta = await res.json();
                        let empresa = respuesta.empresas[0];
                        if (mounted) {
                            if (empresa !== undefined) {
                                if (rol === 'G') {
                                    if (!permisos.find(e => e === empresa.id_empresa))
                                        throw new Error("Not Find")
                                }
                                sendRepre(empresa.id_representante);
                                setOldData(empresa);
                                clearEmpre(empresa, informacion, setInformacion);
                                clearEmpre(empresa, direccion, setDireccion);
                                clearEmpre(empresa, contacto, setContacto);
                                if (empresa.es_interna) {
                                    sendRegistroPatronal();
                                }
                            } else {
                                history.push('/nomina/page-not-found')
                            }
                        }
                    } catch (error) {
                        history.push('/nomina/page-not-found')
                    }
                },
                fallo: fallo,
                error_request: (error) => {
                    history.push("/server-error");
                }
            });
        }

        const sendEmpresaTel = () => {
            RequestFetch.get({
                url: `${RequestFetch.BASE_URL}/api/rh/empresa/telefono/${id}`,
                headers: cabeceras,
                exito: async (res) => {
                    const respuesta = await res.json();
                    if (mounted) {
                        const newTel = [{ value: '', verify: '', tipo: 'C', helperText: '' }, { value: '', verify: '', tipo: 'F', helperText: '' }];
                        const oldTel = respuesta.telefonos;

                        respuesta.telefonos.forEach(item => {
                            switch (item.tipo) {
                                case 'C':
                                    newTel[0] = { value: item.numero, verify: true, tipo: item.tipo, helperText: '' };
                                    break;
                                case 'F':
                                    newTel[1] = { value: item.numero, verify: true, tipo: item.tipo, helperText: '' };
                                    break;
                                default:
                                    newTel.push({ value: item.numero, verify: true, tipo: item.tipo, helperText: '' });
                                    break;
                            }
                        });


                        setEmpresaTel(newTel);
                        setOldTel(oldTel)
                    }
                },
                fallo: fallo,
                error_request: (error) => {
                    history.push("/server-error");
                }
            });
        }

        const clearRepre = (representante) => {
            const keys = Object.keys(representante);
            const newRepre = { ...representante };
            keys.forEach((key) => newRepre[key] = {
                value: representante[key] !== null && representante[key] !== null ? representante[key] : '',
                verify: true
            });
            return newRepre;
        }

        const sendRepre = (id) => {
            RequestFetch.get({
                url: `${RequestFetch.BASE_URL}/api/rh/representante/${id}?query=${RequestFetch.REPRESENTANTE_ALL}`,
                headers: cabeceras,
                exito: async (res) => {
                    const respuesta = await res.json();

                    if (mounted) {
                        //console.log(mounted, respuesta[0]);
                        const newR = clearRepre(respuesta[0]);
                        newR['existe'] = { value: true };
                        setRepre(newR);
                        setBackdrop(false);
                    }
                },
                fallo: fallo,
                error_request: (error) => {
                    history.push("/server-error")
                }
            });
            RequestFetch.get({
                url: `${RequestFetch.BASE_URL}/api/rh/representante/telefono/${id}`,
                headers: cabeceras,
                exito: async (res) => {
                    const respuesta = await res.json();

                    const newTel = [
                        { value: '', verify: '', tipo: 'C', helperText: '' },
                        { value: '', verify: '', tipo: 'F', helperText: '' },
                    ]

                    respuesta.telefonos.forEach(item => {
                        switch (item.tipo) {
                            case 'C': newTel[0] = { value: item.numero, verify: true, tipo: item.tipo, helperText: '' };
                                break;
                            case 'F': newTel[1] = { value: item.numero, verify: true, tipo: item.tipo, helperText: '' }
                                break;
                            default: newTel.push({ value: item.numero, verify: true, tipo: item.tipo, helperText: '' })
                                break;
                        }
                    })
                    setRepreTel(newTel);

                },
                fallo: fallo,
                error_request: (error) => {
                    history.push("/server-error")
                }
            });
        }

        const sendContrato = () => {
            RequestFetch.get({
                url: `${RequestFetch.BASE_URL}/api/rh/empresa/contrato?id_empresa=${id}`,
                headers: cabeceras,
                exito: async (res) => {
                    const respuesta = await res.json();
                    let { contratoEmpresa } = respuesta;
                    if (contratoEmpresa.length > 0) {
                        let oldDate = moment('1900', 'YYYY');
                        let newContrato = {};
                        contratoEmpresa.forEach(c => {
                            const actual = moment(c.fecha_fin);
                            if ((actual.isAfter(oldDate) && actual.isAfter(moment())) || c.fecha_fin == null) newContrato = c;
                        });
                        let oldContrato = { new: false }
                        if (Object.keys(newContrato).length > 0) {
                            Object.keys(newContrato).forEach(e => {
                                if (e.includes('fecha')) {
                                    const value = moment(newContrato[e]);
                                    oldContrato[e] = value;
                                    newContrato[e] = { value, verify: true }
                                }
                                else if (e === 'comision') {
                                    oldContrato[e] = parseInt(newContrato[e] * 100);
                                    newContrato[e] = { value: parseInt(newContrato[e] * 100), verify: true };
                                }
                                else {
                                    oldContrato[e] = newContrato[e];
                                    newContrato[e] = { value: newContrato[e], verify: true };
                                }
                            });
                            setContrato(newContrato);
                            setOldContrato(oldContrato);
                        }
                    }
                },
                fallo: fallo,
                error_request: (error) => {
                    history.push("/server-error");
                }
            });
        }

        const sendRegistroPatronal = () => {
            RequestFetch.get({
                url: `${RequestFetch.BASE_URL}/api/rh/empresa/registro_patronal?id_empresa=${id}`,
                headers: cabeceras,
                exito: async (res) => {
                    const respuesta = await res.json();
                    if (mounted) {
                        const newReg = []
                        const oldReg = [...respuesta.registros];
                        if (respuesta.registros.length > 0)
                            respuesta.registros.forEach(item => {
                                const { id_empresa, ...newItem } = item;
                                newReg.push({ ...newItem, verify: true, helperText: "" })
                            });
                        setRegistros(newReg);
                        setOldRegistros(oldReg)
                    }
                },
                fallo: fallo,
                error_request: (error) => {
                    history.push("/server-error");
                }
            });
        }

        sendEmpresa();
        sendEmpresaTel();
        sendContrato();

        return () => mounted = false;
        // eslint-disable-next-line
    }, [])




    React.useEffect(() => {
        const cl = direccion.colonia.value;
        const rs = (informacion.razon_comercial.value + informacion.razon_social.value + informacion.rfc.value);
        const fc = moment(informacion.fecha_constitucion.value).format('DDMMYYYY');
        const mu = direccion.municipio.value;
        const en = direccion.entidad.value;
        if (rs !== '' && fc !== '' && mu !== '' && en !== '') {
            const partRs = rs.split(" ");
            const partMu = mu.split(" ");
            const partCo = (cl + "GETCONSULTORIA").split(" ");

            let part1 = '';
            if (partRs.length < 2) part1 = partRs[0].substring(0, 4);
            else part1 = partRs[0].substring(0, 2) + partRs[1].substring(0, 2);

            let part2 = '';
            if (partMu.length < 2) part2 = partMu[0].substring(0, 2);
            else part2 = partMu[0].charAt(0) + partMu[1].charAt(0);


            let part3 = '';
            if (partCo.length < 2) part3 = partCo[0].substring(0, 2);
            else part3 = partCo[0].charAt(0) + partCo[1].charAt(0);

            let part4 = fc.substring(fc.length, fc.length - 2);
            const ident = (part1 + en + part2 + part3 + part4).toUpperCase();

            if (identificador !== ident || identificador === null) {
                setIdentificador(ident);
            }
        }
        // eslint-disable-next-line
    }, [
        informacion.razon_social.value,
        informacion.rfc.value,
        informacion.razon_comercial.value,
        informacion.id_regimen_capital.value,
        informacion.fecha_constitucion.value,
        direccion.municipio.value,
        direccion.entidad.value,
        direccion.colonia.value
    ])


    return (
        <React.Fragment>
            <Card className={classes.root}>
                <CardContent className={classes.cardContent}>
                    <List
                        component="nav"
                        aria-labelledby="nested-list-subheader"
                        subheader=
                        {
                            <ListSubheader className={classes.subheader} component="div" display="block">

                                {!onlySee &&
                                    <IconButton className={clsx(classes.back, {
                                        [classes.butonHide]: onlySee
                                    })}
                                        disabled={onlySee} onClick={() => { history.goBack() }}>
                                        <KeyboardBackspaceIcon />
                                    </IconButton>
                                }
                                <Typography className={classes.title} variant="button" gutterBottom>
                                    {`${onlySee ? '' : 'Editando '}`} empresa
                                </Typography>
                                {
                                    !onlySee &&
                                    <ExpandButton onClick={() => { setPart({ ...part, expandable: !part.expandable }) }} open={part.expandable} />
                                }
                            </ListSubheader>
                        }
                        className={classes.root}>
                        <ListItem className={classes.header} button onClick={handleClick.bind(this, 0)}>
                            <ListItemIcon>
                                <InfoIcon />
                            </ListItemIcon>
                            <ListItemText primary="Informacion" />
                            {part.actual === 0 ? <ExpandLess /> : <ExpandMore />}
                        </ListItem>
                        <Collapse in={part.actual === 0 || part.expandable || onlySee} timeout="auto" >
                            <Informacion
                                setContrato={setContrato}
                                data={informacion}
                                setData={setInformacion}
                                buttons={buttons}
                                expandable={part.expandable || onlySee}
                                edit={false}
                                onlySee={onlySee}
                            />
                        </Collapse>
                        <ListItem className={classes.header} button onClick={handleClick.bind(this, 1)}>
                            <ListItemIcon>
                                <RoomIcon />
                            </ListItemIcon>
                            <ListItemText primary="Dirección" />
                            {part.actual === 1 ? <ExpandLess /> : <ExpandMore />}
                        </ListItem>
                        <Collapse in={part.actual === 1 || part.expandable || onlySee} timeout="auto"  >
                            <Direccion
                                data={direccion}
                                setData={setDireccion}
                                buttons={buttons}
                                expandable={part.expandable || onlySee}
                                edit={false}
                                onlySee={onlySee}

                            />
                        </Collapse>
                        <ListItem className={classes.header} button onClick={handleClick.bind(this, 2)}>
                            <ListItemIcon>
                                <AccountCircleIcon />
                            </ListItemIcon>
                            <ListItemText primary="Contacto" />
                            {part.actual === 2 ? <ExpandLess /> : <ExpandMore />}
                        </ListItem>
                        <Collapse in={part.actual === 2 || part.expandable || onlySee} timeout="auto"  >
                            <Contacto
                                data={contacto}
                                setData={setContacto}
                                buttons={buttons}
                                expandable={part.expandable || onlySee}
                                phones={empresaTel}
                                setPhones={setEmpresaTel}
                                onlySee={onlySee}

                            />
                        </Collapse>

                        <ListItem className={classes.header} button onClick={handleClick.bind(this, 3)}>
                            <ListItemIcon>
                                <AssignmentTurnedInIcon />
                            </ListItemIcon>
                            <ListItemText primary="Contrato" />
                            {part.actual === 3 ? <ExpandLess /> : <ExpandMore />}
                        </ListItem>
                        <Collapse in={part.actual === 3 || part.expandable} timeout="auto" unmountOnExit>
                            <Contrato
                                registros={registros}
                                setRegistros={setRegistros}
                                setOldContrato={setOldContrato}
                                isInterna={informacion.es_interna.value}
                                data={contrato}
                                setData={setContrato}
                                editable
                                expandable={part.expandable}
                                buttons={buttons} />
                        </Collapse>

                        <ListItem className={classes.header} button onClick={handleClick.bind(this, 4)}>
                            <ListItemIcon>
                                <WorkIcon />
                            </ListItemIcon>
                            <ListItemText primary="Representante" />
                            {part.actual === 4 ? <ExpandLess /> : <ExpandMore />}
                        </ListItem>
                        <Collapse in={part.actual === 4 || part.expandable || onlySee} timeout="auto"  >
                            <Representante
                                data={repre}
                                setData={setRepre}
                                phones={repreTel}
                                setPhones={setRepreTel}
                                expandable={part.expandable || onlySee}
                                loading={loading}
                                edit={false}
                                buttons={buttons}
                                onlySee={onlySee}
                            />
                        </Collapse>
                    </List>
                </CardContent>

                <CardActions>
                    <Grid container>
                        <Grid
                            item
                            sm={6}
                            container
                            direction="row"
                            justify="flex-start"
                            alignItems="center">
                            <Typography style={{ marginLeft: 24 }} variant="button" color="initial">{`Identificador: ${identificador === null ? "SIN ID" : identificador}`}</Typography>
                        </Grid>
                        {
                            !onlySee &&
                            <Grid
                                item
                                sm={6}
                                container
                                direction="row"
                                justify="flex-end"
                                alignItems="center">

                                <div className={classes.wrapper}>
                                    {
                                        oldData.estatus === 'A' ?
                                            <Button
                                                color="secondary"
                                                className={classes.delete}
                                                startIcon={<DeleteIcon />}
                                                onClick={() => setDialog({ open: true, type: 'B' })}>
                                                {"Eliminar"}
                                            </Button>
                                            :
                                            <Button
                                                color="primary"
                                                style={{ color: green[500], marginRight: 5 }}
                                                startIcon={<ReplayIcon />}
                                                onClick={() => setDialog({ open: true, type: 'A' })}>
                                                {"Activar Empresa"}
                                            </Button>
                                    }

                                    {
                                        oldData.estatus !== 'B' &&
                                        <Button
                                            variant="contained"
                                            className={classes.save}
                                            disabled={loading}
                                            endIcon={<SaveIcon />}
                                            onClick={buttons.send}>
                                            {"Actualizar"}
                                            {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                                        </Button>
                                    }
                                </div>
                            </Grid>
                        }
                    </Grid>
                </CardActions>
            </Card>
            <Grid container spacing={3}>
                {
                    informacion.rfc.value !== '' &&
                    <Grid item xs={12} sm={6} xl={4}>
                        <Documentos id={id} tipo={"empresa"} onlySee={onlySee} />
                    </Grid>
                }
                {
                    registros.length > 0 &&
                    informacion.es_interna.value === 'true' &&
                    <Grid item xs={12} sm={6} xl={4}>
                        <PrimaRiesgo idEmpresa={informacion.id_empresa.value} registrosPatronales={registros} onlySee={onlySee} />
                    </Grid>
                }
                {contrato.id_contrato.value !== '' &&
                    <Grid item xs={12} sm={6} xl={4}>
                        <CalendarioNominas idContrato={contrato.id_contrato.value} periodo={contrato.periodo_calculo.value} onlySee={onlySee} />
                    </Grid>
                }
            </Grid>

            <Snackbar open={toast.open} autoHideDuration={5000} onClose={handleClose}>
                <Alert onClose={handleClose} severity={toast.severity}>
                    <AlertTitle>{toast.title}</AlertTitle>
                    {toast.message}
                </Alert>
            </Snackbar>

            <Dialog
                open={dialog.open}
                TransitionComponent={Transition}
                onClose={() => setDialog({ open: false, type: '' })}>
                <DialogTitle>{`${dialog.type === 'B' ? 'Eliminar' : 'Activar'}`}</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Está apunto de {`${dialog.type === 'B' ? 'dar de baja' : 'activar'}`} a la empresa

                        ¿Está seguro que desea continuar?
                        </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setDialog({ open: false, type: '' })}>Cancelar</Button>
                    <Button onClick={() => {
                        buttons.deleted(dialog.type)
                        setDialog({ open: false, type: '' });
                    }}
                        color="secondary">{`${dialog.type === 'B' ? 'Eliminar' : 'Activar'}`}</Button>
                </DialogActions>
            </Dialog>


            <Backdrop className={classes.backdrop} open={backdrop} >
                <CircularProgress color="inherit" />
            </Backdrop>
        </React.Fragment>
    );
}
export default withSnackbar(EditEmpresa);