import React from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogActions from '@material-ui/core/DialogActions';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useTheme } from '@material-ui/core/styles';

//MATERIAL UI
import {
    Box,
    Grid,
    MenuItem,
    Button,
    RadioGroup,
    FormControl,
    FormLabel,
    FormControlLabel,
    Radio,
    LinearProgress
} from '@material-ui/core';

//VALIDATIONS
import { Formik, Field, Form } from 'formik'
import * as Yup from 'yup';

//HOOKS
import { NominaContext } from '../Nomina'

//EXTRA LIB
import moment from 'moment';
import { DatePickerField, InputFormikUI } from '../FormikUI';

import { put } from './helperSettings';
import { SessionContext } from '../../../../routes/adminrh/CustomRoutes'

export default function Settings({ open, setOpen }) {
    const { cabeceras, isTokenExpired } = React.useContext(SessionContext);
    const theme = useTheme();
    const [loading, setLoading] = React.useState(false);
    const fullScreen = useMediaQuery(theme.breakpoints.down('xs'));

    const { perido_fin, generalSettings, setGeneralSettings, idNomina } = React.useContext(NominaContext);
    const { identificador, estatus, fecha_pago, aplico_costo_social, imss_aplicado, isr_aplicado, retencion_aplicada } = generalSettings;
    const newSettings = { identificador, estatus, fecha_pago, aplico_costo_social, imss_aplicado, isr_aplicado, retencion_aplicada }

    return (
        <Formik
            initialValues={newSettings}
            validationSchema={
                Yup.object().shape({
                    identificador: Yup.string()
                        .max(50, 'Muy largo!')
                        .required('Requerido'),
                    estatus: Yup.string()
                        .required('Requerido'),
                    fecha_pago: Yup.date().nullable(),
                    aplico_costo_social: Yup.string()
                        .max(1, 'Muy largo!')
                        .required('Requerido'),
                    imss_aplicado: Yup.bool().required('Requerido'),
                    isr_aplicado: Yup.bool().required('Requerido'),
                    retencion_aplicada: Yup.bool().required('Requerido'),
                })
            }
            onSubmit={values => {
                setLoading(true);
                const { fecha_pago, ...newValues } = values;
                if (fecha_pago)
                    newValues['fecha_pago'] = moment(fecha_pago).format('YYYY-MM-DD');

                console.log(newValues);
                put(idNomina, newValues, cabeceras).then(response => {
                    if (!isTokenExpired(response)) {
                        setGeneralSettings({ ...generalSettings, ...values });
                        setOpen(false)
                    }
                }).catch(error => {
                    console.log(error);
                })
            }}
        >
            {
                ({ values, handleSubmit, handleReset, setFieldValue }) => {
                    return (
                        <Form>
                            <Dialog fullScreen={fullScreen} open={open} aria-labelledby="form-dialog-title">
                                {loading && <LinearProgress color="secondary" />}
                                <DialogTitle id="form-dialog-title">Ajuste de la nómina</DialogTitle>
                                <DialogContent style={{ paddingLeft: 0, paddingRight: 0 }}>
                                    <Box p={3}>
                                        <Grid container spacing={2}>
                                            <Grid item xs={8}>
                                                <Field name="identificador" placeholder="Ej. Nomina periodo..." label="Nómina" component={InputFormikUI} />
                                            </Grid>
                                            <Grid item xs={4}>
                                                <Field name="estatus" defaultValue='' label="Estatus" select component={InputFormikUI}  >
                                                    <MenuItem value='' disabled>Selecciona</MenuItem>
                                                    <MenuItem value="P">Prenómina</MenuItem>
                                                    <MenuItem value="N">Nómina</MenuItem>
                                                    <MenuItem value="A">Pagada</MenuItem>
                                                </Field>
                                            </Grid>
                                            <Grid item xs={6}>
                                                <Field component={DatePickerField}
                                                    name="fecha_pago"
                                                    label="Fecha Pago"
                                                    minDate={moment(perido_fin)}
                                                    maxDate={moment(perido_fin).add(5, 'days')}
                                                />
                                            </Grid>
                                            <Grid item xs={6}>
                                                <Field name="aplico_costo_social" defaultValue='' label="Costo Social" select component={InputFormikUI}>
                                                    <MenuItem value='' disabled>Seleccionar</MenuItem>
                                                    <MenuItem value='N'>No Aplicar</MenuItem>
                                                    <MenuItem value="S">Semanal</MenuItem>
                                                    <MenuItem value="Q">Quincenal</MenuItem>
                                                    <MenuItem value="M">Mensual</MenuItem>
                                                </Field>
                                            </Grid>
                                            <Grid item xs={4}>
                                                <FormControl component="fieldset">
                                                    <FormLabel>IMSS aplicado a:</FormLabel>
                                                    <RadioGroup name="imss_aplicado" value={values.imss_aplicado}
                                                        onChange={({ target: { value } }) => setFieldValue('imss_aplicado', (value === 'true'))}>
                                                        <FormControlLabel value={false} control={<Radio />} label="Empleado" />
                                                        <FormControlLabel value={true} control={<Radio />} label="Patrón" />
                                                    </RadioGroup>
                                                </FormControl>
                                            </Grid>
                                            <Grid item xs={4}>
                                                <FormControl component="fieldset">
                                                    <FormLabel>ISR aplicado a:</FormLabel>
                                                    <RadioGroup name="isr_aplicado" value={values.isr_aplicado}
                                                        onChange={({ target: { value } }) => setFieldValue('isr_aplicado', (value === 'true'))}>
                                                        <FormControlLabel value={false} control={<Radio />} label="Empleado" />
                                                        <FormControlLabel value={true} control={<Radio />} label="Patrón" />
                                                    </RadioGroup>
                                                </FormControl>
                                            </Grid>
                                            <Grid item xs={4}>
                                                <FormControl component="fieldset">
                                                    <FormLabel>Retención del 6%:</FormLabel>
                                                    <RadioGroup name="retencion_aplicada" value={values.retencion_aplicada}
                                                        onChange={({ target: { value } }) => setFieldValue('retencion_aplicada', (value === 'true'))}>
                                                        <FormControlLabel value={false} control={<Radio />} label="No Aplicar" />
                                                        <FormControlLabel value={true} control={<Radio />} label="Aplicar" />
                                                    </RadioGroup>
                                                </FormControl>
                                            </Grid>
                                        </Grid>
                                    </Box>
                                </DialogContent>
                                <DialogActions>
                                    <Button onClick={() => {
                                        handleReset();
                                        setOpen(false)
                                    }}>Cerrar</Button>
                                    <Button type="submit"
                                        disabled={loading}
                                        onClick={() => {
                                            handleSubmit();
                                        }} color="secondary">Guardar</Button>
                                </DialogActions>
                            </Dialog>
                        </Form>
                    )
                }
            }
        </Formik>
    )
}