import React, { useState } from 'react';
import { Button, CssBaseline, TextField, FormControlLabel, Checkbox, Paper, Box, Grid, Typography, InputAdornment, IconButton, CircularProgress, CardMedia } from '@material-ui/core';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import { BASE_URL } from '../../../libraries/RequestFetch';
import { useHistory } from 'react-router-dom';
import { Link } from "react-router-dom";
import Copyright from '../ExtraComponents/Copyright';
import Cryptr from 'cryptr';
import { useStylesSignIn } from './style';
import { keyStorage, keyCrypt } from '../../../libraries/LoginNomina';
import Validacion from '../../../libraries/Validacion'
const initialState = {
    email: { value: '', verify: '' },
    password: { value: '', verify: '' },
    savesession: false,
    showPassword: false
}

export default function Login(props) {
    const classes = useStylesSignIn();
    const history = useHistory();
    const [loading, setLoading] = React.useState(false);
    const [olvide, setOlvide] = React.useState(false);
    const [message, setMessage] = React.useState();
    const timer = React.useRef();

    const [datos, setDatos] = useState(initialState);

    const handleMouseDownPassword = (event) => {
        event.preventDefault();
    };


    const validarCampos = () => {
        var newData = { ...datos };
        var keys = 0;
        newData['email'].verify = `${datos['email'].verify}`.length > 0 && datos['email'].verify && Validacion.esCorreo(datos['email'].value)
        if (!olvide) {
            newData['password'].verify = `${datos['password'].verify}`.length > 0 && datos['password'].verify
            keys = (datos['email'].verify && datos['password'].verify);
        } else {
            keys = datos['email'].verify
        }
        setDatos(newData);
        return keys;
    }

    const onChangeText = (event) => {
        const key = `${event.target.id}`;
        const { value } = event.target;
        var newData = { ...datos };
        newData[key] = { ...newData[key], value: value, verify: (`${value}`.length > 0) };
        setDatos(newData);
    }

    const handleSaveSession = async (loguser) => {
        const cryptr = new Cryptr(keyCrypt);
        const contentEncrypt = cryptr.encrypt(JSON.stringify(loguser));
        localStorage.setItem(keyStorage, contentEncrypt);
        history.push('/nomina');
    }

    const showMessage = (isError, message) => {
        setMessage({ type: isError, message });
        setLoading(false);
        timer.current = setTimeout(() => {
            setMessage();
        }, 5000);
    }

    const handlePost = (event) => {
        //validar los campos
        event.preventDefault();

        if (validarCampos()) {
            setLoading(true);
            const body = {
                email: datos.email.value,
                password: datos.password.value,
                savesession: datos.savesession
            }

            fetch(BASE_URL + '/api/rh/usuario/ingresar',
                {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify(body)
                })
                .then(resp => resp.json())
                .then(response => {
                    console.log(response);
                    const { error } = response;
                    if (error || response.rol === 'E') {
                        showMessage(true, "Usuario o contraseña incorrectos");
                    }
                    else {
                        handleSaveSession(response);
                    }
                }).catch(error => {
                    showMessage(true, "No hay conexión con el servidor");
                })
        }
    }

    const handleOlvide = (event) => {
        event.preventDefault();
        if (validarCampos()) {
            setLoading(true);
            fetch(BASE_URL + '/api/rh/usuario/solicitar',
                {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify({ email: datos.email.value })
                })
                .then(resp => resp.json())
                .then(response => {
                    const { result, error } = response;
                    console.log(result, error)
                    if (result) {
                        showMessage(
                            false,
                            "Se ha enviado un correo con las instrucciones para reestablecer tu contraseña"
                        );
                        setOlvide(false);
                    }
                    else if (error)
                        showMessage(true, error.message);
                    setLoading(false);
                }).catch(error => {
                    showMessage(true, "No hay conexión con el servidor");
                })
        }
    }

    return (
        <Grid container component="main" className={classes.root}>
            <CssBaseline />
            <Grid item xs={false} sm={4} md={7} className={classes.image} />
            <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
                <div className={classes.paper}>
                    <Link to="/">
                        <CardMedia
                            className={classes.get}
                            image={'https://getconsultoria.com/wp-content/uploads/2018/04/logo_GET_slider-01-645x774-2.png'}
                            title="Get Consultoria"
                        /> </Link>
                    <Typography component="h1" variant="h5">
                        Ingresar
                    </Typography>

                    <form className={classes.form} noValidate>
                        {
                            olvide &&
                            <Typography variant="body1" style={{ marginTop: '3vh' }}>
                                Enviaremos un enlace para reestablecer tu contraseña al correo que proporciones
                            </Typography>
                        }
                        <TextField
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            id="email"
                            label="Correo electrónico"
                            name="email"
                            autoComplete="email"
                            autoFocus
                            onChange={onChangeText}
                            error={datos.email.verify !== '' ? !datos.email.verify : false}
                            helperText={datos.email.verify === false ? 'Campo requerido' : ''}
                        />
                        {!olvide &&
                            <TextField
                                variant="outlined"
                                margin="normal"
                                required
                                fullWidth
                                name="password"
                                label="Contraseña"
                                type={datos.showPassword ? 'text' : 'password'}
                                id="password"
                                autoComplete="current-password"
                                onChange={onChangeText}
                                error={datos.password.verify !== '' ? !datos.password.verify : false}
                                helperText={datos.password.verify === false ? 'Campo requerido' : ''}
                                InputProps={{
                                    endAdornment: (
                                        <InputAdornment position="end">
                                            <IconButton
                                                aria-label="toggle password visibility"
                                                onClick={() => { setDatos({ ...datos, showPassword: !datos.showPassword }) }}
                                                onMouseDown={handleMouseDownPassword}
                                            >
                                                {datos.showPassword ? <Visibility /> : <VisibilityOff />}
                                            </IconButton>
                                        </InputAdornment>
                                    ),
                                }}
                            />
                        }
                        {!olvide &&
                            <FormControlLabel
                                control={<Checkbox value={datos.savesession}
                                    onChange={() => {
                                        setDatos({ ...datos, savesession: !datos.savesession })
                                    }} color="primary" />}
                                label="Recordarme"
                            />
                        }

                        <Grid container >
                            <Button
                                disabled={loading}
                                fullWidth
                                variant="contained"
                                color="primary"
                                type="submit"
                                className={classes.submit}
                                onClick={e => {
                                    !olvide ? handlePost(e) : handleOlvide(e)
                                }}
                            >
                                {!olvide ? 'Ingresar' : 'Enviar correo'}
                            </Button>
                        </Grid>
                        {
                            !loading && <Typography
                                variant="body1"
                                color="initial"
                                display="block"
                                className={classes.olvide}
                                onClick={() => setOlvide(!olvide)}>
                                {!olvide ?
                                    'Olvidaste tu contraseña?'
                                    :
                                    'Volver'
                                }
                            </Typography>}

                        {loading && <CircularProgress size={34} className={classes.buttonProgress} />}

                        {message &&
                            <Typography
                                variant="body1"
                                display="block"
                                style={{ textAlign: 'center', color: `${message.type ? 'red' : 'green'}` }}>
                                {message.message}
                            </Typography>}

                        <Box mt={5}>
                            <Copyright />
                        </Box>

                    </form>
                </div>
            </Grid>
        </Grid>
    )
}