import React from 'react'
import MaterialTable from 'material-table';
import { TextField, Typography } from '@material-ui/core'
import RequestFetch from '../../../libraries/RequestFetch'
import { Snackbar } from '@material-ui/core';
import { AlertTitle } from '@material-ui/lab';
import MuiAlert from '@material-ui/lab/Alert';
import { useHistory } from 'react-router-dom';
import { localization, actions, options } from '../../../libraries/ConfigCatalog';
import { SessionContext } from '../../../routes/adminrh/CustomRoutes'

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

export default function TableGiro() {
    const { cabeceras, isTokenExpired } = React.useContext(SessionContext);
    const history = useHistory();
    const [data, setData] = React.useState([]);
    const [loading, setLoading] = React.useState(false);
    const [toast, setToast] = React.useState({
        open: false,
        message: "",
        severity: "",
        title: ""
    });


    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            setToast({ ...toast, open: false });
            return;
        }
        setToast({ ...toast, open: false });
    };

    const getData = React.useCallback(() => {
        let mounted = true;
        setLoading(true);
        setData([]);

        const _exito = async (res) => {
            const respuesta = await res.json();
            if (mounted) {
                setData(respuesta.giros);
                setLoading(false);
                if (respuesta.giros.length === 0) {
                    setToast({
                        open: true,
                        message: "No existen registros de empresas",
                        severity: "info",
                        title: "Ningun registro encontrado"
                    })
                }
            };
        }

        const _fallo = async (res) => {
            const response = await res.json();
            if (!isTokenExpired(response))
                if (mounted) {
                    setLoading(false);
                    setToast({
                        open: true,
                        message: "Ha ocurrido un error, intenta nuevamente",
                        severity: "error",
                        title: "Uuuups!"
                    })

                };
        }

        if (mounted) {
            RequestFetch.get({
                url: `${RequestFetch.BASE_URL}/api/rh/giro?query=${RequestFetch.GIROS_ALL}`,
                headers: cabeceras,
                exito: _exito,
                fallo: _fallo,
                error_request: () => {
                    history.push(`/server-error`);
                }
            });
        }

        return () => mounted = false;
        // eslint-disable-next-line
    }, [history]);

    actions[0].onClick = getData;
    localization.body.editRow.deleteText = <Typography variant="button" gutterBottom>¿Está seguro que desea continuar?</Typography>




    const columns = [
        { title: 'ID', field: "id_giro", width: 20, editable: 'never', hidden: true },
        {
            title: 'Nombre Giro', field: 'nombre_giro', width: 150, defaultSort: 'asc',
            editComponent: props => (
                <TextField
                    fullWidth
                    size="small"
                    value={props.value || ''}
                    placeholder="Ej. Contrucción"
                    onChange={e => props.onChange(e.target.value)}
                />
            )
        },
        {
            title: 'Descripción', field: 'descripcion', width: 300,
            editComponent: props => (
                <TextField
                    fullWidth
                    size="small"
                    value={props.value || ''}
                    placeholder="Ej. Hacen edificios, casas o todo tipo de edificaciones"
                    onChange={e => props.onChange(e.target.value)}
                />
            )
        }
    ];



    const POST = (giro) => {
        let mounted = true;
        const exito = async res => {
            const respuesta = await res.json();
            if (mounted) {
                giro.id_giro = respuesta.giro[0].id_giro;
                const newData = data;
                newData.push(giro)
                setData(newData);
                setLoading(false);
                setToast({
                    open: true,
                    message: `Giro: ${giro.nombre_giro.toUpperCase()} creado correctamente`,
                    severity: "success",
                    title: "Registro exitoso!"
                });
            }
        }
        const fallo = async res => {
            const response = await res.json();
            if (!isTokenExpired(response))
                if (mounted) {
                    setLoading(false);
                    setToast({
                        open: true,
                        message: `El registro no se ha realizado, intenta de nuevo`,
                        severity: "error",
                        title: "Registro fallido!"
                    });
                }
        }

        const send = () => {
            RequestFetch.post({
                url: `${RequestFetch.BASE_URL}/api/rh/giro`,
                headers: cabeceras,
                data: giro,
                exito: exito,
                fallo: fallo,
                error_request: (error) => {
                    history.push(`/server-error`);
                }
            });
        }

        if (mounted) {
            setLoading(true);
            send();
        }

        return () => mounted = false;
    }

    const PUT = (giro, oldgiro) => {
        let mounted = true;
        const exito = async res => {
            const response = await res.json();
            if (!isTokenExpired(response))
                if (mounted) {
                    const newData = data;
                    const index = newData.indexOf(oldgiro);
                    data[index] = giro;
                    setData(newData);
                    setLoading(false);

                    setToast({
                        open: true,
                        message: `Giro: ${giro.nombre_giro.toUpperCase()} se ha actualizado correctamente`,
                        severity: "success",
                        title: "Actualización exitoso!"
                    });
                }
        }
        const fallo = async res => {
            await res.json();
            if (mounted) {
                setLoading(false);
                setToast({
                    open: true,
                    message: `Giro: ${giro.nombre_giro.toUpperCase()} no ha sido actualizado, intenta de nuevo`,
                    severity: "error",
                    title: "Actualización fallida!"
                });
            }
        }

        const send = () => {
            const putData = {
                nombre_giro: giro.nombre_giro,
                descripcion: giro.descripcion
            }
            RequestFetch.put({
                url: `${RequestFetch.BASE_URL}/api/rh/giro/${giro.id_giro}`,
                headers: cabeceras,
                data: putData,
                exito: exito,
                fallo: fallo,
                error_request: (error) => {
                    history.push(`/server-error`);
                }
            });
        }

        if (mounted) {
            setLoading(true);
            send();
        }
    }

    const DELETE = (giro) => {
        let mounted = true;
        const exito = async res => {
            const response = await res.json();
            if (!isTokenExpired(response))
                if (mounted) {
                    let newData = data;
                    const index = newData.indexOf(giro);
                    data.splice(index, 1);
                    setData(newData);
                    setLoading(false);
                    setToast({
                        open: true,
                        message: `Giro: ${giro.nombre_giro.toUpperCase()} se ha eliminado correctamente`,
                        severity: "success",
                        title: "Eliminación exitosa!"
                    });
                }

        }
        const fallo = async res => {
            await res.json();
            if (mounted) {
                setLoading(false);
                setToast({
                    open: true,
                    message: `Giro: ${giro.nombre_giro.toUpperCase()} tiene registros que dependen de este, primero debes eliminarlos`,
                    severity: "error",
                    title: "Eliminación fallida!"
                });
            }
        }

        const send = () => {
            RequestFetch.deleted({
                url: `${RequestFetch.BASE_URL}/api/rh/giro/${giro.id_giro}`,
                headers: cabeceras,
                exito: exito,
                fallo: fallo,
                error_request: () => {
                    history.push(`/server-error`);
                }
            });
        }

        if (mounted) {
            setLoading(true);
            send();
        }
    }

    React.useEffect(() => {
        getData();
        // eslint-disable-next-line
    }, [])


    return (
        <>
            <MaterialTable
                title="Giros"
                columns={columns}
                localization={localization}
                data={data}
                actions={actions}
                options={options}
                isLoading={loading}
                editable={{
                    onRowAdd: (newData) =>
                        new Promise((resolve, reject) => {
                            setTimeout(() => {
                                if (newData.nombre_giro !== '' && newData.descripcion !== '' && newData.nombre_giro !== undefined && newData.descripcion !== undefined) {
                                    POST(newData);
                                    resolve();
                                }
                                else {
                                    setToast({
                                        open: true,
                                        message: "Rellena los campos \"Nombre Giro\" y \"Descripción\"",
                                        severity: "warning",
                                        title: "Campos vacios!"
                                    })
                                    reject();
                                    return;
                                }
                            }, 600);
                        })
                    ,
                    onRowUpdate: (newData, oldData) =>
                        new Promise((resolve, reject) => {
                            setTimeout(() => {
                                if (newData.nombre_giro !== '' && newData.descripcion !== '' && newData.nombre_giro !== undefined && newData.descripcion !== undefined) {
                                    resolve();
                                    PUT(newData, oldData);
                                } else {
                                    setToast({
                                        open: true,
                                        message: "Rellena los campos \"Nombre Giro\" y \"Descripción\"",
                                        severity: "warning",
                                        title: "Campos vacios!"
                                    })
                                    reject();
                                    return;
                                }
                            }, 600);
                        })
                    ,
                    onRowDelete: (oldData) =>
                        new Promise((resolve) => {
                            setTimeout(() => {
                                resolve();
                                DELETE(oldData);
                            }, 600);
                        })
                    ,
                }}
            />


            <Snackbar open={toast.open} autoHideDuration={6000} onClose={handleClose}>
                <Alert onClose={handleClose} severity={toast.severity}>
                    <AlertTitle>{toast.title}</AlertTitle>
                    {toast.message}
                </Alert>
            </Snackbar>
        </>
    );
}