import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Dialog from '@material-ui/core/Dialog';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
import Grid from '@material-ui/core/Grid'
import EditRepresentante from './EditRepresentante';


const useStyles = makeStyles((theme) => ({
    fullScreen: {
        [theme.breakpoints.down('sm')]: {
            marginLeft: '0px',
        },
        [theme.breakpoints.up('md')]: {
            marginLeft: '50%',
        }
    },
    appBar: {
        position: 'relative',
        backgroundImage: 'linear-gradient(45deg, #D84315, #F57C00)',
    },
    title: {
        marginLeft: theme.spacing(2),
        flex: 1,
    },
    container: {
        padding: theme.spacing(3),
        background: '#fafafa',
    },
}));


const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="left" ref={ref} {...props} />;
});

export default function SeeRepresentante(props) {
    const { open, setOpen } = props;
    const classes = useStyles();

    return (
        <Dialog className={classes.fullScreen} fullScreen open={open} onClose={() => setOpen(false)} TransitionComponent={Transition}>
            <AppBar position="fixed" className={classes.appBar}>
                <Toolbar>
                    <IconButton edge="start" color="inherit" onClick={() => setOpen(false)} aria-label="close">
                        <CloseIcon />
                    </IconButton>
                    <Typography variant="h6" className={classes.title}>
                        Ver Representante
                    </Typography>
                </Toolbar>
            </AppBar>
            <Grid container className={classes.container}>
                <EditRepresentante id={props.data.id_representante} onlySee />
            </Grid>
        </Dialog>
    )
}
