import React, { useState, useContext } from 'react';
import { Grid, TextField, MenuItem, Button, Typography } from '@material-ui/core';
import { configInput, StructReport } from '../config';
import { BASE_URL } from '../../../../libraries/RequestFetch';
import { ReportContext } from '../';
import { EstadoByCode } from '../../../../libraries/Estados'
import moment from 'moment';
import { useHistory } from 'react-router-dom';

export function EmpresaReporte1({ empresas, cabeceras, isTokenExpired }) {
    const { setReport } = useContext(ReportContext);
    const [select, setSelect] = useState('TODAS');
    const [loading, setLoading] = useState(false);

    const onGetData = async () => {
        setLoading(true)
        let message = 'A continuación se muestran la cantidad de empleados activos e inactivos '
        let url = BASE_URL + `/api/rh/empleado?query=estatus-id_empleado`
        if (select !== 'TODAS') {
            message += 'de la empresa: ' + empresas.find(e => e.id_empresa === select).razon_social;
            url += `&id_empresa_labora=${select}`;
        } else {
            message += 'de TODAS las empresas'
        }
        const response = await fetch(url, { method: 'GET', headers: cabeceras });

        const resp = await response.json();
        if (!isTokenExpired(resp)) {
            const { empleados } = resp;
            const activos = empleados.filter(e => e.estatus === 'A').length;
            const bajas = empleados.filter(e => e.estatus === 'B').length;
            setReport({
                show: true,
                type: 'bar',
                title: 'Cantidad de empleados (activos o inactivos) por empresa',
                message,
                data: [bajas, activos],
                labels: ['Bajas', 'Activos'],
                label: 'Empleados',
            })
        }

    }

    return (
        <StructReport loading={loading}>
            <Grid item xs={12} >
                <Typography variant="subtitle1" color="initial">
                    Cantidad de empleados (activos o inactivos) por empresa
                </Typography>
            </Grid>
            <Grid item xs={12} >
                <TextField {...configInput}
                    value={select}
                    onChange={({ target: { value } }) => setSelect(value)}
                    select
                    defaultValue="TODAS" label="Empresa">
                    <MenuItem value={"TODAS"} >TODAS</MenuItem>
                    {
                        empresas
                            .map(e => <MenuItem key={`empresa-labora-${e.id_empresa}`} value={e.id_empresa}>
                                {e.razon_social}
                            </MenuItem>)
                    }
                </TextField>
            </Grid>
            <Grid item xs={12}>
                <Grid container style={{ height: '100%' }}
                    direction="column"
                    justify="flex-end"
                    alignItems="flex-end">
                    <Button color="secondary" disabled={loading} onClick={onGetData}>GENERAR REPORTE</Button>
                </Grid>
            </Grid>
        </StructReport>
    )
}

export function EmpresaReporte2({ cabeceras, isTokenExpired }) {
    const [loading, setLoading] = useState(false);
    const { setReport } = useContext(ReportContext);

    const onGetData = async () => {
        setLoading(true);
        const response = await fetch(`${BASE_URL}/api/rh/empresa/reporte/entidades`, { method: 'GET', headers: cabeceras });
        const resp = await response.json();
        if (!isTokenExpired(resp)) {
            const { empresas } = resp;
            const labels = empresas.map(e => EstadoByCode(e.entidad));
            const data = empresas.map(e => e.cantidad);
            setReport({
                show: true,
                type: 'horizontalbar',
                title: 'Cantidad de empleados por estado de la republica.',
                message: 'A continuación se mostraran la cantidad de empleados en cada estado de la republica con al menos 1 empleado',
                data,
                labels,
                label: 'Empleados',
            })
        }
    }

    return (
        <StructReport loading={loading}>
            <Grid item xs={12} >
                <Typography variant="subtitle1" color="initial">
                    Cantidad de empresas por estado de la republica.
                </Typography>
                <Typography variant="subtitle2" color="initial">
                    (Solo se mostraran los estados con al menos una empresa).
                </Typography>
            </Grid>
            <Grid item xs={12} >
                <Grid container style={{ height: '100%' }}
                    direction="column"
                    justify="flex-end"
                    alignItems="flex-end">
                    <Button color="secondary" onClick={onGetData} >GENERAR REPORTE</Button>
                </Grid>
            </Grid>
        </StructReport>
    )
}

export function EmpresaReporte3({ cabeceras, isTokenExpired }) {
    const { setReport } = useContext(ReportContext);
    const [loading, setLoading] = useState(false);
    const history = useHistory();

    const onGetData = async () => {
        setLoading(true);
        const response = await fetch(`${BASE_URL}/api/rh/empresa/reporte/contrato`, { method: 'GET', headers: cabeceras });
        const resp = await response.json();
        if (!isTokenExpired(resp)) {
            const { empresas } = resp;
            const labels = [
                { title: 'Razon Social', id: 'razon_social', hidden: false, align: 'left' },
                { title: 'Fecha Final', id: 'fecha_fin', hidden: false, align: 'left' },
                { title: 'Accion', id: 'id_empresa', hidden: false, align: 'left' },
            ]
            const data = empresas.map(e => {
                return {
                    razon_social: e.razon_social,
                    fecha_fin: moment(e.fecha_fin).format('DD/MM/YYYY'),
                    id_empresa: <Button color="secondary" onClick={() => history.push(`/nomina/edit-empresa/${e.id_empresa}`)}>Ver</Button>
                }
            });
            setReport({
                show: true,
                type: 'table',
                title: 'Empresas que están por finalizar contrato en un rango de 3 meses.',
                message: 'Se muestran las empresas que la fecha de terminación de contrato se acerca.',
                data,
                labels,
                label: 'Empleados',
            })
        }
    }
    return (
        <StructReport loading={loading}>
            <Grid item xs={12} >
                <Typography variant="subtitle1" color="initial">
                    Empresas que están por finalizar contrato en un rango de 3 meses.
                </Typography>
                <Typography variant="subtitle2" color="initial">
                    (Solo se muestran las empresas que la fecha de terminación de contrato se acerca).
                </Typography>
            </Grid>
            <Grid item xs={12} >
                <Grid container style={{ height: '100%' }}
                    direction="column"
                    justify="flex-end"
                    alignItems="flex-end">
                    <Button disabled={loading} color="secondary" onClick={onGetData} >GENERAR REPORTE</Button>
                </Grid>
            </Grid>
        </StructReport>
    )
}

export default function EmpresaReportes(props) {
    const xs = 12;
    const sm = 6
    const md = 4;
    return (
        <Grid spacing={2}
            container
            direction="row"
            justify="center"
            alignItems="stretch"
        >
            <Grid item xs={xs} sm={sm} md={md}>
                <EmpresaReporte1 {...props} />
            </Grid>
            <Grid item xs={xs} sm={sm} md={md}>
                <EmpresaReporte2 {...props} />
            </Grid>
            <Grid item xs={xs} sm={sm} md={md}>
                <EmpresaReporte3 {...props} />
            </Grid>
        </Grid>

    )
}