import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListSubheader from '@material-ui/core/ListSubheader';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import { ExpandLess, ExpandMore } from '@material-ui/icons';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent';
import InfoIcon from '@material-ui/icons/Info';
import WorkIcon from '@material-ui/icons/Work';
import RoomIcon from '@material-ui/icons/Room';
import { Snackbar, Typography, CardActions, Button, Grid, IconButton } from '@material-ui/core'
import Direccion from './Direccion/Direccion';
import Representante from './Representante/Representante';
import RequestFetch from '../../../libraries/RequestFetch';
import Informacion from './Informacion/Informacion';
import Contacto from './Contacto/Contacto';
import MuiAlert from '@material-ui/lab/Alert';
import { green } from '@material-ui/core/colors';
import moment from 'moment';
import AlertTitle from '@material-ui/lab/AlertTitle';
import { Save as SaveIcon } from '@material-ui/icons'
import CircularProgress from '@material-ui/core/CircularProgress';
import ExpandButton from '../Dash/ExpandButton';
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import { useHistory } from 'react-router-dom';
import Validacion from '../../../libraries/Validacion';
import Contrato from './Contrato/Contrato';
import AssignmentTurnedInIcon from '@material-ui/icons/AssignmentTurnedIn';
import { SessionContext } from '../../../routes/adminrh/CustomRoutes'
import { initRepre, initInfo, initDomi, initCont, initTelEmpre, initTelRepre, initialPart, initialContrato, initialRegistros } from './initialStates';

const useStyles = makeStyles(theme => ({
    root: {
        width: '100%',
        backgroundColor: theme.palette.background.paper,
    },
    cardContent: {
        padding: '0px'
    },
    header: {
        background: '#fafafa'
    },
    title: {
        flexGrow: 1,
        textAlign: 'center',
        marginTop: '15px ',
        marginBottom: '15px'
    },
    actions: {
        padding: theme.spacing(4),
    },
    save: {
        color: theme.palette.common.white,
        backgroundColor: green[500],
        '&:hover': {
            backgroundColor: green[600],
        },
    },
    buttonProgress: {
        color: green[500],
        position: 'absolute',
        top: '50%',
        left: '50%',
        marginTop: -12,
        marginLeft: -12,
    },
    wrapper: {
        margin: '0px 24px 8px 16px',
        position: 'relative',
    },
    subheader: {
        textAlign: 'center'
    },
    back: {
        left: -12,
        float: 'left'
    }
}));


function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}


export default function FormEmpresa() {
    const classes = useStyles();
    const history = useHistory();
    const { cabeceras, isTokenExpired } = React.useContext(SessionContext);

    const [part, setPart] = React.useState(initialPart());
    const [loading, setLoading] = React.useState(false);
    const [id, setId] = React.useState("");

    //STEEPS
    const [informacion, setInformacion] = React.useState(initInfo());
    const [direccion, setDireccion] = React.useState(initDomi());
    const [contacto, setContacto] = React.useState(initCont());
    const [contrato, setContrato] = React.useState(initialContrato());
    const [registros, setRegistros] = React.useState(initialRegistros());

    const [empresaTel, setEmpresaTel] = React.useState(initTelEmpre());
    const [repre, setRepre] = React.useState(initRepre());
    const [repreTel, setRepreTel] = React.useState(initTelRepre());

    const [toast, setToast] = React.useState({
        open: false,
        message: "",
        severity: "",
        title: ""
    });

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            setToast({ ...toast, open: false });
            return;
        }
        setToast({ ...toast, open: false });
    };

    const handleClick = (pos) => {
        setPart({ ...part, actual: pos })
    };


    React.useEffect(() => {
        const cl = direccion.colonia.value.replace(/[<>"',.:;{}\-_[\]áéíóúÁÉÍÓÚñÑ]/g, '');
        const rs = (informacion.razon_comercial.value +
            informacion.razon_social.value +
            informacion.rfc.value).replace(/[<>"',.:;{}\-_[\]áéíóúÁÉÍÓÚñÑ]/g, '');
        const fc = moment(informacion.fecha_constitucion.value).format('DDMMYYYY');
        const mu = direccion.municipio.value.replace(/[<>"',.:;{}\-_[\]áéíóúÁÉÍÓÚñÑ]/g, '');
        const en = direccion.entidad.value.replace(/[<>"',.:;{}\-_[\]áéíóúÁÉÍÓÚñÑ]/g, '');
        if (rs !== '' && fc !== '' && mu !== '' && en !== '') {
            const partRs = rs.split(" ");
            const partMu = mu.split(" ");
            const partCo = (cl + "GETCONSULTORIA").split(" ");

            let part1 = '';
            if (partRs.length < 2) part1 = partRs[0].substring(0, 4);
            else part1 = partRs[0].substring(0, 2) + partRs[1].substring(0, 2);

            let part2 = '';
            if (partMu.length < 2) part2 = partMu[0].substring(0, 2);
            else part2 = partMu[0].charAt(0) + partMu[1].charAt(0);


            let part3 = '';
            if (partCo.length < 2) part3 = partCo[0].substring(0, 2);
            else part3 = partCo[0].charAt(0) + partCo[1].charAt(0);

            let part4 = fc.substring(fc.length, fc.length - 2);

            setId((part1 + en + part2 + part3 + part4).toUpperCase());
        }
    }, [
        informacion.razon_social.value,
        informacion.rfc.value,
        informacion.razon_comercial.value,
        informacion.id_regimen_capital.value,
        informacion.fecha_constitucion.value,
        direccion.municipio.value,
        direccion.entidad.value,
        direccion.colonia.value
    ])


    const auxiliar = (() => {

        const separeEmpresa = (id_repre) => {
            const data = { ...informacion, ...direccion, ...contacto }
            const keys = Object.keys(data);
            const form = new FormData();
            keys.forEach((key) => {
                if (key === 'rfc')
                    form.append(key, data[key].value.toUpperCase())
                else if (key.includes('fecha'))
                    form.append(key, moment.utc(data[key].value, "DD/MM/YYYY").format("YYYY/MM/DD"))
                else if (key === 'fileImg') {
                    if (data.fileImg !== null)
                        form.append('logo', data.fileImg);
                }
                else if (key === 'id_representante')
                    form.append('id_representante', repre.existe.value ? repre.id_representante.value : parseInt(id_repre));
                else {
                    const { value } = data[key];
                    if (value !== undefined && value !== null && value !== '')
                        form.append(key, data[key].value);
                }
            });
            form.append('identificador ', id);
            /* for (var pair of form.entries()) {
                console.log(pair[0] + ', ' + pair[1]);
            } */
            return form;
        }

        const separeContrato = (id) => {
            let keys = [];
            const newContrato = {};
            if (informacion.es_interna.value === 'true') {
                newContrato['comision'] = '0';
                newContrato['motivo_contrato'] = 'A';
                keys = ['fecha_inicio', 'periodo_calculo', 'comision', 'motivo_contrato'];
            }
            else keys = Object.keys(contrato)

            keys.forEach(key => {
                if (informacion.es_interna.value === 'true') {
                    if (key !== 'comision' && key !== 'motivo_contrato')
                        newContrato[key] = contrato[key].value;
                } else newContrato[key] = contrato[key].value;

                if (key.includes('fecha'))
                    newContrato[key] = moment.utc(contrato[key].value, "DD/MM/YYYY").format("YYYY/MM/DD");
                if (key === 'comision') newContrato[key] = parseInt(newContrato[key]) / 100
            });
            return { id_empresa: id, ...newContrato };
        }
        const separeRegistrosPatronales = (id) => {
            const registrosPatronales = [];
            registros.forEach(i => {
                let { helperText, verify, ...y } = i;
                y.clave_patronal = y.clave_patronal.replace(/\s/g, '');
                registrosPatronales.push({ ...y });
            });
            return {
                id_empresa: id,
                registros: registrosPatronales,
            }
        }

        const separeEmpresaTel = (id) => {
            const newTel = [];
            empresaTel.forEach(item => {
                const value = item.value.replace(/\s/g, '');
                if (value.length >= 5)
                    newTel.push({ numero: value, tipo: item.tipo })
            })
            if (newTel.length > 0)
                return {
                    id_empresa: id,
                    telefonos: newTel
                }
            else return null;

        }

        const separeRepreTel = (id) => {
            const newTel = [];
            repreTel.forEach(item => {
                const value = item.value.replace(/\s/g, '');
                if (value.length >= 5)
                    newTel.push({ numero: value, tipo: item.tipo })
            })
            if (newTel.length > 0)
                return {
                    id_representante: id,
                    telefonos: newTel
                }
            else return null;
        }

        const separeRepresentante = () => {
            var newData = {
                representante: {
                    rfc: repre.rfc.value.toUpperCase(),
                    nombre: repre.nombre.value,
                    primer_apellido: repre.primer_apellido.value,
                    segundo_apellido: repre.segundo_apellido.value,
                    genero: repre.genero.value,
                    correo: repre.correo.value,
                    observaciones: repre.observaciones.value,
                }
            }

            return newData;
        }



        const validaInformacion = () => {
            const keys = ['razon_comercial', 'razon_social', 'rfc', 'fecha_constitucion', 'fecha_alta', 'id_regimen_capital', 'es_interna', 'id_giro', 'estatus'];
            const newData = { ...informacion }
            keys.forEach(key => {
                newData[key].verify = `${newData[key].value}`.length > 0 && newData[key].verify;
            });
            setInformacion(newData);
            const result = keys.filter(key => {
                return newData[key].verify !== true;
            });
            return result.length === 0;
        }



        const validaDireccion = () => {
            const keys = ['calle', 'cp', 'colonia', 'municipio', 'entidad'];
            const newData = { ...direccion }
            keys.forEach(key => {
                newData[key].verify = `${newData[key].value}`.length > 0 && newData[key].verify;
            });
            setDireccion(newData);
            const result = keys.filter(key => {
                return newData[key].verify !== true;
            });
            return result.length === 0;
        }

        const validaContacto = () => {
            const keys = ['correo'];
            const newData = { ...contacto }
            newData['correo'].verify = Validacion.esCorreo(newData['correo'].value) && newData['correo'].value.length > 0;
            setContacto(newData);
            const result = keys.filter(key => {
                return newData[key].verify !== true;
            });
            return result.length === 0;
        }

        const validaContrato = () => {
            const newContrato = { ...contrato };
            let keys = [];
            if (informacion.es_interna.value === 'false') {
                keys = Object.keys(contrato);
                keys.forEach(key => {
                    newContrato[key].verify = `${newContrato[key].value}`.length > 0 && newContrato[key].verify;
                });

            } else {
                keys = ['fecha_inicio', 'periodo_calculo'];
                keys.forEach(key => {
                    newContrato[key].verify = `${newContrato[key].value}`.length > 0 && newContrato[key].verify;
                });
            }

            setContrato(newContrato);
            const result = keys.filter(key => {
                return newContrato[key].verify !== true;
            });
            return result.length === 0;
        }

        const validaRegistrosPatronales = () => {
            if (informacion.es_interna.value === 'true') {
                const newRegistros = [...registros];
                const oks = []
                newRegistros.forEach(i => {
                    i.verify = (i.clave_patronal.replace(/\s/g, '').length === 11 && Number.isInteger(i.categoria) && i.verify);
                    if (!i.verify) i.helperText = "Verifica la clase y clave";
                    oks.push(i.verify)
                });
                setRegistros(newRegistros);
                const resul = oks.filter(item => {
                    return item !== true;
                })

                return resul.length === 0;
            } else return true;

        }

        const ValidaRepre = () => {
            var keysRepre = ['rfc', 'nombre', 'primer_apellido', 'genero', 'correo']
            var newDataRepre = { ...repre };
            keysRepre.forEach(key => {
                if (key === 'rfc')
                    newDataRepre[key] = {
                        value: repre[key].value,
                        verify: `${repre[key].value}`.replace(/\s/g, '').length === 13 && repre[key].verify
                    }
                else
                    newDataRepre[key] = {
                        value: repre[key].value,
                        verify: `${repre[key].value}`.length > 0 && repre[key].verify
                    }
            });
            setRepre(newDataRepre);
            const resulRepre = keysRepre.filter(key => {
                return newDataRepre[key].verify !== true;
            });

            return resulRepre.length === 0;
        }

        const ValidaNumeroRepre = () => {
            const newData = [...repreTel];
            const oks = [];
            newData.forEach((element) => {
                const correo = element.value.replace(/\s/g, '');

                if (correo.length === 10) {
                    const verify = element.verify;
                    element.verify = verify;
                    oks.push(verify);
                }
            })
            const resul = oks.filter(item => {
                return item !== true;
            })
            if (oks.length === 0) {
                newData[0].verify = false;
                newData[0].helperText = "Registra al menos uno";
                newData[1].verify = false;
                newData[1].helperText = "Registra al menos uno"
                return false;
            }
            setRepreTel(newData);
            return resul.length === 0;
        }

        const ValidaNumeroEmpresa = () => {
            const newData = [...empresaTel];
            const oks = [];
            newData.forEach((element) => {
                const correo = element.value.replace(/\s/g, '');
                if (correo.length === 10) {
                    const verify = element.verify;
                    element.verify = verify;
                    oks.push(verify);
                }
            })
            const resul = oks.filter(item => {
                return item !== true;
            })
            if (oks.length === 0) {
                newData[0].verify = false;
                newData[0].helperText = "Registra al menos uno";
                newData[1].verify = false;
                newData[1].helperText = "Registra al menos uno"
                return false;
            }
            setEmpresaTel(newData);
            return resul.length === 0;
        }


        const openTab = part => {
            setToast({
                open: true,
                message: "Es necesario rellenar los campos requeridos",
                severity: "warning",
                title: "Campos vacios!"
            });
            setPart({ ...part, actual: part });
            setLoading(false);
        }

        const ValidaAll = () => {

            if (validaInformacion())
                if (validaDireccion())
                    if (validaContacto())
                        if (ValidaNumeroEmpresa())
                            if (validaContrato())
                                if (validaRegistrosPatronales())
                                    if (ValidaRepre())
                                        if (ValidaNumeroRepre())
                                            return true;
                                        else { openTab(5); return false; }
                                    else { openTab(4); return false; }
                                else { openTab(4); return false; }
                            else { openTab(3); return false; }
                        else { openTab(2); return false; }
                    else { openTab(2); return false; }
                else { openTab(1); return false; }
            else { openTab(0); return false; }
        }

        return {
            separeEmpresa,
            separeEmpresaTel,
            separeRepreTel,
            separeRepresentante,
            separeContrato,
            separeRegistrosPatronales,
            ValidaAll
        }
    })();

    const buttons = (() => {

        const next = () => {
            setPart({ ...part, actual: part.actual + 1, global: part.global + 1 });
        }

        const back = () => {
            setPart({ ...part, actual: part.actual - 1 });
        }


        const fallo = async (res) => {
            const response = res.json();
            if (!isTokenExpired(response));
        }

        const send = () => {

            //Envia los telefonos de la empresa
            const POSTSendTelEmpresa = (_id) => {
                RequestFetch.post({
                    url: `${RequestFetch.BASE_URL}/api/rh/empresa/telefono`,
                    headers: cabeceras,
                    data: auxiliar.separeEmpresaTel(_id),
                    exito: async (res) => {
                        const respuesta = await res.json();
                        if (!isTokenExpired(respuesta)) {
                            setLoading(false);
                            setToast({
                                open: true,
                                message: `La empresa ha quedado registrada con ID: ${id}`,
                                severity: "success",
                                title: "Registro con éxito!"
                            });
                            setInformacion(initInfo());
                            setContacto(initCont());
                            setDireccion(initDomi());
                            setRepre(initRepre());
                            setPart(initialPart());
                            setRepreTel(initTelRepre());
                            setEmpresaTel(initTelRepre());
                            setContrato(initialContrato());
                            setId("");
                        }
                    }
                    ,
                    fallo,
                    error_request: () => {
                        history.push(`/server-error`);
                    }
                });
            }

            //Envia los datos de la empresa
            const headers = new Headers();
            headers.append('authorization', cabeceras.authorization)
            const POSTSendEmpresa = (id_repre) => {
                RequestFetch.postForm({
                    url: `${RequestFetch.BASE_URL}/api/rh/empresa`,
                    data: auxiliar.separeEmpresa(id_repre),
                    headers,
                    exito: async (res) => {
                        const respuesta = await res.json();
                        //console.log("Empresa Exito", respuesta);
                        POSTSendContrato(respuesta[0].id_empresa);
                        if (informacion.es_interna.value === 'true')
                            POSTRegistrosPatronales(respuesta[0].id_empresa);

                        POSTSendTelEmpresa(respuesta[0].id_empresa);
                    },
                    fallo: async (res) => {
                        const respuesta = await res.json();
                        if (!isTokenExpired(respuesta)) {
                            //console.log("Empresa Fallo", respuesta.error.detail.includes("rfc"));
                            setLoading(false);
                            const _toast = {
                                open: true,
                                severity: "error",
                            }
                            if (respuesta.error.detail.includes("correo")) {
                                toast['message'] = "Correo duplicado";
                                toast['title'] = "Ya está asociado a otra empresa, proporciona uno distinto";
                            }
                            else if (respuesta.error.detail.includes("razon_social")) {
                                toast['message'] = "Razón Social duplicada";
                                toast['title'] = "Es posible que la empresa ya esté registrada";
                            }
                            else if (respuesta.error.detail.includes("rfc")) {
                                toast['message'] = "RFC duplicado";
                                toast['title'] = "Es posible que la empresa ya esté registrada";
                            }
                            setToast(_toast);
                        }
                    },
                    error_request: (error) => {
                        history.push(`/server-error`);
                    }
                });
            }

            //Envia los datos del representante
            const POSTSendReprese = () => {
                RequestFetch.post({
                    url: `${RequestFetch.BASE_URL}/api/rh/representante`,
                    data: auxiliar.separeRepresentante(),
                    headers: cabeceras,
                    exito: async (res) => {
                        const respuesta = await res.json();
                        if (!isTokenExpired(respuesta)) {
                            //console.log("Representnate Exito", respuesta);
                            const { representate, created } = respuesta;
                            if (created) {
                                setRepre(prevState => {
                                    return {
                                        ...prevState,
                                        id_representante: { value: representate[0].id_representante },
                                        existe: { value: true }
                                    }
                                });
                                setInformacion((prevState) => {
                                    return {
                                        ...prevState,
                                        id_representante: {
                                            value: representate[0].id_representante,
                                        }
                                    }
                                })
                                //SEND TELEFONO
                                POSTSendTelRepre(representate[0].id_representante);
                                POSTSendEmpresa(representate[0].id_representante);
                                if (informacion.es_interna.value === 'false')
                                    POSTSendContrato(representate[0].id_representante);
                            }
                        }
                    },
                    fallo,
                    error_request: (error) => {
                        setLoading(false);
                        history.push(`/server-error`);
                    }
                });
            }

            //Envia los telefonos del representante
            const POSTSendTelRepre = (id) => {
                //console.log(auxiliar.separeRepreTel(id));
                RequestFetch.post({
                    url: `${RequestFetch.BASE_URL}/api/rh/representante/telefono`,
                    headers: cabeceras,
                    data: auxiliar.separeRepreTel(id),
                    exito: async (res) => {
                        const respuesta = await res.json();
                        if (!isTokenExpired(respuesta));
                        //console.log("Representnate Teleofno Exito", respuesta);
                    },
                    fallo,
                    error_request: () => {
                        history.push(`/server-error`);
                    }
                });
            }

            //Envia el contrato de la empresa
            const POSTSendContrato = (id) => {
                RequestFetch.post({
                    url: `${RequestFetch.BASE_URL}/api/rh/empresa/contrato`,
                    data: auxiliar.separeContrato(id),
                    headers: cabeceras,
                    exito: async (res) => {
                        const respuesta = await res.json();
                        if (!isTokenExpired(respuesta));
                    },
                    fallo,
                    error_request: (error) => {
                        history.push(`/server-error`);
                    }
                });
            }

            const POSTRegistrosPatronales = (id) => {
                RequestFetch.post({
                    url: `${RequestFetch.BASE_URL}/api/rh/empresa/registro_patronal`,
                    headers: cabeceras,
                    data: auxiliar.separeRegistrosPatronales(id),
                    exito: async (res) => {
                        const respuesta = await res.json();
                        if (!isTokenExpired(respuesta));
                        //console.log("Empresa registros Exito", respuesta);
                    }
                    ,
                    fallo,
                    error_request: () => {
                        history.push(`/server-error`);
                    }
                });
            }

            if (auxiliar.ValidaAll()) {
                setLoading(true);

                if (repre.existe.value) POSTSendEmpresa()
                else POSTSendReprese();
            }
        }

        return {
            back, next, send
        }
    })();

    return (
        <React.Fragment>
            <Card className={classes.root}>
                <CardContent className={classes.cardContent}>
                    <List
                        component="nav"
                        aria-labelledby="nested-list-subheader"
                        subheader=
                        {
                            <ListSubheader className={classes.subheader} component="div" display="block">
                                <IconButton className={classes.back} onClick={() => { history.goBack() }}>
                                    <KeyboardBackspaceIcon />
                                </IconButton>
                                <Typography className={classes.title} variant="button" gutterBottom>
                                    Formulario Empresa
                                </Typography>
                                <ExpandButton onClick={() => { setPart({ ...part, expandable: !part.expandable }) }} open={part.expandable} />
                            </ListSubheader>
                        }
                        className={classes.root}>
                        <ListItem className={classes.header} button onClick={handleClick.bind(this, 0)}>
                            <ListItemIcon>
                                <InfoIcon />
                            </ListItemIcon>
                            <ListItemText primary="Informacion" />
                            {part.actual === 0 ? <ExpandLess /> : <ExpandMore />}
                        </ListItem>
                        <Collapse in={part.actual === 0 || part.expandable} timeout="auto"  >
                            <Informacion
                                data={informacion}
                                setContrato={setContrato}
                                setData={setInformacion}
                                buttons={buttons}
                                expandable={part.expandable}
                                edit={false} />
                        </Collapse>
                        <ListItem className={classes.header} button onClick={handleClick.bind(this, 1)}>
                            <ListItemIcon>
                                <RoomIcon />
                            </ListItemIcon>
                            <ListItemText primary="Dirección" />
                            {part.actual === 1 ? <ExpandLess /> : <ExpandMore />}
                        </ListItem>
                        <Collapse in={part.actual === 1 || part.expandable} timeout="auto" >
                            <Direccion
                                data={direccion}
                                setData={setDireccion}
                                buttons={buttons}
                                expandable={part.expandable}
                                edit={false} />
                        </Collapse>
                        <ListItem className={classes.header} button onClick={handleClick.bind(this, 2)}>
                            <ListItemIcon>
                                <AccountCircleIcon />
                            </ListItemIcon>
                            <ListItemText primary="Contacto" />
                            {part.actual === 2 ? <ExpandLess /> : <ExpandMore />}
                        </ListItem>
                        <Collapse in={part.actual === 2 || part.expandable} timeout="auto" >
                            <Contacto
                                data={contacto}
                                setData={setContacto}
                                buttons={buttons}
                                expandable={part.expandable}
                                phones={empresaTel}
                                setPhones={setEmpresaTel} />
                        </Collapse>

                        <ListItem className={classes.header} button onClick={handleClick.bind(this, 3)}>
                            <ListItemIcon>
                                <AssignmentTurnedInIcon />
                            </ListItemIcon>
                            <ListItemText primary="Contrato" />
                            {part.actual === 3 ? <ExpandLess /> : <ExpandMore />}
                        </ListItem>
                        <Collapse in={part.actual === 3 || part.expandable} timeout="auto" >
                            <Contrato
                                registros={registros}
                                setRegistros={setRegistros}
                                isInterna={informacion.es_interna.value}
                                data={contrato}
                                setData={setContrato}
                                edit={false}
                                expandable={part.expandable}
                                buttons={buttons} />
                        </Collapse>
                        <ListItem className={classes.header} button onClick={handleClick.bind(this, 4)}>
                            <ListItemIcon>
                                <WorkIcon />
                            </ListItemIcon>
                            <ListItemText primary="Representante" />
                            {part.actual === 4 ? <ExpandLess /> : <ExpandMore />}
                        </ListItem>
                        <Collapse in={part.actual === 4 || part.expandable} timeout="auto" >
                            <Representante
                                data={repre}
                                setData={setRepre}
                                phones={repreTel}
                                setPhones={setRepreTel}
                                expandable={part.expandable}
                                loading={loading}
                                edit={false}
                                buttons={buttons} />
                        </Collapse>
                    </List>
                </CardContent>
                <CardActions>
                    <Grid container>
                        <Grid
                            item
                            xs={12}
                            sm={6}
                            container
                            direction="row"
                            justify="flex-start"
                            alignItems="center">
                            <Typography style={{ marginLeft: 24 }} variant="button" color="initial">{`Identificador: ${id}`}</Typography>
                        </Grid>
                        <Grid
                            item
                            xs={12}
                            sm={6}
                            container
                            direction="row"
                            justify="flex-end"
                            alignItems="center">
                            <div className={classes.wrapper}>
                                <Button
                                    variant="contained"
                                    className={classes.save}
                                    disabled={loading}
                                    endIcon={<SaveIcon />}
                                    onClick={buttons.send}>
                                    {"Guardar"}
                                    {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                                </Button>
                            </div>
                        </Grid>
                    </Grid>
                </CardActions>
            </Card>

            <Snackbar open={toast.open} autoHideDuration={5000} onClose={handleClose}>
                <Alert onClose={handleClose} severity={toast.severity}>
                    <AlertTitle>{toast.title}</AlertTitle>
                    {toast.message}
                </Alert>
            </Snackbar>
        </React.Fragment>
    );
}