import React, { useState, useContext, useEffect } from 'react'
import { BASE_URL } from '../../../../libraries/RequestFetch';
import {
    Grid,
    Paper,
    Typography,
    Button,
    TextField,
    Dialog,
    DialogContent,
    MenuItem,
    DialogTitle,
    DialogContentText,
    DialogActions,
    LinearProgress,
    TableContainer,
    Table,
    TableRow,
    TableBody,
    TableHead,
    TableCell,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import { Skeleton } from '@material-ui/lab';
import { SessionContext } from '../../../../routes/adminrh/CustomRoutes';
import LockIcon from '@material-ui/icons/Lock';

const useStyles = makeStyles((theme) => ({
    root: {
        backgroundColor: theme.palette.background.paper,
        minHeight: '60px'
    },
    paper: {
        padding: theme.spacing(3)
    },
    title: {
        textAlign: 'center',
        paddingTop: '15px'
    }
}));

export default function Permisos({ id, empresas }) {
    const classes = useStyles();
    const { cabeceras, isTokenExpired } = useContext(SessionContext);
    const [permisos, setPermisos] = useState();
    const [addPermiso, setAddPermiso] = useState(false);

    const getData = async () => {
        const resp = await fetch(
            `${BASE_URL}/api/rh/empleado/permisos?e.id_empleado=${id}`,
            {
                method: 'GET',
                headers: cabeceras
            }
        );
        const response = await resp.json();
        if (!isTokenExpired(response)) {
            setPermisos(response.permisos);
        }
    }

    useEffect(() => {
        getData();
        //eslint-disable-next-line
    }, [])



    return (
        <Paper className={classes.root} elevation={2}>
            <Typography className={classes.title} variant="button" display="block" gutterBottom>
                Permisos del Empleado
            </Typography>

            {
                permisos ?
                    permisos.length > 0 ?
                        <TablePermisos id={id} empresas={empresas} permisos={permisos} refresh={getData} add={() => setAddPermiso(true)} />
                        :

                        <Grid container spacing={2} style={{ padding: 15 }}>
                            <Grid item xs={12}>
                                <Grid
                                    container
                                    direction="column"
                                    justify="flex-start"
                                    alignItems="center"
                                >
                                    <Grid item >
                                        <LockIcon style={{ fontSize: 100, opacity: 0.3 }} />
                                    </Grid>
                                    <Grid item>
                                        <Typography variant="h6" gutterBottom>
                                            Sin permisos!
                                     </Typography>
                                    </Grid>
                                    <Grid item>
                                        <Typography variant="body1" gutterBottom>
                                            Para que el empleado pueda ver una empresa, agrega una.
                                    </Typography>
                                    </Grid>
                                    <Grid item>
                                        <Button variant="text" color="secondary" onClick={() => setAddPermiso(true)}>
                                            Agregar
                                    </Button>
                                    </Grid>
                                </Grid>
                            </Grid>
                        </Grid>
                    :

                    <Grid container spacing={2} style={{ padding: 15 }}>
                        <Grid item xs={12}>
                            <Skeleton animation="wave" height={70} />
                        </Grid>
                    </Grid>
            }

            {
                addPermiso &&
                <DialogAddPermiso id={id} open={addPermiso} onClose={() => setAddPermiso(false)} permisos={permisos} empresas={empresas} refresh={getData} />
            }
        </Paper>

    )
}



function DialogAddPermiso({ empresas, permisos, open, onClose, id, refresh }) {
    const { cabeceras, isTokenExpired } = useContext(SessionContext);
    const [empresa, setEmpresa] = useState('DEFAULT');
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState();

    const onSend = async () => {
        if (empresa !== 'DEFAULT') {
            try {
                const body = { id_empleado: id, permisos: [{ id_empresa: empresa }] }
                const resp = await fetch(`${BASE_URL}/api/rh/empleado/permisos`,
                    {
                        method: 'POST',
                        headers: cabeceras,
                        body: JSON.stringify(body)
                    });
                const response = await resp.json();
                if (!isTokenExpired(response)) {
                    const { created } = response;
                    if (created) { refresh(); onClose(); }
                    else throw new Error('No se creó');
                }
            } catch (error) {
                setLoading(false);
                setError('Ocurrió un error')
            }
        }
    }


    return (
        <Dialog open={open} onClose={onClose} aria-labelledby="">
            {loading && <LinearProgress color="secondary" />}
            <DialogTitle id="permiso-add">Agregar Permiso</DialogTitle>
            <DialogContent>
                <DialogContentText>
                    La empresa quedará a cargo de este empleado
            </DialogContentText>
                <TextField
                    select
                    fullWidth
                    value={empresa}
                    label={"Empresa"}
                    onChange={({ target: { value } }) => setEmpresa(value)}>
                    <MenuItem value="DEFAULT">Selecciona</MenuItem>
                    {
                        empresas
                            .filter(e => !permisos.some(p => p.id_empresa === e.id_empresa))
                            .map((e, i) => <MenuItem key={`empresa-permiso-${i}`} value={e.id_empresa}>{e.razon_social}</MenuItem>)
                    }
                </TextField>
                {error && <Typography variant="body1" style={{ color: 'red' }}>{error}</Typography>}
            </DialogContent>
            <DialogActions>
                <Button onClick={onClose} color="default">
                    Cancelar
                </Button>
                <Button onClick={onSend} color="secondary">
                    Guardar
                </Button>
            </DialogActions>
        </Dialog>
    )
}


function TablePermisos({ id, empresas, permisos, refresh, add }) {
    const [deleted, setDeleted] = useState({ open: false, id: '' });
    const { cabeceras, isTokenExpired } = useContext(SessionContext);

    const onDelete = async (id_empresa) => {
        try {
            const resp = await fetch(`${BASE_URL}/api/rh/empleado/permisos/${id}/${id_empresa}`, { method: 'DELETE', headers: cabeceras });
            const response = await resp.json();
            if (!isTokenExpired(response)) {
                setDeleted({ open: false, id: '' });
                refresh();
            }
        } catch (error) {
        }
    }

    return <TableContainer>
        <Table size="small">
            <TableHead>
                <TableRow>
                    <TableCell align="center">
                        Empresa
                </TableCell>
                    <TableCell align="center">
                        Accion
                </TableCell>
                </TableRow>
            </TableHead>
            <TableBody>
                {
                    permisos.map((p, i) => <TableRow key={`row-permisos-${i}`}>
                        <TableCell align="center">
                            {empresas.find(e => e.id_empresa === p.id_empresa).razon_social}
                        </TableCell>
                        <TableCell align="center">
                            <div style={{ minWidth: 150, textAlign: 'center' }}>

                                <Button variant="text" color="secondary" onClick={() => setDeleted({ open: true, id: p.id_empresa })}>
                                    Eliminar
                            </Button>

                            </div>
                        </TableCell>
                    </TableRow>
                    )
                }
                <TableRow>
                    <TableCell align="center" colSpan={2}>
                        <Button variant="text" color="secondary" onClick={add}>Agregar</Button>
                    </TableCell>
                </TableRow>
            </TableBody>
        </Table>
        <Dialog open={deleted.open} onClose={() => setDeleted({ open: false })} aria-labelledby="">
            <DialogTitle id="">Eliminación de permiso</DialogTitle>
            <DialogContent>
                <DialogContentText>
                    Está apunto de eliminar el permiso del empleado
            </DialogContentText>
                <DialogContentText>
                    Está seguro que desea continuar?
            </DialogContentText>
            </DialogContent>
            <DialogActions>
                <Button onClick={() => setDeleted({ open: false })} color="default">
                    Cancelar
            </Button>
                <Button onClick={() => onDelete(deleted.id)} color="secondary">
                    Eliminar
            </Button>
            </DialogActions>
        </Dialog>
    </TableContainer>

}