export const put = async (folio, data, headers) => {
    return fetch(
        `${BASE_URL}/api/rh/empresa/detalle_registro_patronal/${folio}`,
        {
            method: 'PUT',
            headers,
            body: JSON.stringify(data)
        }
    ).then(response => response.json());
}