import React from 'react'
import { Grid, TextField, Button, InputAdornment, Typography, IconButton } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { ArrowBack, ArrowForward } from '@material-ui/icons';
import DeleteIcon from '@material-ui/icons/Delete';
import LocalAtmIcon from '@material-ui/icons/LocalAtm';
import {
    ValidacionTelefono,
    ValidacionCantidad,
    ValidacionPorcentaje
} from '../../../../libraries/Mask';

const useStyles = makeStyles(theme => ({
    container: {
        padding: theme.spacing(4),
    },
}));

/*
const textDisabled = makeStyles({
    root: {
        '&$disabled': {
            color: 'rgba(0, 0, 0, 0.87)'
        },
    },
}, { name: 'MuiInputBase' });
*/


export default function Fiscales(props) {
    const classes = useStyles();
    const { infonavit, setInfonavit } = props;
    const { fonacot, setFonacot } = props;
    const { onlySee } = props;
    //textDisabled();

    const removeCredito = (pos) => {
        let array = [...fonacot];
        array.splice(pos, 1);
        setFonacot(array);
    }

    const addCredito = () => {
        if (fonacot.length < 5) {
            const newTel = [...fonacot];
            newTel.push({ numero_credito: '', cuota_fija: '', verify: '', tipo: fonacot.length });
            setFonacot(newTel);
        }
    }


    const changeFonacot = (e, pos) => {
        const { name, value } = e.target;
        let newArray = [...fonacot];
        newArray[pos][name] = value;
        newArray[pos].verify = true;
        setFonacot(newArray);
    }

    const changeInfonavit = e => {
        const { name, value } = e.target;
        setInfonavit({
            ...infonavit,
            [name]: value,
            verify: true
        })
    }

    const extraFonacot = () => {
        return fonacot.map((item, index) => {
            return <Grid key={`correo-extra-empleado-${index + 1}`}
                item xs={12} md={12} lg={12}>
                <Grid container spacing={2}>
                    <Grid item xs={7} sm={7} md={7} lg={7}>
                        <TextField
                            disabled={onlySee}
                            variant="outlined"
                            label={`Credito Fonacot${index !== 0 ? ' ' + (index + 1) : ''}`}
                            fullWidth
                            name="numero_credito"
                            onChange={(e) => changeFonacot(e, index)}
                            value={fonacot[index].numero_credito}
                            placeholder={`Ej. 000 11 00 111`}
                            InputLabelProps={{ shrink: true }}
                            InputProps={{ inputComponent: ValidacionTelefono }}
                            error={fonacot[index].verify !== '' ? !fonacot[index].verify : false}
                            helperText={`${fonacot[index].verify !== '' ? !fonacot[index].verify ? 'Rellena los campos restantes' : '' : ''}`}
                            autoComplete='off'
                        />
                    </Grid>
                    <Grid item xs={5} sm={5} md={5} lg={5}>
                        <TextField
                            disabled={onlySee}
                            fullWidth
                            variant="outlined"
                            label={`Cuota Fija${index !== 0 ? ' ' + (index + 1) : ''}`}
                            name="cuota_fija"
                            onChange={(e) => changeFonacot(e, index)}
                            value={fonacot[index].cuota_fija}
                            error={fonacot[index].verify !== '' ? !fonacot[index].verify : false}
                            InputLabelProps={{ shrink: true }}
                            placeholder={"Ej. 760.00"}
                            autoComplete='off'
                            InputProps={{
                                startAdornment: <InputAdornment position="start">$</InputAdornment>,
                                inputComponent: ValidacionCantidad,
                                endAdornment: <>{index !== 0 &&
                                    <InputAdornment>
                                        {!onlySee && <IconButton size="small"
                                            onClick={() => removeCredito(index)}><DeleteIcon fontSize="small" />
                                        </IconButton>}
                                    </InputAdornment>}
                                </>
                            }} />
                    </Grid>
                </Grid>
            </Grid>
        });
    }

    return (
        <Grid container spacing={4} className={classes.container}>
            <Grid item xs={12} sm={12} md={onlySee ? 12 : 4} lg={onlySee ? 12 : 5}>
                <Grid container spacing={2}>
                    <Grid item xs={12} sm={12} md={12} lg={12}>
                        <Typography variant="subtitle2" display="block" style={{ textAlign: 'center' }}>Fonacot</Typography>
                    </Grid>
                    {extraFonacot()}

                    {(!onlySee && fonacot.length < 5) &&
                        <Grid item xs={12} sm={12} md={12} lg={12} container
                            direction="row"
                            justify="flex-end"
                            alignItems="center">

                            <Button
                                size="small"
                                color="primary"
                                onClick={addCredito}
                                startIcon={<LocalAtmIcon />}>
                                Agregar
                        </Button>

                        </Grid>}
                </Grid>
            </Grid>

            <Grid item xs={12} sm={12} md={onlySee ? 12 : 8} lg={onlySee ? 12 : 7}>
                <Grid container spacing={2}>
                    <Grid item xs={12} sm={12} md={12} lg={12}>
                        <Typography variant="subtitle2" display="block" style={{ textAlign: 'center' }}>Infonavit</Typography>
                    </Grid>
                    <Grid item xs={12} sm={6} md={onlySee ? 12 : 5} lg={5}>
                        <TextField
                            disabled={onlySee}
                            variant="outlined"
                            fullWidth
                            label="Credito Infonavit"
                            InputLabelProps={{ shrink: true }}
                            placeholder={"Ej. 001 12 12 000"}
                            value={infonavit.numero_credito}
                            name="numero_credito"
                            onChange={changeInfonavit}
                            helperText={`${infonavit.verify !== '' ? !infonavit.verify ? 'Rellena los campos restantes' : '' : ''}`}
                            error={infonavit.verify !== '' ? !infonavit.verify : false}
                            InputProps={{ inputComponent: ValidacionTelefono }}
                            autoComplete='off'
                        />
                    </Grid>
                    <Grid item xs={4} sm={2} md={onlySee ? 4 : 2} lg={2}>
                        <TextField
                            disabled={onlySee}
                            variant="outlined"
                            fullWidth
                            label="% Desc."
                            InputLabelProps={{ shrink: true }}
                            placeholder={"Ej. 12"}
                            name="porcentaje_descuento"
                            value={infonavit.porcentaje_descuento} onChange={changeInfonavit}
                            error={infonavit.verify !== '' ? !infonavit.verify : false}
                            InputProps={{
                                endAdornment: <InputAdornment position="end">%</InputAdornment>,
                                inputComponent: ValidacionPorcentaje
                            }}
                            autoComplete='off'
                        />
                    </Grid>
                    <Grid item xs={4} sm={2} md={onlySee ? 4 : 2} lg={2}>
                        <TextField
                            disabled={onlySee}
                            variant="outlined"
                            fullWidth
                            label="Fac. Desc."
                            InputLabelProps={{ shrink: true }}
                            placeholder={"Ej. 10"}
                            name="factor_descuento"
                            value={infonavit.factor_descuento}
                            error={infonavit.verify !== '' ? !infonavit.verify : false}
                            onChange={changeInfonavit}
                            InputProps={{
                                endAdornment: <InputAdornment position="end">%</InputAdornment>,
                                inputComponent: ValidacionPorcentaje
                            }}
                            autoComplete='off'
                        />
                    </Grid>
                    <Grid item xs={4} sm={2} md={onlySee ? 4 : 3} lg={3}>
                        <TextField
                            disabled={onlySee}
                            variant="outlined"
                            fullWidth label="Cuota Fija"
                            InputLabelProps={{ shrink: true }}
                            placeholder={"Ej. 760.00"}
                            name="cuota_fija"
                            onChange={changeInfonavit}
                            value={infonavit.cuota_fija}
                            error={infonavit.verify !== '' ? !infonavit.verify : false}
                            InputProps={{
                                startAdornment: <InputAdornment position="start">$</InputAdornment>,
                                inputComponent: ValidacionCantidad
                            }} />
                    </Grid>
                    {
                        !props.expandable &&
                        <Grid container item
                            direction="row"
                            justify="flex-end"
                            alignItems="center" xs={12} sm={12} md={12} lg={12} >
                            <Button startIcon={<ArrowBack />}
                                onClick={props.buttons.back}>Anterior</Button>
                            <Button
                                variant="outlined"
                                color="primary"
                                endIcon={<ArrowForward />}
                                onClick={props.buttons.next}
                            >Siguiente</Button>
                        </Grid>
                    }
                </Grid>
            </Grid>
        </Grid>
    )
}
