import React, { useEffect } from 'react';
import { BASE_URL } from '../../../libraries/RequestFetch';
import { calculateNomina } from './helperNomina';
import moment from 'moment';
import { post } from './CollapseRow/helperPD'
import { SessionContext } from '../../../routes/adminrh/CustomRoutes';


const clearSettings = (generalSettings, empresa, contrato, nomina) => {
    const calculateDias = (periodo) => {
        switch (periodo) {
            case 'S': return 7;
            case 'C': return 14;
            case 'Q': return 15.25;
            case 'M': return 30.5
            default: return 0;
        }
    }


    if (empresa && contrato && nomina) {
        if (nomina.estatus === 'C') nomina.estatus = 'P';
        const newSettings = {
            ...generalSettings,
            ...nomina,
            periodo: contrato.periodo_calculo,
            perido_inicio: nomina.fecha_inicio_periodo,
            perido_fin: nomina.fecha_fin_periodo,
            razon_social: empresa.razon_social,
            dias: calculateDias(contrato.periodo_calculo),
            comision: contrato.comision,
            estatus: nomina.estatus
        }
        return newSettings;
    }
    return {}
}

const clearEmpleado = (empleado, detalleNomina, deduccionesPercepciones, inasistencia) => {
    const getPerpcepcionDeduccion = (detalleNomina, deduccionesPercepciones, tipo, id_contrato) => {
        return detalleNomina
            .filter(d => d.id_percepcion_deduccion[0] === tipo && d.id_contrato === id_contrato)
            .map(m => {
                const element = deduccionesPercepciones.find(dp => dp.id_percepcion_deduccion === m.id_percepcion_deduccion);
                const nombre = element.nombre;
                const { monto, ...newElement } = m;
                return { ...newElement, value: monto, nombre: nombre, save: true };
            });
    }

    const getInasistencias = (inasistencia, id_contrato) => {
        const faltas = inasistencia.find(e => e.id_contrato === id_contrato);
        if (faltas) return faltas;
        else return { folio_inasistencia: undefined, cantidad_inasistencias: 0, id_contrato }
    }

    if (empleado && detalleNomina && deduccionesPercepciones)
        return empleado.map(e => {
            const percepciones = getPerpcepcionDeduccion(detalleNomina, deduccionesPercepciones, 'P', e.id_contrato);
            const deducciones = getPerpcepcionDeduccion(detalleNomina, deduccionesPercepciones, 'D', e.id_contrato);
            const faltas = getInasistencias(inasistencia, e.id_contrato);
            return {
                empleado: e,
                tipo: true,
                dias: { total: 0, faltas: faltas },
                percepciones: {
                    total: 0, percepciones: percepciones
                },
                deducciones: {
                    total: 0, deducciones: deducciones
                },
                costo_social: 0
            }
        })
    return [];
}

const clearVaribale = (empleados, idNomina) => {
    return empleados.map(values => {
        Object.keys(values).forEach(key => {
            if (values[key] === null) {
                values[key] = '';
            }
        })
        const newId = ('00000' + values.folio)
        const identificador = `NOMVARIABLE-${newId.substring(newId.length - 5)}`
        return {
            empleado: {
                ...values,
                id_nomina: idNomina,
                identificador: identificador
            },
            tipo: false,
            dias: { total: 0 },
            percepciones: {
                total: 0, percepciones:
                    [{ id_percepcion_deduccion: 'OTRAS', nombre: `OTRAS PERCEPCIONES`, value: values.monto }]
            },
            deducciones: {
                total: 0,
                deducciones: []
            }
        }
    })
}

const filterPD = (type, DeduccionesPercepciones) => {
    if (type === 'Percepcion')
        return DeduccionesPercepciones.filter(i => i.tipo === 'P');
    else
        return DeduccionesPercepciones.filter(i => i.tipo === 'D');
}



function calculaNomina(rows, generalSettings, fiscales, totales) {
    return calculateNomina(
        rows,
        generalSettings,
        totales,
        fiscales
    );
}

function reducer(oldState, { newState, action }) {
    let newNomina = {};
    switch (action) {
        case 'EMPLEADOS':
            newNomina = calculaNomina(
                newState,
                oldState.generalSettings,
                oldState.fiscales,
                oldState.totales
            );
            return { ...oldState, ...newNomina };
        case 'SETTINGS':
            newNomina = calculaNomina(
                oldState.empleados,
                newState,
                oldState.fiscales,
                oldState.totales);
            return { ...oldState, generalSettings: newState, ...newNomina };
        case 'ALL':
            newNomina = calculaNomina(
                newState.empleados,
                newState.generalSettings,
                newState.fiscales,
                oldState.totales
            );
            return { ...oldState, ...newState, ...newNomina };
        default:
            throw new Error();
    }
}


export function useDataNomina({ idNomina, idContrato, idEmpresa, anio }) {
    const { cabeceras, isTokenExpired } = React.useContext(SessionContext);
    const [data, setData] = React.useReducer(reducer, {
        loading: true,
        fiscales: {},
        generalSettings: {
            identificador: '',
            periodo: '',
            perido_inicio: null,
            perido_fin: null,
            fecha_pago: null,
            razon_social: '',
            dias: '',
            aplico_costo_social: 'N',
            comision: 0.0,
            imss_aplicado: false,
            isr_aplicado: false,
            retencion_aplicada: false
        },
        percepciones: [],
        deducciones: [],
        empleados: [],
        totales: {
            nomina: 0,
            comision: 0,
            costo_social: 0,
            isn: 0,
            subtotal: 0,
            iva: 0,
            total: 0
        }
    });

    const setEmpleados = (newState) => {
        setData({ newState, action: 'EMPLEADOS' });
    }

    const setGeneralSettings = (newState) => {
        setData({ newState, action: 'SETTINGS' })
    }

    const getAllDatas = async () => {
        const options = { method: 'GET', headers: cabeceras };
        Promise.all([
            fetch(BASE_URL + `/api/rh/nomina?id_nomina=${idNomina}`, options),
            fetch(BASE_URL + `/api/rh/empleado?query=es_vigente-id_empleado-estatus-identificador-nombre-primer_apellido-segundo_apellido-no_tarjeta-no_cuenta-clabe-id_banco-id_contrato-salario_diario-salario_capturado-clave_patronal-id_empresa_pertene&id_empresa_labora=${idEmpresa}`, options),
            fetch(BASE_URL + `/api/rh/nomina/detalle_nomina?id_nomina=${idNomina}`, options),
            fetch(BASE_URL + '/api/rh/nomina/percepcion_deduccion', options),
            fetch(BASE_URL + `/api/rh/empresa/contrato?id_contrato=${idContrato}`, options),
            fetch(BASE_URL + `/api/rh/empresa?query=razon_social-rfc&id_empresa=${idEmpresa}`, options),
            fetch(BASE_URL + `/api/rh/nomina/nomina_variable?id_nomina=${idNomina}`, options),
            fetch(BASE_URL + `/api/rh/nomina/catalogo/isr?anio=${anio}`, options),
            fetch(BASE_URL + `/api/rh/nomina/catalogo/integracion?anio=${anio}`, options),
            fetch(BASE_URL + `/api/rh/nomina/catalogo/uma?anio=${anio}`, options),
            fetch(BASE_URL + `/api/rh/nomina/catalogo/subsidio?anio=${anio}`, options),
            fetch(BASE_URL + '/api/rh/banco', options),
            fetch(BASE_URL + `/api/rh/nomina/inasistencia?id_nomina=${idNomina}`, options)
        ]).then(resp => resp)
            .then(async response => {
                const responseNomina = await response[0].json();
                if (!isTokenExpired(responseNomina)) {
                    let { empleados } = await response[1].json();
                    let detalle_nomina = await response[2].json();
                    const { percepciondeduccion } = await response[3].json();
                    const { contratoEmpresa } = await response[4].json();
                    const { empresas } = await response[5].json();
                    const empleadosVariables = await response[6].json();
                    const isr = await response[7].json();
                    const integracion = await response[8].json();
                    const { uma } = await response[9].json();
                    const subsidio = await response[10].json();
                    const { bancos } = await response[11].json();
                    const { inasistencia } = await response[12].json();


                    const prima = await getClavesPatronales(responseNomina.nomina[0], cabeceras, isTokenExpired);

                    const fiscales = { ...isr, ...integracion, uma: uma[0].diario, ...subsidio, prima }
                    const generalSettings = clearSettings(data.generalSettings, empresas[0], contratoEmpresa[0], responseNomina.nomina[0]);
                    const percepciones = filterPD("Percepcion", percepciondeduccion);
                    const deducciones = filterPD("Deducciones", percepciondeduccion);

                    if (detalle_nomina.nomina.length === 0) detalle_nomina.nomina = await getOldNomina(responseNomina.nomina[0], idContrato, cabeceras, isTokenExpired);
                    if (responseNomina.nomina.length > 0) {
                        if (responseNomina.nomina[0].estatus === "C" || responseNomina.nomina[0].estatus === "P") empleados = empleados.filter(e => e.es_vigente);
                        else empleados = empleados.filter(e => detalle_nomina.nomina.some(d => d.id_contrato === e.id_contrato));

                        const empleadosNomina = clearEmpleado(empleados, detalle_nomina.nomina, percepciondeduccion, inasistencia);
                        const empleadosVarian = clearVaribale(empleadosVariables.nomina);
                        empleados = [...empleadosNomina, ...empleadosVarian];
                    }

                    setData({
                        newState: { bancos, fiscales, generalSettings, percepciones, deducciones, empleados, loading: false },
                        action: 'ALL'
                    });
                }

            }).catch(error => console.log(error));
    };


    useEffect(() => {
        getAllDatas();
        //eslint-disable-next-line
    }, [])

    return {
        ...data,
        setEmpleados,
        setGeneralSettings
    };
}

const getClavesPatronales = async (nomina, cabeceras, isTokenExpired) => {
    const primaRiesgoResponse = await fetch(BASE_URL + `/api/rh/empresa/detalle_registro_patronal`, { method: 'GET', headers: cabeceras });
    const responseRegistros = await primaRiesgoResponse.json();
    if (!isTokenExpired(responseRegistros)) {
        let { registros } = responseRegistros
        registros = registros.reduce((prev, current) => {
            prev[moment(current.fecha_registro).format('YYYY')] = prev[moment(current.fecha_registro).format('YYYY')] || [];
            prev[moment(current.fecha_registro).format('YYYY')].push(current);
            return prev;
        }, {})

        const yearNomina = parseInt(moment(nomina.fecha_inicio_periodo).format('YYYY'));
        if (Object.keys(registros).length > 0) {
            if (registros[yearNomina]) return registros[yearNomina];
            else if (registros[yearNomina - 1]) return registros[yearNomina - 1];
            else return registros[Object.keys(registros)[0]];
        }
    }
    return [];
}

const getOldNomina = async (nominaActual, idContrato, cabeceras, isTokenExpired) => {
    const anteriorNomina = moment(nominaActual.fecha_inicio_periodo)
        .subtract(1, 'days')
        .format('YYYY-MM-DD');
    const oldNominaReq = await fetch(
        BASE_URL + `/api/rh/nomina?fecha_fin_periodo=${anteriorNomina}&id_contrato=${idContrato}`,
        {
            method: 'GET',
            headers: cabeceras
        });
    const responseNomina = await oldNominaReq.json();
    if (!isTokenExpired(responseNomina)) {
        const { nomina } = responseNomina;
        if (nomina.length > 0) {
            const oldPDReq = await fetch(
                BASE_URL + `/api/rh/nomina/detalle_nomina?id_nomina=${nomina[0].id_nomina}`,
                {
                    method: 'GET',
                    headers: cabeceras
                });
            let oldPD = await oldPDReq.json();
            if (!isTokenExpired(oldPD)) {
                let toSendDetalle = oldPD.nomina.map(e => {
                    return { ...e, id_nomina: nominaActual.id_nomina }
                });
                for (let element of toSendDetalle) {
                    const response = await post(element, cabeceras);
                    if (!isTokenExpired(response));
                }
                return toSendDetalle;
            }
        }
    }
    return [];
}