import React from 'react'
import MaterialTable from 'material-table';
import { Typography } from '@material-ui/core'
import { Snackbar } from '@material-ui/core';
import MuiAlert from '@material-ui/lab/Alert';
import { localization, actions, options } from '../../../../libraries/ConfigCatalog';
import { useFactoryLaw } from '../useFactoryLaw';
import { YearMask, LimitMask } from '../MaskLeyes'
import FieldTable from '../FieldTable';

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

export default function TableISR() {

    const {
        data,
        loading,
        refresh,
        post,
        remove,
        put
    } = useFactoryLaw({ type: 'isr' });
    const [toast, setToast] = React.useState({
        open: false,
        severity: "",
        title: ""
    });


    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            setToast({ ...toast, open: false });
            return;
        }
        setToast({ ...toast, open: false });
    };

    actions[0].onClick = refresh;
    localization.body.editRow.deleteText = <Typography variant="button" gutterBottom>¿Está seguro que desea continuar?</Typography>


    const keys = [
        'anio',
        'limite_inferior',
        'limite_superior',
        'cuota_fija',
        'excendente_limite_inferior'
    ]

    const columns = [
        { title: 'ID', field: "folio_isr", editable: 'never', hidden: true },
        {
            title: 'Año', field: 'anio', defaultSort: 'desc',
            editComponent: props => <FieldTable {...props} placeholder="Ej. 2020"
                InputProps={{ inputComponent: YearMask }} />
        },
        {
            title: 'Limite Inferior', field: 'limite_inferior',
            editComponent: props => <FieldTable {...props} placeholder="Ej. 1"
                InputProps={{ inputComponent: LimitMask }} />
        },
        {
            title: 'Limite Superior', field: 'limite_superior',
            editComponent: props => <FieldTable {...props} placeholder="Ej. 2"
                InputProps={{ inputComponent: LimitMask }} />
        },
        {
            title: 'Cuota Fija', field: 'cuota_fija',
            editComponent: props => <FieldTable {...props} placeholder="Ej. 15"
                InputProps={{ inputComponent: LimitMask }} />
        },
        {
            title: 'Excedente Limite Inf.', field: 'excendente_limite_inferior',
            editComponent: props => <FieldTable {...props} placeholder="Ej. 0.25"
                InputProps={{ inputComponent: LimitMask }} />
        },
    ];

    const validLimit = (newData) => {
        const inferior = parseFloat(newData.limite_inferior)
        const superior = parseFloat(newData.limite_superior);
        return inferior < superior;
    }

    return (
        <>
            <MaterialTable
                title="ISR"
                columns={columns}
                localization={localization}
                data={data}
                actions={actions}
                options={options}
                isLoading={loading}
                editable={{
                    onRowAdd: (newData) =>
                        new Promise((resolve, reject) => {
                            setTimeout(() => {
                                const addKeys = Object.keys(newData);
                                if (!keys.every(key => addKeys.includes(key) && newData[key] !== '')) {
                                    setToast({
                                        open: true,
                                        severity: "warning",
                                        title: "Campos vacios!"
                                    })
                                    reject();
                                    return;
                                } else if (!validLimit(newData)) {
                                    setToast({
                                        open: true,
                                        severity: "warning",
                                        title: "Limite mayor no puede ser menor al mayor!"
                                    })
                                    reject();
                                    return;
                                }
                                else {
                                    resolve();
                                    post({ data: newData }, () => {
                                        setToast({
                                            open: true,
                                            severity: "success",
                                            title: "Creación exitosa",
                                        })
                                    })
                                }
                            }, 600);
                        })
                    ,
                    onRowUpdate: (newData, oldData) =>
                        new Promise((resolve, reject) => {
                            setTimeout(() => {
                                const addKeys = Object.keys(newData);
                                if (!keys.every(key => addKeys.includes(key) && newData[key] !== '')) {
                                    setToast({
                                        open: true,
                                        severity: "warning",
                                        title: "Campos vacios!"
                                    })
                                    reject();
                                    return;
                                } else if (!validLimit(newData)) {
                                    setToast({
                                        open: true,
                                        severity: "warning",
                                        title: "Limite mayor no puede ser menor al mayor!"
                                    })
                                    reject();
                                    return;
                                }
                                else {
                                    resolve();
                                    const { folio_isr, ...data } = newData;
                                    put({ id: folio_isr, data }, () => {
                                        setToast({
                                            open: true,
                                            severity: "success",
                                            title: "Actualización exitosa"
                                        })
                                    })
                                }
                            }, 600);
                        })
                    ,
                    onRowDelete: (oldData) =>
                        new Promise((resolve) => {
                            setTimeout(() => {
                                resolve();
                                remove({ id: oldData['folio_isr'] }, () => {
                                    setToast({
                                        open: true,
                                        severity: "success",
                                        title: "Eliminación exitosa",
                                    })
                                });
                            }, 600);
                        })
                    ,
                }}
            />


            <Snackbar open={toast.open} autoHideDuration={6000} onClose={handleClose}>
                <Alert onClose={handleClose} severity={toast.severity}>
                    {toast.title}
                    {toast.message && toast.message}
                </Alert>
            </Snackbar>
        </>
    );
}