import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ListSubheader from '@material-ui/core/ListSubheader';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import { ExpandLess, ExpandMore } from '@material-ui/icons';
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent';
import InfoIcon from '@material-ui/icons/Info';
import WorkIcon from '@material-ui/icons/Work';
import RoomIcon from '@material-ui/icons/Room';
import { green } from '@material-ui/core/colors';
import MonetizationOnIcon from '@material-ui/icons/MonetizationOn';
import RequestFetch, { BASE_URL } from '../../../libraries/RequestFetch';
import moment from "moment";
import MuiAlert from '@material-ui/lab/Alert';
import AlertTitle from '@material-ui/lab/AlertTitle';
import { Snackbar, Typography, IconButton, CardActions, Grid, Button } from '@material-ui/core';
import ExpandButton from '../Dash/ExpandButton';
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import { useHistory } from 'react-router-dom';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Save as SaveIcon } from '@material-ui/icons';
import WatchLaterRoundedIcon from '@material-ui/icons/WatchLaterRounded';
import PhoneRoundedIcon from '@material-ui/icons/PhoneRounded';
import AccountBalanceRoundedIcon from '@material-ui/icons/AccountBalanceRounded';
import Validacion from '../../../libraries/Validacion';
import Informacion from './Informacion/Informacion';
import Direccion from '../FormEmpresa/Direccion/Direccion';
import Empresa from './Empresa/Empresa';
import Financiero from './Financiero/Financiero';
import Contacto from './Contacto/Contacto';
import Fiscales from './Fiscales/Fiscales'
import Horario from './Horario/Horario';
import { withSnackbar } from 'notistack';

import { SessionContext } from '../../../routes/adminrh/CustomRoutes'

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const useStyles = makeStyles(theme => ({
    root: {
        width: '100%',
        backgroundColor: theme.palette.background.paper,
    },
    cardContent: {
        padding: '0px'
    },
    header: {
        background: '#fafafa'
    },
    float: {
        position: 'fixed',
        bottom: '30px',
        right: '30px',
        color: theme.palette.common.white,
        backgroundColor: green[500],
        '&:hover': {
            backgroundColor: green[600],
        },
    },
    title: {
        flexGrow: 1,
        marginTop: '15px ',
        marginBottom: '15px'
    },
    actions: {
        padding: theme.spacing(4),
    },
    subheader: {
        textAlign: 'center'
    },
    back: {
        left: -12,
        float: 'left'
    },
    save: {
        color: theme.palette.common.white,
        backgroundColor: green[500],
        '&:hover': {
            backgroundColor: green[600],
        },
    },
    buttonProgress: {
        color: green[500],
        position: 'absolute',
        top: '50%',
        left: '50%',
        marginTop: -12,
        marginLeft: -12,
    },
    wrapper: {
        margin: '0px 24px 8px 16px',
        position: 'relative',
    },
}));






function FormEmpleado(props) {
    const initInfo = {
        fileImg: null,
        url_foto: { value: '', verify: '' },
        rfc: { value: '', verify: '' },
        nombre: { value: '', verify: '' },
        primer_apellido: { value: '', verify: '' },
        segundo_apellido: { value: '', verify: '' },
        fecha_nacimiento: { value: '', verify: '' },
        estado_civil: { value: '', verify: '' },
        genero: { value: '', verify: '' },
        curp: { value: '', verify: '' },
        id_nacionalidad: { value: "122", verify: true },
        no_imss: { value: '', verify: '' },
        entidad_labora: { value: '', verify: '' },
        observaciones: { value: '', verify: '' },
    }
    const initDirec = {
        calle: { value: '', verify: '' },
        no_exterior: { value: '', verify: '' },
        no_interior: { value: '', verify: '' },
        colonia: { value: '', verify: '' },
        municipio: { value: '', verify: '' },
        entidad: { value: '', verify: '' },
        cp: { value: '', verify: '' },
    }
    const contratoData = {
        //id_contrato: { value: '' },
        //EMPRESA
        id_empresa_labora: { value: '', verify: '' },
        id_empresa_pertenece: { value: '', verify: '' },
        fecha_contrato_externo: { value: '', verify: '' },
        fecha_inicio_contrato: { value: '', verify: '' },
        fecha_fin_contrato: { value: '', verify: '' },
        motivo_contrato: { value: 'A', verify: true },

        //HORARIO
        id_jornada: { value: '', verify: '' },
        id_puesto: { value: '', verify: '' },
        id_departamento: { value: '', verify: '' },

        //FISCALES
        id_tipo_contrato: { value: '', verify: '' },
        id_regimen_contratacion: { value: '', verify: '' },
        //SALARIO
        salario_diario: { value: '', verify: '' },
        salario_capturado: { value: '', verify: '' },

        clave_patronal: { value: '', verify: '' },
    }

    const initFinan = {
        //FINANCIERO
        no_cuenta: { value: '', verify: '' },
        no_tarjeta: { value: '', verify: '' },
        clabe: { value: '', verify: '' },
        id_banco: { value: '', verify: '' },
    }

    const initInfonavit = {
        numero_credito: '',
        porcentaje_descuento: '',
        factor_descuento: '',
        cuota_fija: '',
        verify: ''
    }

    const initFonacot = [
        { numero_credito: '', cuota_fija: '', verify: '' },
    ]

    const initTel = [
        { value: '', verify: '', tipo: 'C', helperText: '' },
        { value: '', verify: '', tipo: 'F', helperText: '' },
    ]

    const initCorreo = [
        { value: '', verify: '', tipo_correo: 'P', helperText: '' },
        { value: '', verify: '', tipo_correo: 'E', helperText: '' },
    ]

    const diasHorario = [
        { dia_semana: 'Lunes', hora_entrada: '00:00', hora_salida: '00:00', checked: false },
        { dia_semana: 'Martes', hora_entrada: '00:00', hora_salida: '00:00', checked: false },
        { dia_semana: 'Miercoles', hora_entrada: '00:00', hora_salida: '00:00', checked: false },
        { dia_semana: 'Jueves', hora_entrada: '00:00', hora_salida: '00:00', checked: false },
        { dia_semana: 'Viernes', hora_entrada: '00:00', hora_salida: '00:00', checked: false },
        { dia_semana: 'Sabado', hora_entrada: '00:00', hora_salida: '00:00', checked: false },
        { dia_semana: 'Domingo', hora_entrada: '00:00', hora_salida: '00:00', checked: false }
    ]
    const classes = useStyles();
    const history = useHistory();

    //LOGICAL
    const [loading, setLoading] = React.useState(false);
    const [part, setPart] = React.useState({
        expandable: false,
        actual: 0,
        global: 0
    });
    const [toast, setToast] = React.useState({
        open: false,
        message: "",
        severity: "",
        title: ""
    });

    //PETICIONES
    const { cabeceras, isTokenExpired } = React.useContext(SessionContext);
    const [puesto, setPuesto] = React.useState([]);
    const [departamentos, setDepartamentos] = React.useState([]);
    const [jornadas, setJornadas] = React.useState([]);
    const [regimenContrato, setRegimenContrato] = React.useState([]);
    const [tipoContrato, setTipoContrato] = React.useState([]);
    const [int, setInt] = React.useState([]);
    const [all, setAll] = React.useState([]);
    const [nacionalidadades, setNacionalidades] = React.useState([]);
    const [bancos, setBancos] = React.useState([]);

    //STEEPS
    const [informacion, setInformacion] = React.useState(initInfo);
    const [direccion, setDireccion] = React.useState(initDirec);
    const [financiero, setFinanciero] = React.useState(initFinan);
    const [contrato, setContrato] = React.useState(contratoData);
    const [identificador, setIdentificador] = React.useState("");

    //STEEP CONTACTO
    const [dataTel, setDataTel] = React.useState([...initTel]);
    const [dataCorreo, setDataCorreo] = React.useState([...initCorreo]);

    //STEEP HORARIO
    const [dias, setDias] = React.useState([...diasHorario]);
    const [infonavit, setInfonavit] = React.useState(initInfonavit);
    const [fonacot, setFonacot] = React.useState([...initFonacot]);

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            setToast({ ...toast, open: false });
            return;
        }
        setToast({ ...toast, open: false });
    };

    const handleClick = (pos) => {
        setPart({ ...part, actual: pos })
    };

    const buttons = (() => {
        const next = () => {
            setPart({ ...part, actual: part.actual + 1, global: part.global + 1 });
        }

        const back = () => {
            setPart({ ...part, actual: part.actual - 1 });
        }

        return {
            next, back
        }
    })();

    React.useEffect(() => {

        let mounted = true;

        const exitoNacionalidad = async (res) => {
            if (mounted) {
                const respuesta = await res.json();
                setNacionalidades(respuesta);
            }
        }

        const exitoEmpre = async (res) => {
            if (mounted) {
                const respuesta = await res.json();
                const { empresas } = respuesta;
                setInt(empresas.filter((item) => item.es_interna === true));
                setAll(empresas);
            }
        }

        const exitoPuesto = async (res) => {
            if (mounted) {
                const respuesta = await res.json();
                setPuesto(respuesta);
            }
        }

        const exitoBanco = async (res) => {
            if (mounted) {
                const respuesta = await res.json();
                setBancos(respuesta.bancos);
            }
        }

        const exitoJornada = async (res) => {
            if (mounted) {
                const respuesta = await res.json();
                setJornadas(respuesta.jornadas);
            }
        }

        const exitoDepartamento = async (res) => {
            if (mounted) {
                const respuesta = await res.json();
                console.log(respuesta.departamentos);
                setDepartamentos(respuesta.departamentos);
            }
        }

        const exitoContrato = async (res) => {
            if (mounted) {
                const respuesta = await res.json();
                setTipoContrato(respuesta.tiposContrato);
            }
        }

        const fallo = async (res) => {
            const respuesta = await res.json();
            if (!isTokenExpired(respuesta)) console.log(respuesta);
        }


        const getdata = () => {
            if (mounted) {
                RequestFetch.get({
                    url: `${RequestFetch.BASE_URL}/api/rh/empresa?query=id_empresa-razon_social-es_interna-identificador`,
                    headers: cabeceras,
                    exito: exitoEmpre,
                    fallo: fallo,
                    error_request: (error) => {
                        history.push('/server-error')
                    }
                });

                RequestFetch.get({
                    url: `${RequestFetch.BASE_URL}/api/rh/puesto?query=id_puesto-nombre_puesto`,
                    headers: cabeceras,
                    exito: exitoPuesto,
                    fallo: fallo,
                    error_request: (error) => {
                        history.push('/server-error');
                    }
                });

                RequestFetch.get({
                    url: `${RequestFetch.BASE_URL}/api/rh/nacionalidad`,
                    headers: cabeceras,
                    exito: exitoNacionalidad,
                    fallo: fallo,
                    error_request: (error) => {
                        history.push('/server-error');
                    }
                });

                RequestFetch.get({
                    url: `${RequestFetch.BASE_URL}/api/rh/banco`,
                    headers: cabeceras,
                    exito: exitoBanco,
                    fallo: fallo,
                    error_request: (error) => {
                        history.push('/server-error');
                    }
                });

                RequestFetch.get({
                    url: `${RequestFetch.BASE_URL}/api/rh/jornada`,
                    headers: cabeceras,
                    exito: exitoJornada,
                    fallo: fallo,
                    error_request: (error) => {
                        history.push('/server-error');
                    }
                });

                RequestFetch.get({
                    url: `${RequestFetch.BASE_URL}/api/rh/departamento`,
                    headers: cabeceras,
                    exito: exitoDepartamento,
                    fallo: fallo,
                    error_request: (error) => {
                        history.push('/server-error');
                    }
                });

                RequestFetch.get({
                    url: `${RequestFetch.BASE_URL}/api/rh/tipo_contrato`,
                    headers: cabeceras,
                    exito: exitoContrato,
                    fallo: fallo,
                    error_request: (error) => {
                        history.push('/server-error');
                    }
                });

                RequestFetch.get({
                    url: `${RequestFetch.BASE_URL}/api/rh/regimen_contratacion`,
                    headers: cabeceras,
                    exito: async (res) => {
                        if (mounted) {
                            const respuesta = await res.json();
                            setRegimenContrato(respuesta.regimenesContratacion);
                        }
                    },
                    fallo: fallo,
                    error_request: (error) => {
                        history.push('/server-error');
                    }
                });
            }
        }

        getdata();

        return () => mounted = false;
        // eslint-disable-next-line
    }, [])


    const openTab = (parte, message) => {
        const newMessage = message !== undefined ? message : "Es necesario rellenar los campos requeridos";
        setToast({
            open: true,
            message: newMessage,
            severity: "warning",
            title: "Error en algunos campos!"
        });
        setPart({ ...part, actual: parte });
        setLoading(false);
    }

    const validaAll = () => {
        const validaInfomacion = () => {
            const newData = { ...informacion };
            const keys = ['nombre', 'primer_apellido', 'fecha_nacimiento', 'estado_civil', 'genero', 'id_nacionalidad', 'rfc', 'curp', 'no_imss', 'entidad_labora'];
            keys.forEach((key) => {
                switch (key) {
                    case 'rfc':
                        newData['rfc'].verify = `${newData['rfc'].value.replace(/\s/g, '')}`.length === 13;
                        break;
                    case 'curp':
                        newData['curp'].verify = `${newData['curp'].value.replace(/\s/g, '')}`.length === 18;
                        break;
                    case 'no_imss':
                        newData['no_imss'].verify = `${newData['no_imss'].value.replace(/\s/g, '')}`.length === 11;
                        break;
                    default:
                        newData[key].verify = `${newData[key].value}`.length > 0 && newData[key].verify;
                        break;
                }
            });


            const resul = keys.filter(key => {
                return newData[key].verify !== true;
            });
            setInformacion(newData);
            return resul.length === 0;
        }

        const validaDireccion = () => {
            const newData = { ...direccion };
            const keys = ['calle', 'colonia', 'municipio', 'entidad', 'cp'];
            keys.forEach((key) => {
                newData[key].verify = `${newData[key].value}`.length > 0 && newData[key].verify;
            })
            const resul = keys.filter(key => {
                return newData[key].verify !== true;
            })
            setDireccion(newData);
            return resul.length === 0;
        }

        const validaContrato = () => {
            const newData = { ...contrato };
            const keys = Object.keys(newData);
            keys.forEach((key) => {
                switch (key) {
                    case 'id_contrato': case 'id_empresa_pertenece': break;
                    case 'fecha_fin_contrato':
                        newData[key].verify = true;
                        break;
                    default:
                        newData[key].verify = `${newData[key].value}`.length > 0 && newData[key].verify;
                }
            })
            console.log(newData);
            const resul = keys.filter(key => {
                return newData[key].verify !== true;
            })
            setContrato(newData);
            return resul.length === 0;
        }

        const validaFinanciero = () => {
            const newData = { ...financiero };
            const keys = ['no_cuenta', 'no_tarjeta', 'clabe', 'id_banco'];

            keys.forEach((key) => {
                const value = `${newData[key].value}`.replace(/\s/g, '');
                if (value.length > 0) {
                    switch (key) {
                        case 'no_cuenta': newData[key].verify = value.length === 10 && newData[key].verify; break;
                        case 'no_tarjeta': newData[key].verify = value.length === 16 && newData[key].verify; break;
                        case 'clabe': newData[key].verify = value.length === 18 && newData[key].verify; break;
                        default: newData[key].verify = true;;
                    }
                } else newData[key].verify = true;;
            })
            const resul = keys.filter(key => {
                return newData[key].verify !== true;
            });
            setFinanciero(newData);
            return resul.length === 0;
        }

        const validaCorreos = () => {
            const newData = [...dataCorreo];
            const oks = [];
            newData.forEach((element) => {
                const correo = element.value.replace(/\s/g, '');
                if (correo.length > 0) {
                    const verify = Validacion.esCorreo(correo) && element.verify;
                    element.verify = verify;
                    oks.push(verify);
                }
            })
            const resul = oks.filter(item => {
                return item !== true;
            });
            if (oks.length === 0) {
                newData[0].verify = false;
                newData[0].helperText = "Registra al menos uno";
                newData[1].verify = false;
                newData[1].helperText = "Registra al menos uno";
                return false;
            }
            setDataCorreo(newData);
            return resul.length === 0;
        }

        const validaNumero = () => {
            const newData = [...dataTel];
            const oks = [];
            newData.forEach((element) => {
                const correo = element.value.replace(/\s/g, '');

                if (correo.length === 10) {
                    const verify = element.verify;
                    element.verify = verify;
                    oks.push(verify);
                }
            })
            const resul = oks.filter(item => {
                return item !== true;
            })
            if (oks.length === 0) {
                newData[0].verify = false;
                newData[0].helperText = "Registra al menos uno";
                newData[1].verify = false;
                newData[1].helperText = "Registra al menos uno"
                return false;
            }
            setDataTel(newData);
            return resul.length === 0;
        }

        const validaInfonavit = () => {
            let { numero_credito, porcentaje_descuento, factor_descuento, cuota_fija } = infonavit;
            numero_credito = numero_credito.replace(/\s/g, '');
            if (numero_credito.length > 0) {
                let error = numero_credito.length === 10 && (porcentaje_descuento.length > 0 || factor_descuento.length > 0 || cuota_fija.length > 0);
                setInfonavit({ ...infonavit, verify: error });
                return error;
            }
            else {
                let error = numero_credito.length === 0 && porcentaje_descuento.length === 0 && factor_descuento.length === 0 && cuota_fija.length === 0;
                setInfonavit({ ...infonavit, verify: error });
                return error;
            }
        }

        const validaFonacot = () => {
            let array = [...fonacot];
            array.forEach((item, index) => {
                let error = '';
                const numero_credito = item.numero_credito.replace(/\s/g, '');
                const cuota_fija = item.cuota_fija.replace(/\s/g, '');
                if (numero_credito.length > 0)
                    error = numero_credito.length === 10 && cuota_fija.length > 0;
                else
                    error = numero_credito.length === 0 && cuota_fija.length === 0;

                array[index] = { numero_credito: numero_credito, cuota_fija: cuota_fija, verify: error };
            });
            setFonacot(array);

            const resul = array.filter(item => {
                return item.verify !== true;
            })
            return resul.length === 0;
        }



        const allValidaciones = () => {
            if (validaInfomacion())
                if (validaDireccion())
                    if (validaCorreos())
                        if (validaNumero())
                            if (validaFinanciero())
                                if (validaInfonavit())
                                    if (validaFonacot())
                                        if (validaContrato())
                                            return true;
                                        else {
                                            openTab(6, "Contrato tiene campos vacios");
                                            return false;
                                        }
                                    else {
                                        openTab(5, "El credito Fonacot tiene campos vacios");
                                        return false;
                                    }
                                else {
                                    openTab(5, "El credito Infonavit tiene campos vacios");
                                    return false;
                                }
                            else {
                                openTab(4, "Debe registrar el banco y al menos un dato bancario");
                                return false;
                            }
                        else {
                            openTab(2);
                            return false;
                        }
                    else {
                        openTab(2);
                        return false;
                    }
                else {
                    openTab(1);
                    return false;
                }
            else {
                openTab(0);
                return false;
            }

        }

        return allValidaciones();
    }

    const clearArray = (data) => data.map(e => {
        const newObject = {};
        for (let key in e) {
            if (key !== 'dia_semana' && key !== 'tipo' && key !== 'tipo_correo' && key !== 'checked' && key !== 'hora_entrada' && key !== 'hora_salida')
                newObject[key] = ""
            else if (key === 'checked') newObject[key] = false;
            else newObject[key] = e[key];
        }
        return newObject
    })

    const separe = (() => {

        const general = () => {
            const data = { ...informacion, ...direccion, ...financiero }
            const keys = Object.keys(data);
            let newData = new FormData();
            keys.forEach((key) => {
                switch (key) {
                    case 'url_foto':
                        break;
                    case 'fileImg':
                        if (data[key] !== null)
                            newData.append('foto', data[key]);
                        break;
                    case 'rfc': case 'curp':
                        newData.append(key, data[key].value.toUpperCase());
                        break;
                    case 'fecha_nacimiento': case 'fecha_traspaso': case 'fecha_contrato':
                        newData.append(key, moment.utc(data[key].value, "DD/MM/YYYY").format("YYYY/MM/DD"));
                        break;
                    case 'entidad':
                        newData.append('entidad_reside', data[key].value);
                        break;
                    case 'no_cuenta': case 'no_tarjeta': case 'clabe':
                        let newValue = data[key].value.replace(/\s/g, '');
                        if (newValue.length > 0)
                            newData.append(key, newValue);
                        break;
                    default:
                        if (`${data[key].value}`.length > 0)
                            newData.append(key, data[key].value)
                        break;
                }
            });
            return newData;
        }

        const contratoDat = (id) => {
            const data = { ...contrato }
            const keys = Object.keys(data);
            var newData = {};
            keys.forEach((key) => {
                switch (key) {
                    case 'id_contrato': case 'id_empresa_pertence': case 'id_empresa_pertenece': break;
                    case 'fecha_contrato_externo': case 'fecha_inicio_contrato': case 'fecha_fin_contrato':
                        const date = moment.utc(data[key].value)
                        if (date.isValid())
                            newData[key] = date.format("YYYY/MM/DD");
                        break;
                    case 'id_empresa_labora':
                        newData[key] = data[key].value.id_empresa;
                        break;
                    case 'id_puesto': case 'id_departamento':
                        newData[key] = data[key].value[key];
                        break;
                    default:
                        if (`${data[key].value}`.length > 0)
                            newData[key] = data[key].value;
                        break;
                }
            });
            newData['id_empleado'] = id;
            return newData;
        }

        const telefonos = (id) => {
            const newTel = [];
            dataTel.forEach(item => {
                const value = item.value.replace(/\s/g, '');
                if (value.length >= 5)
                    newTel.push({ numero: value, tipo: item.tipo })
            })
            if (newTel.length > 0)
                return {
                    id_empleado: id,
                    telefonos: newTel
                }
            else return null;
        }

        const correos = (id) => {
            const array = [];
            dataCorreo.forEach(item => {
                const value = item.value.replace(/\s/g, '');
                if (value.length >= 5)
                    array.push({ correo: value, tipo_correo: item.tipo_correo })
            })
            if (array.length > 0)
                return {
                    id_empleado: id,
                    correos: array
                }
            else return null;
        }

        const creditoFonacot = (id) => {
            const array = [];
            fonacot.forEach(item => {
                const value = item.numero_credito.replace(/\s/g, '');
                if (value.length === 10)
                    array.push({ numero_credito: value, cuota_fija: item.cuota_fija })
            })
            return array.length > 0 ? {
                id_empleado: id,
                creditos: array
            } : null;
        }

        const creditoInfonavit = (id) => {
            const newInfo = {}
            newInfo['id_empleado'] = id;
            newInfo['numero_credito'] = infonavit.numero_credito.replace(/\s/g, '');
            if (infonavit.porcentaje_descuento !== '')
                newInfo['porcentaje_descuento'] = infonavit.porcentaje_descuento;
            if (infonavit.factor_descuento !== '')
                newInfo['factor_descuento'] = infonavit.factor_descuento;
            if (infonavit.cuota_fija !== '')
                newInfo['cuota_fija'] = infonavit.cuota_fija;
            return newInfo.numero_credito.length === 10 ? newInfo : null;
        }

        const horarioDias = (id) => {
            const array = [];
            dias.forEach(item => {
                array.push({ dia_semana: item.dia_semana, hora_entrada: item.hora_entrada, hora_salida: item.hora_salida })
            })
            return {
                id_empleado: id,
                horario: array
            }
        }

        return {
            general,
            telefonos,
            correos,
            creditoFonacot,
            creditoInfonavit,
            horarioDias,
            contratoDat
        }
    })();

    const send = () => {
        const _exito = async (res) => {
            const respuesta = await res.json();
            if (respuesta.created) {
                postExtras(respuesta.empleado[0].id_empleado);
            } else {
                setToast({
                    open: true,
                    message: "Ha ocurrido un error con el registro, intenta nuevamente",
                    severity: "success",
                    title: "Registro fallido!"
                })
            }
            setLoading(false);
        }

        const _fallo = async (res) => {
            const resp = await res.json();
            if (!isTokenExpired(resp)) {
                const { error } = resp;
                const { detail } = error;
                if (detail.includes('no_imss')) {
                    const newInfo = { ...informacion };
                    newInfo['no_imss'] = { ...newInfo['no_imss'], verify: false };
                    setInformacion(newInfo);
                    openTab(0, 'No IMMS está asignado a alguién más');
                }
                else if (detail.includes('no_cuenta')) {
                    const newInfo = { ...financiero };
                    newInfo['no_cuenta'] = { ...newInfo['no_cuenta'], verify: false };
                    setFinanciero(newInfo);
                    openTab(4, 'No. Cuenta está asignado a alguién más')
                }
                else if (detail.includes('no_tarjeta')) {
                    const newInfo = { ...financiero };
                    newInfo['no_tarjeta'] = { ...newInfo['no_tarjeta'], verify: false };
                    setFinanciero(newInfo);
                    openTab(4, 'No. Tarjeta está asignado a alguién más')
                }
                else if (detail.includes('clabe')) {
                    const newInfo = { ...financiero };
                    newInfo['clabe'] = { ...newInfo['clabe'], verify: false };
                    setFinanciero(newInfo);
                    openTab(4, 'No. Tarjeta está asignado a alguién más')
                }
                else if (detail.includes('rfc')) {
                    const newInfo = { ...informacion };
                    newInfo['rfc'] = { ...newInfo['clabe'], verify: false };
                    setInformacion(newInfo);
                    openTab(4, 'RFC está asignado a alguién más')
                }
                else
                    setToast({
                        open: true,
                        message: 'Algo salió mal y revisa tus campos',
                        severity: "error",
                        title: "Registro fallido!"
                    })
                setLoading(false);
            }
        }

        const postEmpleado = () => {
            const headers = new Headers();
            headers.append('authorization', cabeceras.authorization)
            RequestFetch.postForm({
                url: `${RequestFetch.BASE_URL}/api/rh/empleado`,
                headers,
                data: separe.general(),
                exito: _exito,
                fallo: _fallo,
                error_request: (error) => {
                    history.push('/server-error');
                }
            });
        }

        const postExtras = (id) => {
            const fallo = async (res) => {
                const resp = await res.json();
                if (!isTokenExpired(resp)) {
                    console.log(resp)
                }
            }

            RequestFetch.post({
                url: `${RequestFetch.BASE_URL}/api/rh/empleado/telefono`,
                headers: cabeceras,
                data: separe.telefonos(id),
                exito: async (res) => {
                    setDataTel(clearArray(dataTel));
                },
                fallo,
                error_request: (error) => { history.push('/server-error') }
            });

            RequestFetch.post({
                url: `${RequestFetch.BASE_URL}/api/rh/empleado/correo`,
                headers: cabeceras,
                data: separe.correos(id),
                exito: async (res) => {
                    console.log(await res.json());
                    setDataCorreo(clearArray(dataCorreo));
                },
                fallo,
                error_request: (error) => {
                    history.push('/server-error');
                }
            });

            RequestFetch.post({
                url: `${RequestFetch.BASE_URL}/api/rh/empleado/horario`,
                headers: cabeceras,
                data: separe.horarioDias(id),
                exito: async (res) => {
                    console.log(await res.json());
                    setDias(clearArray(diasHorario));
                },
                fallo,
                error_request: (error) => {
                    history.push('/server-error');
                }
            });

            RequestFetch.post({
                url: `${RequestFetch.BASE_URL}/api/rh/empleado/contrato`,
                headers: cabeceras,
                data: separe.contratoDat(id),
                exito: async (res) => {
                    console.log(await res.json());
                    setContrato(contratoData);
                },
                fallo,
                error_request: (error) => {
                    history.push('/server-error');
                }
            });

            const credFonacot = separe.creditoFonacot(id);
            if (credFonacot !== null) {
                RequestFetch.post({
                    url: `${RequestFetch.BASE_URL}/api/rh/empleado/credito_fonacot`,
                    headers: cabeceras,
                    data: credFonacot,
                    exito: async (res) => {
                        console.log(await res.json());
                        setFonacot(clearArray(initFonacot));
                    },
                    fallo,
                    error_request: (error) => {
                        history.push('/server-error');
                    }
                });
            }

            const credInfonavit = separe.creditoInfonavit(id);
            if (credInfonavit !== null) {
                RequestFetch.post({
                    url: `${RequestFetch.BASE_URL}/api/rh/empleado/credito_infonavit`,
                    headers: cabeceras,
                    data: credInfonavit,
                    exito: async (res) => {
                        console.log(await res.json());
                        setInfonavit(initInfonavit);
                    },
                    fallo,
                    error_request: (error) => {
                        history.push('/server-error');
                    }
                });
            }

            let newId = ('00000' + id);
            RequestFetch.put({
                url: `${RequestFetch.BASE_URL}/api/rh/empleado/${id}`,
                headers: cabeceras,
                data: { identificador: (identificador + newId.substring(newId.length - 5)) },
                exito: async (res) => {
                    const respuesta = await res.json();
                    if (respuesta.updated) {
                        setIdentificador('');
                        setInformacion(initInfo);
                        setDireccion(initDirec);
                        setFinanciero(initFinan);
                        setToast({
                            open: true,
                            message: "Ha quedado registrado el empleado con IDENTIFICADOR: " + respuesta.empleado[0].identificador,
                            severity: "success",
                            title: "Registro exitoso!"
                        })
                    }

                },
                fallo,
                error_request: (error) => {
                    history.push('/server-error');
                }
            });

            const createUser = async () => {
                const getCorreo = dataCorreo.find(element => element.value.replace(/\s/g, '').length > 0);
                const getType = () => {
                    if (contrato.id_empresa_labora.value.es_interna) return 'G'
                    else return 'E';
                }
                const body = JSON.stringify({
                    rfc: informacion.rfc.value.toUpperCase(),
                    correo: getCorreo.value,
                    rol: getType(),
                    tipo: 'E'
                });

                console.log(body);
                const resp = await fetch(`${BASE_URL}/api/rh/usuario`, { method: 'POST', headers: cabeceras, body });
                const response = resp.json();
                if (!isTokenExpired(response)) {
                    const { error } = response
                    if (error)
                        props.enqueueSnackbar("Usuario no creado", { variant: 'error', preventDuplicate: true, });
                    else
                        props.enqueueSnackbar("Usuario creado", { variant: 'success', preventDuplicate: true, });

                }
            }

            createUser();
        }



        if (validaAll()) {
            setLoading(true);
            postEmpleado();
        }
    }

    return (
        <React.Fragment>
            <Card className={classes.root}>
                <CardContent className={classes.cardContent}>
                    <List
                        component="nav"
                        aria-labelledby="nested-list-subheader"
                        subheader=
                        {
                            <ListSubheader component="div" display="block" className={classes.subheader}>
                                <IconButton className={classes.back} onClick={() => { history.goBack() }}>
                                    <KeyboardBackspaceIcon />
                                </IconButton>
                                <Typography className={classes.title} variant="button" gutterBottom>
                                    formulario empleado
                                </Typography>
                                <ExpandButton onClick={() => { setPart({ ...part, expandable: !part.expandable }) }} open={part.expandable} />
                            </ListSubheader>
                        }
                        className={classes.root}>

                        <ListItem className={classes.header} button onClick={handleClick.bind(this, 0)}>
                            <ListItemIcon>
                                <InfoIcon />
                            </ListItemIcon>
                            <ListItemText primary="Informacion" />
                            {part.actual === 0 ? <ExpandLess /> : <ExpandMore />}
                        </ListItem>
                        <Collapse in={part.actual === 0 || part.expandable} timeout="auto" unmountOnExit >
                            <Informacion
                                data={informacion}
                                setData={setInformacion}
                                buttons={buttons}
                                expandable={part.expandable}
                                nacionalidadades={nacionalidadades}
                            />
                        </Collapse>

                        <ListItem className={classes.header} button onClick={handleClick.bind(this, 1)}>
                            <ListItemIcon>
                                <RoomIcon />
                            </ListItemIcon>
                            <ListItemText primary="Dirección" />
                            {part.actual === 1 ? <ExpandLess /> : <ExpandMore />}
                        </ListItem>
                        <Collapse in={part.actual === 1 || part.expandable} timeout="auto" unmountOnExit >
                            <Direccion
                                data={direccion}
                                setData={setDireccion}
                                buttons={buttons}
                                expandable={part.expandable}
                                edit={false} />
                        </Collapse>

                        <ListItem className={classes.header} button onClick={handleClick.bind(this, 2)}>
                            <ListItemIcon>
                                <PhoneRoundedIcon />
                            </ListItemIcon>
                            <ListItemText primary="Contacto" />
                            {part.actual === 2 ? <ExpandLess /> : <ExpandMore />}
                        </ListItem>
                        <Collapse in={part.actual === 2 || part.expandable} timeout="auto" unmountOnExit >
                            <Contacto
                                buttons={buttons}
                                expandable={part.expandable}
                                phones={dataTel} setPhones={setDataTel}
                                correos={dataCorreo} setCorreos={setDataCorreo} />
                        </Collapse>

                        <ListItem className={classes.header} button onClick={handleClick.bind(this, 3)}>
                            <ListItemIcon>
                                <WatchLaterRoundedIcon />
                            </ListItemIcon>
                            <ListItemText primary="Horario" />
                            {part.actual === 3 ? <ExpandLess /> : <ExpandMore />}
                        </ListItem>
                        <Collapse in={part.actual === 3 || part.expandable} timeout="auto" unmountOnExit >
                            <Horario
                                buttons={buttons}
                                expandable={part.expandable}

                                horario={dias}
                                setHorario={setDias}
                            />
                        </Collapse>


                        <ListItem className={classes.header} button onClick={handleClick.bind(this, 4)}>
                            <ListItemIcon>
                                <MonetizationOnIcon />
                            </ListItemIcon>
                            <ListItemText primary="Financiero" />
                            {part.actual === 4 ? <ExpandLess /> : <ExpandMore />}
                        </ListItem>
                        <Collapse in={part.actual === 4 || part.expandable} timeout="auto" unmountOnExit >
                            <Financiero
                                data={financiero}
                                setData={setFinanciero}
                                buttons={buttons}
                                expandable={part.expandable}
                                bancos={bancos}
                            />
                        </Collapse>

                        <ListItem className={classes.header} button onClick={handleClick.bind(this, 5)}>
                            <ListItemIcon>
                                <AccountBalanceRoundedIcon />
                            </ListItemIcon>
                            <ListItemText primary="Creditos" />
                            {part.actual === 5 ? <ExpandLess /> : <ExpandMore />}
                        </ListItem>
                        <Collapse in={part.actual === 5 || part.expandable} timeout="auto" unmountOnExit >
                            <Fiscales
                                fonacot={fonacot}
                                setFonacot={setFonacot}
                                infonavit={infonavit}
                                setInfonavit={setInfonavit}
                                buttons={buttons}
                                expandable={part.expandable}
                            />
                        </Collapse>

                        <ListItem className={classes.header} button onClick={handleClick.bind(this, 6)}>
                            <ListItemIcon>
                                <WorkIcon />
                            </ListItemIcon>
                            <ListItemText primary="Contrato" />
                            {part.actual === 6 ? <ExpandLess /> : <ExpandMore />}
                        </ListItem>
                        <Collapse in={part.actual === 6 || part.expandable} timeout="auto" unmountOnExit >
                            <Empresa
                                data={contrato}
                                setData={setContrato}
                                buttons={buttons}
                                expandable={part.expandable}
                                empresasInt={[...int]}
                                empresasAll={[...all]}
                                departamentos={[...departamentos]}
                                puestos={[...puesto]}
                                jornadas={[...jornadas]}
                                tipoContrato={tipoContrato}
                                regimenContrato={regimenContrato}
                                setIdentificador={setIdentificador}
                                editable={false}

                            />
                        </Collapse>

                    </List>
                </CardContent>
                <CardActions>
                    <Grid container>
                        <Grid
                            item
                            xs={12}
                            sm={6}
                            container
                            direction="row"
                            justify="flex-start"
                            alignItems="center">
                            <Typography style={{ marginLeft: 24 }} variant="button" color="initial">{`Identificador: ${identificador}`}</Typography>
                        </Grid>
                        <Grid
                            item
                            xs={12}
                            sm={6}
                            container
                            direction="row"
                            justify="flex-end"
                            alignItems="center">
                            <div className={classes.wrapper}>
                                <Button
                                    variant="contained"
                                    className={classes.save}
                                    disabled={loading}
                                    endIcon={<SaveIcon />}
                                    onClick={send}>
                                    {"Guardar"}
                                    {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                                </Button>
                            </div>
                        </Grid>
                    </Grid>
                </CardActions>
            </Card>
            <Snackbar open={toast.open} autoHideDuration={5000} onClose={handleClose}>
                <Alert onClose={handleClose} severity={toast.severity}>
                    <AlertTitle>{toast.title}</AlertTitle>
                    {toast.message}
                </Alert>
            </Snackbar>
        </React.Fragment>
    );
}

export default withSnackbar(FormEmpleado);