import React, { useContext, useState } from 'react';
import { TableCell, TableRow, Table, TableBody, Button, TextField, DialogContentText } from '@material-ui/core'
import { Dialog, DialogContent, DialogTitle, DialogActions } from '@material-ui/core';
import { ValidacionCantidadNomina } from '../../../../../libraries/Mask';
import { NominaContext } from '../../Nomina'
import moment from 'moment';
import { useStyles } from '../Styles';
import { put, post } from './helper';
import { SessionContext } from '../../../../../routes/adminrh/CustomRoutes';

export default function DisplayFaltas({ row, identificador }) {
    const { generalSettings, empleados, setEmpleados, idNomina } = useContext(NominaContext);
    const classes = useStyles();
    const { cabeceras, isTokenExpired } = useContext(SessionContext);
    let defaultFaltas = 0;
    if (row.tipo) defaultFaltas = row.dias.faltas.cantidad_inasistencias;

    const [faltas, setFaltas] = useState(defaultFaltas);
    const [open, setOpen] = useState(false);
    /*     console.log(row.dias.faltas);
     */

    const okReponse = (response, post) => {
        if (!isTokenExpired(response)) {
            const indexEmpleado = empleados.findIndex(i => i.empleado.identificador === identificador);
            const newEmpleados = [...empleados];
            newEmpleados[indexEmpleado].dias.faltas.cantidad_inasistencias = faltas;
            if (post)
                newEmpleados[indexEmpleado].dias.faltas.folio_inasistencia = response[0].folio_inasistencia;

            setEmpleados(newEmpleados);
            setOpen(false);
        }
    }

    const onClick = () => {
        const { folio_inasistencia, id_contrato } = row.dias.faltas;
        if (folio_inasistencia) {
            put({ cantidad_inasistencias: faltas }, folio_inasistencia, cabeceras)
                .then(response => {
                    okReponse(response, false)
                })
        } else {
            post({
                folio_inasistencia,
                cantidad_inasistencias: faltas,
                id_contrato,
                id_nomina: idNomina
            }, cabeceras).then(response => {
                okReponse(response, true)
            })
        }


    }

    const onChange = ({ target: { value } }) => {
        if (parseInt(value) <= parseInt(generalSettings.dias))
            setFaltas(value)
    }

    return (
        <>
            <Table size="small">
                <TableBody>
                    <TableRow className={classes.internalRow} hover>
                        <TableCell align="center" className={classes.internalCell} size="small"
                            onClick={() => {
                                if (row.tipo)
                                    if (generalSettings.estatus === 'C' || generalSettings.estatus === 'P')
                                        setOpen(true)
                            }}
                        >
                            {row.tipo ? `${defaultFaltas} ` : 'N/A '}
                            <b>FALTAS</b>
                        </TableCell>
                    </TableRow>
                </TableBody>
            </Table>

            {
                open && (generalSettings === 'C' || generalSettings.estatus === 'P') &&
                <Dialog open={open} onClose={() => setOpen(false)} aria-labelledby="">
                    <DialogTitle id="">Editando faltas</DialogTitle>
                    <DialogContent>
                        <DialogContentText>
                            Numero de faltas en el periodo:
                    </DialogContentText>
                        <DialogContentText>
                            {moment(generalSettings.perido_inicio).format('DD/MM/YYYY')} al {moment(generalSettings.periodo_fin).format('DD/MM/YYYY')}
                        </DialogContentText>
                        <TextField
                            fullWidth
                            value={faltas}
                            onChange={onChange}
                            InputProps={{ inputComponent: ValidacionCantidadNomina }}
                        />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={onClick} color="default">
                            Guardar
                </Button>
                    </DialogActions>
                </Dialog>
            }
        </>
    )
}