import { makeStyles } from "@material-ui/core/styles";
import moment from "moment";

const getPulse = (fechaFinPerido) => {
  let days = moment(fechaFinPerido).diff(moment(), "days");
  return days <= 3 ? 0.5 : 0;
};

const getColor = (status) => {
  let color = "";
  let secondary = "";
  switch (status) {
    case "C":
      color = "#e53935";
      secondary = "#ff6f60";
      break;
    case "P":
      color = "#ffb300";
      secondary = "#ffe54c";
      break;
    case "N":
      color = "#0277bd";
      secondary = "#58a5f0";
      break;
    case "A":
      color = "#2e7d32";
      secondary = "#60ad5e";
      break;
    default:
      break;
  }

  return { color, secondary };
};

export const textEstatus = (estatus) => {
  let text = "";
  switch (estatus) {
    case "C":
      text = "EN ESPERA";
      break;
    case "P":
      text = "PRENÓMINA";
      break;
    case "N":
      text = "NÓMINA";
      break;
    case "A":
      text = "PAGADA";
      break;
    default:
      break;
  }

  return text;
};

export const textButton = (estatus) => {
  let text = "";
  switch (estatus) {
    case "C":
      text = "COMENZAR";
      break;
    case "P":
      text = "APROBAR";
      break;
    case "N":
      text = "PAGAR";
      break;
    case "A":
      text = "VER";
      break;
    default:
      break;
  }

  return text;
};

export const useStyles = (status, fechaFinPerido) => {
  const { color, secondary } = getColor(status);
  const timePulse = getPulse(fechaFinPerido);
  return makeStyles((theme) => ({
    root: {
      borderTopColor: color,
      borderTopStyle: "solid",
      borderWidth: "25px",
    },
    icono: {
      fontSize: "5em",
      color: "#757575",
    },
    btnMain: {
      backgroundColor: color,
      "&:hover": {
        backgroundColor: secondary,
      },
      color: "white",
      marginTop: "5px",
      fontSize: "10px",
    },
    containerSatus: {
      paddingTop: theme.spacing(2),
      borderBottom: color,
      borderBottomStyle: "solid",
      borderWidth: 0.5,
      marginBottom: "5px",
    },
    avatar: {
      backgroundColor: color,
      width: "3.5em",
      height: "3.5em",
      fontSize: 22,
      animation: `$pulse ${timePulse}s infinite`,
    },
    "@keyframes pulse": {
      "0%": {
        boxShadow: "0 0 0 0 rgba(245,124,0,0.7)",
      },
      "70%": {
        boxShadow: "0 0 0 12px rgba(245,124,0,0)",
      },
      "100%": {
        boxShadow: "0 0 0 0 rgba(245,124,0,0)",
      },
    },
  }));
};

export const textTipoNomina = (periodo_calculo) => {
  let text = "";
  switch (periodo_calculo) {
    case "S":
      text = "Semanal";
      break;
    case "C":
      text = "Catorcenal";
      break;
    case "Q":
      text = "Quincenal";
      break;
    case "M":
      text = "Mensual";
      break;
    default:
      console.log(periodo_calculo);
      break;
  }
  return text;
};
