import React from 'react'
import { Grid, TextField, MenuItem, Snackbar, Button, Typography, IconButton, InputAdornment } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import MuiAlert from '@material-ui/lab/Alert';
import AlertTitle from '@material-ui/lab/AlertTitle';
import { ArrowBack, } from '@material-ui/icons';
import { ValidacionTelefono, ValidacionRFCFisica, ValidacionEmail } from '../../../../libraries/Mask';
import RequestFetch from '../../../../libraries/RequestFetch';
import PhoneIcon from '@material-ui/icons/Phone';
import DeleteIcon from '@material-ui/icons/Delete';
import { useHistory } from 'react-router-dom';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import { useContext } from 'react';
import { SessionContext } from '../../../../routes/adminrh/CustomRoutes';

const useStyles = makeStyles(theme => ({
    container: {
        padding: theme.spacing(4),
    },
}));

const textDisabled = makeStyles({
    root: {
        '&$disabled': {
            color: 'rgba(0, 0, 0, 0.87)'
        },
    },
    disabled: {},
}, { name: 'MuiInputBase' });


const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});


function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

export default function Representante(props) {
    const { cabeceras, isTokenExpired } = useContext(SessionContext)
    const classes = useStyles();
    const history = useHistory();

    const [telDele, setTelDele] = React.useState({
        open: false
    });
    const { data, setData } = props;
    const { phones, setPhones } = props;
    const { onlySee } = props;

    const [toast, setToast] = React.useState({
        open: false,
        message: "",
        severity: "",
        title: ""
    });

    textDisabled();

    const _exitoRFC = async (res) => {
        const respuesta = await res.json();
        if (respuesta.length > 0) {
            const { id_representante, rfc, nombre, primer_apellido, segundo_apellido, genero, correo, observaciones } = respuesta[0];
            setData(prevState => {
                return {
                    ...prevState,
                    existe: { value: true },
                    id_representante: { value: id_representante, verify: true },
                    rfc: { value: rfc, verify: true },
                    nombre: { value: nombre, verify: true },
                    primer_apellido: { value: primer_apellido, verify: true },
                    segundo_apellido: { value: segundo_apellido === null || segundo_apellido === undefined ? '' : segundo_apellido, verify: true },
                    genero: { value: genero, verify: true },
                    correo: { value: correo, verify: true },
                    observaciones: { value: observaciones === null || observaciones === undefined ? '' : observaciones, verify: true }
                }
            });
            _getTel(id_representante);
            setToast({
                open: true,
                message: `El RFC ya se encuentra registrado`,
                severity: "success",
                title: "Representante encontrado"
            });
        } else {
            setToast({
                open: true,
                message: `El RFC no está registrado, se hará el registro automáticamente`,
                severity: "info",
                title: "Representante no encontrado"
            });
        }
    }
    const fallo = async (res) => {
        const response = await res.json();
        if (!isTokenExpired(response))
            setToast({
                open: true,
                message: `Ha ocurrido un error`,
                severity: "error",
                title: "Busqueda de representante fallida"
            });
    }

    const _exitoTel = async (res) => {
        const respuesta = await res.json();
        const newTel = [
            { value: '', verify: '', tipo: 'C', helperText: '' },
            { value: '', verify: '', tipo: 'F', helperText: '' },
        ]
        respuesta.telefonos.forEach(item => {
            switch (item.tipo) {
                case 'C': newTel[0] = { value: item.numero, verify: true, tipo: item.tipo, helperText: '' };
                    break;
                case 'F': newTel[1] = { value: item.numero, verify: true, tipo: item.tipo, helperText: '' }
                    break;
                default: newTel.push({ value: item.numero, verify: true, tipo: item.tipo, helperText: '' })
                    break;
            }
        })
        setPhones(newTel)


    }

    const _getRFC = (value) => {
        RequestFetch.get({
            url: `${RequestFetch.BASE_URL}/api/rh/representante/${value.toUpperCase()}?query=${RequestFetch.REPRESENTANTE_ALL}`,
            headers: cabeceras,
            exito: _exitoRFC,
            fallo,
            error_request: (error) => {
                history.push("/server-error");
            }
        });
    }

    const _getTel = (id) => {
        RequestFetch.get({
            url: `${RequestFetch.BASE_URL}/api/rh/representante/telefono/${id}`,
            headers: cabeceras,
            exito: _exitoTel,
            fallo,
            error_request: (error) => {
                history.push("/server-error");
            }
        });
    }

    const changeRFC = (event) => {
        var { value } = event.target;
        value = value.replace(/\s/g, '').toUpperCase();
        if (value.length < 13) {
            setData(prevState => {
                return {
                    ...prevState,
                    existe: { value: false },
                    id_representante: { value: '', verify: '' },
                    rfc: { value: value, verify: false },
                    nombre: { value: '', verify: '' },
                    primer_apellido: { value: '', verify: '' },
                    segundo_apellido: { value: '', verify: '' },
                    genero: { value: '', verify: '' },
                    correo: { value: '', verify: '' },
                    observaciones: { value: '', verify: '' }
                }
            });
            setPhones([
                { value: '', verify: true, tipo: 'C', helperText: '' },
                { value: '', verify: true, tipo: 'F', helperText: '' },
            ]);
        }
        else if (value.length === 13) {
            setData(prevState => {
                return {
                    ...prevState,
                    rfc: { value: value, verify: true },
                }
            });
            _getRFC(value)
        }
    }

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            setToast({ ...toast, open: false }); return;
        }
        setToast({ ...toast, open: false })
    };

    const repeted = (pos, array) => {
        const repeted = array.filter(item => item.value === array[pos].value);
        return repeted.length === 1;
    }

    const changeTel = (pos, event) => {
        const { value, name } = event.target;
        let newTel = [...phones];

        newTel[pos].value = value.replace(/\s/g, '');
        newTel[pos].tipo = name;

        const repetido = repeted(pos, [...newTel]);

        newTel[pos].verify = value.length > 0 ? repetido : true;
        newTel[pos].helperText = value.length > 0 && !repetido ? 'Repetido' : '';

        newTel.forEach((item, index) => {
            if (repeted(index, [...newTel])) {
                item.helperText = ''
                item.verify = true;
            }
        })
        setPhones(newTel);
    }

    const changeTextRequired = e => {
        const { name, value } = e.target;
        setData(prevState => {
            return {
                ...prevState, [name]: { value: value, verify: `${value}`.length > 0 }
            }
        })
    }

    const changeTextRequiredNo = e => {
        const { name, value } = e.target;
        setData(prevState => {
            return {
                ...prevState, [name]: { value: value, verify: true }
            }
        })
    }

    const addTel = () => {
        const newTel = [...phones];
        newTel.push({ value: '', verify: '', tipo: `${newTel.length - 2}`, helperText: '' });
        setPhones(newTel);
    }

    const removeTelConfirm = () => {
        let newTel = [...phones];
        newTel.splice(telDele.pos, 1);
        setPhones(newTel);
        setTelDele({ open: false });
    }

    const removeTel = (pos) => {
        let newTel = [...phones];
        const deleteTel = newTel.splice(pos, 1)[0];
        if (deleteTel.value.length === 0)
            setPhones(newTel);
        else
            setTelDele({ open: true, pos: pos, telefono: deleteTel });


    }


    const extraPhones = () => {
        const array = [...phones];
        array.splice(0, 2);

        return array.map((item, index) => {
            return <Grid key={`telefono-extra-representante-${index + 1}`} item xs={6} md={6} lg={6}>

                <TextField
                    variant="outlined"
                    name={`${index + 1}`}
                    disabled={data.existe.value || onlySee}
                    label={`Tel. Extra ${index + 1}`}
                    fullWidth
                    placeholder="Ej. 111 222 3344"
                    InputLabelProps={{ shrink: true }}
                    value={props.phones[index + 2].value}
                    onChange={(e) => changeTel(index + 2, e)}
                    InputProps={{
                        inputComponent: ValidacionTelefono,
                        endAdornment: <InputAdornment position="end">
                            <IconButton disabled={onlySee} aria-label="delete" onClick={() => removeTel(index + 2)}>
                                <DeleteIcon />
                            </IconButton>
                        </InputAdornment>
                    }}
                    error={props.phones[index + 2].verify !== '' ? !props.phones[index + 2].verify : false}
                    helperText={props.phones[index + 2].helperText}
                    autoComplete='off'
                />
            </Grid>
        });
    }

    return (
        <>
            <Grid container spacing={3} className={classes.container} id={"representante"}>
                <Grid item xs={12} sm={12} md={12} lg={12}>
                    <Grid container spacing={3}>
                        <Grid item xs={12} sm={6} lg={onlySee ? 6 : 3}>
                            <TextField
                                required
                                variant="outlined"
                                label="RFC"
                                fullWidth
                                placeholder="Ej. ABCE010101B01"
                                InputLabelProps={{ shrink: true }}
                                value={data.rfc.value}
                                onChange={changeRFC}
                                disabled={onlySee}
                                inputProps={{ className: classes.uppercase }}
                                error={data.rfc.verify !== '' ? !data.rfc.verify : false}
                                InputProps={{ inputComponent: ValidacionRFCFisica }}
                                autoComplete='off'
                            />
                        </Grid>
                        <Grid item xs={12} sm={6} lg={onlySee ? 6 : 3}>
                            <TextField
                                required
                                label="Nombre"
                                variant="outlined"
                                name="nombre"
                                fullWidth
                                disabled={data.existe.value || onlySee}
                                placeholder="Ej. Juana"
                                InputLabelProps={{ shrink: true }}
                                value={data.nombre.value}
                                onChange={changeTextRequired}
                                error={data.nombre.verify !== '' ? !data.nombre.verify : false}
                                autoComplete='off'
                            />
                        </Grid>
                        <Grid item xs={12} sm={4} md={4} lg={onlySee ? 4 : 2}>
                            <TextField
                                required
                                variant="outlined"
                                label="Primer Apellido"
                                fullWidth
                                disabled={data.existe.value || onlySee}
                                placeholder="Ej. Pérez"
                                InputLabelProps={{ shrink: true }}
                                value={data.primer_apellido.value}
                                name="primer_apellido"
                                onChange={changeTextRequired}
                                error={data.primer_apellido.verify !== '' ? !data.primer_apellido.verify : false}
                                autoComplete='off'
                            />
                        </Grid>
                        <Grid item xs={12} sm={4} md={4} lg={onlySee ? 4 : 2}>
                            <TextField
                                label="Segundo Apellido"
                                fullWidth
                                variant="outlined"
                                disabled={data.existe.value || onlySee}
                                placeholder="Ej. López"
                                InputLabelProps={{ shrink: true }}
                                value={data.segundo_apellido.value}
                                name="segundo_apellido"
                                onChange={changeTextRequiredNo}
                                error={data.segundo_apellido.verify !== '' ? !data.segundo_apellido.verify : false}
                                autoComplete='off'
                            />
                        </Grid>
                        <Grid item xs={12} sm={4} md={4} lg={onlySee ? 4 : 2}>
                            <TextField
                                required
                                fullWidth
                                variant="outlined"
                                label="Género"
                                disabled={data.existe.value || onlySee}
                                select
                                className={classes.select}
                                value={`${data.genero.value !== '' ? data.genero.value : 'DEFAULT'}`}
                                name="genero"
                                onChange={changeTextRequired}
                                error={data.genero.verify !== '' ? !data.genero.verify : false}>
                                <MenuItem value="DEFAULT" disabled>Selecciona</MenuItem>
                                <MenuItem value="M">Mujer</MenuItem>
                                <MenuItem value="H">Hombre</MenuItem>
                            </TextField>
                        </Grid>

                    </Grid>
                </Grid>

                <Grid item xs={12} sm={12} md={onlySee ? 12 : 6} lg={onlySee ? 12 : 6}>
                    <Grid container spacing={2}>
                        <Grid item xs={12} md={12} lg={12}>
                            <TextField
                                required
                                label="Correo"
                                variant="outlined"
                                fullWidth
                                disabled={data.existe.value || onlySee}
                                placeholder="Ej. ejemplo@ejemplo.com"
                                InputLabelProps={{ shrink: true }}
                                value={data.correo.value}
                                name="correo"
                                onChange={changeTextRequired}
                                error={data.correo.verify !== '' ? !data.correo.verify : false}
                                InputProps={{ inputComponent: ValidacionEmail }}
                                autoComplete='off'
                            />
                        </Grid>
                        <Grid item xs={12} md={12} lg={12}>
                            <Typography variant="subtitle2" style={{ textAlign: 'center' }} color="initial"> Telefonos</Typography>
                        </Grid>

                        <Grid item xs={6} md={6} lg={6}>
                            <TextField
                                variant="outlined"
                                name="C"
                                disabled={data.existe.value || onlySee}
                                label="Tel. Celular"
                                fullWidth placeholder="Ej. 111 222 3344"
                                InputLabelProps={{ shrink: true }}
                                value={props.phones[0].value}
                                onChange={(e) => changeTel(0, e)}
                                InputProps={{ inputComponent: ValidacionTelefono }}
                                error={props.phones[0].verify !== '' ? !props.phones[0].verify : false}
                                helperText={phones[0].helperText}
                                autoComplete='off'
                            />
                        </Grid>
                        <Grid item xs={6} md={6} lg={6}>
                            <TextField
                                variant="outlined"
                                name="F"
                                disabled={data.existe.value || onlySee}
                                label="Tel. Fijo"
                                fullWidth
                                placeholder="Ej. 111 222 3344"
                                InputLabelProps={{ shrink: true }}
                                value={props.phones[1].value}
                                onChange={(e) => changeTel(1, e)}
                                InputProps={{ inputComponent: ValidacionTelefono }}
                                error={props.phones[1].verify !== '' ? !props.phones[1].verify : false}
                                helperText={phones[1].helperText}
                                autoComplete='off'
                            />
                        </Grid>
                        {
                            extraPhones()
                        }

                        {
                            (!data.existe.value && !onlySee) &&
                            <Grid item xs={12} md={12} lg={12}>
                                <Grid container
                                    direction="column"
                                    justify="center"
                                    alignItems="center">
                                    <Button
                                        disabled={data.existe.value}
                                        size="small"
                                        onClick={addTel}
                                        startIcon={<PhoneIcon />}>
                                        Agregar</Button>
                                </Grid>
                            </Grid>
                        }
                    </Grid>
                </Grid>

                <Grid item container xs={12} md={onlySee ? 12 : 6} lg={onlySee ? 12 : 6}>
                    <TextField
                        variant="outlined"
                        label="Observaciones"
                        fullWidth
                        disabled={data.existe.value || onlySee}
                        multiline
                        placeholder={`Ej. Representante: \nPago en cheque\nCada 14 del mes`}
                        InputLabelProps={{ shrink: true }}
                        value={data.observaciones.value}
                        name="observaciones"
                        onChange={changeTextRequiredNo}
                    />
                </Grid>

                {
                    !props.expandable &&
                    <Grid container item
                        direction="row"
                        justify="flex-end"
                        alignItems="center" xs={12} md={12} lg={12}>
                        <Button startIcon={<ArrowBack />} onClick={props.buttons.back}>Anterior</Button>
                    </Grid>
                }

            </Grid>
            <Snackbar open={toast.open} autoHideDuration={6000} onClose={handleClose}>
                <Alert onClose={handleClose} severity={toast.severity}>
                    <AlertTitle>{toast.title}</AlertTitle>
                    {toast.message}
                </Alert>
            </Snackbar>
            <Dialog
                open={telDele.open}
                TransitionComponent={Transition}
                onClose={() => setTelDele({ open: false })}>
                <DialogTitle>{"Eliminar Teléfono"}</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        {`Está apunto de eliminar un teléfono ${telDele['telefono'] === undefined ? '' : telDele.telefono.value}`}
                    </DialogContentText>
                    <DialogContentText>
                        ¿Está seguro que desea continuar?
                        </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setTelDele({ open: false })}>Cancelar</Button>
                    <Button onClick={() => {
                        removeTelConfirm();
                        setTelDele({ open: false })
                    }}
                        color="secondary">Eliminar</Button>
                </DialogActions>
            </Dialog>
        </>

    )
}
