import React, { createContext } from 'react';
import { Redirect, Route, useHistory } from 'react-router-dom';
import { useSession, isLogin, logout } from '../../libraries/LoginNomina';

export const SessionContext = createContext(null);

export function PrivateRoute({ children, redirect, ...rest }) {
    const history = useHistory();
    const { getSession } = useSession();

    let cabeceras = {};
    if (getSession)
        cabeceras = {
            'Content-Type': 'application/json',
            'authorization': getSession.token
        }

    const isTokenExpired = ({ error }) => {
        if (error)
            if (error.httpCode === 401 || error.status === 401) {
                logout();
                history.push('/nomina/signin');
                return true;
            }
        return false;
    }

    return (
        <Route
            {...rest}
            render={({ location }) =>
                isLogin() ?
                    (
                        <SessionContext.Provider value={{
                            cabeceras,
                            rol: getSession.rol,
                            permisos: getSession.permisos,
                            rfc: getSession.rfc,
                            logout,
                            isTokenExpired
                        }}>
                            {children}
                        </SessionContext.Provider>
                    )
                    :

                    (
                        <Redirect
                            to={{
                                pathname: redirect,
                                state: { from: location }
                            }}
                        />
                    )
            }
        />
    );

}

export function PublicRoute({ children, restricted, path, redirect, ...rest }) {

    return (
        <Route
            {...rest}
            render={({ location }) =>
                isLogin() && restricted ?
                    <Redirect to={redirect} />
                    :
                    { ...children }
            }
        />
    )
}