import { post, put, remove } from "../crud";

const isEmpty = (newData) => {
  const { clave_sat, tipo, descripcion } = newData;
  return !(Object.keys(newData).length === 3 && clave_sat.toString().trim() && tipo.trim() && descripcion.trim());
};


export const handlerAdd = (props) => {
  const { url_api, controllerAlerts, otherProps } = props;
  return (newData) =>
    new Promise((resolve, reject) => {
      if (!isEmpty(newData)) {
        post({
          url_api,
          values: newData,
          controllerAlerts,
          resolve,
          reject,
          ...otherProps
        });
      } else {
        controllerAlerts("REQUIERED");
        reject();
      }
    });
};

export const handlerUpdate = (props) => {
  const { url_api, controllerAlerts, ...otherProps } = props;
  return (newData, oldData) => {
    return new Promise((resolve, reject) => {
      const { clave_sat, tipo } = oldData;
      if (!isEmpty(newData)) {
        put({
          url: `${url_api}/${clave_sat}/${tipo}`,
          values: newData,
          ...otherProps,
          resolve,
          oldValues: oldData,
          reject,
        });
      } else {
        controllerAlerts("REQUIERED");
        reject();
      }
    });
  };
};

export const handlerDelete = (props) => {
  const { url_api, ...otherProps } = props;
  return (oldData) =>
    new Promise((resolve, reject) => {
      const { clave_sat, tipo } = oldData;
      remove({
        url: `${url_api}/${clave_sat}/${tipo}`,
        oldData,
        resolve,
        reject,
        ...otherProps
      });
    });
};
