import React from 'react'
import { Grid, TextField, MenuItem, FormControlLabel, Radio, FormControl, RadioGroup, InputLabel, Button, Typography } from '@material-ui/core';
import { KeyboardDatePicker } from '@material-ui/pickers';
import { ArrowForward } from '@material-ui/icons'
import RequestFetch from '../../../../libraries/RequestFetch';
import moment from "moment";
import { ValidacionRFCMoral } from '../../../../libraries/Mask';
import ImageAvatar from '../../ExtraComponents/ImgAvatar';
import { useHistory } from 'react-router-dom';
import { SessionContext } from '../../../../routes/adminrh/CustomRoutes'
import { makeStyles } from '@material-ui/core/styles';


const useStyles = makeStyles(theme => ({
    container: {
        padding: theme.spacing(4),
    },
    uppercase: {
        textTransform: 'uppercase'
    },
    radioGroup: {
        marginTop: '15px'
    },
}));

function Informacion(props) {
    const classes = useStyles();
    const history = useHistory();
    const { cabeceras, isTokenExpired } = React.useContext(SessionContext);
    const { data, setData, onlySee, setContrato } = props;

    const [regimenes, setRegimenes] = React.useState([])
    const [giros, setGiros] = React.useState([])
    const [estados] = React.useState([
        { title: "Activo", value: 'A' },
        { title: "Pendiente", value: 'P' },
        { title: "Baneado", value: 'B' }
    ]);

    React.useEffect(() => {
        let mounted = true;
        const getRegimenesExito = async (res) => {
            const respuesta = await res.json();
            if (mounted) {
                const { regimenesCapitales } = respuesta;
                setRegimenes(regimenesCapitales);
            }

        }


        const getGirosExito = async (res) => {
            if (mounted) {
                const respuesta = await res.json();
                const { giros } = respuesta;
                setGiros(giros);
            }
        }

        const fallo = async (res) => {
            const respuesta = await res.json();
            if (!isTokenExpired(respuesta))
                if (mounted) {
                    console.log(respuesta);
                }
        }

        RequestFetch.get({
            url: `${RequestFetch.BASE_URL}/api/rh/giro?query=${RequestFetch.GIROS_ALL}`,
            headers: cabeceras,
            exito: getGirosExito,
            fallo: fallo,
            error_request: (error) => {
                history.push("/nomina/server-error")
            }
        });

        RequestFetch.get({
            url: `${RequestFetch.BASE_URL}/api/rh/regimen`,
            headers: cabeceras,
            exito: getRegimenesExito,
            fallo: fallo,
            error_request: (error) => {
                history.push("/nomina/server-error")
            }
        });


        return () => mounted = false;
        // eslint-disable-next-line
    }, []);

    const changeRFC = (event) => {
        var { value } = event.target;
        value = value.replace(/\s/g, '');
        if (value.length < 12) setData({ ...data, rfc: { value: value, verify: false } })
        else if (value.length === 12) setData({ ...data, rfc: { value: value, verify: true } })
    }

    const changeTextRequired = e => {
        const { name, value } = e.target;
        if (name === 'es_interna') {
            if (value === 'true') {
                setContrato(prevState => {
                    return { ...prevState, fecha_inicio: data.fecha_alta }
                });
            } else {
                setContrato(prevState => {
                    return { ...prevState, fecha_inicio: { value: '', verify: '' } }
                });
            }
        }
        setData(prevState => {
            return {
                ...prevState, [name]: { value: value, verify: `${value}`.length > 0 }
            }
        })
    }



    const changeDate = (key, e) => {
        console.log(e)
        if (e !== null) {
            const fecha_inp = e.format("YYYY");
            const fecha_min = moment("1900", "YYYY").format("YYYY");
            const fecha_max = moment(new Date(), "YYYY").format("YYYY");
            const fechita = fecha_inp >= fecha_min && fecha_inp <= fecha_max;
            //console.log(event.format("DD/MM/YYYY"));
            let newDate = { value: e, verify: (e instanceof moment && !isNaN(e) && fechita) };
            setData(prevState => {
                return {
                    ...prevState, [key]: newDate
                }
            })
        }
    }

    const changeImg = file => {
        setData((prevState) => {
            return {
                ...prevState,
                fileImg: file
            }
        });
    }


    return (
        <Grid container spacing={3} className={classes.container}>
            <Grid item xs={12} sm={12} md={onlySee ? 12 : 3} lg={onlySee ? 12 : 2}>
                <Grid container spacing={0}>
                    <Grid item xs={12} sm={12} md={12} lg={12} >
                        <Typography variant="subtitle2" style={{ textAlign: 'center' }} color="initial" gutterBottom> Logo</Typography>
                    </Grid>
                    <Grid item xs={12} sm={12} md={12} lg={12} >
                        <ImageAvatar file={data.fileImg} url={data.url_logo.value} changeImg={changeImg} onlySee={onlySee} />
                    </Grid>
                </Grid>
            </Grid>

            <Grid item xs={12} sm={12} md={onlySee ? 12 : 9} lg={onlySee ? 12 : 10}>
                <Grid container spacing={2}>
                    <Grid item xs={12} sm={12} md={12} lg={12} >
                        <Typography variant="subtitle2" style={{ textAlign: 'center' }} color="initial" gutterBottom>General</Typography>
                    </Grid>
                    <Grid item xs={12} sm={8} md={8} lg={onlySee ? 8 : 4} >
                        <TextField
                            variant="outlined"
                            required
                            disabled={onlySee}
                            label="Razón Social"
                            fullWidth placeholder="Ej. Grupo Empresarial"
                            error={props.data.razon_social.verify !== '' ? !props.data.razon_social.verify : false}
                            InputLabelProps={{ shrink: true, }}
                            value={props.data.razon_social.value}
                            name="razon_social"
                            onChange={changeTextRequired}
                            autoComplete='off'
                        />
                    </Grid>
                    <Grid item xs={12} sm={4} md={4} lg={onlySee ? 4 : 2} >
                        <TextField
                            variant="outlined"
                            required
                            fullWidth
                            label="Régimen Capital"
                            select
                            disabled={onlySee}
                            value={`${regimenes.length > 0 && props.data.id_regimen_capital.value !== '' ? props.data.id_regimen_capital.value : 'DEFAULT'}`}
                            error={props.data.id_regimen_capital.verify !== '' ? !props.data.id_regimen_capital.verify : false}
                            name="id_regimen_capital"
                            onChange={changeTextRequired}
                            autoComplete='off'>
                            <MenuItem value="DEFAULT" disabled>Selecciona</MenuItem>
                            {regimenes.map((item) => {
                                return <MenuItem key={`regimenes-${item.id_regimen_capital}`} value={item.id_regimen_capital}>{item.nombre_regimen}</MenuItem>
                            })}
                        </TextField>
                    </Grid>
                    <Grid item xs={12} sm={8} md={8} lg={onlySee ? 8 : 4} >
                        <TextField
                            variant="outlined"
                            required
                            label="Razón Comercial"
                            fullWidth
                            disabled={onlySee}
                            placeholder="Ej. Grupo..."
                            InputLabelProps={{ shrink: true, }}
                            value={props.data.razon_comercial.value}
                            name="razon_comercial"
                            onChange={changeTextRequired}
                            error={props.data.razon_comercial.verify !== '' ? !props.data.razon_comercial.verify : false}
                            autoComplete='off'
                        />
                    </Grid>
                    <Grid item xs={12} sm={4} md={4} lg={onlySee ? 4 : 2} >
                        <TextField
                            required id="rfc"
                            variant="outlined"
                            label="RFC"
                            fullWidth
                            disabled={onlySee}
                            placeholder="Ej. ABC010101D01"
                            InputLabelProps={{ shrink: true }}
                            value={props.data.rfc.value}
                            onChange={changeRFC}
                            inputProps={{ className: classes.uppercase }}
                            error={props.data.rfc.verify !== '' ? !props.data.rfc.verify : false}
                            InputProps={{ inputComponent: ValidacionRFCMoral }}
                            autoComplete='off'
                        />
                    </Grid>
                    <Grid item xs={12} sm={4} md={4} lg={onlySee ? 4 : 3} >
                        <KeyboardDatePicker
                            autoOk
                            disabled={onlySee}
                            inputVariant="outlined"
                            maxDateMessage={"Fecha es mayor al máximo permitido"}
                            minDateMessage={"Fecha es menor al mínimo permitido"}
                            invalidDateMessage={"Formato invalido"}
                            InputLabelProps={{ shrink: true }}
                            fullWidth required
                            error={props.data.fecha_constitucion.verify !== '' ? !props.data.fecha_constitucion.verify : false}
                            id="fecha_constitucion"
                            label="Fecha Constitución"
                            format="DD/MM/YYYY"
                            placeholder="Mes/Día/Año"
                            value={props.data.fecha_constitucion.value !== '' ? moment(props.data.fecha_constitucion.value, "DD/MM/YYYY") : null}
                            maxDate={moment(new Date(), "DD/MM/YYYY")}
                            onChange={e => changeDate("fecha_constitucion", e)}
                            autoComplete='off'
                        />
                    </Grid>
                    <Grid item xs={12} sm={4} md={4} lg={onlySee ? 4 : 3} >
                        <KeyboardDatePicker
                            autoOk
                            disabled={onlySee}
                            inputVariant="outlined"
                            maxDateMessage={"Fecha es mayor al máximo permitido"}
                            minDateMessage={"Fecha es menor al mínimo permitido"}
                            invalidDateMessage={"Formato invalido"}
                            InputLabelProps={{ shrink: true }}
                            fullWidth required
                            error={props.data.fecha_alta.verify !== '' ? !props.data.fecha_alta.verify : false}
                            placeholder="Mes/Día/Año"
                            id="fecha_alta"
                            label="Fecha Alta"
                            format="DD/MM/YYYY"
                            value={props.data.fecha_alta.value !== '' ? moment(props.data.fecha_alta.value, "DD/MM/YYYY") : null}
                            minDate={props.data.fecha_constitucion.value !== '' ? moment(props.data.fecha_constitucion.value, "DD/MM/YYYY") : moment(new Date(), "DD/MM/YYYY")}
                            maxDate={moment(new Date(), "DD/MM/YYYY")}
                            onChange={e => changeDate("fecha_alta", e)}
                            autoComplete='off'
                        />
                    </Grid>
                    <Grid item xs={12} sm={4} md={4} lg={onlySee ? 4 : 3} >
                        <TextField
                            variant="outlined"
                            required
                            fullWidth
                            disabled={onlySee}
                            label="Giro"
                            select className={classes.select}
                            value={`${giros.length > 0 && props.data.id_giro.value !== '' ? props.data.id_giro.value : 'DEFAULT'}`}
                            InputLabelProps={{ shrink: true }}
                            name="id_giro"
                            onChange={changeTextRequired}
                            error={props.data.id_giro.verify !== '' ? !props.data.id_giro.verify : false}
                        >
                            <MenuItem value="DEFAULT" disabled>Selecciona</MenuItem>
                            {
                                giros.map((item) => {
                                    return <MenuItem key={`giros-${item.id_giro}`} value={item.id_giro}>{item.nombre_giro}</MenuItem>
                                })
                            }
                        </TextField>

                    </Grid>
                    <Grid item xs={12} sm={4} md={4} lg={onlySee ? 4 : 3} >
                        <TextField
                            variant="outlined"
                            required
                            fullWidth
                            disabled={onlySee}
                            label="Estatus"
                            select className={classes.select}
                            value={`${estados.length > 0 && props.data.estatus.value !== '' ? props.data.estatus.value : 'DEFAULT'}`}
                            InputLabelProps={{ shrink: true }}
                            name="estatus"
                            onChange={changeTextRequired}
                            error={props.data.estatus.verify !== '' ? !props.data.estatus.verify : false}>
                            <MenuItem value="DEFAULT" disabled>Selecciona</MenuItem>
                            {estados.map((item) => {
                                return <MenuItem key={`estatus-${item.value}`} value={item.value}>{item.title}</MenuItem>
                            })}
                        </TextField>

                    </Grid>

                    <Grid item xs={12} sm={4} md={4} lg={onlySee ? 4 : 3} >
                        <FormControl
                            required
                            disabled={onlySee}
                            component="fieldset"
                            className={classes.select}
                            fullWidth
                            error={props.data.es_interna.verify !== '' ? !props.data.es_interna.verify : false}>
                            <InputLabel shrink={true}>Tipo Empresa</InputLabel>
                            <RadioGroup
                                row
                                className={classes.radioGroup}
                                name="es_interna"
                                onChange={changeTextRequired}
                                value={props.data.es_interna.value} >
                                <FormControlLabel color="secondary" style={{ margin: 0 }} value="false" control={<Radio style={{ padding: 3 }} color="primary" />} label={<Typography variant="body2" color="initial">Externa</Typography>} />
                                <FormControlLabel color="secondary" style={{ margin: 0 }} value="true" control={<Radio style={{ padding: 3 }} color="primary" />} label={<Typography variant="body2" color="initial">Interna</Typography>} />

                            </RadioGroup>
                        </FormControl>
                    </Grid>

                    {
                        !props.expandable &&
                        <Grid container item
                            direction="row"
                            justify="flex-end"
                            alignItems="center" xs={12} sm={4} lg={9}>

                            <Button
                                variant="outlined"
                                color="primary"
                                endIcon={<ArrowForward />}
                                onClick={props.buttons.next}
                            >Siguiente</Button>
                        </Grid>
                    }
                </Grid>
            </Grid>
        </Grid>
    )
}

export default Informacion;











