import React from "react";
import { Image } from 'cloudinary-react'
import { makeStyles } from '@material-ui/core/styles';
import IconButton from '@material-ui/core/IconButton';
import PhotoCamera from '@material-ui/icons/PhotoCamera';
import { Badge, Avatar } from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';

const useStyles = makeStyles(theme => ({
    orange: {
        color: theme.palette.common.white,
        backgroundColor: '#F57C00'
    },
    perfil: {
        textAlign: 'center',
        width: '100%',
        height: '100%'
    },
    imagen: {
        width: '100%',
        height: 'auto',
        maxWidth: '150px',
        maxHeight: '150px',
        minWidth: '150px',
        minHeight: '150px',
        borderRadius: '100%',
        boxShadow: "-5px 5px 25px 2px rgba(0,0,0,0.14);",
    },
    textPerfil: {
        textAlign: 'center'
    },
    input: {
        display: 'none',
    },
    editar: {
        backgroundColor: '#626262',
        color: '#fff',
        '&:hover': {
            backgroundColor: '#4D4D4D',
        },
    },
}));

export default function ImgAvatar(props) {
    const classes = useStyles();
    const { onlySee } = props;

    const uploadFile = (event) => {
        const { files } = event.target;
        if (files.length === 1) {
            if (files[0].size > 0)
                props.changeImg(files[0]);
        }
        else
            props.changeImg(null);

    }

    const deleteFile = () => {
        props.changeImg(null);
    }


    const Subirarchivo = () => {
        if (props.file === null)
            return <>
                <input accept="image/*" className={classes.input} onChange={uploadFile} id="url_avatar" type="file" name="url_avatar" />
                <label htmlFor="url_avatar">
                    <IconButton size="small" color="primary" aria-label="imagen_subir" component="span" className={classes.editar}>
                        <PhotoCamera fontSize="small" />
                    </IconButton>
                </label>
            </>
        else
            return <IconButton size="small" color="primary" onClick={deleteFile} aria-label="upload picture" component="span" className={classes.editar}>
                <CloseIcon fontSize="small" />
            </IconButton>

    }

    const showPhoto = () => {
        if (props.file === null && (props.url === null || props.url === ''))
            return <Avatar className={`${classes.imagen} ${classes.orange}`}>PNG/JPG</Avatar>
        else if (props.file === null && props.url !== null)
            return <Image cloudName="get-consultoria" publicId={props.url} responsive width="auto" className={classes.imagen} />
        else if (props.file !== null)
            return <div>
                <img alt="foto" className={classes.imagen} src={window.URL.createObjectURL(props.file)} />
            </div>
    }

    return (
        <div className={classes.perfil}>
            <Badge
                overlap="circle"
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'right',
                }}
                badgeContent={
                    <>{!onlySee && <Subirarchivo />}</>
                }
            >
                {showPhoto()}
            </Badge>
        </div>
    )
}