import React, { useState } from "react";
import IconButton from "@material-ui/core/IconButton";
import InputAdornment from "@material-ui/core/InputAdornment";
import SearchIcon from "@material-ui/icons/Search";
import TextField from "@material-ui/core/TextField";

export const InputSearch = (props) => {
  const { search } = props;
  
  const { nominasSemanal, setNominasSemanal } = search;
  const { nominasCatorcenal, setNominasCatorcenal } = search;
  const { nominasQuincenal, setNominasQuincenal } = search;
  const { nominasMensual, setNominasMensual } = search;
  const { value } = search;
  const [inputText, setInputText] = useState("");
  
  const handleChange = (event) => {
    setInputText(event.target.value);
  };

  const keyUpHandler = (e) => {
    filterNominas(e.target.value);
  };

  const containsValue = (nomina, text) => {
    let flag = false;
    Object.keys(nomina).forEach((key) => {
      if (nomina[key] !== null && key !== "flag") {
        flag |= nomina[key]
          .toString()
          .toLowerCase()
          .includes(text.toLowerCase());
      }
    });

    return flag;
  };

  const searchNomina = (nominas, text) => {
    nominas.forEach((nomina) => (nomina.flag = containsValue(nomina, text)));
  };

  const filterNominas = (text) => {
    switch (value) {
      case 0:
        searchNomina(nominasSemanal, text);
        setNominasSemanal([...nominasSemanal]);
        break;
      case 1:
        searchNomina(nominasCatorcenal, text);
        setNominasCatorcenal([...nominasCatorcenal]);
        break;
      case 2:
        searchNomina(nominasQuincenal, text);
        setNominasQuincenal([...nominasQuincenal]);
        break;
      case 3:
        searchNomina(nominasMensual, text);
        setNominasMensual([...nominasMensual]);
        break;

      default:
        break;
    }
  };
  return (
    <TextField
      label="Buscar"
      value={inputText}
      onChange={handleChange}
      onKeyUp={keyUpHandler}
      InputProps={{
        endAdornment: (
          <InputAdornment>
            <IconButton>
              <SearchIcon />
            </IconButton>
          </InputAdornment>
        ),
      }}
    />
  );
};
