import React, { useState, useEffect, createContext, useContext } from 'react'
import {
    Paper,
    Typography,
    ListItem,
    ListItemText,
    Collapse,
    Table,
    TableCell,
    TableContainer,
    TableRow,
    TableBody,
    TableHead,
    Dialog,
    DialogActions,
    DialogContent,
    DialogTitle,
    Button,
    Grid,
    TextField,
    MenuItem,
    LinearProgress,
    ListItemIcon
} from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import { BASE_URL } from '../../../../libraries/RequestFetch';
import moment from 'moment';
import { Skeleton } from '@material-ui/lab';
import { post, put } from './helperPrimaRiesgo';
import CalendarTodayIcon from '@material-ui/icons/CalendarToday';
import { ValidacionPrimaRiesgo } from '../../../../libraries/Mask'
import { SessionContext } from '../../../../routes/adminrh/CustomRoutes';

const useStyles = makeStyles((theme) => ({
    root: {
        backgroundColor: theme.palette.background.paper,
        minHeight: '60px'
    },
    paper: {
        padding: theme.spacing(3)
    },
    title: {
        textAlign: 'center',
        paddingTop: '15px'
    }
}));

const PrimaRiesgoContext = createContext(null);

export default function PrimaRiesgo({ idEmpresa, registrosPatronales }) {
    const { cabeceras, isTokenExpired } = useContext(SessionContext);
    const classes = useStyles();
    const [registros, setRegistros] = useState();

    const getAllDatas = async (idEmpresa) => {
        const respuesta = await fetch(`${BASE_URL}/api/rh/empresa/detalle_registro_patronal?id_empresa=${idEmpresa}`,
            {
                method: 'GET',
                headers: cabeceras
            })
        const resp = await respuesta.json();
        if (!isTokenExpired(resp)) {
            const { registros } = resp;
            const registrosByYear = groupByYear(registros)
            const anio = moment().format('YYYY');
            registrosByYear[anio] = registrosByYear[anio] ? registrosByYear[anio] : [];
            setRegistros(registrosByYear)
        }
    }

    const groupByYear = (registros) => {
        let result = registros.reduce((prev, current) => {
            const value = moment(current.fecha_registro).format('YYYY')
            prev[value] = prev[value] || [];
            prev[value].push(current);
            return prev;
        }, {})
        return result;
    }

    const refresh = () => getAllDatas(idEmpresa);

    useEffect(() => {
        getAllDatas(idEmpresa);
        // eslint-disable-next-line
    }, []);

    return (
        <PrimaRiesgoContext.Provider value={{ refresh }}>
            <Paper className={classes.root} elevation={2}>
                <Typography className={classes.title} variant="button" display="block" gutterBottom>
                    Primas de Riesgo
                </Typography>
                {
                    registros ?
                        Object.keys(registros).length > 0 &&
                        Object.keys(registros).map((key, index) =>
                            <CollapseTable
                                data={registros[key]}
                                name={key}
                                key={`collapsible-table-registros-${index}`}
                                defaulOpen={index === Object.keys(registros).length - 1}
                                registrosPatronales={registrosPatronales}
                            />
                        )
                        :
                        <Grid container spacing={2} style={{ padding: 15 }}>
                            <Grid item xs={6}>
                                <Skeleton animation="wave" height={70} />
                            </Grid>
                            <Grid item xs={6}>
                                <Skeleton animation="wave" height={70} />
                            </Grid>
                        </Grid>
                }
            </Paper>
        </PrimaRiesgoContext.Provider>
    )
}


function CollapseTable({ data, name, defaulOpen, registrosPatronales }) {
    const [open, setOpen] = useState(defaulOpen ? true : false);
    const [dialog, setDialog] = useState({ open: false, edit: false });

    return (
        <>
            <ListItem button onClick={() => setOpen(!open)}>
                <ListItemIcon>
                    <CalendarTodayIcon />
                </ListItemIcon>
                <ListItemText primary={name} />
                {open ? <ExpandLess /> : <ExpandMore />}
            </ListItem>
            <Collapse in={open} timeout="auto" unmountOnExit>

                <TableContainer>
                    <Table size="small">
                        {
                            data.length > 0 &&
                            <TableHead>
                                <TableRow>
                                    <TableCell align="center">Clase</TableCell>
                                    <TableCell align="center">Clave</TableCell>
                                    <TableCell align="center">Valor</TableCell>
                                </TableRow>
                            </TableHead>}
                        <TableBody>
                            {
                                data.sort((a, b) => a.categoria - b.categoria).map((i, index) => {
                                    return <TableRow key={`prima-riesgo-inicio-fin-${index}`} hover
                                        onClick={() => {
                                            if (name === moment().format('YYYY'))
                                                setDialog({
                                                    open: true,
                                                    edit: true,
                                                    categoria: i.categoria,
                                                    valor_por_riesgo: i.valor_por_riesgo,
                                                    folio_registro: i.folio_registro
                                                });
                                        }}>
                                        <TableCell align="center">{i.categoria}</TableCell>
                                        <TableCell align="center">{i.clave_patronal}</TableCell>
                                        <TableCell align="center">{i.valor_por_riesgo}</TableCell>
                                    </TableRow>
                                })
                            }
                            {
                                name === moment().format('YYYY') && data.length < 5 &&
                                <TableRow>
                                    <TableCell align="center" colSpan="4">
                                        <Button onClick={() => setDialog({ open: true, edit: false })}>Agregar</Button>
                                    </TableCell>
                                </TableRow>
                            }
                        </TableBody>
                    </Table>
                </TableContainer>
            </Collapse>
            {
                dialog.open &&
                <DialogPrima {...dialog}
                    onClose={() => setDialog({ ...dialog, open: false })}
                    registrosPatronales={registrosPatronales}
                    registrosActuales={data}
                />
            }
        </>
    );
}


function DialogPrima({ open, edit, folio_registro, categoria, valor_por_riesgo, onClose, registrosPatronales, registrosActuales }) {
    const { cabeceras, isTokenExpired } = useContext(SessionContext);
    const [values, setValues] = useState({
        categoria: categoria ? categoria : '0',
        valor_por_riesgo: valor_por_riesgo ? valor_por_riesgo : ''
    });
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState({ isError: false, message: '' })
    const { refresh } = useContext(PrimaRiesgoContext);

    const separeClases = (todos, actuales) => {
        if (!edit)
            return todos.filter(t => !actuales.some(a => t.categoria === a.categoria));
        return todos;
    }

    const sendData = () => {
        setLoading(true);
        if (values.categoria === '0' || isNaN(parseFloat(values.valor_por_riesgo))) {
            setError({ isError: true, message: "Revisa los campos" });
            setLoading(false);
            setTimeout(() => {
                setError({ isError: false, message: "" })
            }, 1500)
        }
        else {
            const { clave_patronal } = registrosPatronales.find(e => e.categoria === values.categoria);
            if (edit) {
                put(folio_registro, { valor_por_riesgo: values.valor_por_riesgo }, cabeceras)
                    .then(response => {
                        if (!isTokenExpired(response)) {
                            setLoading(false);
                            refresh();
                            onClose();
                        }
                    })
            } else {
                const date = moment().format("YYYY-MM-DD");
                const data = { registros: [{ clave_patronal: clave_patronal, fecha_registro: date, valor_por_riesgo: values.valor_por_riesgo }] };
                post(data, cabeceras).then((response) => {
                    if (!isTokenExpired(response)) {
                        setLoading(false);
                        refresh();
                        onClose();
                    }
                })
            }

        }

    }


    return (
        <Dialog open={open} onClose={onClose} maxWidth="xs" fullWidth={true}>
            {loading && <LinearProgress color="secondary" />}
            <DialogTitle>{edit ? "Editanto " : "Agregando "} Prima de Riesgo </DialogTitle>
            <DialogContent>
                <Grid spacing={2} style={{ padding: 24 }} container >
                    <Grid item xs={6}>
                        <TextField fullWidth select
                            error={error.isError}
                            label="Clave"
                            disabled={edit}
                            value={values.categoria}
                            onChange={({ target: { value } }) => setValues({ ...values, categoria: value })}
                            variant="outlined">
                            <MenuItem value="0" disabled>Selecciona</MenuItem>
                            {
                                separeClases(registrosPatronales, registrosActuales).map((e, index) => {
                                    return <MenuItem
                                        key={`menu-item-clase-${index}`} value={e.categoria}>{`CLASE ${e.categoria}`}
                                    </MenuItem>
                                }
                                )}

                        </TextField>
                    </Grid>
                    <Grid item xs={6}>

                        <TextField fullWidth label="Prima Riesgo" variant="outlined"
                            error={error.isError}
                            placeholder="0.542"
                            InputProps={{ inputComponent: ValidacionPrimaRiesgo }}
                            value={values.valor_por_riesgo}
                            onChange={({ target: { value } }) => setValues({ ...values, valor_por_riesgo: value })} />
                    </Grid>
                    <Grid item xs={12}>
                        {error.isError &&
                            <Typography variant="button" color="secondary">{error.message}</Typography>
                        }
                    </Grid>
                </Grid>
            </DialogContent>
            <DialogActions>
                <Button onClick={onClose} color="default">
                    Cancel
                </Button>
                <Button onClick={sendData} color="secondary">
                    Guardar
                </Button>
            </DialogActions>
        </Dialog>
    )
}
