import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import InputLabel from "@material-ui/core/InputLabel";
import Input from "@material-ui/core/Input";
import MenuItem from "@material-ui/core/MenuItem";
import NumberFormat from "react-number-format";
import TextField from "@material-ui/core/TextField";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import Fab from "@material-ui/core/Fab";
import Grid from "@material-ui/core/Grid";
import { InputSearch } from "./InputSearch";

import CalendarTodayIcon from "@material-ui/icons/CalendarToday";

const months = [
  "Enero",
  "Febrero",
  "Marzo",
  "Abril",
  "Mayo",
  "Junio",
  "Julio",
  "Agosto",
  "Septiembre",
  "Octubre",
  "Noviembre",
  "Diciembre",
];

const useStyles = makeStyles((theme) => ({
  container: {
    display: "flex",
    flexWrap: "wrap",
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  box: {
    width: 85
  },
  extendedIcon: {
    marginRight: theme.spacing(1)
  }
}));

function NumberFormatCustom(props) {
  const { inputRef, onChange, ...other } = props;

  return (
    <NumberFormat
      {...other}
      getInputRef={inputRef}
      onValueChange={(values) => {
        onChange({
          target: {
            name: props.name,
            value: values.value,
          },
        });
      }}
      format="####"
      isNumericString
    />
  );
}

export const OptionsPanel = (props) => {
  const { month, changeMonth } = props;
  const { year, changeYear } = props;
  const { valuesSearch } = props;
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const handleChange = (event) => {
    changeMonth(Number(event.target.value) || "");
  };

  const handleChangeYear = (event) => {
    changeYear(Number(event.target.value) || "");
  };

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div style={{ margin: 16 }}>
      <Grid spacing={2} container direction="row" justify="flex-end" alignItems="flex-end">
        <Grid item xs={6} md={2}>
          <Fab
            variant="extended"
            size="medium"
            color="primary"
            aria-label="add"
            className={classes.margin}
            onClick={handleClickOpen}
          >
            <CalendarTodayIcon className={classes.extendedIcon} />
            {months[month - 1]} {year}
          </Fab>
        </Grid>
        <Grid item xs={6} md={2}>
          <InputSearch search={valuesSearch} />
        </Grid>
      </Grid>
      <Dialog
        disableBackdropClick
        disableEscapeKeyDown
        open={open}
        onClose={handleClose}
      >
        <DialogTitle>Periodo</DialogTitle>
        <DialogContent>
          <form className={classes.container}>
            <FormControl className={classes.formControl}>
              <InputLabel htmlFor="demo-dialog-native">Mes</InputLabel>
              <Select
                value={month}
                onChange={handleChange}
                input={<Input id="demo-dialog-native" />}
              >
                {months.map((month, index) => (
                  <MenuItem key={`months-elements-${index}`} value={index + 1}>{month}</MenuItem>
                ))}
              </Select>
            </FormControl>
            <FormControl className={classes.formControl}>
              <TextField
                id="standard-number"
                label="Año"
                value={year}
                InputLabelProps={{
                  shrink: true,
                }}
                onChange={handleChangeYear}
                InputProps={{
                  inputComponent: NumberFormatCustom,
                }}
                className={classes.box}
              />
            </FormControl>
          </form>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancelar
          </Button>
          <Button onClick={handleClose} color="primary">
            Aceptar
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};
