import { BASE_URL } from '../../../../libraries/RequestFetch'
export const post = async (data, headers) => {
    return fetch(
        `${BASE_URL}/api/rh/nomina/comprobante`,
        {
            method: 'POST',
            headers: headers,
            body: data
        }
    ).then(response => response.json())
}


export const get = async (idNomina, headers) => {
    return fetch(
        `${BASE_URL}/api/rh/nomina/comprobante/${idNomina}`,
        {
            method: 'GET',
            headers: headers,
        }
    ).then(response => response.json())
}
