function clearPalabra(estado) {
    return estado.normalize("NFD").replace(/[\u0300-\u036f]/g, "").toLowerCase()
}

export function CodeByEstado(estado) {
    estado = clearPalabra(estado);
    switch (estado) {
        case 'mexico': case 'estado de mexico':
            return estados[0].code;
        case 'coahuila de zaragoza': case 'coahuila':
            return estados[1].code;
        case 'michoacan de ocampo': case 'michoacan':
            return estados[2].code;
        case 'veracruz de ignacio de la llave': case 'veracruz':
            return estados[3].code;
        default:
            return estados.find(item => clearPalabra(item.name) === estado).code;
    }
};

export function isCode(text) {
    return estados.some(item => item.code === text.toUpperCase());
}

export function EstadoByCode(code) {
    const estado = estados.find(item => item.code === code.toUpperCase())
    return estado ? estado.name : null;
}

export const EstadosFull = [...estados];