import React from 'react';
import HomeWorkOutlinedIcon from '@material-ui/icons/HomeWork';
import PeopleOutlineIcon from '@material-ui/icons/People';
import AddOutlinedIcon from '@material-ui/icons/AddCircleOutline';
import ListIcon from '@material-ui/icons/List';
import WorkOutlineIcon from '@material-ui/icons/Work';
import ListAltIcon from '@material-ui/icons/ListAlt';
import StarRateOutlinedIcon from '@material-ui/icons/StarRate';
import ExternalIcon from '@material-ui/icons/CallMade';
import InternalIcon from '@material-ui/icons/CallReceived';
import AllIcon from '@material-ui/icons/SyncAlt';
import MonetizationOnRoundedIcon from '@material-ui/icons/MonetizationOnRounded';
import LibraryAddCheckOutlinedIcon from '@material-ui/icons/LibraryAddCheckOutlined';
import EqualizerIcon from '@material-ui/icons/Equalizer';
import AssessmentIcon from '@material-ui/icons/Assessment';
import SupervisedUserCircleIcon from '@material-ui/icons/SupervisedUserCircle';
import RootIcon from '@material-ui/icons/VerifiedUserOutlined';
import WorkOutlineOutlinedIcon from '@material-ui/icons/WorkOutlineOutlined';

import Divider from '@material-ui/core/Divider';
import ListMenu from './ListMenu';
import { Scrollbars } from 'react-custom-scrollbars';

import { makeStyles } from '@material-ui/core/styles';

import {
    Route,
    Redirect,
    Switch,
    Link
} from "react-router-dom";

import FormEmpresa from '../FormEmpresa/FormEmpresa';
import EditEmpresa from '../FormEmpresa/EditEmpresa';
import TableEmpresa from '../FormEmpresa/TableEmpresa'

import EditEmpleado from '../FormEmpleado/EditEmpleado';
import FormEmpleado from '../FormEmpleado/FormEmpleado';
import TableEmpleado from '../FormEmpleado/TableEmpleado';

import FormRepresentante from '../FormRepresentante/FormRepresentante';
import TableRepresentante from '../FormRepresentante/TableRepresentante';
import EditRepresentante from '../FormRepresentante/EditRepresentante';

import NominaActivas from '../NominaActivas';
import Nomina from '../Nomina/Nomina';
import { Reportes } from '../Reportes'

import TableDocumento from '../TableCatalogos/TableDocumento';
import TableGiro from '../TableCatalogos/TableGiro';
import TableRegimen from '../TableCatalogos/TableRegimen';
import TableRegimenContratacion from '../TableCatalogos/TableRegimenContratacion';
import TableJornada from '../TableCatalogos/TableJornada';
import TableDepartamento from '../TableCatalogos/TableDepartamento';
import TableContratacion from '../TableCatalogos/TableContratacion';
import TablePercepcionDeducion from '../TableCatalogos/PercepcionDeduccion';
import TablePercepcionDeduccionSat from '../TableCatalogos/percepcionDeduccionSAT';
import { TableIntegracion, TableISR, TableSubsidio, TableUMA } from '../LeyesFiscales'
import { HomeRoot, Home } from '../Home'
import ImageGet from '../../../assets/img/get.png';
import Usuario from '../Ususarios/Usuario';
import GavelIcon from '@material-ui/icons/Gavel';
import TrendingUpIcon from '@material-ui/icons/TrendingUp';

const prefix = "/nomina";

const fiscales = [
    {
        icon: <GavelIcon />, name: "Fiscales",
        childs: [
            { icon: <TrendingUpIcon />, name: "Integración", route: `${prefix}/fiscales/integracion` },
            { icon: <TrendingUpIcon />, name: "Subsidio", route: `${prefix}/fiscales/subsidio` },
            { icon: <TrendingUpIcon />, name: "ISR", route: `${prefix}/fiscales/isr` },
            { icon: <TrendingUpIcon />, name: "UMA", route: `${prefix}/fiscales/uma` },
        ]
    }
]

const usuarios = [{
    icon: <SupervisedUserCircleIcon />, name: "Usuarios",
    childs: [
        { icon: <RootIcon />, name: "Adminstradores", route: `${prefix}/usuario/administrador` },
        { icon: <InternalIcon />, name: "Internos", route: `${prefix}/usuario/interno` },
        { icon: <ExternalIcon />, name: "Externos", route: `${prefix}/usuario/externo` },
        { icon: <WorkOutlineOutlinedIcon />, name: "Representantes", route: `${prefix}/usuario/representante` },
    ]
}];

const catalogos = [
    {
        icon: <ListAltIcon />, name: "Todos",
        childs: [
            { icon: <StarRateOutlinedIcon />, name: "Deparamentos", route: `${prefix}/departamento` },
            { icon: <StarRateOutlinedIcon />, name: "Reg. Contratación", route: `${prefix}/regimen-contratacion` },
            { icon: <StarRateOutlinedIcon />, name: "Giros", route: `${prefix}/giro` },
            { icon: <StarRateOutlinedIcon />, name: "Reg. Capital", route: `${prefix}/regimen-capital` },
            { icon: <StarRateOutlinedIcon />, name: "Jornadas", route: `${prefix}/jornada` },
            { icon: <StarRateOutlinedIcon />, name: "Tipo Contrato", route: `${prefix}/tipo-contrato` },
            { icon: <StarRateOutlinedIcon />, name: "Tipo Documento", route: `${prefix}/tipo-documento` },
            { icon: <StarRateOutlinedIcon />, name: "Percepción Deducción", route: `${prefix}/percepcion-deduccion` },
            { icon: <StarRateOutlinedIcon />, name: "Percepción Deducción SAT", route: `${prefix}/percepcion-deduccion-sat` },
        ]
    }
];

const usePermissions = (type) => {
    switch (type) {
        case 'S': return {
            entidades: [
                {
                    icon: <MonetizationOnRoundedIcon />, name: "Nóminas",
                    childs: [
                        { icon: <LibraryAddCheckOutlinedIcon />, name: "Activas", route: `${prefix}/activas` },
                    ]
                },
                {
                    icon: <HomeWorkOutlinedIcon />, name: "Empresas",
                    childs: [
                        { icon: <AddOutlinedIcon />, name: "Nueva", route: `${prefix}/nueva-empresa` },
                        { icon: <InternalIcon />, name: "Internas", route: `${prefix}/listar-empresa-por/true` },
                        { icon: <ExternalIcon />, name: "Clientes", route: `${prefix}/listar-empresa-por/false` },
                        { icon: <AllIcon />, name: "Todas", route: `${prefix}/listar-empresa` },
                    ]
                },
                {
                    icon: <PeopleOutlineIcon />, name: "Empleados",
                    childs: [
                        { icon: <AddOutlinedIcon />, name: "Nuevo", route: `${prefix}/nuevo-empleado` },
                        { icon: <ListIcon />, name: "Listar", route: `${prefix}/listar-empleado-by/all/all` },
                    ]
                },
                {
                    icon: <WorkOutlineIcon />, name: "Representantes",
                    childs: [
                        { icon: <AddOutlinedIcon />, name: "Nuevo", route: `${prefix}/nuevo-representante` },
                        { icon: <ListIcon />, name: "Listar", route: `${prefix}/listar-representante` }
                    ]
                },
            ],
            catalogos,
            usuarios,
            fiscales,
            reportes: [
                {
                    icon: <AssessmentIcon />, name: "Reportes",
                    childs: [
                        { icon: <EqualizerIcon />, name: "Gráficas", route: `${prefix}/reportes` },
                    ]
                }
            ]
        };
        case 'G': return {
            entidades: [
                {
                    icon: <MonetizationOnRoundedIcon />, name: "Nóminas",
                    childs: [
                        { icon: <LibraryAddCheckOutlinedIcon />, name: "Activas", route: `${prefix}/activas` },

                    ]
                },
                {
                    icon: <HomeWorkOutlinedIcon />, name: "Empresas",
                    childs: [
                        { icon: <ExternalIcon />, name: "Mis Clientes", route: `${prefix}/listar-empresa-por/false` },
                    ]
                },
                {
                    icon: <PeopleOutlineIcon />, name: "Empleados",
                    childs: [
                        { icon: <AddOutlinedIcon />, name: "Nuevo", route: `${prefix}/nuevo-empleado` },
                        { icon: <ListIcon />, name: "Listar", route: `${prefix}/listar-empleado-by/all/all` },
                    ]
                },
            ],
            catalogos
        }
        case 'C': return {
            entidades: [
                {
                    icon: <MonetizationOnRoundedIcon />, name: "Nóminas",
                    childs: [
                        { icon: <LibraryAddCheckOutlinedIcon />, name: "Activas", route: `${prefix}/activas` },
                    ]
                }
            ]
        }
        default: return {}
    }
}
const useStyles = makeStyles(theme => ({
    divider: { backgroundColor: '#D1D2D4', margin: '0px 15px' },
    drawerHeader: {
        display: "flex",
        alignItems: "center",
        padding: theme.spacing(1),
        // necessary for content to be below app bar
        ...theme.mixins.toolbar,
        justifyContent: "center"
    },
}))
export function RoutesMenu({ type }) {
    const classes = useStyles();
    const listDrawer = usePermissions(type);

    const RoutesMenu = () => {
        const renderThumb = ({ style, ...props }) => {
            const thumbStyle = {
                backgroundColor: `rgb(214,214,214, 0.3)`
            };
            return (
                <div style={{ ...style, ...thumbStyle }} {...props} />
            );
        }
        return <Scrollbars
            style={{ position: 'absolute' }}
            renderThumbVertical={renderThumb}
            autoHide
            autoHideTimeout={1000}
            autoHideDuration={200}>
            <div className={classes.drawerHeader}>
                <Link to="/nomina/">
                    <img alt="logo" src={ImageGet} height="40" />
                </Link>
            </div>
            <Divider className={classes.divider} />
            {
                listDrawer.entidades &&
                <>
                    <ListMenu items={listDrawer.entidades} keyElement={"entidades"} name={"Entidades"} />
                    <Divider className={classes.divider} />
                </>

            }
            {
                listDrawer.reportes &&
                <>
                    <ListMenu items={listDrawer.reportes} keyElement={"reportes"} name={"Reportes"} />
                    <Divider className={classes.divider} />
                </>
            }

            {
                listDrawer.fiscales &&
                <>
                    <ListMenu items={listDrawer.fiscales} keyElement={"fiscales"} name={"Fiscales"} />
                    <Divider className={classes.divider} />
                </>
            }
            {
                listDrawer.usuarios &&
                <>
                    <ListMenu items={listDrawer.usuarios} keyElement={"usuarios"} name={"Usuarios"} />
                    <Divider className={classes.divider} />
                </>
            }
            {
                listDrawer.catalogos &&
                <>
                    <ListMenu items={listDrawer.catalogos} keyElement={"catalogos"} name={"Catálogos"} />
                    <Divider className={classes.divider} />
                </>
            }
        </Scrollbars>
    }

    return <RoutesMenu />
}

export const routesUser = (type) => {
    switch (type) {
        case 'S':
            return (
                <Switch>
                    <Route exact path={`${prefix}`}> <HomeRoot /> </Route>
                    <Route exact path={`${prefix}/`}> <HomeRoot /> </Route>

                    <Route exact path={`${prefix}/nueva-empresa`}> <FormEmpresa /> </Route>
                    <Route exact path={`${prefix}/edit-empresa/:id(\\d+)`}> <EditEmpresa /> </Route>
                    <Route exact path={`${prefix}/listar-empresa`}> <TableEmpresa /> </Route>
                    <Route exact path={`${prefix}/listar-empresa-por/:tipo(true|false)`} > <TableEmpresa /> </Route>

                    <Route exact path={`${prefix}/nuevo-representante`} > <FormRepresentante /> </Route>
                    <Route exact path={`${prefix}/edit-representante/:id(\\d+)`} > <EditRepresentante /> </Route>
                    <Route exact path={`${prefix}/listar-representante`}> <TableRepresentante /> </Route>

                    <Route exact path={`${prefix}/nuevo-empleado`} > <FormEmpleado /> </Route>
                    <Route exact path={`${prefix}/edit-empleado/:id(\\d+)`}> <EditEmpleado /> </Route>
                    <Route exact path={`${prefix}/listar-empleado-by/:interna/:externa`}> <TableEmpleado /> </Route>

                    <Route exact path={`${prefix}/activas`} > <NominaActivas /> </Route>
                    <Route exact path={`${prefix}/activas/:idNomina(\\d+)&:idContrato(\\d+)&:idEmpresa(\\d+)&:anio(\\d+)`}> <Nomina /> </Route>

                    <Route exact path={`${prefix}/reportes`}> <Reportes /> </Route>

                    <Route exact path={`${prefix}/giro`}><TableGiro /></Route>
                    <Route exact path={`${prefix}/regimen-capital`}><TableRegimen /></Route>
                    <Route exact path={`${prefix}/regimen-contratacion`}><TableRegimenContratacion /></Route>
                    <Route exact path={`${prefix}/jornada`}><TableJornada /></Route>
                    <Route exact path={`${prefix}/departamento`}><TableDepartamento /></Route>
                    <Route exact path={`${prefix}/tipo-contrato`}><TableContratacion /></Route>
                    <Route exact path={`${prefix}/tipo-documento`}><TableDocumento /></Route>
                    <Route exact path={`${prefix}/percepcion-deduccion`}><TablePercepcionDeducion /></Route>
                    <Route exact path={`${prefix}/percepcion-deduccion-sat`}><TablePercepcionDeduccionSat /></Route>

                    <Route exact path={`${prefix}/usuario/administrador`}><Usuario type="S" /></Route>
                    <Route exact path={`${prefix}/usuario/interno`}><Usuario type="G" /></Route>
                    <Route exact path={`${prefix}/usuario/externo`}><Usuario type="E" /></Route>
                    <Route exact path={`${prefix}/usuario/representante`}><Usuario type="C" /></Route>

                    <Route exact path={`${prefix}/fiscales/integracion`}><TableIntegracion /></Route>
                    <Route exact path={`${prefix}/fiscales/isr`}><TableISR /></Route>
                    <Route exact path={`${prefix}/fiscales/subsidio`}><TableSubsidio /></Route>
                    <Route exact path={`${prefix}/fiscales/uma`}><TableUMA /></Route>

                    <Route path={`${prefix}/*`}>
                        <Redirect from={`${prefix}/*`} to="/page-not-found" />
                    </Route>

                </Switch>
            )
        case 'G': return (
            <Switch>
                <Route exact path={`${prefix}`}> <Home /> </Route>
                <Route exact path={`${prefix}/`}> <Home /> </Route>
                <Route exact path={`${prefix}/activas`} > <NominaActivas /> </Route>
                <Route exact path={`${prefix}/activas/:idNomina(\\d+)&:idContrato(\\d+)&:idEmpresa(\\d+)&:anio(\\d+)`}> <Nomina /> </Route>

                <Route exact path={`${prefix}/edit-empresa/:id(\\d+)`}> <EditEmpresa /> </Route>
                <Route exact path={`${prefix}/listar-empresa-por/false`} > <TableEmpresa /> </Route>
                <Route exact path={`${prefix}/nuevo-empleado`} > <FormEmpleado /> </Route>
                <Route exact path={`${prefix}/edit-empleado/:id(\\d+)`}> <EditEmpleado /> </Route>
                <Route exact path={`${prefix}/listar-empleado-by/:interna/:externa`}> <TableEmpleado /> </Route>

                <Route exact path={`${prefix}/giro`}><TableGiro /></Route>
                <Route exact path={`${prefix}/regimen-capital`}><TableRegimen /></Route>
                <Route exact path={`${prefix}/regimen-contratacion`}><TableRegimenContratacion /></Route>
                <Route exact path={`${prefix}/jornada`}><TableJornada /></Route>
                <Route exact path={`${prefix}/departamento`}><TableDepartamento /></Route>
                <Route exact path={`${prefix}/tipo-contrato`}><TableContratacion /></Route>
                <Route exact path={`${prefix}/tipo-documento`}><TableDocumento /></Route>
                <Route exact path={`${prefix}/percepcion-deduccion`}><TablePercepcionDeducion /></Route>
                <Route exact path={`${prefix}/percepcion-deduccion-sat`}><TablePercepcionDeduccionSat /></Route>

                <Route path={`${prefix}/*`}>
                    <Redirect from={`${prefix}/*`} to="/page-not-found" />
                </Route>
            </Switch>
        )
        case 'C': return <Switch>
            <Route exact path={`${prefix}`}> <Home /> </Route>
            <Route exact path={`${prefix}/`}> <Home /> </Route>
            <Route exact path={`${prefix}/activas`} > <NominaActivas /> </Route>
            <Route exact path={`${prefix}/activas/:idNomina(\\d+)&:idContrato(\\d+)&:idEmpresa(\\d+)&:anio(\\d+)`}> <Nomina /> </Route>
        </Switch>

        default: return <></>;
    }
}



