export const put = async (data, folio, headers) => {
    return fetch(
        `${BASE_URL}/api/rh/nomina/inasistencia/${folio}`,
        {
            method: 'PUT',
            headers,
            body: JSON.stringify(data)
        }
    ).then(response => response.json());
}