import React, { useEffect, useState, useContext } from 'react'
import { Grid, Avatar, Typography, Card, CardActionArea, CardContent, CardMedia } from '@material-ui/core'
import { makeStyles, createStyles } from '@material-ui/core/styles';
import { deepOrange } from '@material-ui/core/colors';
import ImgEmployes from '../../../assets/img/employes.jpg';
import ImgClient from '../../../assets/img/deal.jpg';
import ImgTower from '../../../assets/img/tower.jpg';
import ImgNomina from '../../../assets/img/nomina.jpg';
import { motion } from "framer-motion";
import { useHistory } from 'react-router-dom';
import getFrase from './frasesEmpleado';
import { DashBoardContext } from '../../../views/adminrh/Dashboard';

const useStyles = makeStyles((theme) =>
    createStyles({
        avatar: {
            marginTop: '5vh',
            marginBottom: '5vh',
            height: '8vh',
            width: '8vh',
            color: theme.palette.getContrastText(deepOrange[500]),
            backgroundColor: '#F57C00'
        }
    }),
);

export default function HomeRoot() {
    const classes = useStyles();
    const [frase, setFrase] = useState({});
    const { setUser } = useContext(DashBoardContext);

    const cards = [
        {
            image: ImgEmployes,
            title: 'Empleados',
            url: '/nomina/listar-empleado-by/all/all',
            descripcion: 'En este apartado podrás crear, editar, subir una foto, descargarlos...',
            autor: 'Photo by Christina Morillo from Pexels'
        },
        {
            image: ImgClient,
            title: 'Representantes',
            url: '/nomina/listar-representante',
            descripcion: 'En este apartado podrás editar a los representantes, crear uno nuevo, descargarlos...',
            autor: 'Photo by fauxels from Pexels'
        },
        {
            image: ImgTower,
            title: 'Empresas',
            url: '/nomina/listar-empresa-por/false',
            descripcion: 'En este apartado podrás editar las empresas, crear una nueva, descargarlas...',
            autor: 'Photo by SevenStorm JUHASZIMRUS from Pexels'
        },
        {
            image: ImgNomina,
            title: 'Nóminas',
            url: '/nomina/activas',
            descripcion: 'En este apartado podrás consultar las nóminas, asignar faltas, descargarlas...',
            autor: 'Photo by Lukas from Pexels'
        },
    ]

    useEffect(() => {
        setFrase(getFrase());
        setUser({ nombre: 'Administrador' })
        // eslint-disable-next-line
    }, [])

    return (
        <Grid
            container
            direction="column"
            justify="flex-start"
            alignItems="center"
            spacing={3}>
            <Grid item>
                <motion.div
                    animate={{ scale: 2 }}
                    transition={{ duration: 0.5 }}
                    whileHover={{ scale: 2.5 }}
                    whileTap={{ scale: 1.8 }}>
                    <Avatar className={classes.avatar}>SU</Avatar>
                </motion.div>
            </Grid>
            <Grid item>
                <Typography variant="h2" color="initial" align="center" display="block">Bienvenido Administrador</Typography>
            </Grid>
            <Grid item>
                <Typography variant="h4" color="initial" align="center" display="block">Tienes el control de todo el sistema!</Typography>
            </Grid>
            <Grid item >
                <motion.div
                    initial="hidden"
                    animate="visible"
                    variants={{
                        visible: { opacity: 1 },
                        hidden: { opacity: 0 },
                    }}>

                    <Typography variant="body1" color="initial" align="center" display="block">
                        <i>{`${frase.frase} `}</i><span>{frase.autor}</span>
                    </Typography>
                </motion.div>
            </Grid>
            <Grid item xs={12}>
                <Grid
                    container
                    direction="row"
                    justify="center"
                    alignItems="center"
                    spacing={3}>
                    {cards.map((e, i) => <Grid item key={`card-element-${i}`}>
                        <CardElement {...e} />
                    </Grid>)}
                </Grid>
            </Grid>
        </Grid>
    )
}


const useStylesCard = makeStyles({
    root: {
        maxWidth: 250,
    },
    media: {
        height: 140,
    },
});

function CardElement({ image, title, url, descripcion, autor }) {
    const history = useHistory();
    const classes = useStylesCard();
    return (
        <motion.div
            initial="hidden"
            animate="visible"
            variants={{
                visible: { opacity: 1 },
                hidden: { opacity: 0 },
            }}
            whileHover={{ scale: 1.1 }}
            whileTap={{ scale: 0.9 }}
        >
            <Card className={classes.root} elevation={3} onClick={() => history.push(url)}>
                <CardActionArea>
                    <CardMedia
                        className={classes.media}
                        image={image}
                        title={autor}
                    />
                    <CardContent>
                        <Typography gutterBottom variant="h5" component="h2">
                            {title}
                        </Typography>
                        <Typography variant="body2" color="textSecondary" component="p">
                            {descripcion}
                        </Typography>
                    </CardContent>
                </CardActionArea>
            </Card>
        </motion.div>
    );
}

