import React from 'react'
import MaterialTable from 'material-table';
import { Typography } from '@material-ui/core'
import { Snackbar } from '@material-ui/core';
import MuiAlert from '@material-ui/lab/Alert';
import { localization, actions, options } from '../../../../libraries/ConfigCatalog';
import { useFactoryLaw } from '../useFactoryLaw';
import { YearMask, LimitMask } from '../MaskLeyes'
import FieldTable from '../FieldTable';

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

export default function TableUMA() {

    const {
        data,
        loading,
        refresh,
        post,
        remove,
        put
    } = useFactoryLaw({ type: 'uma' });
    const [toast, setToast] = React.useState({
        open: false,
        severity: "",
        title: ""
    });


    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            setToast({ ...toast, open: false });
            return;
        }
        setToast({ ...toast, open: false });
    };

    actions[0].onClick = refresh;
    localization.body.editRow.deleteText = <Typography variant="button" gutterBottom>¿Está seguro que desea continuar?</Typography>


    const keys = [
        'anio',
        'diario',
    ]

    const columns = [
        {
            title: 'Año', field: 'anio', defaultSort: 'desc',
            editComponent: props => <FieldTable {...props} placeholder="Ej. 2020"
                InputProps={{ inputComponent: YearMask }} />
        },
        {
            title: 'Diario', field: 'diario',
            editComponent: props => <FieldTable {...props} placeholder="Ej. 15"
                InputProps={{ inputComponent: LimitMask }} />
        },
    ];

    const validAnio = (isNew, newData) => {
        const anios = data.filter(d => d.anio === newData.anio);
        return isNew ? anios.length === 0 : anios.length === 1
    }

    return (
        <>
            <MaterialTable
                title="UMA"
                columns={columns}
                localization={localization}
                data={data}
                actions={actions}
                options={options}
                isLoading={loading}
                editable={{
                    onRowAdd: (newData) =>
                        new Promise((resolve, reject) => {
                            setTimeout(() => {
                                const addKeys = Object.keys(newData);
                                if (!keys.every(key => addKeys.includes(key) && newData[key] !== '')) {
                                    setToast({
                                        open: true,
                                        severity: "warning",
                                        title: "Campos vacios!"
                                    })
                                    reject();
                                    return;
                                } else if (!validAnio(true, newData)) {
                                    setToast({
                                        open: true,
                                        severity: "warning",
                                        title: "Año no puede repetirse!"
                                    })
                                    reject();
                                    return;
                                }
                                else {
                                    resolve();
                                    post({ data: newData }, () => {
                                        setToast({
                                            open: true,
                                            severity: "success",
                                            title: "Creación exitosa",
                                        })
                                    })
                                }

                            }, 600);
                        })
                    ,
                    onRowUpdate: (newData, oldData) =>
                        new Promise((resolve, reject) => {
                            setTimeout(() => {
                                const addKeys = Object.keys(newData);
                                if (!keys.every(key => addKeys.includes(key) && newData[key] !== '')) {
                                    setToast({
                                        open: true,
                                        severity: "warning",
                                        title: "Campos vacios!"
                                    })
                                    reject();
                                    return;
                                } else if (!validAnio(false, newData)) {
                                    setToast({
                                        open: true,
                                        severity: "warning",
                                        title: "Año no puede repetirse!"
                                    })
                                    reject();
                                    return;
                                }
                                else {
                                    resolve();
                                    const { anio, ...data } = newData;
                                    put({ id: anio, data }, () => {
                                        setToast({
                                            open: true,
                                            severity: "success",
                                            title: "Actualización exitosa"
                                        })
                                    })
                                }

                            }, 600);
                        })
                    ,
                    onRowDelete: (oldData) =>
                        new Promise((resolve) => {
                            setTimeout(() => {
                                resolve();
                                remove({ id: oldData['anio'] }, () => {
                                    setToast({
                                        open: true,
                                        severity: "success",
                                        title: "Eliminación exitosa",
                                    })
                                });
                            }, 600);
                        })
                    ,
                }}
            />
            <Snackbar open={toast.open} autoHideDuration={6000} onClose={handleClose}>
                <Alert onClose={handleClose} severity={toast.severity}>
                    {toast.title}
                    {toast.message && toast.message}
                </Alert>
            </Snackbar>
        </>
    );
}