import React, { useContext } from 'react'
import { TableCell, TableRow, Button } from '@material-ui/core'
import { NominaContext } from '../Nomina'

export default function TotalRows({ totales }) {
    const [detalles, setDetalles] = React.useState(false);
    const { format, generalSettings, seePDF } = useContext(NominaContext);
    return (
        <>
            <TableRow>
                <TableCell rowSpan={10} colSpan={6} />
                <TableCell align="left">Nómina</TableCell>
                <TableCell align="right">{format(totales.nomina)}</TableCell>
            </TableRow>
            {
                (detalles || seePDF) &&
                <>

                    <TableRow>
                        <TableCell align="left">Comisión {`%${parseInt(generalSettings.comision * 100)}`}</TableCell>
                        <TableCell align="right">{format(totales.comision)}</TableCell>
                    </TableRow>
                    <TableRow>
                        <TableCell align="left">Costo Social</TableCell>
                        <TableCell align="right">{format(totales.costo_social)}</TableCell>
                    </TableRow>
                    <TableRow>
                        <TableCell align="left">ISN</TableCell>
                        <TableCell align="right">{format(totales.isn)}</TableCell>
                    </TableRow>
                    {
                        generalSettings.imss_aplicado &&
                        <TableRow>
                            <TableCell align="left">IMSS</TableCell>
                            <TableCell align="right">{format(totales.imss)}</TableCell>
                        </TableRow>
                    }
                    {
                        generalSettings.isr_aplicado &&
                        <TableRow>
                            <TableCell align="left">ISR</TableCell>
                            <TableCell align="right">{format(totales.isr)}</TableCell>
                        </TableRow>

                    }
                    <TableRow>
                        <TableCell align="left">Subtotal</TableCell>
                        <TableCell align="right">{format(totales.subtotal)}</TableCell>
                    </TableRow>
                    <TableRow>
                        <TableCell align="left">IVA 16%</TableCell>
                        <TableCell align="right">{format(totales.iva)}</TableCell>
                    </TableRow>
                    <TableRow>
                        <TableCell align="left">Retencion 6%</TableCell>
                        <TableCell align="right">{format(totales.retencion)}</TableCell>
                    </TableRow>
                </>
            }

            <TableRow>
                <TableCell align="left">TOTAL</TableCell>
                <TableCell align="right">{format(totales.total)}</TableCell>
            </TableRow>
            {!seePDF &&
                <TableRow>
                    <TableCell colSpan={2} align="center">
                        <Button style={{ fontSize: 12 }} onClick={
                            () => setDetalles(!detalles)}>
                            Mostrar {`${!detalles ? 'más' : 'menos'}`}
                        </Button>
                    </TableCell>
                </TableRow>
            }
        </>
    )
}