const frases = [
    { frase: "Llegar juntos es el principio. Mantenerse juntos, es el progreso. Trabajar juntos es el éxito.", autor: " Henry Ford" },
    { frase: "El talento gana partidos, pero el trabajo en equipo y la inteligencia ganan campeonatos.", autor: " Michael Jordan" },
    { frase: "Ninguno de nosotros es tan bueno como todos nosotros juntos.", autor: " Ray Kroc" },
    { frase: "La fuerza del equipo viene de cada miembro. La fuerza de cada miembro es el equipo.", autor: " Phil Jackson" },
    { frase: "Si quieres ir rápido, ve solo. Si quieres llegar lejos, ve acompañado.", autor: " Proverbio africano" },
    { frase: "Cuando las arañas tejen juntas, pueden atar a un león.", autor: " Proverbio etíope" },
    { frase: "No hay problema que no podamos resolver juntos, y muy pocos que podamos resolver por nosotros mismos.", autor: " Lyndon Johnson" },
    { frase: "Solos podemos hacer poco, juntos podemos hacer mucho.", autor: " Hellen Keller" },
    { frase: "Un bote no va hacia delante si cada uno rema a su propia manera.", autor: " Proverbio swahili" },
    { frase: "La gente consigue más como resultado de trabajar con otros que contra ellos. ", autor: "Dr. Allan Fromme" }
]

export default function getFrase() {
    const index = Math.floor(Math.random() * (frases.length + 1))
    if (index > 0 && index < frases.length)
        return frases[index];
    else
        return frases[0];
}