import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Grid, TextField, MenuItem, Button, InputAdornment, Typography, IconButton } from '@material-ui/core'
import { KeyboardDatePicker } from '@material-ui/pickers';
import { ArrowBack, ArrowForward } from '@material-ui/icons';
import { ValidacionPorcentaje, ValidacionRegistroPatronal } from '../../../../libraries/Mask'
import moment from 'moment';
import DeleteIcon from '@material-ui/icons/Delete';
import AddIcon from '@material-ui/icons/Add';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import AddCircleOutlineIcon from '@material-ui/icons/AddCircleOutline';
import CloseIcon from '@material-ui/icons/Close';
import DoneAllIcon from '@material-ui/icons/DoneAll';
import RequestFetch from '../../../../libraries/RequestFetch';
import { withSnackbar } from 'notistack';

import { SessionContext } from '../../../../routes/adminrh/CustomRoutes';

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});


const useStyles = makeStyles(theme => ({
    container: {
        padding: theme.spacing(4),
    },
    select: {
        textAlign: 'left'
    },
    radioGroup: {
        marginTop: '15px'
    },
    uppercase: {
        textTransform: 'uppercase'
    }
}));

const periodos = [
    { value: 'S', title: 'Semanal' },
    { value: 'Q', title: 'Quincenal' },
    { value: 'C', title: 'Catorcenal' },
    { value: 'M', title: 'Mensual' },
];

const motivos = [
    { title: "Alta", value: 'A' },
    { title: "Reingreso", value: 'R' },
    { title: "Taslado", value: 'T' }
];



function Contrato(props) {
    const { editable } = props;
    const { data, setData, onlySee, registros, setRegistros } = props;
    const classes = useStyles();
    const { cebeceras, isTokenExpired } = React.useContext(SessionContext);

    const [old, setOld] = React.useState({});
    const [dialog, setDialog] = React.useState({
        open: false
    });

    let categorias = [
        { "categoria": 1, "nombre_categoria": "CLASE 1" },
        { "categoria": 2, "nombre_categoria": "CLASE 2" },
        { "categoria": 3, "nombre_categoria": "CLASE 3" },
        { "categoria": 4, "nombre_categoria": "CLASE 4" },
        { "categoria": 5, "nombre_categoria": "CLASE 5" },
    ]

    let newCategorias = categorias;


    const changeDateInicio = (e, key) => {
        if (e !== null) {
            let newDate = {
                value: e,
                verify: (e instanceof moment && !isNaN(e) && e.isBefore(data.fecha_fin.value !== '' ? data.fecha_fin.value : moment()))
            };
            setData(prevState => {
                return {
                    ...prevState, [key]: newDate
                }
            })
        }
    }

    const changeDateFin = (e, key) => {
        if (e !== null) {
            let newDate = {
                value: e,
                verify: (e instanceof moment && !isNaN(e) && e.isAfter(data.fecha_inicio.value !== '' ? data.fecha_inicio.value : moment()))
            };
            setData(prevState => {
                return {
                    ...prevState, [key]: newDate
                }
            })
        }
    }

    const changeTextRequired = e => {
        const { name, value } = e.target;
        setData(prevState => {
            return {
                ...prevState, [name]: { value: value, verify: `${value}`.length > 0 }
            }
        });
    }

    const changeRegistroPatronalSelect = (e, i) => {
        const { name, value } = e.target;
        let newArray = [...registros];
        newArray[i][name] = value;
        newArray[i].verify = !newArray.some((e, idx) => e.clave_patronal.replace(/\s/g, '') === newArray[i].clave_patronal.replace(/\s/g, '') && i !== idx);
        setRegistros(newArray);
    }

    const changeRegistroPatronalClave = (e, i) => {
        const { name, value } = e.target;
        let newArray = [...registros];
        newArray[i][name] = value.toUpperCase();
        newArray[i].verify = !newArray.some((e, idx) => e.clave_patronal.replace(/\s/g, '') === value.replace(/\s/g, '') && i !== idx);
        !newArray[i].verify ? newArray[i].helperText = 'Repetido' : newArray[i].helperText = '';

        setRegistros(newArray);
    }

    const addRegistroPatronal = () => {
        const newRegistro = [...registros];
        newRegistro.push({ categoria: '', clave_patronal: '', verify: '', helperText: '' });
        setRegistros(newRegistro);
    }

    const removeRegistroPatronal = (index) => {
        if (index > 0) {
            const newRegistro = registros.filter((itm, idx) => idx !== index);
            setRegistros(newRegistro);
        }
        else {
            const newRegi = [...registros];
            newRegi[0] = { categoria: '', clave_patronal: '', verify: '', helperText: '' }
            setRegistros(newRegi)
        }
    }

    const optionRegistros = (value) => {
        newCategorias = categorias.filter(w => !registros.some(u =>
            `${u.categoria}` === `${w.categoria}` &&
            `${u.categoria}` !== `${value}`));
        return newCategorias;
    }

    const RegistrosPantronales = () => {
        const newRegistros = [];
        registros.forEach((i, index) => {
            newRegistros.push(
                <Grid key={`registro-patronal-${index + 1}`}
                    item xs={12} md={12} lg={12}>
                    <Grid container spacing={2}>
                        <Grid item xs={12} sm={6} >
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                label="Clase IMSS"
                                select
                                disabled={onlySee}
                                className={classes.select}
                                value={`${categorias.length > 0 && registros[index].categoria !== '' ? registros[index].categoria : 'DEFAULT'}`}
                                InputLabelProps={{ shrink: true }}
                                name="categoria"
                                onChange={e => changeRegistroPatronalSelect(e, index)}
                                error={registros[index].verify !== '' ? !registros[index].verify : false}>
                                <MenuItem value="DEFAULT" disabled>Selecciona</MenuItem>
                                {optionRegistros(registros[index].categoria).map((elem) => {
                                    return <MenuItem key={`categorias-imss-${elem.categoria}`} value={elem.categoria}>{elem.nombre_categoria}</MenuItem>
                                })}
                            </TextField>
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                variant="outlined"
                                name="clave_patronal"
                                required
                                label={`Clave ${index + 1}`}
                                fullWidth
                                placeholder="Ej. X111 222 333"
                                InputLabelProps={{ shrink: true }}
                                value={registros[index].clave_patronal}
                                onChange={e => changeRegistroPatronalClave(e, index)}
                                error={registros[index].verify !== '' ? !registros[index].verify : false}
                                helperText={registros[index].helperText}
                                inputProps={{ className: classes.uppercase, }}
                                InputProps={{
                                    inputComponent: ValidacionRegistroPatronal,
                                    endAdornment: <InputAdornment position="end">
                                        <IconButton size="small" aria-label="delete" onClick={() => removeRegistroPatronal(index)}>
                                            <DeleteIcon />
                                        </IconButton>
                                    </InputAdornment>
                                }}
                                autoComplete='off'
                            />
                        </Grid>
                    </Grid>
                </Grid>
            )
        })

        return newRegistros;
    }

    const newContrato = () => {
        if (data.new) {
            setData({ ...old, new: false });
            setOld({});
        } else {
            setOld({ ...data });
            const newData = {};
            const keys = Object.keys(data);
            keys.forEach(key => newData[`${key}`] = { value: '', verify: '' });
            newData['new'] = true;
            setData({ ...newData, motivo_contrato: { value: 'A', verify: true } });
        }
    }

    const finishContrato = () => {

        RequestFetch.put({
            url: `${RequestFetch.BASE_URL}/api/rh/empresa/contrato/${data.id_contrato.value}`,
            headers: cebeceras,
            data: { fecha_fin: moment().format("YYYY/MM/DD") },
            exito: async (res) => {
                console.log(await res.json());

                props.enqueueSnackbar("Contrato terminado", { variant: 'success', preventDuplicate: true, })
                const newData = {};
                const newOld = {};
                const keys = Object.keys(data);
                keys.forEach(key => {
                    newData[key] = { value: '', verify: '' }
                    newOld[key] = '';
                });
                newData['new'] = false;
                setData({ ...newData, motivo_contrato: 'A' });
                props.setOldContrato(newOld);
            },
            fallo: async (res) => {
                const response = res.json();
                if (!isTokenExpired(response))
                    console.log(response);
            },
            error_request: async (error) => console.log(await error.json())

        });
    }

    const EsInterna = () => {
        return (
            <Grid container spacing={4} className={classes.container}>
                <Grid item xs={12} sm={12} md={5} lg={5}>
                    <Grid container spacing={2} >
                        <Grid item xs={12} sm={12} md={12} lg={12}>
                            <Typography variant="subtitle2" display="block" style={{ textAlign: 'center' }}>Registros Patronales</Typography>
                        </Grid>
                        {RegistrosPantronales()}
                        {
                            (!onlySee && registros.length < 5) &&
                            <Grid item xs={12} sm={12} md={12} lg={12} container
                                direction="row"
                                justify="flex-end"
                                alignItems="center">
                                <Button
                                    size="small"
                                    color="primary"
                                    onClick={addRegistroPatronal}
                                    startIcon={<AddIcon />}>
                                    Agregar
                                </Button>
                            </Grid>
                        }
                    </Grid>
                </Grid>

                <Grid item xs={12} sm={12} md={7} lg={7}>
                    <Grid container spacing={2} >
                        <Grid item xs={12} sm={12} md={12} lg={12}>
                            <Typography variant="subtitle2" style={{ textAlign: 'center' }} color="initial">Nómina</Typography>
                        </Grid>
                        <Grid item xs={12} sm={6} md={4} lg={5}>
                            <KeyboardDatePicker
                                autoOk
                                disabled={!editable || onlySee}
                                inputVariant="outlined"
                                maxDateMessage={"Fecha es mayor al máximo permitido"}
                                minDateMessage={"Fecha es menor al mínimo permitido"}
                                invalidDateMessage={"Formato invalido"}
                                InputLabelProps={{ shrink: true }}
                                fullWidth required
                                error={data.fecha_inicio.verify !== '' ? !data.fecha_inicio.verify : false}
                                placeholder="Día/Mes/Año"
                                id="fecha_inicio"
                                label="Fecha Inicio"
                                format="DD/MM/YYYY"
                                value={data.fecha_inicio.value !== '' ? moment(data.fecha_inicio.value) : null}
                                maxDate={data.fecha_fin.value !== '' ? moment(data.fecha_fin.value).add(1, 'months') : moment()}
                                onChange={e => changeDateInicio(e, "fecha_inicio")}
                                autoComplete='off'
                            />
                        </Grid>
                        <Grid item xs={12} sm={6} md={4} lg={4}>
                            <TextField
                                disabled={!editable || onlySee}
                                variant="outlined"
                                required
                                fullWidth
                                label="Periodo"
                                select
                                name="periodo_calculo"
                                className={classes.select}
                                value={`${periodos.length > 0 && data.periodo_calculo.value !== '' ? data.periodo_calculo.value : 'DEFAULT'}`}
                                InputLabelProps={{ shrink: true }}
                                onChange={changeTextRequired}
                                error={data.periodo_calculo.verify !== '' ? !data.periodo_calculo.verify : false}>
                                <MenuItem value="DEFAULT" disabled>Selecciona</MenuItem>
                                {
                                    periodos.map((item) => {
                                        return <MenuItem key={`periodo_calculo-${item.value}`} value={item.value}>{item.title}</MenuItem>
                                    })
                                }
                            </TextField>
                        </Grid>
                        <Grid item xs={12} sm={6} md={4} lg={3}>
                            <TextField
                                disabled={true}
                                variant="outlined"
                                required
                                label="Comisión"
                                fullWidth
                                name="comision"
                                placeholder="0.0"
                                InputLabelProps={{ shrink: true, }}
                                InputProps={{
                                    inputComponent: ValidacionPorcentaje,
                                    endAdornment: <InputAdornment position="end">%</InputAdornment>
                                }}
                                autoComplete='off'
                            />
                        </Grid>
                        {
                            !props.expandable &&
                            <Grid container item
                                direction="row"
                                justify="flex-end"
                                alignItems="center" xs={12} sm={12} md={12} lg={12}>
                                <Button startIcon={<ArrowBack />}
                                    onClick={props.buttons.back}>Anterior</Button>

                                <Button
                                    variant="outlined"
                                    color="primary"
                                    endIcon={<ArrowForward />}
                                    onClick={props.buttons.next}
                                >Siguiente</Button>
                            </Grid>
                        }
                    </Grid>
                </Grid>
            </Grid>
        )
    }

    const EsExterna = () => {
        return (
            <Grid container spacing={3} className={classes.container}>
                <Grid item xs={12} sm={6} md={4} lg={3}>
                    <KeyboardDatePicker
                        autoOk
                        disabled={(editable && !data.new) || onlySee}
                        inputVariant="outlined"
                        maxDateMessage={"Fecha es mayor al máximo permitido"}
                        minDateMessage={"Fecha es menor al mínimo permitido"}
                        invalidDateMessage={"Formato invalido"}
                        InputLabelProps={{ shrink: true }}
                        fullWidth required
                        error={data.fecha_inicio.verify !== '' ? !data.fecha_inicio.verify : false}
                        placeholder="Día/Mes/Año"
                        id="fecha_inicio"
                        label="Fecha Inicio"
                        format="DD/MM/YYYY"
                        value={data.fecha_inicio.value !== '' ? moment(data.fecha_inicio.value) : null}
                        maxDate={data.fecha_fin.value !== '' ? moment(data.fecha_fin.value).subtract(1, 'days') : moment()}
                        onChange={e => changeDateInicio(e, "fecha_inicio")}
                        autoComplete='off'
                    />
                </Grid>
                <Grid item xs={12} sm={6} md={4} lg={3}>
                    <KeyboardDatePicker
                        autoOk
                        disabled={(editable && !data.new) || onlySee}
                        inputVariant="outlined"
                        maxDateMessage={"Fecha es mayor al máximo permitido"}
                        minDateMessage={"Fecha es menor al mínimo permitido"}
                        invalidDateMessage={"Formato invalido"}
                        InputLabelProps={{ shrink: true }}
                        fullWidth required
                        error={data.fecha_fin.verify !== '' ? !data.fecha_fin.verify : false}
                        placeholder="Día/Mes/Año"
                        id="fecha_fin"
                        label="Fecha Fin"
                        format="DD/MM/YYYY"
                        value={data.fecha_fin.value !== '' ? moment(data.fecha_fin.value) : null}
                        minDate={data.fecha_inicio.value !== '' ? moment(data.fecha_inicio.value).add(1, 'days') : moment().add(1, 'days')}
                        onChange={e => changeDateFin(e, "fecha_fin")}
                        autoComplete='off'
                    />
                </Grid>
                <Grid item xs={12} sm={4} md={4} lg={2}>
                    <TextField
                        disabled={(editable && !data.new) || onlySee}
                        variant="outlined"
                        required
                        label="Comisión"
                        fullWidth
                        name="comision"
                        placeholder="Ej. 10"
                        InputLabelProps={{ shrink: true, }}
                        value={data.comision.value}
                        onChange={changeTextRequired}
                        InputProps={{
                            inputComponent: ValidacionPorcentaje,
                            endAdornment: <InputAdornment position="end">%</InputAdornment>
                        }}
                        error={data.comision.verify !== '' ? !data.comision.verify : false}
                        autoComplete='off'
                    />
                </Grid>
                <Grid item xs={12} sm={4} md={4} lg={2}>
                    <TextField
                        disabled={(editable && !data.new) || onlySee}
                        variant="outlined"
                        required
                        fullWidth
                        label="Periodo"
                        select
                        name="periodo_calculo"
                        className={classes.select}
                        value={`${periodos.length > 0 && data.periodo_calculo.value !== '' ? data.periodo_calculo.value : 'DEFAULT'}`}
                        InputLabelProps={{ shrink: true }}
                        onChange={changeTextRequired}
                        error={data.periodo_calculo.verify !== '' ? !data.periodo_calculo.verify : false}>
                        <MenuItem value="DEFAULT" disabled>Selecciona</MenuItem>
                        {
                            periodos.map((item) => {
                                return <MenuItem key={`periodo_calculo-${item.value}`} value={item.value}>{item.title}</MenuItem>
                            })
                        }
                    </TextField>
                </Grid>
                <Grid item xs={12} sm={4} md={4} lg={2}>
                    <TextField
                        disabled={(editable && !data.new) || onlySee}
                        variant="outlined"
                        required
                        fullWidth
                        label="Motivo"
                        select
                        name="motivo_contrato"
                        className={classes.select}
                        value={`${motivos.length > 0 && data.motivo_contrato.value !== '' ? data.motivo_contrato.value : 'DEFAULT'}`}
                        InputLabelProps={{ shrink: true }}
                        onChange={changeTextRequired}
                        error={data.motivo_contrato.verify !== '' ? !data.motivo_contrato.verify : false}>
                        <MenuItem value="DEFAULT" disabled>Selecciona</MenuItem>
                        {
                            motivos.map((item) => {
                                return <MenuItem key={`motivo_contrato-${item.value}`} value={item.value}>{item.title}</MenuItem>
                            })
                        }
                    </TextField>
                </Grid>

                {
                    !props.expandable &&
                    <>
                        {
                            editable &&
                            <Grid container item
                                direction="row"
                                justify="flex-end"
                                alignItems="center" xs={12} sm={12} md={12} lg={12} >
                                {console.log(data)}
                                {
                                    (data['id_contrato']['value'] !== undefined && data['id_contrato']['value'] !== '') &&
                                    <Button
                                        color="secondary"
                                        startIcon={<DoneAllIcon />}
                                        onClick={() => setDialog({ open: true, tipo: 'FIN' })}
                                    >{`${'Terminar Contrato'}`}</Button>
                                }

                                <Button
                                    color="primary"
                                    endIcon={data.new ? <CloseIcon /> : <AddCircleOutlineIcon />}
                                    onClick={data.new ? newContrato : () => setDialog({ open: true, tipo: 'NEW' })}
                                >{`${data.new ? 'Cancelar' : 'Nuevo Contrato'}`}</Button>
                            </Grid>
                        }
                        <Grid container item
                            direction="row"
                            justify="flex-end"
                            alignItems="center" xs={12} sm={12} md={12} lg={12}>
                            <Button startIcon={<ArrowBack />}
                                onClick={props.buttons.back}>Anterior</Button>

                            <Button
                                variant="outlined"
                                color="primary"
                                endIcon={<ArrowForward />}
                                onClick={props.buttons.next}
                            >Siguiente</Button>
                        </Grid>
                    </>
                }
                <Dialog
                    open={dialog.open}
                    TransitionComponent={Transition}
                    onClose={() => setDialog({ open: false })}>
                    <DialogTitle>{`${dialog.tipo === 'NEW' ? 'Nuevo' : 'Terminar'} Contrato`}</DialogTitle>
                    <DialogContent>
                        <DialogContentText>
                            {`${dialog.tipo === 'NEW' ?
                                'Está apunto de crear un nuevo contrato, si existe un anterior este finalizará con fecha de hoy' :
                                'El contrato finalizará con fecha de hoy'} ${moment().format('DD/MM/YYYY')}, `}

                        ¿Está seguro que desea continuar?
                    </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={() => setDialog({ open: false })}>Cancelar</Button>
                        <Button onClick={() => {
                            dialog.tipo === 'NEW' ? newContrato() : finishContrato();
                            setDialog({ open: false });
                        }}
                            color="secondary">Continuar</Button>
                    </DialogActions>
                </Dialog>

            </Grid>
        )
    }
    return (
        <>
            {
                props.isInterna === 'true' ? EsInterna() : EsExterna()
            }
        </>

    )
}

export default withSnackbar(Contrato);