import React from "react";
import { useHistory } from "react-router-dom";
import {
  useStyles,
  textEstatus,
  textButton,
  textTipoNomina,
} from "./CartNominaStyle";
import Card from "@material-ui/core/Card";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import MomentComponent from "react-moment";
import moment from "moment";
import Avatar from "@material-ui/core/Avatar";

export const CardNomina = (props) => {
  const history = useHistory();
  const {
    fecha_inicio_periodo,
    fecha_fin_periodo,
    estatus,
    razon_social,
    periodo_calculo,
    id_contrato, id_nomina, id_empresa
  } = props.data;

  const pushNomina = () => {
    const anio = moment.utc(fecha_fin_periodo).format('YYYY');
    history.push(`/nomina/activas/${id_nomina}&${id_contrato}&${id_empresa}&${anio}`)
  }

  const classes = useStyles(estatus)();

  return (
    <Card className={classes.root}>
      <Grid container justify="center" alignItems="center" spacing={1}>
        <Grid item sm={12} md={2} lg={2}>
          <Avatar className={classes.avatar}>
            {razon_social.substring(0, 1)}
          </Avatar>
        </Grid>
        <Grid item sm={12} md={7} lg={7}>
          <Typography variant="h6" component="h6">
            Nomina {textTipoNomina(periodo_calculo)}:
          </Typography>
          <Typography variant="body2" component="h6">
            {razon_social}
          </Typography>
          <Typography variant="body2" component="h6">
            Periodo:
            <br />
            <MomentComponent format="DD/MM/YYYY">
              {fecha_inicio_periodo}
            </MomentComponent>
            {" - "}
            <MomentComponent format="DD/MM/YYYY">
              {fecha_fin_periodo}
            </MomentComponent>
          </Typography>
        </Grid>
        <Grid item sm={12} md={2} lg={2}>
          <Button variant="contained" className={classes.btnMain}
            onClick={pushNomina}>
            {textButton(estatus)}
          </Button>
          <div className={classes.containerSatus}>
            <Typography variant="subtitle2" component="h6">
              {textEstatus(estatus)}
            </Typography>
          </div>
        </Grid>
      </Grid>
    </Card>
  );
};
