import React, { useEffect, useState, useContext } from 'react'
import { Grid, Avatar, Typography, Card, CardActionArea, CardContent, CardMedia } from '@material-ui/core'
import { makeStyles, createStyles } from '@material-ui/core/styles';
import { deepOrange } from '@material-ui/core/colors';
import ImgTower from '../../../assets/img/tower.jpg';
import { motion } from "framer-motion";
import { useHistory } from 'react-router-dom';
import getFrase from './frasesEmpleado';
import { SessionContext } from '../../../routes/adminrh/CustomRoutes';
import { BASE_URL } from '../../../libraries/RequestFetch';
import { DashBoardContext } from '../../../views/adminrh/Dashboard';

const useStyles = makeStyles((theme) =>
    createStyles({
        avatar: {
            marginTop: '5vh',
            marginBottom: '5vh',
            height: '8vh',
            width: '8vh',
            color: theme.palette.getContrastText(deepOrange[500]),
            backgroundColor: '#F57C00'
        },
    }),
);

export default function Root() {
    const classes = useStyles();
    const [frase, setFrase] = useState({});
    const { rfc, permisos, isTokenExpired, rol, cabeceras } = useContext(SessionContext);
    const [data, setData] = useState();
    const { setUser } = useContext(DashBoardContext);

    const getData = async (url, headers, key) => {
        try {
            const resp = await fetch(url, { method: 'GET', headers })
            const response = await resp.json();
            if (!isTokenExpired(response)) {
                const { url_foto, nombre } = response[key][0]
                setData(response[key][0]);
                setUser({ nombre: nombre, url: url_foto });
            }
        } catch (error) {

        }
    }

    useEffect(() => {
        setFrase(getFrase());
        let url = '';
        let headers = {};
        if (rol === 'G') {
            headers = new Headers();
            headers.append('authorization', cabeceras.authorization);
            url = BASE_URL + `/api/rh/empleado?query=nombre-url_foto-genero&rfc=${rfc}`
            getData(url, headers, 'empleados');
        }
        else if (rol === 'C') {
            url = BASE_URL + `/api/rh/representante?query=nombre-genero&rfc=${rfc}`
            getData(url, cabeceras, 'representantes');
        }
        // eslint-disable-next-line
    }, [])

    if (!data) return null;

    return (
        <div style={{ padding: 20 }}>

            <Grid
                container
                direction="column"
                justify="flex-start"
                alignItems="center"
                spacing={3}
            >
                <Grid item>
                    <motion.div
                        animate={{ scale: 2 }}
                        transition={{ duration: 0.5 }}
                        whileHover={{ scale: 2.5 }}
                        whileTap={{ scale: 1.8 }}>
                        {
                            data && data.url_foto ?
                                <Avatar src={`http://res.cloudinary.com/get-consultoria/image/upload/v1/${data.url_foto}`} className={classes.avatar} /> :
                                <Avatar className={classes.avatar}>{
                                    data ? data.nombre && data.nombre[0]
                                        :
                                        'U'
                                }</Avatar>
                        }
                    </motion.div>
                </Grid>
                <Grid item>
                    <Typography variant="h2" color="initial" align="center" display="block">
                        {data && data.genero === 'F' ? 'Bienvenida ' : 'Bienvenido '}
                        {data && data.nombre}
                    </Typography>
                </Grid>

                <Grid item>
                    <motion.div
                        initial="hidden"
                        animate="visible"
                        variants={{
                            visible: { opacity: 1 },
                            hidden: { opacity: 0 },
                        }}>
                        <Typography variant="h4" color="initial" align="center" display="block">
                            {rol === 'C' ?
                                'Eres nuestra prioridad.'
                                :
                                'Somos un equipo!'
                            }
                        </Typography>
                    </motion.div>
                </Grid>

                <Grid item xs={12} md={8} lg={6}>
                    <motion.div
                        initial="hidden"
                        animate="visible"
                        variants={{
                            visible: { opacity: 1 },
                            hidden: { opacity: 0 },
                        }}>
                        <Typography variant="body1" color="initial" align="center" display="block">
                            {
                                rol === 'C' ?
                                    'Contamos con el mejor equipo en la gestión integral de talento humano, lo que hace que podamos colaborar en la administración de tu nómina de manera eficiente, así como en la gestión del modelo de negocio adecuado, aportando de inmediato beneficios tangibles como ahorro de dinero y tiempo.'
                                    :
                                    <> <i>"{frase.frase}"</i><span>{frase.autor}</span> </>
                            }
                        </Typography>
                    </motion.div>
                </Grid>
                <Grid item xs={12}>
                    <Grid
                        container
                        direction="row"
                        justify="center"
                        alignItems="center"
                        spacing={3}>
                        {permisos.map((e, i) => <Grid item key={`card-element-${i}`}>
                            <CardElement permiso={e} />
                        </Grid>)}
                    </Grid>
                </Grid>
            </Grid>
        </div>
    )
}


const useStylesCard = makeStyles({
    root: {
        maxWidth: 250,
    },
    media: {
        height: 140,
    },
});

function CardElement({ permiso }) {
    const { isTokenExpired, rol, cabeceras } = useContext(SessionContext);
    const history = useHistory();
    const classes = useStylesCard();
    const [data, setData] = useState();


    const getData = async () => {
        try {
            const headers = new Headers();
            headers.append('authorization', cabeceras.authorization);
            const resp = await fetch(`${BASE_URL}/api/rh/empresa?query=razon_social-url_logo&id_empresa=${permiso}`, { method: 'GET', headers })
            const response = await resp.json();
            if (!isTokenExpired(response)) {
                setData(response.empresas[0]);
            }
        } catch (error) {

        }
    }

    useEffect(() => {
        getData();
        // eslint-disable-next-line
    }, [])

    if (!data) return null;

    const getImage = () => {
        if (!data.url_logo) return ImgTower;
        else return `http://res.cloudinary.com/get-consultoria/image/upload/v1/${data.url_logo}`;
    }

    return (
        <motion.div
            initial="hidden"
            animate="visible"
            variants={{
                visible: { opacity: 1 },
                hidden: { opacity: 0 },
            }}
            whileHover={{ scale: 1.1 }}
            whileTap={{ scale: 0.9 }}
        >
            <Card className={classes.root} elevation={4} onClick={() => {
                if (rol === 'G')
                    history.push(`/nomina/edit-empresa/${permiso}`);
            }}>
                <CardActionArea>
                    <CardMedia
                        className={classes.media}
                        image={getImage()}
                        title={data.razon_social}
                    />
                    <CardContent>
                        <Typography gutterBottom variant="h5" component="h2" noWrap>
                            {data && data.razon_social && data.razon_social}
                        </Typography>
                        <Typography variant="body2" color="textSecondary" component="p" paragraph>
                            {rol === 'C' ?
                                "Nos comprometemos a obtener el mejor resultado favorable para nuestros clientes."
                                :
                                "Aqui podras ver la informacion de la empresa, el calendario de las nóminas y los documentos..."
                            }
                        </Typography>
                    </CardContent>
                </CardActionArea>
            </Card>
        </motion.div>
    );
}



